<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.id') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`jobId`])">
								{{ `(${curLengthCnt[`jobId`]}/${maxLengthCnt[`jobId`]})` }}
							</span>
						</span>
						<b class="icon-star"></b>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`jobId`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`jobId`]) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="jobId"
							vModel="jobId"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`jobId`]),
								'regExp-text': !getReadonly(regExpInfo[`jobId`]),
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly || getReadonly(regExpInfo[`jobId`])"
							regExpType="id"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.name') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`jobName`])">
								{{ `(${curLengthCnt[`jobName`]}/${maxLengthCnt[`jobName`]})` }}
							</span>
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`jobName`]) ? 'regExp' : 'common'}`"
						:style="{
							'padding-left': `${!getReadonly(regExpInfo[`jobName`]) ? '8px' : '0px'}`
						}"
					>
						<inputText
							ref="jobName"
							vModel="jobName"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`jobName`]),
								'regExp-text': !getReadonly(regExpInfo[`jobName`]),
								'view-disabled': true
							}"
							:readonly="readonly"
							regExpType="name"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('igate.job.concurrentExecuteYn') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="concurrentExecuteYn"
							vModel="concurrentExecuteYn"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Yn',
									orderByKey: true
								},
								optionListName: 'concurrentExecuteYns',
								optionFor: 'option in concurrentExecuteYns',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('igate.operation') }} {{ this.$t('head.id') }} </span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<div class="input-group">
							<inputText
								ref="operationId"
								vModel="operationId"
								:classObject="{ 'form-control': true, 'view-disabled': true }"
								:readonly="true"
							/>
							<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openOperationModal" v-if="!readonly">
								<i class="icon-srch"></i>{{ $t('head.search') }}
							</button>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="$refs[`operationId`].setData(null)"
								v-if="!readonly"
								:title="$t('head.initialize')"
							>
								<i class="icon-reset" style="margin-right: 0px"></i>
							</button>
						</div>
					</div>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('common.type') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="jobType"
							vModel="jobType"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Job.JobType',
									orderByKey: true
								},
								optionListName: 'jobTypes',
								optionFor: 'option in jobTypes',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.job.cronExpression') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`cronExpression`])">
								{{ `(${curLengthCnt[`cronExpression`]}/${maxLengthCnt[`cronExpression`]})` }}
							</span>
						</span>
					</label>
					<div class="detail-content-common">
						<div class="input-group">
							<inputText
								ref="cronExpression"
								vModel="cronExpression"
								:classObject="{ 'form-control': true, 'view-disabled': true }"
								regExpType="cron"
								@setLengthCnt="setLengthCnt"
								:readonly="readonly"
							/>
							<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openCronModal" v-if="!readonly">
								<i class="icon-srch"></i>{{ $t('head.search') }}
							</button>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="$refs[`cronExpression`].setData(null)"
								v-if="!readonly"
								:title="$t('head.initialize')"
							>
								<i class="icon-reset" style="margin-right: 0px"></i>
							</button>
						</div>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('igate.job.calendarId') }}</span>
					</label>
					<div class="detail-content-common">
						<div class="input-group">
							<inputText
								ref="calendarId"
								vModel="calendarId"
								:classObject="{ 'form-control': true, 'view-disabled': true }"
								:readonly="true"
							/>
							<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openCalendarModal" v-if="!readonly">
								<i class="icon-srch"></i>{{ $t('head.search') }}
							</button>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="$refs[`calendarId`].setData(null)"
								v-if="!readonly"
								:title="$t('head.initialize')"
							>
								<i class="icon-reset" style="margin-right: 0px"></i>
							</button>
						</div>
					</div>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('head.log.level') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="jobLogLevel"
							vModel="jobLogLevel"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.LogLevel',
									orderByKey: false
								},
								optionListName: 'jobLogLevels',
								optionFor: 'option in jobLogLevels',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('igate.job.logResultYn') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="logResultYn"
							vModel="logResultYn"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Yn',
									orderByKey: true
								},
								optionListName: 'logResultYns',
								optionFor: 'option in logResultYns',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
						/>
					</div>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.description') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`jobDesc`])">
								{{ `(${curLengthCnt[`jobDesc`]}/${maxLengthCnt[`jobDesc`]})` }}
							</span>
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`jobDesc`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`jobDesc`]) ? '8px' : '0px'}` }"
					>
						<textArea
							ref="jobDesc"
							vModel="jobDesc"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`jobDesc`]),
								'regExp-textarea': !getReadonly(regExpInfo[`jobDesc`]),
								'view-disabled': true
							}"
							:readonly="readonly"
							regExpType="desc"
							height="60px"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import { getRegExpInfo } from '@/utils/common.js';
import DetailBasic from '@/components/detail/DetailBasic.vue';

export default {
	name: 'JobBasicInfo',
	extends: DetailBasic,
	data() {
		return {
			keyArr: [
				'jobId',
				'jobName',
				'jobType',
				'calendarId',
				'cronExpression',
				'operationId',
				'jobLogLevel',
				'concurrentExecuteYn',
				'logResultYn',
				'jobDesc'
			],

			regExpInfo: {
				jobId: {
					regExpType: 'id',
					isPK: true,
					readonly: false
				},
				jobName: {
					regExpType: 'name',
					isPK: false,
					readonly: false
				},
				jobDesc: {
					regExpType: 'desc',
					isPK: false,
					readonly: false
				},
				cronExpression: {
					regExpType: 'cron',
					isPK: false,
					readonly: false
				}
			}
		};
	},
	methods: {
		getReadonly(colInfo) {
			return this.readonly || (colInfo.isPK && 'modify' === this.mode);
		},
		getData() {
			let rtnObj = {};

			for (let key in this.$refs) {
				if (0 === this.$refs[key].length) continue;

				rtnObj = merge(rtnObj, this.$refs[key].getData());
			}

			return rtnObj;
		},
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;

				const regExpType = !Object.keys(this.regExpInfo).includes(key) ? 'default' : this.regExpInfo[key].regExpType;

				this.setLengthCnt({ [key]: 0 });
				this.maxLengthCnt[key] = getRegExpInfo(regExpType).maxLength;
			});

			for (const key in this.$refs) {
				if (!this.$refs[key].setData) continue;
				this.$refs[key].setData(detailDataFormalize[this.$refs[key]._props.vModel]);
			}
		},
		openCalendarModal() {
			const _this = this;
			const modalInstance = this.$openModal({
				title: this.$t('igate.calendar'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'calendarId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'text',
										vModel: 'calendarName',
										label: this.$t('head.name'),
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'text',
										vModel: 'calendarDesc',
										label: this.$t('head.description'),
										placeholder: this.$t('head.searchComment'),
										regExpType: 'desc'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										placeholder: this.$t('head.searchCoditionList'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/calendar/searchPopup.json',
									totalCntUrl: '/igate/calendar/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'calendarId',
												width: '20%'
											},
											{
												header: this.$t('head.name'),
												name: 'calendarName',
												width: '20%'
											},
											{
												header: this.$t('head.description'),
												name: 'calendarDesc',
												width: '20%'
											},
											{
												header: this.$t('igate.calendar.saturday'),
												name: 'saturdayYn',
												width: '20%'
											},
											{
												header: this.$t('igate.calendar.sunday'),
												name: 'sundayYn',
												width: '20%'
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs[`calendarId`].setData(info.calendarId);
								modalInstance.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		},
		openCronModal() {
			let cron = null;
			const data = this.$refs['cronExpression'].getData().cronExpression;
			const regexr =
				// eslint-disable-next-line no-useless-escape
				/^\s*($|#|\w+\s*=|(\?|\*|(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?(?:,(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?)*)\s(\?|\*|(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?(?:,(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?)*)\s(\?|\*|(?:[01]?\d|2[0-3])(?:(?:-|\/|\,)(?:[01]?\d|2[0-3]))?(?:,(?:[01]?\d|2[0-3])(?:(?:-|\/|\,)(?:[01]?\d|2[0-3]))?)*)\s(\?|\*|(?:0?[1-9]|[12]\d|3[01])(?:(?:-|\/|\,)(?:0?[1-9]|[12]\d|3[01]))?(?:,(?:0?[1-9]|[12]\d|3[01])(?:(?:-|\/|\,)(?:0?[1-9]|[12]\d|3[01]))?)*)\s(\?|\*|(?:[1-9]|1[012])(?:(?:-|\/|\,)(?:[1-9]|1[012]))?(?:L|W)?(?:,(?:[1-9]|1[012])(?:(?:-|\/|\,)(?:[1-9]|1[012]))?(?:L|W)?)*|\?|\*|(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC)(?:(?:-)(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC))?(?:,(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC)(?:(?:-)(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC))?)*)\s(\?|\*|(?:[0-6])(?:(?:-|\/|\,|#)(?:[0-6]))?(?:,(?:[0-6])(?:(?:-|\/|\,|#)(?:[0-6]))?)*|\?|\*|(?:MON|TUE|WED|THU|FRI|SAT|SUN)(?:(?:-)(?:MON|TUE|WED|THU|FRI|SAT|SUN))?(?:,(?:MON|TUE|WED|THU|FRI|SAT|SUN)(?:(?:-)(?:MON|TUE|WED|THU|FRI|SAT|SUN))?)*))$/i;

			if (data) {
				cron = data.split(' ');

				if (!regexr.test(data) || cron[0] == '' || cron[3] == cron[5]) {
					this.$alert({ type: 'warn', message: this.$t('igate.job.error') });
					return;
				}
			} else {
				cron = ['*', '*', '*', '*', '*', '?'];
			}

			const _this = this;
			const modalInstance = this.$openModal({
				title: this.$t('igate.job.cronExpression'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
										<CronExpressionModal ref="CronExpressionModal" :cron="cron" />
									</div>`,
						components: {
							CronExpressionModal: require(`@/views/modal/job/CronExpressionModal.vue`).default
						},
						data() {
							return {
								cron: cron
							};
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" class="btn btn-primary" @click="clickOk">{{ $t('head.ok') }}</button>`,
						methods: {
							clickOk() {
								_this.$refs[`cronExpression`].setData(modalInstance.$refs.content[0].$refs.CronExpressionModal.cronResult);

								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		openOperationModal() {
			const _this = this;
			const modalInstance = this.$openModal({
				title: this.$t('igate.operation'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'operationId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'searchId'
									},
									{
										type: 'text',
										vModel: 'operationName',
										label: this.$t('head.name'),
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'select',
										vModel: 'operationType',
										label: this.$t('common.type'),
										val: 'J',
										optionInfo: {
											url: '/common/property/properties.json',
											params: {
												propertyId: 'List.Operation.OperationType',
												orderByKey: true
											},
											optionListName: 'operationTypeList',
											optionFor: 'option in operationTypeList',
											optionValue: 'option.pk.propertyKey',
											optionText: 'option.propertyValue',
											optionIf: "option.pk.propertyKey === 'J'",
											isViewAll: false
										}
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										placeholder: this.$t('head.searchCoditionList'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/operation/searchPopup.json',
									totalCntUrl: '/igate/operation/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'operationId'
											},
											{
												header: this.$t('head.name'),
												name: 'operationName'
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs[`operationId`].setData(info.operationId);
								modalInstance.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
