<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<input
						type="text"
						v-model="object.interfaceId"
						class="form-control view-disabled dataKey"
						:readonly="'add' !== this.mode && 'clone' !== this.mode"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.name') }}</span>
						<b class="icon-star"></b>
					</label>
					<input type="text" v-model="object.interfaceName" class="form-control view-disabled" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.private') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privateYn" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in privateYnList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.type') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.interfaceType" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in interfaceTypeList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.fieldMeta.metaDomain') }} </span>
					</label>
					<input type="text" v-model="object.metaDomain" class="form-control view-disabled dataKey" :readonly="readonly" />
				</div>

				<div class="form-group">
					<label class="control-label">
						<span>{{ `${$t('igate.adapter')} ${$t('head.id')}` }} </span>
					</label>
					<input type="text" v-model="object.adapterId" class="form-control view-disabled dataKey" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.operation') }} </span>
					</label>
					<div class="input-group" :title="object.interfaceOperation">
						<input type="text" v-model="object.interfaceOperation" class="form-control view-disabled" :readonly="readonly" />
						<button class="btn btn-icon search-linkBtn" :title="$t('head.open')" @click="openOperationModal()">
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ `${$t(`head.schedule`)} ${$t('common.type')}` }} </span>
					</label>
					<select v-model="object.scheduleType" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in scheduleTypeList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.interface.cronExpression') }} </span>
					</label>
					<input type="text" v-model="object.cronExpression" class="form-control view-disabled" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.calendar') }}</span>
					</label>
					<input type="text" v-model="object.calendarId" class="form-control view-disabled dataKey" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.interface.disabledYn') }}</span>
					</label>
					<select v-model="object.disabledYn" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in disabledYnList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.group') }}</span>
					</label>
					<input type="text" v-model="object.interfaceGroup" class="form-control view-disabled" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.privilege') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privilegeId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in privilegeTypeList" :key="option.privilegeId" :value="option.privilegeId" :disabled="option.disabled">
							{{ option.privilegeId }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.description') }}</span>
					</label>
					<textarea v-model="object.interfaceDesc" class="form-control view-disabled" :readonly="readonly" style="height: 200px" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import { HttpReq, parseHierarchyObj } from '@/utils/common.js';
import cloneDeep from 'lodash/cloneDeep';

export default {
	name: 'InterfaceBasicInfo',
	props: {
		detailData: Object,
		mode: String
	},
	data() {
		return {
			privateYnList: [],
			privilegeTypeList: [],
			interfaceTypeList: [],
			scheduleTypeList: [],
			disabledYnList: [],

			object: {
				interfaceId: null,
				interfaceName: null,
				interfaceType: null,
				privilegeId: null,
				interfaceGroup: null,
				privateYn: null,
				adapterId: null,
				interfaceOperation: null,
				scheduleType: null,
				metaDomain: null,
				cronExpression: null,
				disabledYn: null,
				interfaceDesc: null
			},

			httpReqList: [
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Interface.InterfaceType', orderByKey: true },
					itemName: 'interfaceTypeList'
				},
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Interface.ScheduleType', orderByKey: true },
					itemName: 'scheduleTypeList'
				},
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Yn', orderByKey: true },
					itemName: 'privateYnList'
				},
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Yn', orderByKey: true },
					itemName: 'disabledYnList'
				},
				{
					url: `/common/privilege/list.json`,
					param: { privilegeType: 'b' },
					itemName: 'privilegeTypeList'
				}
			]
		};
	},
	watch: {
		detailData() {
			for (const key in this.object) {
				this.object[key] = 'undefined' === typeof this.detailData[key] ? null : this.detailData[key];
			}
		},
		mode() {
			if ('add' === this.mode) {
				for (const key in this.object) {
					this.object[key] = null;
				}
			}
		}
	},
	computed: {
		...mapState(['menuList', 'userInfo']),
		...mapGetters(['isUserAdmin']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},

	async created() {
		const promiseAllList = [];

		this.httpReqList.forEach(content => {
			promiseAllList.push(
				new Promise((resolve, reject) => {
					(async () => {
						const result = await new HttpReq(content.url).read(content.param ? content.param : null);

						if ('ok' === result.data.result) {
							if (`privilegeTypeList` === content.itemName) {
								const privilegeTypeList = result.data.object;

								const bTotalUserPrivileges = this.userInfo.totalUserPrivileges.filter(
									userPrivilege => `b` === userPrivilege.privilegeType
								);

								for (const info of privilegeTypeList) {
									if (this.isUserAdmin) {
										info.disabled = false;
									} else {
										info.disabled =
											`undefined` ===
											typeof bTotalUserPrivileges.find(userPrivilege => userPrivilege.privilegeId == info.privilegeId);
									}
								}

								this.privilegeTypeList = privilegeTypeList;
							} else {
								this[content.itemName] = result.data.object;
							}

							resolve(result);
						} else {
							reject(result);
						}
					})();
				})
			);
		});

		try {
			const resultList = await Promise.all(promiseAllList);

			for (let result of resultList) {
				if ('error' === result.data.result) throw result.data;
			}
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('head.error.load.data.warn') });
		}
	},
	methods: {
		async openOperationModal() {
			const operationId = this.object.interfaceOperation;

			if (!operationId) {
				this.$alert({ type: 'warn', message: this.$t('head.no.data.warn') });
				return;
			}

			this.$openModal({
				title: this.$t('igate.operation'),
				fullscreen: true,
				contentList: [
					{
						template: `<OperationModal ref="OperationModal" :config="config"/>`,
						components: {
							OperationModal: require(`@/views/modal/operation/OperationModal.vue`).default
						},
						data() {
							return {
								config: {
									operationId: operationId
								}
							};
						}
					}
				]
			});
		},
		getData() {
			return {
				...parseHierarchyObj(cloneDeep(this.object)),
				publishStatus: this.detailData.publishStatus
			};
		}
	}
};
</script>
