<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t(`igate.migration.userId`) }}</span>
					</label>
					<input type="text" class="form-control view-disabled" v-model="userId" :readonly="true" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t(`igate.migration.name`) }}</span>
					</label>
					<input type="text" class="form-control view-disabled" v-model="name" :readonly="true" />
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t(`igate.migration.migrationDate`) }}</span>
					</label>
					<input type="text" class="form-control view-disabled" v-model="migrationDate" :readonly="true" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t(`igate.migration.migrationTime`) }}</span>
					</label>
					<input type="text" class="form-control view-disabled" v-model="migrationTime" :readonly="true" />
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<table class="migration-table">
					<colgroup>
						<col width="33%" />
						<col width="33%" />
						<col width="34%" />
					</colgroup>
					<thead>
						<tr align="center" style="background-color: #f5f6fb">
							<th style="height: 32px">{{ $t(`igate.record`) }}</th>
							<th style="height: 32px">{{ $t(`igate.migration.includeList`) }}</th>
							<th style="height: 32px">{{ $t(`igate.migration.referList`) }}</th>
						</tr>
					</thead>
					<tr v-for="(record, recordIdx) in records" style="height: 28px" :key="recordIdx">
						<td>{{ record.record.recordId }}({{ record.record.recordName }})</td>
						<td valign="top">
							<table width="100%">
								<tr>
									<td width="20%">{{ $t(`igate.record`) }}</td>
									<td>
										<table width="100%">
											<tr>
												<td>{{ record.record.recordId }}({{ record.record.recordName }})</td>
											</tr>
										</table>
									</td>
								</tr>
							</table>
						</td>
						<td valign="top">
							<table width="100%">
								<tr v-if="record.referRecords.length">
									<td width="30%">{{ $t(`igate.record`) }}</td>
									<td>
										<table width="100%">
											<tr v-for="(referRecord, referRecordIdx) in record.referRecords" :key="`${recordIdx}_${referRecordIdx}`">
												<td>{{ referRecord.recordId }}({{ referRecord.recordName }})</td>
											</tr>
										</table>
									</td>
								</tr>
							</table>
						</td>
					</tr>
				</table>
			</div>
		</div>
		<div class="row frm-row" v-if="editor">
			<div class="col-lg-12">
				<a
					href="javascript:void(0);"
					class="btn detail btn-primary viewGroup"
					:title="$t('head.migration')"
					style="float: right"
					@click="migration"
				>
					{{ $t(`head.migration`) }}
				</a>
				<a
					href="javascript:void(0);"
					class="btn detail viewGroup"
					:title="$t('head.download')"
					style="float: right; right: 10px"
					@click="downloadFile"
				>
					{{ $t(`head.download`) }}
				</a>
			</div>
		</div>
	</div>
</template>

<script>
import { HttpReq, escapeHtml, getHttpUrl } from '@/utils/common.js';

export default {
	name: `MigrationMake`,
	props: {
		detailData: Object,
		viewer: Boolean,
		editor: Boolean
	},
	data() {
		return {
			userId: null,
			name: null,
			migrationDate: null,
			migrationTime: null,
			records: [],
			targetIds: null
		};
	},
	watch: {
		detailData: {
			deep: true,
			handler() {
				this.userId = null;
				this.name = null;
				this.migrationDate = null;
				this.migrationTime = null;
				this.records = [];
				this.targetIds = null;

				if (!this.detailData) return;

				const { userId, name, migrationDate, migrationTime, records, targetIds } = this.detailData;

				this.userId = userId;
				this.name = name;
				this.migrationDate = migrationDate;
				this.migrationTime = migrationTime;
				this.records = records;
				this.targetIds = targetIds;
			}
		}
	},
	methods: {
		async migration() {
			const result = await new HttpReq(`/igate/migration/record/send.json`).update({ targetIds: this.targetIds }, true);

			if (`ok` !== result.data.result) {
				this.$alert({
					type: `warn`,
					message: `${escapeHtml(result.data.error[0].className)}<br/><br/>${escapeHtml(result.data.error[0].message)}`,
					isXSSMode: true
				});
			} else {
				this.$alert({
					type: `normal`,
					message: this.$t(`igate.migration.migration`)
				});
			}
		},
		downloadFile() {
			this.$startSpinner();

			let makeData = ``;

			this.targetIds.forEach(targetId => {
				makeData += `targetIds=${targetId}&`;
			});

			const req = new XMLHttpRequest();
			req.open('POST', getHttpUrl(`/igate/migration/record/down.json?${makeData}`));

			const csrfToken = JSON.parse(localStorage.getItem(`csrfToken`));
			req.setRequestHeader(csrfToken.headerName, csrfToken.token);

			req.withCredentials = true;

			req.responseType = 'blob';

			req.send();

			req.onload = async event => {
				this.$stopSpinner();

				const blob = req.response;

				const promise = new Promise(resolve => {
					const reader = new FileReader();

					reader.onload = () => {
						resolve(JSON.parse(reader.result));
					};

					reader.readAsText(blob);
				});

				const result = await promise;

				const file_name = result.name;

				if (0 >= blob.size || 200 !== event.target.status) {
					this.$alert({ type: 'warn', message: this.$t(`head.fail.notice`) });
					return;
				}

				if (window.navigator && window.navigator.msSaveOrOpenBlob) {
					window.navigator.msSaveOrOpenBlob(blob, file_name);
				} else {
					const link = document.createElement('a');
					link.href = window.URL.createObjectURL(blob);
					link.download = file_name;
					link.click();
					URL.revokeObjectURL(link.href);
					link.remove();
				}
			};
		}
	}
};
</script>
