import { mathRandom } from '@/utils/common.js';

export function getYAxisTicksInfo(pMaxValue, pTickCount) {
	let yAxisTicksInfo = { maxValue: null, stepSize: null };

	yAxisTicksInfo.maxValue = pMaxValue < 10 ? 10 : pMaxValue;
	yAxisTicksInfo.stepSize = yAxisTicksInfo.maxValue / (pTickCount - 1);

	return yAxisTicksInfo;
}

export function convertValue(pValue, unitType, p_div_unit) {
	pValue = Number(pValue);

	if (`PERCENT` == unitType) {
		return (0 === pValue ? pValue : pValue.toFixed(2)) + ` %`;
	}

	let div_unit = `BYTE` == unitType ? 1024 : 1000;

	let unitArr = p_div_unit ? p_div_unit : [``, `K`, `M`, `G`, `T`, `P`];

	let dividedCnt = 0;

	while (true) {
		if (pValue < div_unit) break;

		pValue = pValue / div_unit;

		dividedCnt = dividedCnt + 1;
	}

	return (0 === pValue ? pValue : pValue.toFixed(2)) + (`` == unitArr[dividedCnt] ? `` : ` ` + unitArr[dividedCnt]) + (1024 == div_unit ? `B` : ``);
}

export function binanrySearch(dataArr, x) {
	let start = 0;
	let end = dataArr.length - 1;

	while (start <= end) {
		const mid = Math.floor((start + end) / 2);

		if (Math.floor(dataArr[mid].x / 1000) * 1000 == x) return { dataInfo: dataArr[mid], idx: mid };
		else if (Math.floor(dataArr[mid].x / 1000) * 1000 < x) start = mid + 1;
		else end = mid - 1;
	}

	return null;
}

export function getCanvasMousePos(canvas, evt) {
	const rect = canvas.getBoundingClientRect();

	return {
		x: evt.clientX - rect.left,
		y: evt.clientY - rect.top
	};
}

export function shuffle(a) {
	let j, x, i;

	for (i = a.length - 1; i > 0; i--) {
		j = Math.floor(mathRandom() * (i + 1));
		x = a[i];
		a[i] = a[j];
		a[j] = x;
	}

	return a;
}

export const WORKER_RESOURCE = `
	self.onmessage = e => {
		const data = e.data;

		const dataArr = data.dataArr;
		const startFilterTime = data.startFilterTime;
		const endFilterTime = data.endFilterTime;
		const startFilterValue = data.startFilterValue;
		const endFilterValue = data.endFilterValue;
		const yAxisMaxValue = data.yAxisMaxValue;

		let filterDataArr = dataArr.filter(data => {
			if (yAxisMaxValue < endFilterValue) return startFilterTime <= data.x && endFilterTime >= data.x && startFilterValue <= data.y;
			else return startFilterTime <= data.x && endFilterTime >= data.x && startFilterValue <= data.y && endFilterValue >= data.y;
		});

		if (0 < Object.keys(data.adapterFilterObj).length || 0 < Object.keys(data.instanceFilterObj).length || 0 < data.transactionFilter.length) {
			filterDataArr = filterDataArr.filter(filterData => {
				const isAdapterFilter = 0 < Object.keys(data.adapterFilterObj).length ? data.adapterFilterObj[filterData.adapterId] : true;

				const isInstanceFilter = 0 < Object.keys(data.instanceFilterObj).length ? data.instanceFilterObj[filterData.instanceId] : true;

				const isTransactionFilter = 0 < data.transactionFilter.length ? data.transactionFilter == filterData.transactionId : true;

				return isAdapterFilter && isInstanceFilter && isTransactionFilter;
			});
		}

		if (data.isSort) filterDataArr.sort((a, b) => b.y - a.y);

		const rtnObj = {
			mod: data.mod,
			dataArr: filterDataArr,
		};

		if ('export' === rtnObj.mod) rtnObj.csvFileName = data.csvFileName;

		postMessage(rtnObj);
	};
`;

export class Particle {
	constructor(x, y, radius) {
		this.init(x, y, radius);
	}
	init(x, y, radius) {
		this.alive = true;

		this.radius = radius || 10;
		this.wander = 0.15;
		this.theta = random(TWO_PI);
		this.drag = 0.92;
		this.color = '#fff';

		this.x = x || 0.0;
		this.y = y || 0.0;

		this.vx = 0.0;
		this.vy = 0.0;
	}
	move() {
		this.x += this.vx;
		this.y += this.vy;

		this.vx *= this.drag;
		this.vy *= this.drag;

		this.theta += random(-0.5, 0.5) * this.wander;
		this.vx += sin(this.theta) * 0.1;
		this.vy += cos(this.theta) * 0.1;

		this.radius *= 0.96;
		this.alive = this.radius > 0.5;
	}
	draw(ctx) {
		ctx.beginPath();
		ctx.arc(this.x, this.y, this.radius, 0, TWO_PI);
		ctx.fillStyle = this.color;
		ctx.fill();
		ctx.closePath();
	}
}

export function cloneObject(obj) {
	const clone = {};

	for (let i in obj) {
		if (typeof obj[i] == 'object' && obj[i] != null) clone[i] = cloneObject(obj[i]);
		else clone[i] = obj[i];
	}

	return clone;
}
