<template>
	<div class="ct-content">
		<Button
			:list="buttonList"
			:viewer="viewer"
			:editor="editor"
			:totalCount="totalCount"
			:gridInstanceInfo="gridInstanceInfo"
			@initialize="initialize"
			@newTab="newTab"
			@add="add"
			@excelExport="excelExport"
			@excelImport="excelImport"
			@refresh="refresh"
			@updateStatus="updateStatus"
			@gridControl="gridControl"
			@reorder="reorder"
			@fileMake="fileMake"
			@fileImport="fileImport"
		/>
		<NoData v-if="!isGridView" />
		<Grid
			ref="grid"
			:info="gridInfo"
			:searchObj="gridSearchObj"
			:type="gridType"
			@rowClicked="rowClicked"
			@gridCount="gridCount"
			v-if="isGridView"
		/>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import Button from '@/components/Button.vue';
import NoData from '@/components/NoData.vue';
import Grid from '@/components/Grid.vue';
import { numberWithComma } from '@/utils/common';

export default {
	name: 'Main',
	props: {
		gridInfo: Object,
		gridSearchObj: Object,
		gridType: String,
		buttonList: Array,
		viewer: Boolean,
		editor: Boolean
	},
	data() {
		return {
			isGridView: false,
			totalCount: null,
			gridInstanceInfo: null
		};
	},
	components: {
		Button,
		NoData,
		Grid
	},
	watch: {
		gridSearchObj() {
			this.isGridView = true;
		}
	},
	methods: {
		initialize() {
			this.$emit('initialize');
		},
		add() {
			this.$emit('add');
		},
		newTab() {
			this.$emit('newTab');
		},
		excelExport() {
			this.$emit('excelExport');
		},
		excelImport() {
			this.$emit('excelImport');
		},
		rowClicked(info) {
			this.gridInstanceInfo = cloneDeep(this.$refs.grid.getGrid().gridInstance);

			if (info) this.$emit('rowClicked', info);
		},
		gridCount(count) {
			this.totalCount = numberWithComma(count);
		},
		refresh() {
			this.$emit(`refresh`);
		},
		updateStatus(mod) {
			this.$emit(`updateStatus`, {
				gridObj: this.$refs.grid ? this.$refs.grid.getGrid() : null,
				mod
			});
		},
		gridControl(mod) {
			this.$emit(`gridControl`, {
				gridObj: this.$refs.grid ? this.$refs.grid.getGrid() : null,
				mod
			});
		},
		reorder() {
			this.$emit(`reorder`);
		},
		fileMake() {
			this.$emit('fileMake', {
				gridObj: this.$refs.grid ? this.$refs.grid.getGrid() : null
			});
		},
		fileImport() {
			this.$emit('fileImport');
		}
	}
};
</script>
