							<span class="custom-control-label"></span>
						</label>
						<select class="form-control view-disabled cron-select-ie" v-model="weekResult1" :disabled="weekCheck != 2" style="margin-left: -16px">
							<option value="Sun">{{ this.$t('igate.job.sunday') }}</option>
							<option value="Mon">{{ this.$t('igate.job.monday') }}</option>
							<option value="Tue">{{ this.$t('igate.job.tuesday') }}</option>
							<option value="Wed">{{ this.$t('igate.job.wednesday') }}</option>
							<option value="Thu">{{ this.$t('igate.job.thursday') }}</option>
							<option value="Fri">{{ this.$t('igate.job.friday') }}</option>
							<option value="Sat">{{ this.$t('igate.job.saturday') }}</option>
						</select>
					</div>
					<label class="custom-control custom-radio custom-control-inline custom-cron" style="display: block">
						<input type="radio" name="week" value="3" class="custom-control-input" v-model="weekCheck" />
						<span class="custom-control-label">
							<input
								type="text"
								class="form-control view-disabled cron-input"
								:placeholder="$t('igate.job.input')"
								v-model="weekResult2"
								:disabled="weekCheck != 3"
							/>
						</span>
					</label>
				</div>
			</div>
		</div>
		<div class="table-responsive" style="overflow: hidden; height: 60px">
			<TuiGrid ref="tuiGrid" v-bind="options" />
		</div>
	</div>
</template>

<script>
import { Grid } from '@toast-ui/vue-grid';
import { makeGridOptions } from '@/utils/common.js';

export default {
	name: 'CronExpressionModal',
	props: {
		cron: Array
	},
	components: {
		TuiGrid: Grid
	},
	data() {
		return {
			selectedIdx: 0,
			mode: 'detail',
			hideIe: true,

			// Regex
			astro: `*`,
			slash: `/`,
			hyphen: `-`,
			question: `?`,
			comma: `,`,
			hash: `#`,

			/* eslint-disable no-useless-escape */
			reg1: /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣|a-z|A-Z|.~!?@;:\#$\\\%<>^&\()\=+_\’`]/,
			reg2: /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣|a-z|A-Z|.~!@;:\#$\\\%<>^&\()\=+_\’`]/,
			reg3: /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣|.~!?@;:\#$\\\%<>^&\()\=+_\’`]/,
			reg4: /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣|.~!@;:\$\\\%<>^&\()\=+_\’`]/,
			regexr: /^\s*($|#|\w+\s*=|(\?|\*|(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?(?:,(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?)*)\s(\?|\*|(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?(?:,(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?)*)\s(\?|\*|(?:[01]?\d|2[0-3])(?:(?:-|\/|\,)(?:[01]?\d|2[0-3]))?(?:,(?:[01]?\d|2[0-3])(?:(?:-|\/|\,)(?:[01]?\d|2[0-3]))?)*)\s(\?|\*|(?:0?[1-9]|[12]\d|3[01])(?:(?:-|\/|\,)(?:0?[1-9]|[12]\d|3[01]))?(?:,(?:0?[1-9]|[12]\d|3[01])(?:(?:-|\/|\,)(?:0?[1-9]|[12]\d|3[01]))?)*)\s(\?|\*|(?:[1-9]|1[012])(?:(?:-|\/|\,)(?:[1-9]|1[012]))?(?:L|W)?(?:,(?:[1-9]|1[012])(?:(?:-|\/|\,)(?:[1-9]|1[012]))?(?:L|W)?)*|\?|\*|(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC)(?:(?:-)(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC))?(?:,(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC)(?:(?:-)(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC))?)*)\s(\?|\*|(?:[0-6])(?:(?:-|\/|\,|#)(?:[0-6]))?(?:,(?:[0-6])(?:(?:-|\/|\,|#)(?:[0-6]))?)*|\?|\*|(?:MON|TUE|WED|THU|FRI|SAT|SUN)(?:(?:-)(?:MON|TUE|WED|THU|FRI|SAT|SUN))?(?:,(?:MON|TUE|WED|THU|FRI|SAT|SUN)(?:(?:-)(?:MON|TUE|WED|THU|FRI|SAT|SUN))?)*))$/i,

			secondSelect: null,
			minuteSelect: null,
			hourSelect: null,
			monthSelect: null,

			// Radio vModel
			secondCheck: null,
			minuteCheck: null,
			hourCheck: null,
			dayCheck: null,
			monthCheck: null,
			weekCheck: null,

			// User Input Cron Result
			newDataObj: {
				second: this.astro,
				minute: this.astro,
				hour: this.astro,
				dayOfMonth: this.astro,
				month: this.astro,
				dayOfWeek: this.question
			},
			result: null,
			cronResult: null,
			secondResult1: '',
			secondResult2: '',
			secondResult3: '',
			secondResult4: '',
			secondResult5: '',
			minuteResult1: '',
			minuteResult2: '',
			minuteResult3: '',
			minuteResult4: '',
			minuteResult5: '',
			hourResult1: '',
			hourResult2: '',
			hourResult3: '',
			hourResult4: '',
			hourResult5: '',
			dayResult1: '',
			monthResult1: '',
			monthResult2: '',
			monthResult3: '',
			monthResult4: '',
			monthResult5: '',
			weekResult1: '',
			weekResult2: '',

			// v-for
			items: [
				{
					id: 'JobSecondTab',
					name: this.$t('igate.job.second')
				},
				{
					id: 'JobMinuteTab',
					name: this.$t('igate.job.minute')
				},
				{
					id: 'JobHourTab',
					name: this.$t('igate.job.hour')
				},
				{
					id: 'JobDayTab',
					name: this.$t('igate.job.dayOfMonth')
				},
				{
					id: 'JobMonthTab',
					name: this.$t('igate.job.month')
				},
				{
					id: 'JobWeekTab',
					name: this.$t('igate.job.dayOfWeek')
				}
			],
			// Grid
			options: {
				scrollX: false,
				scrollY: false,
				columns: [
					{
						name: 'second',
						header: this.$t('igate.job.second'),
						align: 'center'
					},
					{
						name: 'minute',
						header: this.$t('igate.job.minute'),
						align: 'center'
					},
					{
						name: 'hour',
						header: this.$t('igate.job.hour'),
						align: 'center'
					},
					{
						name: 'dayOfMonth',
						header: this.$t('igate.job.dayOfMonth'),
						align: 'center'
					},
					{
						name: 'month',
						header: this.$t('igate.job.month'),
						align: 'center'
					},
					{
						name: 'dayOfWeek',
						header: this.$t('igate.job.dayOfWeek'),
						align: 'center'
					}
				]
			}
		};
	},
	async created() {
		this.options = makeGridOptions(this.options);

		// Second
		if (this.cron[0].indexOf(this.comma) != -1) {
			this.secondCheck = 4;
			this.secondResult5 = this.cron[0];
		} else if (this.cron[0].indexOf(this.astro) != -1) {
			this.secondCheck = 1;
		} else if (this.cron[0].indexOf(this.slash) != -1) {
			this.secondCheck = 2;
			this.secondSelect = this.cron[0].split(this.slash);
			this.secondResult1 = this.secondSelect[0];
			this.secondResult2 = this.secondSelect[1];
		} else if (this.cron[0].indexOf(this.hyphen) != -1) {
			this.secondCheck = 3;
			this.secondSelect = this.cron[0].split(this.hyphen);
			this.secondResult3 = this.secondSelect[0];
			this.secondResult4 = this.secondSelect[1];
		} else {
			this.secondCheck = 4;
			this.secondResult5 = this.cron[0];
		}

		// Minute
		if (this.cron[1].indexOf(this.comma) != -1) {
			this.minuteCheck = 4;
			this.minuteResult5 = this.cron[1];
		} else if (this.cron[1].indexOf(this.astro) != -1) {
			this.minuteCheck = 1;
		} else if (this.cron[1].indexOf(this.slash) != -1) {
			this.minuteCheck = 2;
			this.minuteSelect = this.cron[1].split(this.slash);
			this.minuteResult1 = this.minuteSelect[0];
			this.minuteResult2 = this.minuteSelect[1];
		} else if (this.cron[1].indexOf(this.hyphen) != -1) {
			this.minuteCheck = 3;
			this.minuteSelect = this.cron[1].split(this.hyphen);
			this.minuteResult3 = this.minuteSelect[0];
			this.minuteResult4 = this.minuteSelect[1];
		} else {
			this.minuteCheck = 4;
			this.minuteResult5 = this.cron[1];
		}

		// Hour
		if (this.cron[2].indexOf(this.comma) != -1) {
			this.hourCheck = 4;
			this.hourResult5 = this.cron[1];
		} else if (this.cron[2].indexOf(this.astro) != -1) {
			this.hourCheck = 1;
		} else if (this.cron[2].indexOf(this.slash) != -1) {
			this.hourCheck = 2;
			this.hourSelect = this.cron[2].split(this.slash);
			this.hourResult1 = this.hourSelect[0];
			this.hourResult2 = this.hourSelect[1];
		} else if (this.cron[2].indexOf(this.hyphen) != -1) {
			this.hourCheck = 3;
			this.hourSelect = this.cron[2].split(this.hyphen);
			this.hourResult3 = this.hourSelect[0];
			this.hourResult4 = this.hourSelect[1];
		} else {
			this.hourCheck = 4;
			this.hourResult5 = this.cron[2];
		}

		// Day
		if (this.cron[3].indexOf(this.comma) != -1) {
			this.dayCheck = 3;
			this.dayResult1 = this.cron[3];
		} else if (this.cron[3].indexOf(this.astro) != -1) {
			this.dayCheck = 1;
		} else if (this.cron[3].indexOf(this.question) != -1) {
			this.dayCheck = 2;
		} else {
			this.dayCheck = 3;
			this.dayResult1 = this.cron[3];
		}

		// Month
		if (this.cron[4].indexOf(this.comma) != -1) {
			this.monthCheck = 4;
			this.monthResult5 = this.cron[1];
		} else if (this.cron[4].indexOf(this.astro) != -1) {
			this.monthCheck = 1;
		} else if (this.cron[4].indexOf(this.slash) != -1) {
			this.monthCheck = 2;
			this.monthSelect = this.cron[4].split(this.slash);
			this.monthResult1 = this.monthSelect[0];
			this.monthResult2 = this.monthSelect[1];
		} else if (this.cron[4].indexOf(this.hyphen) != -1) {
			this.monthCheck = 3;
			this.monthSelect = this.cron[4].split(this.hyphen);
			this.monthResult3 = this.monthSelect[0];
			this.monthResult4 = this.monthSelect[1];
		} else {
			this.monthCheck = 4;
			this.monthResult5 = this.cron[4];
		}

		// Week
		if (this.cron[5].indexOf(this.question) != -1) {
			this.weekCheck = 1;
		} else if (
			this.cron[5].indexOf(this.astro) != -1 ||
			this.cron[5].indexOf(this.slash) != -1 ||
			this.cron[5].indexOf(this.hyphen) != -1 ||
			this.cron[5].indexOf(this.comma) != -1 ||
			this.cron[5].indexOf(this.hash) != -1
		) {
			this.weekCheck = 3;
			this.weekResult2 = this.cron[5];
		} else {
			this.weekCheck = 2;

			if (this.cron[5] === `0` || this.cron[5] === `SUN` || this.cron[5] === `sun` || this.cron[5] === `Sun`) this.weekResult1 = `Sun`;
			else if (this.cron[5] === `1` || this.cron[5] === `MON` || this.cron[5] === `mon` || this.cron[5] === `Mon`) this.weekResult1 = `Mon`;
			else if (this.cron[5] === `2` || this.cron[5] === `TUE` || this.cron[5] === `tue` || this.cron[5] === `Tue`) this.weekResult1 = `Tue`;
			else if (this.cron[5] === `3` || this.cron[5] === `WED` || this.cron[5] === `wed` || this.cron[5] === `Wed`) this.weekResult1 = `Wed`;
			else if (this.cron[5] === `4` || this.cron[5] === `THU` || this.cron[5] === `thu` || this.cron[5] === `Thu`) this.weekResult1 = `Thu`;
			else if (this.cron[5] === `5` || this.cron[5] === `FRI` || this.cron[5] === `fri` || this.cron[5] === `Fri`) this.weekResult1 = `Fri`;
			else if (this.cron[5] === `6` || this.cron[5] === `SAT` || this.cron[5] === `sat` || this.cron[5] === `Sat`) this.weekResult1 = `Sat`;
		}
	},
	mounted() {
		if (!this.$refs.tuiGrid) return;

		let setDataObj = {
			second: this.cron[0],
			minute: this.cron[1],
			hour: this.cron[2],
			dayOfMonth: this.cron[3],
			month: this.cron[4],
			dayOfWeek: this.cron[5]
		};

		this.gridData(setDataObj);

		//Browser Check
		const agt = navigator.userAgent.toLowerCase();
		this.hideIe = !((navigator.appName == 'Netscape' && agt.indexOf('trident') != -1) || agt.indexOf('msie') != -1);
	},
	watch: {
		secondCheck() {
			if (this.secondCheck == 1) this.result = this.astro;
			else if (this.secondCheck == 2) this.result = `${this.secondResult1}${this.slash}${this.secondResult2}`;
			else if (this.secondCheck == 3) this.result = `${this.secondResult3}${this.hyphen}${this.secondResult4}`;
			else if (this.secondCheck == 4) this.result = `${this.secondResult5}`;
			this.gridData({ second: this.result });
		},
		secondResult1() {
			this.gridData({ second: `${this.secondResult1}${this.slash}${this.secondResult2}` });
		},
		secondResult2() {
			this.gridData({ second: `${this.secondResult1}${this.slash}${this.secondResult2}` });
		},
		secondResult3() {
			this.gridData({ second: `${this.secondResult3}${this.hyphen}${this.secondResult4}` });
		},
		secondResult4() {
			this.gridData({ second: `${this.secondResult3}${this.hyphen}${this.secondResult4}` });
		},
		secondResult5() {
			this.gridData({ second: this.secondResult5 });
		},
		minuteCheck() {
			if (this.minuteCheck == 1) this.result = this.astro;
			else if (this.minuteCheck == 2) this.result = `${this.minuteResult1}${this.slash}${this.minuteResult2}`;
			else if (this.minuteCheck == 3) this.result = `${this.minuteResult3}${this.hyphen}${this.minuteResult4}`;
			else if (this.minuteCheck == 4) this.result = `${this.minuteResult5}`;
			this.gridData({ minute: this.result });
		},
		minuteResult1() {
			this.gridData({ minute: `${this.minuteResult1}${this.slash}${this.minuteResult2}` });
		},
		minuteResult2() {
			this.gridData({ minute: `${this.minuteResult1}${this.slash}${this.minuteResult2}` });
		},
		minuteResult3() {
			this.gridData({ minute: `${this.minuteResult3}${this.hyphen}${this.minuteResult4}` });
		},
		minuteResult4() {
			this.gridData({ minute: `${this.minuteResult3}${this.hyphen}${this.minuteResult4}` });
		},
		minuteResult5() {
			this.gridData({ minute: this.minuteResult5 });
		},
		hourCheck() {
			if (this.hourCheck == 1) this.result = this.astro;
			else if (this.hourCheck == 2) this.result = `${this.hourResult1}${this.slash}${this.hourResult2}`;
			else if (this.hourCheck == 3) this.result = `${this.hourResult3}${this.hyphen}${this.hourResult4}`;
			else if (this.hourCheck == 4) this.result = `${this.hourResult5}`;
			this.gridData({ hour: this.result });
		},
		hourResult1() {
			this.gridData({ hour: `${this.hourResult1}${this.slash}${this.hourResult2}` });
		},
		hourResult2() {
			this.gridData({ hour: `${this.hourResult1}${this.slash}${this.hourResult2}` });
		},
		hourResult3() {
			this.gridData({ hour: `${this.hourResult3}${this.hyphen}${this.hourResult4}` });
		},
		hourResult4() {
			this.gridData({ hour: `${this.hourResult3}${this.hyphen}${this.hourResult4}` });
		},
		hourResult5() {
			this.gridData({ hour: this.hourResult5 });
		},
		dayCheck() {
			if (this.dayCheck == 1) {
				this.result = this.astro;
				if (this.weekCheck != -1) {
					this.weekCheck = 1;
					this.gridData({ dayOfWeek: this.question });
				}
			} else if (this.dayCheck == 2) {
				this.result = this.question;

				if (this.weekCheck == 1) {
					this.weekCheck = 3;
					this.weekResult2 = this.astro;
					this.gridData({ dayOfWeek: this.astro });
				}
			} else if (this.dayCheck == 3) {
				this.result = this.dayResult1;

				if (this.weekCheck != 1) {
					this.weekCheck = 1;
					this.gridData({ dayOfWeek: this.question });
				}
			}

			this.gridData({ dayOfMonth: this.result });
		},
		dayResult1() {
			this.gridData({ dayOfMonth: this.dayResult1 });
		},
		monthCheck() {
			if (this.monthCheck == 1) this.result = this.astro;
			else if (this.monthCheck == 2) this.result = `${this.monthResult1}${this.slash}${this.monthResult2}`;
			else if (this.monthCheck == 3) this.result = `${this.monthResult3}${this.hyphen}${this.monthResult4}`;
			else if (this.monthCheck == 4) this.result = `${this.monthResult5}`;
			this.gridData({ month: this.result });
		},
		monthResult1() {
			this.gridData({ month: `${this.monthResult1}${this.slash}${this.monthResult2}` });
		},
		monthResult2() {
			this.gridData({ month: `${this.monthResult1}${this.slash}${this.monthResult2}` });
		},
		monthResult3() {
			this.gridData({ month: `${this.monthResult3}${this.hyphen}${this.monthResult4}` });
		},
		monthResult4() {
			this.gridData({ month: `${this.monthResult3}${this.hyphen}${this.monthResult4}` });
		},
		monthResult5() {
			this.gridData({ month: this.monthResult5 });
		},
		weekCheck() {
			if (this.weekCheck == 1) {
				this.result = this.question;

				if (this.dayCheck == 2) {
					this.dayCheck = 1;
					this.gridData({ dayOfMonth: this.astro });
				}
			} else if (this.weekCheck == 2) {
				this.result = this.weekResult1;
				if (this.dayCheck != 2) {
					this.dayCheck = 2;
					this.gridData({ dayOfMonth: this.question });
				}
			} else if (this.weekCheck == 3) {
				this.result = this.weekResult2;

				if (this.dayCheck != 2) {
					this.dayCheck = 2;
					this.gridData({ dayOfMonth: this.question });
				}
			}

			this.gridData({ dayOfWeek: this.result });
		},
		weekResult1() {
			this.gridData({ dayOfWeek: this.weekResult1 });
		},
		weekResult2() {
			this.gridData({ dayOfWeek: this.weekResult2 });
		}
	},
	methods: {
		changeTab(idx) {
			const { second, minute, hour, dayOfMonth, month, dayOfWeek } = this.$refs.tuiGrid.invoke('getRowAt', 0);

			let message = null;

			if (this.reg1.test(second) || this.reg1.test(minute) || this.reg1.test(hour)) {
				message = `${this.$t('igate.job.validationBasic')} ${this.$t('igate.job.validationEn')} ${this.$t('igate.job.validationKo')}`;
			} else if (this.reg2.test(dayOfMonth)) {
				message = `${this.$t('igate.job.validationQuestion')} ${this.$t('igate.job.validationEn')} ${this.$t('igate.job.validationKo')}`;
			} else if (this.reg3.test(month)) {
				message = this.$t('igate.job.validationBasic');
			} else if (this.reg4.test(dayOfWeek)) {
				message = this.$t('igate.job.validationHash');
			} else if (!this.regexr.test(this.cronResult)) {
				message = this.$t('igate.job.error');
			}

			if (message) this.$alert({ type: 'warn', message });
			else this.selectedIdx = idx;
		},
		gridData(params) {
			for (const key in params) {
				this.newDataObj[key] = params[key];
			}

			this.$refs.tuiGrid.invoke('resetData', [this.newDataObj]);

			const { second, minute, hour, dayOfMonth, month, dayOfWeek } = this.$refs.tuiGrid.invoke('getRowAt', 0);

			this.cronResult = `${second} ${minute} ${hour} ${dayOfMonth} ${month} ${dayOfWeek}`;
		}
	}
};
</script>

<style>
.cron-input {
	width: 105% !important;
}
.cron-span {
	display: -webkit-box !important;
}
.cron-select {
	display: initial !important;
	width: 0 !important;
}
.cron-select-ie {
	display: inline-block !important;
	width: 0 !important;
}
.cron-select + .cron-select {
	margin-top: 0 !important;
}
.custom-radio.custom-cron .custom-control-label::before,
.custom-radio.custom-cron .custom-control-label::after {
	top: 9px;
}
.custom-radio.custom-cron-ie .custom-control-label::before,
.custom-radio.custom-cron-ie .custom-control-label::after {
	top: -13px;
}
</style>
