<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group" style="margin-bottom: 16px">
					<label class="control-label">
						<span>{{ `${$t(`head.file`)} ${$t(`head.select`)}` }}</span>
					</label>
					<div class="input-group">
						<input
							class="form-control view-disabled"
							type="text"
							v-model="fileName"
							readonly="readonly"
							:placeholder="$t(`igate.migration.fileSelectError`)"
						/>
						<button
							type="button"
							class="btn"
							@click="selectFile"
							:title="`${$t(`head.file`)} ${$t(`head.select`)}`"
							style="margin-left: 5px; margin-right: 5px"
						>
							{{ `${$t(`head.file`)} ${$t(`head.select`)}` }}
						</button>
						<button type="button" class="btn btn-primary" :title="$t(`head.upload`)" @click="uploadFile">
							{{ $t(`head.upload`) }}
						</button>
					</div>
				</div>
			</div>
			<div class="col-lg-12" v-show="errorMessage">
				<div class="form-group" style="margin-bottom: 16px">
					<label class="control-label">
						<span>{{ $t(`head.exception`) }}</span>
					</label>
					<div class="input-group">
						<textarea class="form-control view-disabled" v-model="errorMessage" rows="10" :readonly="true"></textarea>
					</div>
				</div>
			</div>
			<div class="col-lg-6" v-show="0 < services.length">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t(`igate.migration.userId`) }}</span>
					</label>
					<div class="input-group">
						<input type="text" class="form-control view-disabled" v-model="userId" :disabled="true" />
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t(`igate.migration.name`) }}</span>
					</label>
					<div class="input-group">
						<input type="text" class="form-control view-disabled" v-model="name" :disabled="true" />
					</div>
				</div>
			</div>
			<div class="col-lg-6" v-show="0 < services.length">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t(`igate.migration.migrationDate`) }}</span>
					</label>
					<div class="input-group">
						<input type="text" class="form-control view-disabled" v-model="migrationDate" :disabled="true" />
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t(`igate.migration.migrationTime`) }}</span>
					</label>
					<div class="input-group">
						<input type="text" class="form-control view-disabled" v-model="migrationTime" :disabled="true" />
					</div>
				</div>
			</div>
			<div class="col-lg-12" v-show="0 < services.length" style="margin-top: 1rem">
				<div class="form-group" style="margin-bottom: 16px">
					<table class="migration-table" width="100%">
						<colgroup>
							<col width="50%" />
							<col width="50%" />
						</colgroup>
						<thead>
							<tr align="center" style="background-color: #f5f6fb">
								<th>{{ $t(`igate.service`) }}</th>
								<th>{{ $t(`igate.migration.includeList`) }}</th>
							</tr>
						</thead>
						<tr v-for="(service, serviceIdx) in services" :key="serviceIdx">
							<td>{{ service.service.serviceId }}({{ service.service.serviceName }})</td>
							<td valign="top">
								<table width="100%">
									<tr>
										<td width="20%">{{ $t(`igate.service`) }}</td>
										<td>
											<table width="100%">
												<tr>
													<td>{{ service.service.serviceId }}({{ service.service.serviceName }})</td>
												</tr>

												<tr></tr>
											</table>
										</td>
									</tr>
									<tr v-if="service.service.requestRecordObject">
										<td width="30%">{{ $t(`igate.record`) }}</td>
										<td>
											<table width="100%">
												<tr>
													<td>
														{{ service.service.requestRecordObject.recordId }}
														({{ service.service.requestRecordObject.recordName }})
													</td>
												</tr>
												<tr v-if="service.service.responseRecordObject">
													<td>
														{{ service.service.responseRecordObject.recordId }}
														({{ service.service.responseRecordObject.recordName }})
													</td>
												</tr>
											</table>
										</td>
									</tr>
									<tr v-if="service.service.operationObject">
										<td width="30%">{{ $t(`igate.operation`) }}</td>
										<td>
											<table width="100%">
												<tr>
													<td>
														{{ service.service.operationObject.operationId }}
														({{ service.service.operationObject.operationName }})
													</td>
												</tr>
											</table>
										</td>
									</tr>
									<tr v-if="service.service.operationObject && service.service.operationObject.referenceMappings.length">
										<td width="30%">{{ $t(`igate.mapping`) }}</td>
										<td>
											<table width="100%">
												<tr
													v-for="(referenceMapping, referenceMappingIdx) in service.service.operationObject
														.referenceMappings"
													:key="`${serviceIdx}_${referenceMappingIdx}`"
												>
													<td>
														{{ referenceMapping.mappingObject.mappingId }}
														({{ referenceMapping.mappingObject.mappingName }})
													</td>
												</tr>
											</table>
										</td>
									</tr>
								</table>
							</td>
						</tr>
					</table>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { HttpFileReq } from '@/utils/common.js';

export default {
	name: `MigrationImport`,
	props: {
		detailData: Object,
		viewer: Boolean,
		editor: Boolean
	},
	data() {
		return {
			fileName: null,
			errorMessage: null,
			userId: null,
			name: null,
			migrationDate: null,
			migrationTime: null,
			services: [],
			fileData: null
		};
	},
	watch: {
		detailData: {
			deep: true,
			handler() {
				this.fileName = null;
				this.errorMessage = null;
				this.userId = null;
				this.name = null;
				this.migrationDate = null;
				this.migrationTime = null;
				this.services = [];
				this.fileData = null;
			}
		}
	},
	methods: {
		selectFile() {
			const fileInput = document.createElement(`input`);

			fileInput.setAttribute(`type`, `file`);

			fileInput.addEventListener(`change`, evt => {
				this.fileData = evt.target.files[0];
				this.fileName = this.fileData.name;
			});

			fileInput.click();
		},
		async uploadFile() {
			if (null === this.fileData) {
				this.$alert({
					type: `warn`,
					message: this.$t(`igate.migration.fileSelectError`)
				});
				return;
			}

			this.$startSpinner();

			const result = await new HttpFileReq(`/igate/migration/service/import.json`).update({ body: this.fileData }, true);

			if (`ok` !== result.data.result) {
				this.$alert({
					type: `warn`,
					message: `${this.$t(`head.upload`)} ${this.$t(`head.fail`)}`
				});

				this.errorMessage = `${result.data.error[0].className} ${result.data.error[0].message}`;
			} else {
				this.$alert({
					type: `normal`,
					message: `${this.$t(`head.upload`)} ${this.$t(`head.success`)}`
				});

				this.errorMessage = null;

				if (result.data.object) {
					const { userId, name, migrationDate, migrationTime, services } = result.data.object;

					this.userId = userId;
					this.name = name;
					this.migrationDate = migrationDate;
					this.migrationTime = migrationTime;
					this.services = services;
				}
			}

			this.$stopSpinner();
		}
	}
};
</script>
