<script>
import { mapState, mapGetters } from 'vuex';
import Detail from '@/components/detail/Detail.vue';
import { HttpStreamReq } from '@/utils/common.js';

export default {
	name: 'ServiceDetail',
	extends: Detail,
	data() {
		return {
			serviceTabList: [],
			newEditor: false
		};
	},
	computed: {
		...mapState(['userInfo']),
		...mapGetters(['isUserAdmin'])
	},
	watch: {
		cudUrl: {
			immediate: true,
			handler() {
				this.httpReq = new HttpStreamReq(`${this.cudUrl}?ext=1`);
			}
		}
	},
	methods: {
		openPanel() {
			if (`detail` === this.panelMode) {
				if (this.isUserAdmin) {
					this.newEditor = true;
				} else {
					const bTotalUserPrivileges = this.userInfo.totalUserPrivileges.filter(
						userPrivilege => `b` === userPrivilege.privilegeType && userPrivilege.privilegeId === this.detailData.privilegeId
					);

					this.newEditor = this.editor && 0 < bTotalUserPrivileges.length;
				}
			} else {
				this.newEditor = this.editor;
			}

			Detail.methods.openPanel.bind(this)();
		}
	}
};
</script>
