<script>
import Basic from '@/components/main/Basic.vue';
import { getPrivilege, HttpReq } from '@/utils/common.js';

export default {
	name: 'ServicePolicy',
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	async mounted() {
		let result = await new HttpReq(`/common/property/properties.json`).read({ propertyId: 'List.Service.ServiceType', orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.serviceTypeList = result.data.object;

		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
