									label: this.$t('igate.activity') + ' ' + this.$t('head.id'),
									isRequired: true,
									placeholder: this.$t('head.searchId'),
									modalInfo: {
										title: this.$t('igate.activity'),
										search: {
											list: [
												{
													type: 'text',
													vModel: 'activityId',
													label: this.$t('head.id'),
													placeholder: this.$t('head.searchId'),
													regExpType: 'id'
												},
												{
													type: 'select',
													vModel: 'activityType',
													label: this.$t('common.type'),
													val: 'S',
													optionInfo: {
