<template>
	<DashboardStructure ref="dashboardStructure" @initDashboardStructure="initDashboardConfigStructure" :containerInfo="containerInfoOrg">
		<template slot="dashboardLbtnArea">
			<a class="btn btn-icon" href="javascript:void(0);" :title="$t(`dashboard.head.back`)" @click="changeContainer">
				<i class="icon-left"></i>
			</a>
		</template>
		<template slot="dashboardRbtnArea">
			<button type="button" :class="toggleWidgetBtnClass" @click="toggleWidget">
				<span class="custom-control-label">{{ $t(`dashboard.head.add.widget`) }}</span>
			</button>
			<a class="btn" id="redoContainer" href="javascript:void(0);" :disabled="redoDisabled" @click="setContainerState('redo')">
				<i class="icon-left"></i>
				{{ $t(`head.goBackward`) }}
			</a>
			<a class="btn" id="undoContainer" href="javascript:void(0);" :disabled="undoDisabled" @click="setContainerState('undo')">
				<i class="icon-right"></i>
				{{ $t(`head.goForward`) }}
			</a>
			<a class="btn" href="javascript:void(0);" @click="resetContainer">
				<i class="icon-reset"></i>
				{{ $t(`head.initialize`) }}
			</a>
			<slot name="dashboardRbtnConfigArea"> </slot>
		</template>
		<template slot="dashboardBodyArea">
			<div id="dashboardEmpty" class="card w-100 h-100" v-if="isEmpty">
				<div class="empty">
					<p>{{ $t(`dashboard.drag.widget`) }}</p>
					<img :src="require(`@/assets/img/empty.svg`)" class="center-block" alt="" />
				</div>
			</div>
			<div :style="dashBodyStyle" v-html="dashBodyHtml" v-else></div>
		</template>
		<template slot="dashboardWidgetArea">
			<div id="widget" :class="widgetClass" :style="widgetStyle">
				<h2 class="h2 flex-shrink-0">{{ $t(`dashboard.head.add.widget`) }}</h2>
				<section class="widget">
					<header class="sub-bar flex-shrink-0">
						<h3 class="sub-bar-text">{{ $t(`dashboard.head.widget`) }}</h3>
					</header>
					<ul ref="perfItemList" class="widget-item-list scrollbar-inner">
						<li
							class="nav-link"
							v-for="(perfItem, idx) in perfItemList"
							:data-item-id="perfItem.itemId"
							:key="idx"
							:title="`${dashboardWidget[perfItem.itemId].name}\n${dashboardWidget[perfItem.itemId].desc}`"
						>
							<i :class="perfItem.itemIcon" />
							{{ dashboardWidget[perfItem.itemId].name }}
						</li>
					</ul>
				</section>
				<section class="flex-shrink-0">
					<header class="sub-bar mb-2">
						<h3 class="sub-bar-text">{{ $t(`dashboard.head.setting`) }}</h3>
					</header>
					<div class="form-group">
						<div class="control-label">{{ $t(`dashboard.head.theme`) }}</div>
						<label class="custom-control custom-radio custom-control-inline">
							<input type="radio" v-model="containerInfo.darkmodeYn" value="N" class="custom-control-input" @change="toggleTheme" />
							<span class="custom-control-label">{{ $t(`dashboard.theme.light`) }}</span>
						</label>
						<label class="custom-control custom-radio custom-control-inline">
							<input type="radio" v-model="containerInfo.darkmodeYn" value="Y" class="custom-control-input" @change="toggleTheme" />
							<span class="custom-control-label">{{ $t(`dashboard.theme.dark`) }}</span>
						</label>
					</div>
					<div class="form-group">
						<div class="control-label">{{ $t(`dashboard.head.legend`) }}</div>
						<label class="custom-control custom-radio custom-control-inline">
							<input type="radio" v-model="containerInfo.remarkYn" value="Y" class="custom-control-input" />
							<span class="custom-control-label">{{ $t(`dashboard.legend.on`) }}</span>
						</label>
						<label class="custom-control custom-radio custom-control-inline">
							<input type="radio" v-model="containerInfo.remarkYn" value="N" class="custom-control-input" />
							<span class="custom-control-label">{{ $t(`dashboard.legend.off`) }}</span>
						</label>
					</div>
				</section>
			</div>
		</template>
	</DashboardStructure>
</template>

<script>
import { mapState } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import isNil from 'lodash/isNil';
import { HttpReq, WindowResizeEventBus, getUUID, escapeHtml } from '@/utils/common.js';
import DashboardStructure from '@/views/main/dashboard/DashboardStructure.vue';

export default {
	name: `DashboardConfigStructure`,
	props: {
		mod: String,
		containerInfoOrg: Object,
		dashConstInfo: Object
	},
	components: {
		DashboardStructure
	},
	data() {
		return {
			isEmpty: true,
			toggleWidgetBtnClass: {
				'custom-control': true,
				'custom-switch': true,
				'btn-widget': true,
				checked: false
			},
			widgetClass: {
				card: true,
				open: false
			},
			widgetStyle: {
				height: 'auto'
			},

			instanceList: [],
			adapterList: [],
			connectorList: [],
			queueList: [],
			threadList: [],
			externalLineList: [],

			perfItemList: [],
			dashboardWidget: {
				cpuUsage: {
					name: this.$t(`dashboard.widget.cpuUsage`),
					desc: this.$t(`dashboard.widget.cpuUsage.desc`)
				},
				connectorSummary: {
					name: this.$t(`dashboard.widget.connectorSummary`),
					desc: this.$t(`dashboard.widget.connectorSummary.desc`)
				},
				externalLineStatus: {
					name: this.$t(`dashboard.widget.externalLineSummary`),
					desc: this.$t(`dashboard.widget.externalLineSummary.desc`)
				},
				fileMainUsage: {
					name: this.$t(`dashboard.widget.diskUsage`),
					desc: this.$t(`dashboard.widget.diskUsage.desc`)
				},
				fileMainUsed: {
					name: this.$t(`dashboard.widget.diskUsed`),
					desc: this.$t(`dashboard.widget.diskUsed.desc`)
				},
				heapUsage: {
					name: this.$t(`dashboard.widget.memoryUsage`),
					desc: this.$t(`dashboard.widget.memoryUsage.desc`)
				},
				heapUsed: {
					name: this.$t(`dashboard.widget.memoryUsed`),
					desc: this.$t(`dashboard.widget.memoryUsed.desc`)
				},
				instanceSummary: {
					name: this.$t(`dashboard.widget.instanceSummary`),
					desc: this.$t(`dashboard.widget.instanceSummary.desc`)
				},
				activeTransaction: {
					name: this.$t(`dashboard.widget.livedTransaction`),
					desc: this.$t(`dashboard.widget.livedTransaction.desc`)
				},
				queueSummary: {
					name: this.$t(`dashboard.widget.queueSummary`),
					desc: this.$t(`dashboard.widget.queueSummary.desc`)
				},
				threadPoolSummary: {
					name: this.$t(`dashboard.widget.threadSummary`),
					desc: this.$t(`dashboard.widget.threadSummary.desc`)
				},
				deal: {
					name: this.$t(`dashboard.widget.transactionCount`),
					desc: this.$t(`dashboard.widget.transactionCount.desc`)
				},
				elapsed: {
					name: this.$t(`dashboard.widget.transactionElapsed`),
					desc: this.$t(`dashboard.widget.transactionElapsed.desc`)
				},
				xView: {
					name: this.$t(`dashboard.widget.xView`),
					desc: this.$t(`dashboard.widget.xView.desc`)
				},
				transaction: {
					name: this.$t(`dashboard.widget.dateTable`),
					desc: this.$t(`dashboard.widget.dateTable.desc`)
				}
			},
			dashBodyHtml: null,
			containerInfo: {
				darkmodeYn: `Y`,
				remarkYn: `Y`
			},
			componentList: [],
			componentDataMapping: {},
			containerHistoryList: [],
			currentContainerHistory: {},
			containerOptions: {},

			redoDisabled: true,
			undoDisabled: true,

			dashBodyStyle: {
				height: `100%`,
				width: `100%`,
				maxHeight: null
			}
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	created() {
		if (`modify` === this.mod) {
			this.containerInfo.darkmodeYn = this.containerInfoOrg.darkmodeYn;
			this.containerInfo.remarkYn = this.containerInfoOrg.remarkYn;
			this.componentList = cloneDeep(this.containerInfoOrg.monitorComponents);

			this.containerOptions = cloneDeep(
				this.containerInfoOrg.monitorContainerUsers.find(
					monitorContainerUser => monitorContainerUser.pk.containerUserId === this.userInfo.userId
				)
			).containerOptions;
		}

		this.setContainerHistory();
	},
	mounted() {
		this.toggleTheme();

		this.resize();

		WindowResizeEventBus.$on('MainResize', this.resize);
	},
	methods: {
		async initDashboardConfigStructure({ parentData }) {
			if (parentData) {
				document.querySelector(`#dashboard`).classList.add(`edit-mode`, `widget-set-mode`);

				await this.getTargetInfo();

				const { perfItemList } = parentData;

				this.perfItemList = perfItemList;

				this.$nextTick(() => {
					this.initEvent();

					this.initDraw();
				});
			}

			document.querySelector(`#ct`).classList.add(`ct-dashboard`);
		},
		async getTargetInfo() {
			try {
				const httpReq = new HttpReq(`/igate/monitoring/dashboard/targetInfo.json`);

				const result = await httpReq.read();

				if (`ok` !== result.data.result) throw `Error Init Target Info`;

				const { instanceList, adapterList, connectorList, queueList, threadList, externalLineList } = result.data.object;

				this.instanceList = instanceList;
				this.adapterList = adapterList;
				this.connectorList = connectorList;
				this.queueList = queueList;
				this.threadList = threadList;
				this.externalLineList = externalLineList;
			} catch (error) {
				console.log(error);
			}
		},
		initEvent() {
			//perf item
			$(this.$refs.perfItemList.querySelectorAll(`li`)).draggable({
				cursor: 'pointer',
				revert: true,
				scroll: false,
				helper: 'clone',
				appendTo: document.querySelector(`.wrap`)
			});

			//Root Component Append
			$(document.querySelector(`#containerBody`)).droppable({
				drop: (event, ui) => {
					if (!this.isEmpty) return;

					this.componentList.push(this.$refs.dashboardStructure.getBasicComponentStruct());

					const rootDataObj = this.componentList[this.componentList.length - 1];

					rootDataObj.componentId = getUUID();
					rootDataObj.pComponentId = `-1`;
					rootDataObj.componentOrder = 1;
					rootDataObj.componentWidth = `100%`;
					rootDataObj.componentHeight = `100%`;
					rootDataObj.componentType = `G`;

					const rootDiv = document.createElement(`div`);

					rootDiv.setAttribute(`id`, `root`);
					rootDiv.style.width = rootDataObj.componentWidth;
					rootDiv.style.height = rootDataObj.componentHeight;

					this.componentDataMapping[rootDataObj.componentId] = rootDataObj;

					//component
					this.componentList.push(this.$refs.dashboardStructure.getBasicComponentStruct());

					const newComponentDataObj = this.componentList[this.componentList.length - 1];

					newComponentDataObj.componentId = getUUID();
					newComponentDataObj.pComponentId = rootDataObj.componentId;
					newComponentDataObj.componentOrder = 1;
					newComponentDataObj.componentWidth = `100%`;
					newComponentDataObj.componentHeight = `100%`;
					newComponentDataObj.componentType = `C`;
					newComponentDataObj.itemId = ui.draggable[0].getAttribute(`data-item-id`);

					const perfItemInfo = this.perfItemList.find(perfItemInfo => perfItemInfo.itemId == newComponentDataObj.itemId);

					newComponentDataObj.componentName = perfItemInfo.itemName;

					const defaultTypeObj = this.getDefaultType(perfItemInfo);
					newComponentDataObj.chartType = defaultTypeObj.chartType;
					newComponentDataObj.targetType = defaultTypeObj.targetType;
					newComponentDataObj.unitType = defaultTypeObj.unitType;
					newComponentDataObj.inoutType = defaultTypeObj.inoutType;

					this.componentDataMapping[newComponentDataObj.componentId] = newComponentDataObj;

					this.setContainerHistory();

					this.initDraw();
				}
			});
		},
		toggleWidget() {
			this.toggleWidgetBtnClass.checked = !this.toggleWidgetBtnClass.checked;
			this.widgetClass.open = !this.widgetClass.open;
		},
		toggleTheme() {
			document.querySelector(`.wrap`).classList[`Y` === this.containerInfo.darkmodeYn ? `add` : `remove`](`dark`);
		},
		appendComponentTag(pComponentObj, parentTag) {
			const appendDiv = document.createElement(`div`);

			appendDiv.style.width = pComponentObj.componentWidth;
			appendDiv.style.height = pComponentObj.componentHeight;

			if (`-1` === pComponentObj.pComponentId) appendDiv.setAttribute(`id`, `root`);

			appendDiv.setAttribute(`component-id`, pComponentObj.componentId);
			this.componentDataMapping[appendDiv.getAttribute(`component-id`)] = pComponentObj;

			if ('h' == pComponentObj.componentTypeDirection) appendDiv.classList.add(`component-horizontal`);
			else if ('v' == pComponentObj.componentTypeDirection) appendDiv.classList.add(`component-vertical`);

			const childComponentList = this.componentList
				.filter(componentInfo => componentInfo.pComponentId === pComponentObj.componentId)
				.sort((a, b) => a.componentOrder - b.componentOrder);

			if (0 < childComponentList.length) {
				childComponentList.forEach(childComponent => {
					if (`G` === childComponent.componentType) {
						this.appendComponentTag(childComponent, appendDiv);
					} else {
						const perfItemInfo = this.perfItemList.find(perfItemInfo => perfItemInfo.itemId === childComponent.itemId);

						const componentDiv = document.createElement(`div`);

						componentDiv.setAttribute(`component-id`, childComponent.componentId);
						this.componentDataMapping[componentDiv.getAttribute(`component-id`)] = childComponent;

						componentDiv.classList.add(`component`);

						componentDiv.style.width = childComponent.componentWidth;
						componentDiv.style.height = childComponent.componentHeight;

						const card = document.createElement(`section`);
						card.classList.add(`card`);

						const cardHeader = document.createElement(`h2`);
						cardHeader.classList.add(`card-header`);

						const perfItemUl = document.createElement(`ul`);

						const perfItemLi = document.createElement(`li`);
						perfItemLi.classList.add(`nav-link`);

						const perfItemIcon = document.createElement(`i`);
						perfItemIcon.classList.add(this.perfItemList.find(perfItem => perfItem.itemId == childComponent.itemId).itemIcon);

						const perfItemName = document.createElement(`span`);
						perfItemName.innerText = perfItemInfo.itemName;

						perfItemLi.appendChild(perfItemIcon);
						perfItemLi.appendChild(perfItemName);

						perfItemUl.appendChild(perfItemLi);

						cardHeader.appendChild(perfItemUl);

						const cardBody = document.createElement(`div`);
						cardBody.classList.add(`card-body`);
						cardBody.style.paddingTop = `0px`;

						const typeList = this.$refs.dashboardStructure.getTypeList(perfItemInfo);

						let detailInfoHtml = `
							<div class="widget-set">
								<div class="row">
									<div class="col-6">
										<div class="form-group">
											<label class="control-label">${this.$t(`dashboard.head.name`)}</label>
											<input type="text" name="componentName" class="form-control" value="${childComponent.componentName}">
										</div>
										<div class="form-group">
											<label class="control-label">${this.$t(`dashboard.head.chartType`)}</label>
											<select name="chartType" class="form-control">
												${(() => {
													let rtnHtml = ``;

													typeList.chartTypeList.forEach(chartType => {
														const isSelected = chartType.value === childComponent.chartType;

														rtnHtml += `<option value="${chartType.value}" ${isSelected ? `selected` : ``}>
																		${escapeHtml(chartType.name)}
																	</option>`;
													});

													return rtnHtml;
												})()}
											</select>
										</div>
									</div>
									<div class="col-6">
										<div class="form-group">
											<label class="control-label">${this.$t(`dashboard.head.target`)}</label>
											<select name="targetType" class="form-control">
												${(() => {
													let rtnHtml = ``;

													typeList.targetTypeList.forEach(targetType => {
														const isSelected = targetType.value === childComponent.targetType;

														rtnHtml += `<option value="${targetType.value}" ${isSelected ? `selected` : ``}>
																		${escapeHtml(targetType.name)}
																	</option>`;
													});

													return rtnHtml;
												})()}
											</select>
											${(() => {
												if (`EXTERNALLINE` === childComponent.targetType || `INSTANCE` === childComponent.targetType)
													return '';

												let rtnHtml = ``;

												const title =
													0 < childComponent.monitorComponentTargets.length
														? escapeHtml(childComponent.monitorComponentTargets[0].pk.componentTargetId)
														: '';

												rtnHtml += `<select name="selectedTarget" class="form-control" title="${title}">`;

												let targetIdKey = null,
													targetList = [];

												if (`ADAPTER` === childComponent.targetType) {
													targetIdKey = `adapterId`;
													targetList = this.adapterList;
												} else if (`CONNECTOR` === childComponent.targetType) {
													targetIdKey = `connectorId`;
													targetList = this.connectorList;
												} else if (`QUEUE` === childComponent.targetType) {
													targetIdKey = `adapterId`;
													targetList = this.queueList;
												} else if (`THREAD` === childComponent.targetType) {
													targetIdKey = `threadPoolId`;
													targetList = this.threadList;
												}

												targetList.forEach(info => {
													const isSelected =
														0 < childComponent.monitorComponentTargets.length &&
														info[targetIdKey] == childComponent.monitorComponentTargets[0].pk.componentTargetId;

													rtnHtml += `<option value="${info[targetIdKey]}" ${isSelected ? `selected` : ``}>
																	${escapeHtml(info[targetIdKey])}
														        </option>`;
												});

												rtnHtml += `</select>`;

												return rtnHtml;
											})()}
										</div>
										${(() => {
											if (0 === typeList.inoutTypeList.length) return ``;

											let rtnHtml = ``;

											const isDisabled = 'INSTANCE' === childComponent.targetType;

											rtnHtml += `<div class="form-group">
															<label class="control-label">${this.$t(`dashboard.head.inoutType`)}</label>
															<select name="inoutType" class="form-control" ${isDisabled ? `disabled="disabled"` : ``}>
																${(() => {
																	let rtnHtml = ``;

																	typeList.inoutTypeList.forEach(inoutType => {
																		const isSelected = inoutType.value === childComponent.inoutType;

																		rtnHtml += `
																			<option value="${inoutType.value}" ${isSelected ? `selected` : ``}>
																				${escapeHtml(inoutType.name)}
														        			</option>
																		`;
																	});

																	return rtnHtml;
																})()}
															</select>
														</div>`;

											return rtnHtml;
										})()}
										${(() => {
											if (`XVIEW` !== childComponent.chartType) return ``;

											const xViewYAxisMaxKey = `xViewYAxisMax_${childComponent.componentId}`;
											const xViewTransKey = `xViewTrans_${childComponent.componentId}`;
											const xViewMinDataKey = `xViewMinData_${childComponent.componentId}`;

											const { xViewDefaultYAxisMax, xViewDefaultTrans, xViewDefaultxMinData } = this.dashConstInfo;

											this.containerOptions[xViewYAxisMaxKey] = isNil(this.containerOptions[xViewYAxisMaxKey])
												? xViewDefaultYAxisMax
												: Number(this.containerOptions[xViewYAxisMaxKey]);

											this.containerOptions[xViewTransKey] = isNil(this.containerOptions[xViewTransKey])
												? xViewDefaultTrans
												: Number(this.containerOptions[xViewTransKey]);

											this.containerOptions[xViewMinDataKey] = isNil(this.containerOptions[xViewMinDataKey])
												? xViewDefaultxMinData
												: Number(this.containerOptions[xViewMinDataKey]);

											return `
												<div class="form-group">
													<label class="control-label">${this.$t(`dashboard.xview.yAxis.max`)}</label>
													<input type="text" name="xViewYAxisMax" class="form-control" maxlength="8" value="${this.containerOptions[xViewYAxisMaxKey]}">
												</div>
												<div class="form-group">
													<label class="control-label">${this.$t(`dashboard.xview.data.trans`)}</label>
													<input type="text" name="xViewTrans" class="form-control" value="${this.containerOptions[xViewTransKey].toFixed(1)}">
												</div>			
												<div class="form-group">
													<label class="control-label">${this.$t(`dashboard.xview.data.min`)}</label>
													<input type="text" name="xViewMinData" class="form-control" value="${this.containerOptions[xViewMinDataKey]}">
												</div>
											`;
										})()}
										${(() => {
											if (`DATATABLE` !== childComponent.chartType) return ``;

											const datatableRowCntKey = `datatableRowCnt_${childComponent.componentId}`;

											const { datatableDefaultRowCnt } = this.dashConstInfo;

											this.containerOptions[datatableRowCntKey] = isNil(this.containerOptions[datatableRowCntKey])
												? datatableDefaultRowCnt
												: Number(this.containerOptions[datatableRowCntKey]);

											return `
												<div class="form-group">
													<label class="control-label">${this.$t(`dashboard.row.cnt`)}</label>
													<input type="text" name="datatableRowCnt" class="form-control" maxlength="2" value="${this.containerOptions[datatableRowCntKey]}">
												</div>
											`;
										})()}
										${(() => {
											if (`INSTANCE` !== childComponent.chartType) return ``;

											const instanceSummaryColCntKey = `instanceSummaryColCnt_${childComponent.componentId}`;

											const { instanceSummaryDefaultColCnt } = this.dashConstInfo;

											this.containerOptions[instanceSummaryColCntKey] = isNil(this.containerOptions[instanceSummaryColCntKey])
												? instanceSummaryDefaultColCnt
												: Number(this.containerOptions[instanceSummaryColCntKey]);

											return `<div class="form-group">
														       <label class="control-label">${this.$t(`dashboard.col.cnt`)}</label>
															   <input type="text" name="instanceSummaryColCnt" class="form-control" maxlength="2" value="${this.containerOptions[instanceSummaryColCntKey]}"/>
														   </div>`;
										})()}
									</div>
								</div>
							</div>
						`;

						cardBody.innerHTML = detailInfoHtml;

						card.appendChild(cardHeader);
						card.appendChild(cardBody);

						componentDiv.appendChild(card);

						const d = document.createElement(`div`);
						d.classList.add(`componentDelete`);
						componentDiv.appendChild(d);

						const t = document.createElement(`div`);
						t.classList.add(`componentDroppable`, `top`);
						t.setAttribute(`data-type`, `t`);

						const b = document.createElement(`div`);
						b.classList.add(`componentDroppable`, `bottom`);
						b.setAttribute(`data-type`, `b`);

						const l = document.createElement(`div`);
						l.classList.add(`componentDroppable`, `left`);
						l.setAttribute(`data-type`, `l`);

						const r = document.createElement(`div`);
						r.classList.add(`componentDroppable`, `right`);
						r.setAttribute(`data-type`, `r`);

						componentDiv.appendChild(t);
						componentDiv.appendChild(b);
						componentDiv.appendChild(l);
						componentDiv.appendChild(r);

						appendDiv.appendChild(componentDiv);
					}
				});
			}

			if (parentTag) parentTag.appendChild(appendDiv);
			else return appendDiv;
		},
		initDraw() {
			if (0 === this.componentList.length) {
				this.isEmpty = true;
				this.dashBodyHtml = null;
			} else {
				this.isEmpty = false;

				this.dashBodyHtml = this.appendComponentTag(this.componentList.find(componentInfo => `-1` === componentInfo.pComponentId)).outerHTML;

				this.$nextTick(() => {
					this.initComponentEvent();
				});
			}
		},
		initComponentEvent() {
			//component droppable
			$(document.querySelectorAll(`.componentDroppable`)).droppable({
				tolerance: 'pointer',
				drop: (event, ui) => {
					const type = event.target.getAttribute(`data-type`);

					const componentTypeDirection =
						this.componentDataMapping[event.target.parentNode.parentNode.getAttribute(`component-id`)].componentTypeDirection;

					const itemId = ui.draggable[0].getAttribute(`data-item-id`);

					const selectAddType = callBack => {
						const componentCnt = document
							.querySelector(`#root`)
							.querySelectorAll(`.component, .component-horizontal, .component-vertical`).length;

						if (2 > componentCnt) {
							callBack(event.target.parentNode);
						} else {
							const groupArr = [event.target.parentNode];

							let group = event.target.parentNode.parentNode;

							while (true) {
								if (1 < group.querySelectorAll(`.component, .component-horizontal, .component-vertical`).length) {
									groupArr.push(group);
								}

								if (`root` === group.getAttribute(`id`)) break;
								else group = group.parentNode;
							}

							let selectedGroup = null;

							const modalInstance = this.$openModal({
								title: `${this.$t(`dashboard.head.add.widget`)} ${this.$t(`head.type`)}`,
								modalWidth: `800px`,
								contentList: [
									{
										template: `<select v-model="selectedGroupIdx" class="form-control" @change="groupChange">
													               <option v-for="(group, idx) in groupArr" :key="idx" :value="idx">
																       {{ $t('dashboard.component') + '_' + (idx + 1) }}
																   </option>
													           </select>`,
										data() {
											return {
												selectedGroupIdx: 0,
												groupArr
											};
										},
										mounted() {
											this.groupChange();
										},
										methods: {
											groupChange() {
												this.removeHover();

												selectedGroup = this.groupArr[this.selectedGroupIdx];

												const addHover = document.createElement(`div`);
												addHover.classList.add(`addHover`);

												selectedGroup.insertBefore(addHover, selectedGroup.firstChild);

												const addAreaHover = document.createElement(`div`);
												addAreaHover.classList.add(`addAreaHover`);

												let top, left, width, height;

												if (`l` == type) {
													top = `5%`;
													left = `5%`;
													width = `40%`;
													height = `90%`;
												} else if (`t` == type) {
													top = `5%`;
													left = `5%`;
													width = `90%`;
													height = `40%`;
												} else if (`r` == type) {
													top = `5%`;
													left = `55%`;
													width = `40%`;
													height = `90%`;
												} else if (`b` == type) {
													top = `55%`;
													left = `5%`;
													width = `90%`;
													height = `40%`;
												}

												addAreaHover.style.top = top;
												addAreaHover.style.left = left;
												addAreaHover.style.width = width;
												addAreaHover.style.height = height;

												selectedGroup.insertBefore(addAreaHover, selectedGroup.firstChild);
											},
											removeHover() {
												document.querySelectorAll(`.addHover, .addAreaHover`).forEach(element => {
													element.parentNode.removeChild(element);
												});
											}
										},
										destroyed() {
											this.removeHover();
										}
									}
								],
								buttonList: [
									{
										template: `<button type="button" @click="confirm" class="btn btn-primary">
															       {{ $t('head.ok') }}
															   </button>`,
										methods: {
											confirm() {
												callBack(selectedGroup);
												modalInstance.hideModal();
											}
										}
									}
								]
							});
						}
					};

					if ('l' === type || 'r' === type) {
						if (!componentTypeDirection || 'h' === componentTypeDirection) {
							selectAddType(element => {
								if (element.classList.contains(`component`)) initSameDirection(element, type, 'h', itemId);
								else initGroupDirection(element, type, itemId);

								this.setContainerHistory();

								this.initDraw();
							});
						} else {
							selectAddType(element => {
								if (element.classList.contains(`component`)) initDifferentDirection(element, type, 'h', itemId);
								else initGroupDirection(element, type, itemId);

								this.setContainerHistory();

								this.initDraw();
							});
						}
					} else {
						if (!componentTypeDirection || 'v' == componentTypeDirection) {
							selectAddType(element => {
								if (element.classList.contains(`component`)) initSameDirection(element, type, 'v', itemId);
								else initGroupDirection(element, type, itemId);

								this.setContainerHistory();

								this.initDraw();
							});
						} else {
							selectAddType(element => {
								if (element.classList.contains(`component`)) initDifferentDirection(element, type, 'v', itemId);
								else initGroupDirection(element, type, itemId);

								this.setContainerHistory();

								this.initDraw();
							});
						}
					}
				}
			});

			const initSameDirection = (element, type, componentTypeDirection, itemId) => {
				const groupDataObj = this.componentDataMapping[element.parentNode.getAttribute(`component-id`)];
				groupDataObj.componentTypeDirection = componentTypeDirection;

				//componentDataObj
				const componentDataObj = this.componentDataMapping[element.getAttribute(`component-id`)];

				//newComponentDataObj
				this.componentList.push(this.$refs.dashboardStructure.getBasicComponentStruct());

				const newComponentDataObj = this.componentList[this.componentList.length - 1];

				newComponentDataObj.componentId = getUUID();
				newComponentDataObj.pComponentId = groupDataObj.componentId;
				newComponentDataObj.componentType = 'C';
				newComponentDataObj.itemId = itemId;

				const perfItemInfo = this.perfItemList.find(perfItemInfo => perfItemInfo.itemId == newComponentDataObj.itemId);

				newComponentDataObj.componentName = perfItemInfo.itemName;

				const defaultTypeObj = this.getDefaultType(perfItemInfo);
				newComponentDataObj.chartType = defaultTypeObj.chartType;
				newComponentDataObj.targetType = defaultTypeObj.targetType;
				newComponentDataObj.unitType = defaultTypeObj.unitType;
				newComponentDataObj.inoutType = defaultTypeObj.inoutType;

				const adjustWidth =
					`h` === componentTypeDirection ? `${(componentDataObj.componentWidth.replace(/[^.0-9]/g, '') / 2).toFixed(4)}%` : `100%`;
				const adjustHeight =
					`h` === componentTypeDirection ? `100%` : `${(componentDataObj.componentHeight.replace(/[^.0-9]/g, '') / 2).toFixed(4)}%`;

				componentDataObj.componentWidth = adjustWidth;
				componentDataObj.componentHeight = adjustHeight;

				newComponentDataObj.componentWidth = adjustWidth;
				newComponentDataObj.componentHeight = adjustHeight;

				const newComponentOrder = `l` === type || `t` === type ? componentDataObj.componentOrder : componentDataObj.componentOrder + 1;

				this.componentList
					.filter(component => component.pComponentId === groupDataObj.componentId)
					.sort((a, b) => a.componentOrder - b.componentOrder)
					.forEach(component => {
						if (component.componentId === newComponentDataObj.componentId) {
							component.componentOrder = newComponentOrder;
						} else {
							component.componentOrder =
								component.componentOrder >= newComponentOrder ? component.componentOrder + 1 : component.componentOrder;
						}
					});
			};

			const initDifferentDirection = (element, type, componentTypeDirection, itemId) => {
				const componentDataObj = this.componentDataMapping[element.getAttribute(`component-id`)];

				//group
				this.componentList.push(this.$refs.dashboardStructure.getBasicComponentStruct());

				const newGroupDataObj = this.componentList[this.componentList.length - 1];

				newGroupDataObj.componentId = getUUID();
				newGroupDataObj.pComponentId = componentDataObj.pComponentId;
				newGroupDataObj.componentOrder = componentDataObj.componentOrder;
				newGroupDataObj.componentWidth = componentDataObj.componentWidth;
				newGroupDataObj.componentHeight = componentDataObj.componentHeight;
				newGroupDataObj.componentTypeDirection = componentTypeDirection;
				newGroupDataObj.componentType = `G`;

				//component element
				componentDataObj.pComponentId = newGroupDataObj.componentId;

				//new component element
				this.componentList.push(this.$refs.dashboardStructure.getBasicComponentStruct());

				const newComponentDataObj = this.componentList[this.componentList.length - 1];

				newComponentDataObj.componentId = getUUID();
				newComponentDataObj.pComponentId = newGroupDataObj.componentId;
				newComponentDataObj.componentType = `C`;
				newComponentDataObj.itemId = itemId;

				const perfItemInfo = this.perfItemList.find(perfItemInfo => perfItemInfo.itemId == newComponentDataObj.itemId);

				newComponentDataObj.componentName = perfItemInfo.itemName;

				const defaultTypeObj = this.getDefaultType(perfItemInfo);
				newComponentDataObj.chartType = defaultTypeObj.chartType;
				newComponentDataObj.targetType = defaultTypeObj.targetType;
				newComponentDataObj.unitType = defaultTypeObj.unitType;
				newComponentDataObj.inoutType = defaultTypeObj.inoutType;

				const adjustWidth = `h` === componentTypeDirection ? '50%' : '100%';
				const adjustHeight = `h` === componentTypeDirection ? '100%' : '50%';

				componentDataObj.componentWidth = adjustWidth;
				componentDataObj.componentHeight = adjustHeight;

				newComponentDataObj.componentWidth = adjustWidth;
				newComponentDataObj.componentHeight = adjustHeight;

				componentDataObj.componentOrder = `l` === type || `t` === type ? 2 : 1;
				newComponentDataObj.componentOrder = `l` === type || `t` === type ? 1 : 2;
			};

			const initGroupDirection = (element, type, itemId) => {
				const groupDataObj = this.componentDataMapping[element.getAttribute(`component-id`)];

				const groupComponentTypeDirection = groupDataObj.componentTypeDirection;

				groupDataObj.componentTypeDirection = `l` === type || `r` === type ? `h` : `v`;

				//group
				this.componentList.push(this.$refs.dashboardStructure.getBasicComponentStruct());

				const newGroupDataObj = this.componentList[this.componentList.length - 1];

				newGroupDataObj.componentId = getUUID();
				newGroupDataObj.pComponentId = groupDataObj.componentId;
				newGroupDataObj.componentOrder = `t` === type || `l` === type ? 2 : 1;
				newGroupDataObj.componentWidth = `t` === type || `b` === type ? '100%' : '50%';
				newGroupDataObj.componentHeight = `t` === type || `b` === type ? '50%' : '100%';
				newGroupDataObj.componentTypeDirection = groupComponentTypeDirection;
				newGroupDataObj.componentType = `G`;

				//new component element
				this.componentList.push(this.$refs.dashboardStructure.getBasicComponentStruct());

				const newComponentDataObj = this.componentList[this.componentList.length - 1];

				newComponentDataObj.componentId = getUUID();
				newComponentDataObj.pComponentId = groupDataObj.componentId;
				newComponentDataObj.componentOrder = `t` === type || `l` === type ? 1 : 2;
				newComponentDataObj.componentWidth = `t` === type || `b` === type ? '100%' : '50%';
				newComponentDataObj.componentHeight = `t` === type || `b` === type ? '50%' : '100%';
				newComponentDataObj.componentType = `C`;
				newComponentDataObj.itemId = itemId;

				const perfItemInfo = this.perfItemList.find(perfItemInfo => perfItemInfo.itemId === newComponentDataObj.itemId);

				newComponentDataObj.componentName = perfItemInfo.itemName;

				const defaultTypeObj = this.getDefaultType(perfItemInfo);
				newComponentDataObj.chartType = defaultTypeObj.chartType;
				newComponentDataObj.targetType = defaultTypeObj.targetType;
				newComponentDataObj.unitType = defaultTypeObj.unitType;
				newComponentDataObj.inoutType = defaultTypeObj.inoutType;

				this.componentList.filter(component => {
					if (component.pComponentId !== groupDataObj.componentId) return;

					if (component.componentId === newGroupDataObj.componentId) return;

					if (component.componentId === newComponentDataObj.componentId) return;

					component.pComponentId = newGroupDataObj.componentId;
				});
			};
			//component droppable

			//component resizable
			let totalOrgWidth, totalOrgHeight;

			const removeResizeHover = () => {
				document.querySelectorAll(`.componentHover`).forEach(element => {
					element.parentNode.removeChild(element);
				});
			};

			$(document.querySelectorAll(`.component, .component-horizontal, .component-vertical`)).resizable({
				handles: `w, n`,
				start: event => {
					removeResizeHover();

					const componentDataObj = this.componentDataMapping[event.target.getAttribute(`component-id`)];
					const prevComponentDataObj = this.componentDataMapping[event.target.previousSibling.getAttribute(`component-id`)];

					totalOrgWidth =
						Number(componentDataObj.componentWidth.replace(/[^.0-9]/g, ``)) +
						Number(prevComponentDataObj.componentWidth.replace(/[^.0-9]/g, ``));
					totalOrgHeight =
						Number(componentDataObj.componentHeight.replace(/[^.0-9]/g, ``)) +
						Number(prevComponentDataObj.componentHeight.replace(/[^.0-9]/g, ``));

					const axis = $(event.target).data('ui-resizable').axis;

					const comoponentTooltip = document.createElement('span');
					comoponentTooltip.classList.add(`resize-tooptip`);

					const componentHover = document.createElement(`div`);
					componentHover.setAttribute(`id`, `componentHover`);
					componentHover.classList.add(`componentHover`);
					componentHover.style.width = `${event.target.clientWidth - 10}px`;
					componentHover.style.height = `${event.target.clientHeight - 5}px`;
					componentHover.appendChild(comoponentTooltip);

					const preComoponentTooltip = document.createElement('span');
					preComoponentTooltip.classList.add(`resize-tooptip`);

					const prevCompoentHover = document.createElement(`div`);
					prevCompoentHover.setAttribute(`id`, `prevCompoentHover`);
					prevCompoentHover.classList.add(`componentHover`, `prevCompoentHover`);
					prevCompoentHover.style.width = `${event.target.previousSibling.clientWidth - 10}px`;
					prevCompoentHover.style.height = `${event.target.previousSibling.clientHeight - 5}px`;
					prevCompoentHover.appendChild(preComoponentTooltip);

					if (`w` === axis) {
						//27 >> resize-tooptip height
						const resizeTooltipTop = event.target.parentNode.clientHeight <= event.offsetY + 27 ? event.offsetY - 27 : event.offsetY;

						comoponentTooltip.style.top = `${resizeTooltipTop}px`;
						comoponentTooltip.style.left = `5px`;
						comoponentTooltip.innerText = `${Number(componentDataObj.componentWidth.replace(`%`, ``)).toFixed(2)}%`;

						preComoponentTooltip.style.top = `${resizeTooltipTop}px`;
						preComoponentTooltip.style.right = `5px`;
						preComoponentTooltip.innerText = `${Number(prevComponentDataObj.componentWidth.replace(`%`, ``)).toFixed(2)}%`;
					} else if ('n' == axis) {
						//60 >> resize-tooptip width
						const resizeTooltipLeft = event.target.parentNode.clientWidth <= event.offsetX + 60 ? event.offsetX - 60 : event.offsetX;

						comoponentTooltip.style.left = `${resizeTooltipLeft}px`;
						comoponentTooltip.style.top = `5px`;
						comoponentTooltip.innerText = `${Number(componentDataObj.componentHeight.replace(`%`, ``)).toFixed(2)}%`;

						preComoponentTooltip.style.left = `${resizeTooltipLeft}px`;
						preComoponentTooltip.style.bottom = `5px`;
						preComoponentTooltip.innerText = `${Number(prevComponentDataObj.componentHeight.replace(`%`, ``)).toFixed(2)}%`;
					}

					event.target.appendChild(componentHover);
					event.target.previousSibling.appendChild(prevCompoentHover);
				},
				resize: event => {
					componentResize(event);
				},
				stop: () => {
					removeResizeHover();

					this.setContainerHistory();

					this.initDraw();
				}
			});

			$(document.querySelectorAll(`.ui-resizable-handle`)).dblclick(event => {
				event.target = event.target.parentNode;
				componentResize(event, 50);
				this.setContainerHistory();
			});

			const componentResize = (event, sizePer) => {
				const componentDataObj = this.componentDataMapping[event.target.getAttribute(`component-id`)];
				const prevComponentDataObj = this.componentDataMapping[event.target.previousSibling.getAttribute(`component-id`)];
				const axis = $(event.target).data('ui-resizable').axis;

				if (sizePer) {
					totalOrgWidth =
						Number(componentDataObj.componentWidth.replace(/[^.0-9]/g, '')) +
						Number(prevComponentDataObj.componentWidth.replace(/[^.0-9]/g, ''));
					totalOrgHeight =
						Number(componentDataObj.componentHeight.replace(/[^.0-9]/g, '')) +
						Number(prevComponentDataObj.componentHeight.replace(/[^.0-9]/g, ''));
				}

				if ('w' === axis) {
					let prevWidthPer = null;

					if (sizePer) {
						prevWidthPer = sizePer;
					} else {
						const currentWidth = event.target.clientWidth;
						const prevWidth = event.target.previousSibling.clientWidth;
						const totalWidth = currentWidth + prevWidth;

						prevWidthPer = (prevWidth * 100) / totalWidth;
						prevWidthPer = 30 > prevWidthPer ? 30 : 70 < prevWidthPer ? 70 : prevWidthPer;
					}

					const prevWidthVal = totalOrgWidth * (prevWidthPer / 100);

					prevComponentDataObj.componentWidth = `${prevWidthVal.toFixed(4)}%`;
					event.target.previousSibling.style.width = prevComponentDataObj.componentWidth;

					componentDataObj.componentWidth = (totalOrgWidth - prevWidthVal).toFixed(4) + '%';
					event.target.style.width = componentDataObj.componentWidth;

					if (sizePer) return;

					const prevCompoentHover = document.querySelector(`#prevCompoentHover`);
					const componentHover = document.querySelector(`#componentHover`);

					prevCompoentHover.style.width = `${event.target.previousSibling.clientWidth - 10}px`;
					prevCompoentHover.querySelector(`.resize-tooptip`).innerText = `${prevWidthPer.toFixed(2)}%`;

					componentHover.style.width = `${event.target.clientWidth - 10}px`;
					componentHover.querySelector(`.resize-tooptip`).innerText = `${(100 - prevWidthPer).toFixed(2)}%`;
				} else if (`n` === axis) {
					let prevHeightPer = null;

					if (sizePer) {
						prevHeightPer = sizePer;
					} else {
						const currentHeight = event.target.clientHeight;
						const prevHeight = event.target.previousSibling.clientHeight;
						const totalHeight = currentHeight + prevHeight;

						prevHeightPer = (prevHeight * 100) / totalHeight;
						prevHeightPer = 30 > prevHeightPer ? 30 : 70 < prevHeightPer ? 70 : prevHeightPer;
					}

					const prevHeightVal = totalOrgHeight * (prevHeightPer / 100);

					prevComponentDataObj.componentHeight = `${prevHeightVal.toFixed(4)}%`;
					event.target.previousSibling.style.height = prevComponentDataObj.componentHeight;

					componentDataObj.componentHeight = `${(totalOrgHeight - prevHeightVal).toFixed(4)}%`;
					event.target.style.height = componentDataObj.componentHeight;

					if (sizePer) return;

					const prevCompoentHover = document.querySelector(`#prevCompoentHover`);
					const componentHover = document.querySelector(`#componentHover`);

					prevCompoentHover.style.height = `${event.target.previousSibling.clientHeight - 5}px`;
					prevCompoentHover.querySelector(`.resize-tooptip`).innerText = `${prevHeightPer.toFixed(2)}%`;

					componentHover.style.height = `${event.target.clientHeight - 10}px`;
					componentHover.querySelector(`.resize-tooptip`).innerText = `${(100 - prevHeightPer).toFixed(2)}%`;
				}
			};
			//component resizable

			//component delete
			document.querySelector(`#root`).addEventListener(`click`, event => {
				if (!event.target.classList.contains(`componentDelete`)) return;

				const componentDataObj = this.componentDataMapping[event.target.parentNode.getAttribute(`component-id`)];

				const reMainComponentList = this.componentList.filter(
					component => component.pComponentId === componentDataObj.pComponentId && component.componentId !== componentDataObj.componentId
				);

				[
					`xViewYAxisMax`,
					`xViewTrans`,
					`xViewMinData`,
					`xViewInstanceFilter`,
					`xViewadapterFilter`,
					`xViewtransactionFilter`,
					`datatableRowCnt`,
					`instanceSummaryColCnt`
				].forEach(key => {
					delete this.containerOptions[`${key}_${componentDataObj.componentId}`];
				});

				if (`-1` !== this.componentDataMapping[componentDataObj.pComponentId].pComponentId && 1 === reMainComponentList.length) {
					const anotherElementDataObj = this.componentDataMapping[reMainComponentList[0].componentId];

					const componentTypeDirection =
						this.componentDataMapping[this.componentDataMapping[anotherElementDataObj.pComponentId].pComponentId].componentTypeDirection;

					anotherElementDataObj.componentWidth =
						`h` === componentTypeDirection ? this.componentDataMapping[anotherElementDataObj.pComponentId].componentWidth : '100%';

					anotherElementDataObj.componentHeight =
						`h` === componentTypeDirection ? `100%` : this.componentDataMapping[anotherElementDataObj.pComponentId].componentHeight;

					anotherElementDataObj.pComponentId = this.componentDataMapping[anotherElementDataObj.pComponentId].pComponentId;

					this.componentList = this.componentList
						.filter(component => component.componentId !== componentDataObj.componentId)
						.filter(component => component.componentId !== componentDataObj.pComponentId);
				} else {
					const componentTypeDirection = this.componentDataMapping[componentDataObj.pComponentId].componentTypeDirection;

					const adjustWidth =
						`h` === componentTypeDirection
							? (componentDataObj.componentWidth.replace(/[^.0-9]/g, '') / reMainComponentList.length).toFixed(4)
							: 100;

					const adjustHeight =
						`h` === componentTypeDirection
							? 100
							: (componentDataObj.componentHeight.replace(/[^.0-9]/g, '') / reMainComponentList.length).toFixed(4);

					this.componentList
						.filter(
							component =>
								component.pComponentId === componentDataObj.pComponentId && component.componentId !== componentDataObj.componentId
						)
						.sort((a, b) => a.componentOrder - b.componentOrder)
						.forEach((component, idx) => {
							component.componentWidth =
								100 === adjustWidth ? `100%` : `${Number(component.componentWidth.replace(/[^.0-9]/g, ``)) + Number(adjustWidth)}%`;

							component.componentHeight =
								100 === adjustHeight
									? `100%`
									: `${Number(component.componentHeight.replace(/[^.0-9]/g, ``)) + Number(adjustHeight)}%`;

							component.componentOrder = idx + 1;
						});

					this.componentList = this.componentList.filter(component => component.componentId !== componentDataObj.componentId);

					if (
						`-1` === this.componentDataMapping[componentDataObj.pComponentId].pComponentId &&
						0 === this.componentList.filter(component => component.pComponentId === componentDataObj.pComponentId).length
					) {
						this.componentList = [];
					}
				}

				const componentDataMapping = {};

				for (const componentId in this.componentDataMapping) {
					if (!this.componentList.find(component => component.componentId === componentId)) continue;
					componentDataMapping[componentId] = this.componentDataMapping[componentId];
				}

				this.componentDataMapping = componentDataMapping;

				this.setContainerHistory();

				this.initDraw();
			});
			//component delete

			//config element event
			const getComponentId = element => {
				while (!element.getAttribute(`component-id`)) {
					element = element.parentNode;
				}

				return element.getAttribute(`component-id`);
			};

			document.querySelector(`#root`).addEventListener(`input`, event => {
				const name = event.target.getAttribute(`name`);

				if (
					![`componentName`, `xViewYAxisMax`, `xViewTrans`, `xViewMinData`, `datatableRowCnt`, `instanceSummaryColCnt`].find(
						pName => pName === name
					)
				)
					return;

				if (`componentName` === name) this.componentDataMapping[getComponentId(event.target)].componentName = event.target.value;
				else this.containerOptions[`${name}_${getComponentId(event.target)}`] = Number(event.target.value);
			});

			document.querySelector(`#root`).addEventListener(`change`, event => {
				const name = event.target.getAttribute(`name`);

				if (![`chartType`, `inoutType`, `selectedTarget`, `targetType`].find(pName => pName === name)) return;

				if (`chartType` === name) {
					this.componentDataMapping[getComponentId(event.target)].chartType = event.target.value;
				} else if (`inoutType` === name) {
					this.componentDataMapping[getComponentId(event.target)].inoutType = event.target.value;
				} else if (`selectedTarget` === name) {
					this.componentDataMapping[getComponentId(event.target)].monitorComponentTargets = [
						{
							pk: {
								componentId: getComponentId(event.target),
								componentTargetId: event.target.value
							}
						}
					];
				} else if (`targetType` === name) {
					this.componentDataMapping[getComponentId(event.target)].targetType = event.target.value;

					if (`EXTERNALLINE` === event.target.value || `INSTANCE` === event.target.value) {
						this.componentDataMapping[getComponentId(event.target)].monitorComponentTargets = [];
					} else {
						let targetIdKey, targetList;

						if (`ADAPTER` === event.target.value) {
							targetIdKey = `adapterId`;
							targetList = this.adapterList;
						} else if (`CONNECTOR` === event.target.value) {
							targetIdKey = `connectorId`;
							targetList = this.connectorList;
						} else if (`QUEUE` === event.target.value) {
							targetIdKey = `adapterId`;
							targetList = this.queueList;
						} else if (`THREAD` === event.target.value) {
							targetIdKey = `threadPoolId`;
							targetList = this.threadList;
						}

						this.componentDataMapping[getComponentId(event.target)].monitorComponentTargets = [
							{
								pk: {
									componentId: getComponentId(event.target),
									componentTargetId: targetList[0][targetIdKey]
								}
							}
						];
					}
				}

				this.setContainerHistory();

				this.initDraw();
			});
		},
		getDefaultType(perfItemObj) {
			let chartType = `NONE`;
			let targetType = `NONE`;

			//chart
			const chartTypeArr = [`LINE`, `COLUMN`, `INSTANCE`, `CONNECTOR`, `QUEUE`, `THREAD`, `SPEEDBAR`, `EXTERNALLINE`, `DATATABLE`, `XVIEW`];

			for (let i = 0; i < chartTypeArr.length; i++) {
				const findChartType = perfItemObj.monitorChartGroup.find(chartGroupInfo => chartTypeArr[i] === chartGroupInfo.pk.chartType);

				if (findChartType) {
					chartType = findChartType.pk.chartType;
					break;
				}
			}

			//target
			const targetTypeArr = [`INSTANCE`, `ADAPTER`, `CONNECTOR`, `QUEUE`, `THREAD`, `EXTERNALLINE`, `DATATABLE`];

			for (let i = 0; i < targetTypeArr.length; i++) {
				const tmpTargetType = perfItemObj.monitorTargetGroup.find(targetGroupInfo => targetTypeArr[i] == targetGroupInfo.pk.targetType);

				if (tmpTargetType) {
					targetType = tmpTargetType.pk.targetType;
					break;
				}
			}

			return {
				chartType: chartType,
				targetType: targetType,
				unitType: perfItemObj.unitType,
				inoutType: `Y` === perfItemObj.inOutExistYn ? `IN` : `NONE`
			};
		},
		setContainerState(type) {
			const sortedContainerStateList = this.containerHistoryList
				.map(containerState => cloneDeep(containerState))
				.sort((a, b) => a.saveTime - b.saveTime);

			const findIndex = sortedContainerStateList.findIndex(containerState => containerState.saveTime === this.currentContainerHistory.saveTime);

			this.redoDisabled = `redo` === type ? 0 === findIndex - 1 : false;
			this.undoDisabled = `redo` === type ? false : sortedContainerStateList.length - 1 == findIndex + 1;

			const tmpCurrentSaveContainer = 'redo' === type ? sortedContainerStateList[findIndex - 1] : sortedContainerStateList[findIndex + 1];

			if (this.currentContainerHistory.saveComponentList)
				this.currentContainerHistory.saveComponentList.splice(0, this.currentContainerHistory.saveComponentList.length);

			this.currentContainerHistory = merge(cloneDeep(this.currentContainerHistory), cloneDeep(tmpCurrentSaveContainer));

			this.componentList = this.currentContainerHistory.saveComponentList.map(component => cloneDeep(component));

			this.initDraw();
		},
		setContainerHistory() {
			const sortedContainerStateList = this.containerHistoryList
				.map(containerState => cloneDeep(containerState))
				.sort((a, b) => a.saveTime - b.saveTime);

			const findIndex = sortedContainerStateList.findIndex(containerState => containerState.saveTime === this.currentContainerHistory.saveTime);

			if (-1 < findIndex && findIndex !== this.containerHistoryList.length - 1) {
				this.containerHistoryList.splice(findIndex + 1);
				this.undoDisabled = true;
			}

			this.containerHistoryList.push({
				saveTime: Date.now(),
				saveComponentList: this.componentList.map(component => cloneDeep(component))
			});

			if (this.dashConstInfo.containerStateCnt === this.containerHistoryList.length) this.containerHistoryList.shift();

			this.redoDisabled = 1 >= this.containerHistoryList.length;

			if (this.currentContainerHistory.saveComponentList)
				this.currentContainerHistory.saveComponentList.splice(0, this.currentContainerHistory.saveComponentList.length);

			this.currentContainerHistory = merge(
				cloneDeep(this.currentContainerHistory),
				cloneDeep(this.containerHistoryList[this.containerHistoryList.length - 1])
			);
		},
		resetContainer() {
			this.redoDisabled = true;
			this.undoDisabled = true;

			this.componentList = `modify` === this.mod ? cloneDeep(this.containerInfoOrg.monitorComponents) : [];

			this.containerHistoryList = [];
			this.currentContainerHistory = {};

			this.setContainerHistory();

			this.initDraw();
		},
		resize() {
			this.widgetStyle.height = `${window.innerHeight - 72 - 56 - 12}px`;
			this.dashBodyStyle.maxHeight = `${document.body.querySelector(`#dashboard`).clientHeight}px`;
		},
		changeContainer() {
			this.$emit(`changeContainer`);
		},
		unloadContainer() {
			document.querySelector(`.wrap`).classList.remove(`dark`);
			document.querySelector(`#ct`).classList.remove(`ct-dashboard`);
		}
	}
};
</script>
