<template>
	<div id="panel" ref="panel">
		<header id="panel-header" class="panel-header sub-bar">
			<h2 class="sub-bar-tit">Insert</h2>
		</header>

		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item">
				<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 0 === selectedIdx }" @click="changeTab(0)">
					{{ $t('igate.adapterwizard') }}
				</a>
			</li>
			<li class="nav-item">
				<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 1 === selectedIdx }" @click="changeTab(1)">
					{{ $t('head.executionResult') }}
				</a>
			</li>
		</ul>

		<div class="panel-body tab-content">
			<div :class="{ [panelMode]: true, 'tab-pane': true, active: 0 === selectedIdx }">
				<section class="tab-section">
					<header class="sub-bar">
						<h3 class="sub-bar-text">
							{{ `${this.$t('igate.adapter')} ${this.$t('head.info')}` }}
						</h3>
						<div class="alert alert-warning" role="alert">
							<span style="font-size: 0.875rem">{{ $t('igate.adapterwizard.adapter.inputWarn') }}</span>
						</div>
					</header>
					<component
						ref="AdapterInfo"
						:is="require(`@/views/detail/adapterWizard/AdapterInfo.vue`).default"
						:detailInfo="{}"
						:detailData="detailData"
						:mode="panelMode"
						@changeAdapterInfo="changeAdapterWizardInfo"
					/>
					<PropertyInfo
						ref="AdapterPropertyInfo"
						:detailInfo="propertyInfo"
						:detailData="detailData"
						:mode="panelMode"
						:adapterType="adapterType"
						:messageType="messageType"
					/>
				</section>

				<hr style="background: #8a8b92" />

				<section class="tab-section">
					<header class="sub-bar" style="padding-top: 0px">
						<h3 class="sub-bar-text">
							{{ `${this.$t('igate.connector')} ${this.$t('head.info')}` }}
						</h3>
						<div class="alert alert-warning" role="alert">
							<span style="font-size: 0.875rem">{{ $t('igate.adapterwizard.connector.inputWarn') }}</span>
						</div>
					</header>
					<component
						ref="ConnectorInfo"
						:is="require(`@/views/detail/adapterWizard/ConnectorInfo.vue`).default"
						:detailInfo="{}"
						:detailData="detailData"
						:mode="panelMode"
						:adapterType="adapterType"
						:transactions="transactions"
						@changeConnectorInfo="changeAdapterWizardInfo"
					/>
					<PropertyInfo
						ref="ConnectorPropertyInfo"
						:detailInfo="propertyInfo"
						:detailData="detailData"
						:mode="panelMode"
						:adapterType="adapterType"
						:connectorInfo="connectorInfo"
					/>
				</section>

				<hr style="background: #8a8b92" />

				<section class="tab-section" style="padding-top: 0px">
					<header class="sub-bar">
						<h3 class="sub-bar-text">{{ `${this.$t('igate.instance')} ${this.$t('head.id')}` }}</h3>
					</header>
					<component
						ref="DeploiesInfo"
						:is="require(`@/views/detail/adapterWizard/DeploiesInfo.vue`).default"
						:detailInfo="{}"
						:detailData="detailData"
						:mode="panelMode"
						:adapterType="adapterType"
					/>
				</section>
			</div>
			<DetailResult
				:class="{ 'tab-pane': true, active: selectedIdx === 1 && showResultTab }"
				:httpReqResult="httpReqResult"
				v-if="toShowResult"
			/>
		</div>

		<footer id="panel-footer" class="panel-footer sub-bar">
			<div class="ml-auto">
				<a
					href="javascript:void(0);"
					class="btn btn-m"
					@click="newTab"
					v-if="newTabBtn && viewer && !isTools"
					:title="$t('common.open.new.tab')"
				>
					<i class="icon-info"></i><span class="hide">{{ $t('common.open.new.tab') }}</span>
				</a>
				<a href="javascript:void(0);" class="btn btn-m" @click="initialize" v-if="initializeBtn && editor" :title="$t('head.initialize')">
					<i class="icon-reset"></i><span class="hide">{{ $t('head.initialize') }}</span>
				</a>
				<a href="javascript:void(0);" class="btn btn-primary" @click="saveInfo" v-if="insertBtn && editor" :title="$t('head.insert')">
					<i class="icon-plus"></i>{{ $t('head.insert') }}
				</a>
			</div>
		</footer>
	</div>
</template>
<script>
import { mapState } from 'vuex';
import Detail from '@/components/detail/Detail.vue';
import PropertyInfo from '@/views/detail/adapterWizard/PropertyInfo.vue';
import { HttpReq, SearchToggleEventBus, WindowResizeEventBus, SideMenuToggleEventBus, getNumFromStr, openNewTab } from '@/utils/common.js';
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'Detail',
	extends: Detail,
	components: {
		PropertyInfo
	},
	props: {
		tabList: Array,
		info: Object,
		cudUrl: String,
		viewer: Boolean,
		editor: Boolean
	},
	data() {
		return {
			panelMode: 'add',
			panelBodyHeight: null,
			addPossible: true,

			//adapter
			adapterType: null,
			messageType: null,
			transactions: null,

			//connector
			connectorInfo: null,

			propertyInfo: {
				content: [
					{ type: 'text', vModel: 'pk.propertyKey', label: this.$t('common.property.key'), regExpType: 'id', readonly: true },
					{ type: 'text', vModel: 'propertyValue', label: this.$t('common.property.value'), regExpType: 'value' },
					{ type: 'text', vModel: 'propertyDesc', label: this.$t('head.description'), regExpType: 'desc', readonly: true }
				]
			}
		};
	},
	computed: {
		...mapState(['isTools'])
	},
	watch: {
		cudUrl: {
			immediate: true,
			handler() {
				this.httpReq = new HttpReq(`${this.cudUrl}`);
			}
		}
	},
	mounted() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);

		this.resize();
	},
	methods: {
		changeAdapterWizardInfo(info) {
			this[info.key] = info.value;
		},
		newTab() {
			openNewTab(JSON.parse(sessionStorage.getItem(`selectedMenuPathIdList`)).pop());
		},
		initialize() {
			this.panelMode = 'add';
			this.addPossible = true;
			this.adapterType = null;
			this.messageType = null;
			this.transactions = null;
			this.connectorInfo = null;
			this.$refs.AdapterInfo.initData();
			this.$refs.DeploiesInfo.initData();
			this.tabAllData = {};
		},
		async saveInfo() {
			if (!this.addPossible) {
				this.$alert({ type: 'warn', isXSSMode: true, message: this.$t('igate.adapterwizard.already.insertWarn') });
				return;
			}

			const checkComponent = ['AdapterInfo', 'ConnectorInfo'];
			const duplicateLabel = {
				AdapterInfo: this.$t('igate.duplicate.adapter.label'),
				ConnectorInfo: this.$t('igate.duplicate.connector.label')
			};

			let duplicateFailList = [];

			for await (const compName of checkComponent) {
				const tabAllData = this.$refs[compName].getData();

				//check null
				if (isEmpty(tabAllData)) {
					this.$alert({ type: 'warn', isXSSMode: true, message: this.$t('igate.adapterwizard.insertWarn') });
					return;
				}

				// check duplication
				const isDuplicateFail = await this.$refs[compName].checkDuplicate();

				if (isDuplicateFail) duplicateFailList.push(duplicateLabel[compName]);

				this.tabAllData = merge(this.tabAllData, tabAllData);
			}

			if (!isEmpty(duplicateFailList)) {
				this.$alert({ type: 'warn', message: this.$t('igate.duplicate.check', { 0: duplicateFailList.join(',') }) });
				return;
			}

			this.tabAllData = merge(this.tabAllData, this.$refs.AdapterPropertyInfo.getData('adapter'));
			this.tabAllData = merge(this.tabAllData, this.$refs.ConnectorPropertyInfo.getData('connector'));
			this.tabAllData = merge(this.tabAllData, this.$refs.DeploiesInfo.getData());

			const result = await this.httpReq.update(this.tabAllData, true);

			this.setData(result);
		},
		async setData(result) {
			const resultState = 'ok' === result.data.result;

			this.httpReqResult = {
				state: resultState,
				content: resultState ? result.data.response : result.data.error
			};

			this.selectedIdx = 1;
			this.showResultTab = true;

			if (!resultState) {
				this.tabAllData = {};
				return;
			}

			this.addPossible = false;
			this.panelMode = 'done';

			this.$alert({ type: 'normal', message: this.$t('head.insert.notice') });
		},
		resize() {
			const ct = document.getElementById('ct');
			const ctComputedStyle = getComputedStyle(ct);
			const ctPaddingTop = getNumFromStr(ctComputedStyle.getPropertyValue('padding-top'));
			const ctPaddingBottom = getNumFromStr(ctComputedStyle.getPropertyValue('padding-bottom'));

			const panelHeaderHeight = document.querySelector(`.panel-header`).offsetHeight;
			const navHeight = document.querySelector(`#panel`).querySelector(`.nav`).offsetHeight;
			const panelFooterHeight = document.querySelector(`.panel-footer`).offsetHeight;

			this.panelBodyHeight = `${window.innerHeight - ctPaddingTop - ctPaddingBottom - panelHeaderHeight - navHeight - panelFooterHeight}px`;
		}
	}
};
</script>
