<template>
	<component
		:is="component"
		ref="component"
		:containerInfo="containerInfo"
		@changeContainer="changeContainer"
		:dashConstInfo="dashConstInfo"
		:viewer="viewer"
		:editor="editor"
	/>
</template>

<script>
import { HttpReq, getPrivilege, getHttpUrl } from '@/utils/common.js';

import DashboardView from '@/views/main/dashboard/DashboardView.vue';
import DashboardAdd from '@/views/main/dashboard/DashboardAdd.vue';
import DashboardModify from '@/views/main/dashboard/DashboardModify.vue';

export default {
	name: `Dashboard`,
	props: {
		pageInfo: {
			type: Object,
			default: () => ({})
		}
	},
	data() {
		return {
			component: null,
			containerInfo: null,
			dashConstInfo: null,

			scriptUrlList: [`/custom/js/sketch.min.js`],
			cssNameList: [`dashboard`, `gicon`, `theme.dark`],

			viewer: false,
			editor: false
		};
	},
	async created() {
		const promiseList = [];

		this.cssNameList.forEach(name => {
			const link = document.createElement(`link`);
			link.id = `dashboard_${name}`;
			link.rel = `stylesheet`;
			link.type = `text/css`;
			link.href = getHttpUrl(`/custom/css/${name}.css`);

			document.head.appendChild(link);

			promiseList.push(
				new Promise(resolve => {
					link.addEventListener(`load`, () => {
						resolve();
					});
				})
			);
		});

		this.scriptUrlList.forEach(scriptUrl => {
			const script = document.createElement(`script`);
			script.type = `text/javascript`;
			script.src = getHttpUrl(scriptUrl);

			document.querySelector(`#ct`).appendChild(script);

			promiseList.push(
				new Promise(resolve => {
					script.addEventListener(`load`, () => {
						resolve();
					});
				})
			);
		});

		await Promise.all(promiseList);

		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;

		const result = await new HttpReq(`/igate/page/common/pageInfo.json`).read({ menuUrl: `/Dashboard` });

		if (`ok` !== result.data.result) return;

		this.dashConstInfo = new Function(`${result.data.object} return info;`)();

		this.component = DashboardView;
	},
	methods: {
		changeContainer(mod, containerInfo) {
			this.component = null;

			this.$nextTick(() => {
				this.containerInfo = null;

				if (`add` === mod) {
					this.component = DashboardAdd;
					this.containerInfo = containerInfo;
				} else if (`modify` === mod) {
					this.component = DashboardModify;
					this.containerInfo = containerInfo;
				} else if (`view` === mod) {
					this.component = DashboardView;
				}
			});
		}
	},
	beforeDestroy() {
		document.querySelectorAll(`[id*=dashboard_]`).forEach(info => {
			document.head.removeChild(info);
		});

		document
			.querySelector(`#ct`)
			.querySelectorAll(`script`)
			.forEach(info => {
				document.querySelector(`#ct`).removeChild(info);
			});
	}
};
</script>
