<template>
	<div id="panel" ref="panel" :class="panelClass">
		<div class="panel-dialog">
			<div class="panel-content" :style="panelContentStyle">
				<div class="resize-bar"></div>

				<header id="panel-header" class="panel-header sub-bar">
					<h2 class="sub-bar-tit">Detail</h2>
					<div class="ml-auto">
						<button type="button" class="btn-icon" @click="expandCollapsePanel" :title="$t('head.expand')">
							<i class="icon-expand"></i>
						</button>
						<button type="button" class="btn-icon" @click="closePanel" :title="$t('head.close')">
							<i class="icon-close"></i>
						</button>
					</div>
				</header>
				<ul class="nav nav-tabs flex-shrink-0">
					<li class="nav-item">
						<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 0 === selectedIdx }" @click="changeTab(0)">
							{{ $t(`head.make`) }}
						</a>
					</li>
					<li class="nav-item">
						<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 1 === selectedIdx }" @click="changeTab(1)">
							{{ $t(`head.import`) }}
						</a>
					</li>
				</ul>
				<!-- tab-pane -->
				<div class="panel-body tab-content">
					<div :class="{ detail: true, 'tab-pane': true, active: 0 === selectedIdx }">
						<DetailMake :detailData="selectedDetailData" :viewer="viewer" :editor="editor" />
					</div>
					<div :class="{ detail: true, 'tab-pane': true, active: 1 === selectedIdx }">
						<DetailImport :detailData="selectedDetailData" :viewer="viewer" :editor="editor" />
					</div>
				</div>

				<footer id="panel-footer" class="panel-footer sub-bar"></footer>
			</div>
		</div>
	</div>
</template>

<script>
import Detail from '@/components/detail/Detail.vue';
import DetailMake from '@/views/detail/migration/record/DetailMake.vue';
import DetailImport from '@/views/detail/migration/record/DetailImport.vue';

export default {
	name: `MigrationDetail`,
	extends: Detail,
	props: {
		selectedDetailData: Object
	},
	components: {
		DetailMake,
		DetailImport
	},
	watch: {
		mode: {
			deep: true,
			handler() {
				this.selectedIdx = `make` === this.mode ? 0 : 1;
				this.openPanel();
			}
		}
	},
	methods: {
		getTargetObj() {
			return;
		}
	}
};
</script>
