<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('head.insert')">
					<i class="icon-plus-circle"></i>
				</button>
				<label class="col">{{ $t('igate.adapter.event') }}</label>
				<label class="col">{{ `${$t('igate.operation')} ${$t('head.id')}` }}</label>
				<label class="col">{{ $t('igate.adapter.cronExpression') }}</label>
				<label class="col">{{ `${$t('igate.calendar')} ${$t('head.id')}` }}</label>
				<label class="col">{{ $t('igate.adapter.disabledYn') }}</label>
			</div>
			<div class="form-table-wrap">
				<div class="form-table-body" v-for="(detailObj, index) in propertyList" :key="detailObj.idx">
					<button type="button" class="btn-icon" v-if="!readonly && detailObj.require"><i class="icon-star"></i></button>
					<button
						type="button"
						class="btn-icon"
						@click="removeProperty(index)"
						v-if="!readonly && !detailObj.require"
						:title="$t('head.delete')"
					>
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div class="detail-content-common">
							<selectBox
								:ref="`pk.adapterEvent_${index}`"
								vModel="pk.adapterEvent"
								:val="detailObj['pk.adapterEvent']"
								:classObject="{ 'form-control': true, 'view-disabled': true }"
								:optionInfo="{
									url: '/common/property/properties.json',
									params: {
										propertyId: 'List.Adapter.Event',
										orderByKey: true
									},
									optionListName: 'adapterEventList',
									optionFor: 'option in adapterEventList',
									optionValue: 'option.pk.propertyKey',
									optionText: 'option.propertyValue'
								}"
								:readonly="readonly"
								@changeEvt="changeEvt"
							/>
						</div>
					</div>
					<div class="col">
						<div class="detail-content-common">
							<div class="input-group">
								<inputText
									:ref="`operationId_${index}`"
									vModel="operationId"
									:val="detailObj['operationId']"
									:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
									:readonly="true"
									style="width: calc(100% - 110px)"
								/>
								<button
									type="button"
									class="btn"
									style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
									@click="openModalAccordingToType('operationModalInfo', $refs[`operationId_${index}`])"
									v-if="!readonly"
								>
									<i class="icon-srch" style="margin-right: 0px"></i>
								</button>
								<button
									type="button"
									class="btn"
									style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
									@click="$refs[`operationId_${index}`][0].setData(null)"
									v-if="!readonly"
									:title="$t('head.initialize')"
								>
									<i class="icon-reset" style="margin-right: 0px"></i>
								</button>
								<button
									type="button"
									class="btn"
									style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
									@click="openOperationModal($refs[`operationId_${index}`])"
									:title="$t('head.open')"
								>
									<i class="icon-link" style="margin-right: 0px"></i>
								</button>
							</div>
						</div>
					</div>
					<div class="col">
						<div
							:class="`detail-content-${readonly || detailObj[`isDisabled`] ? 'common' : 'regExp'}`"
							:style="{ width: readonly || detailObj[`isDisabled`] ? '100%' : 'calc(100% - 70px)' }"
						>
							<div class="input-group">
								<inputText
									:ref="`cronExpression_${index}`"
									vModel="cronExpression"
									:val="detailObj['cronExpression']"
									:classObject="{
										'form-control': readonly || detailObj[`isDisabled`],
										'regExp-text': !readonly && !detailObj[`isDisabled`],
										'view-disabled': true,
										dataKey: false
									}"
									:readonly="readonly || detailObj[`isDisabled`]"
									@setLengthCnt="info => setLengthCnt({ [`cronExpression_${index}`]: info.cronExpression })"
									:style="{ width: readonly || detailObj[`isDisabled`] ? '100%' : 'calc(100% - 170px)' }"
								/>
								<span class="letterLength" v-if="!readonly && !detailObj[`isDisabled`]" style="margin: 0px">
									{{ `(${curLengthCnt[`cronExpression_${index}`]}/${maxLengthCnt[`cronExpression_${index}`]})` }}
								</span>
							</div>
						</div>
						<button
							type="button"
							class="btn"
							style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
							@click="openCronModal($refs[`cronExpression_${index}`])"
							v-if="!readonly && !detailObj[`isDisabled`]"
						>
							<i class="icon-srch" style="margin-right: 0px"></i>
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
							@click="
								() => {
									$refs[`cronExpression_${index}`][0].setData(null);
									curLengthCnt[`cronExpression_${index}`] = 0;
								}
							"
							v-if="!readonly && !detailObj[`isDisabled`]"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
					<div class="col">
						<div class="detail-content-common">
							<div class="input-group">
								<inputText
									:ref="`calendarId_${index}`"
									vModel="calendarId"
									:val="detailObj['calendarId']"
									:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
									:readonly="true"
									style="width: calc(100% - 110px)"
								/>

								<button
									type="button"
									class="btn"
									style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
									@click="openModalAccordingToType('calendarModalInfo', $refs[`calendarId_${index}`])"
									v-if="!readonly && !detailObj[`isDisabled`]"
								>
									<i class="icon-srch" style="margin-right: 0px"></i>
								</button>
								<button
									type="button"
									class="btn"
									style="margin-left: 3px; min-width: 0px; padding-left: 0.5rem"
									@click="$refs[`calendarId_${index}`][0].setData(null)"
									v-if="!readonly && !detailObj[`isDisabled`]"
									:title="$t('head.initialize')"
								>
									<i class="icon-reset" style="margin-right: 0px"></i>
								</button>
							</div>
						</div>
					</div>
					<div class="col">
						<div class="detail-content-common">
							<selectBox
								:ref="`disabledYn_${index}`"
								vModel="disabledYn"
								:val="detailObj['disabledYn']"
								:classObject="{ 'form-control': true, 'view-disabled': true }"
								:optionInfo="{
									url: '/common/property/properties.json',
									params: {
										propertyId: 'List.Yn',
										orderByKey: true
									},
									optionListName: 'adapterDisabledYnList',
									optionFor: 'option in adapterDisabledYnList',
									optionValue: 'option.pk.propertyKey',
									optionText: 'option.propertyValue'
								}"
								:readonly="readonly || detailObj[`isDisabled`]"
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import DetailProperty from '@/components/detail/DetailProperty.vue';
import { getUUID, parseFlattenObj, getRegExpInfo, parseHierarchyObj } from '@/utils/common.js';
import isEmpty from 'lodash/isEmpty';
import merge from 'lodash/merge';
import isNil from 'lodash/isNil';

export default {
	name: 'PropertyInfo',
	extends: DetailProperty,
	props: {
		adapterType: String
	},
	data() {
		return {
			keyArr: ['pk.adapterEvent', 'operationId', 'cronExpression', 'calendarId', 'disabledYn'],

			evtNameArr: [`request.extract`, `process.open`, `process.close`, `process.pause`, `process.resume`],

			operationModalInfo: {
				title: this.$t('igate.operation'),
				search: {
					list: [
						{
							type: 'text',
							vModel: 'operationId',
							label: this.$t('head.id'),
							placeholder: this.$t('head.searchId'),
							regExpType: 'searchId'
						},
						{
							type: 'text',
							vModel: 'operationName',
							label: this.$t('head.name'),
							placeholder: this.$t('head.searchName'),
							regExpType: 'name'
						},
						{
							type: 'select',
							vModel: 'operationType',
							label: this.$t('common.type'),
							val: 'A',
							optionInfo: {
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Operation.OperationType',
									orderByKey: true
								},
								optionListName: 'operationTypeList',
								optionFor: 'option in operationTypeList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue',
								optionIf: `option.pk.propertyKey === 'A'`,
								isViewAll: false
							}
						},
						{
							type: 'dataList',
							vModel: 'pageSize',
							label: this.$t('head.listCount'),
							val: '10',
							optionInfo: {
								optionFor: 'option in [10, 100, 1000]',
								optionValue: 'option',
								optionText: 'option'
							}
						}
					]
				},
				button: {
					list: [{ id: 'initialize', isUse: true }]
				},
				grid: {
					url: '/igate/operation/searchPopup.json',
					totalCntUrl: '/igate/operation/rowCount.json',
					paging: {
						isUse: true,
						side: 'server'
					},
					options: {
						columns: [
							{
								header: this.$t('head.id'),
								name: 'operationId'
							},
							{
								header: this.$t('head.name'),
								name: 'operationName'
							}
						]
					}
				},
				rowClickedCallback: function (rowInfo) {
					return rowInfo.operationId;
				}
			},

			calendarModalInfo: {
				title: this.$t('igate.calendar'),
				search: {
					list: [
						{
							type: 'text',
							vModel: 'calendarId',
							label: this.$t('head.id'),
							placeholder: this.$t('head.searchId'),
							regExpType: 'id'
						},
						{
							type: 'text',
							vModel: 'calendarName',
							label: this.$t('head.name'),
							placeholder: this.$t('head.searchName'),
							regExpType: 'name'
						},
						{
							type: 'text',
							vModel: 'calendarDesc',
							label: this.$t('head.description'),
							placeholder: this.$t('head.searchComment'),
							regExpType: 'desc'
						},
						{
							type: 'dataList',
							vModel: 'pageSize',
							label: this.$t('head.listCount'),
							val: '10',
							optionInfo: {
								optionFor: 'option in [10, 100, 1000]',
								optionValue: 'option',
								optionText: 'option'
							}
						}
					]
				},
				button: {
					list: [{ id: 'initialize', isUse: true }]
				},
				grid: {
					url: '/igate/calendar/searchPopup.json',
					totalCntUrl: '/igate/calendar/rowCount.json',
					paging: {
						isUse: true,
						side: 'server'
					},
					options: {
						columns: [
							{
								header: this.$t('head.id'),
								name: 'calendarId'
							},
							{
								header: this.$t('head.name'),
								name: 'calendarName'
							},
							{
								header: this.$t('head.description'),
								name: 'calendarDesc'
							},
							{
								header: this.$t('igate.calendar.saturday'),
								name: 'saturdayYn'
							},
							{
								header: this.$t('igate.calendar.sunday'),
								name: 'sundayYn'
							}
						]
					}
				},
				rowClickedCallback: function (rowInfo) {
					return rowInfo.calendarId;
				}
			}
		};
	},
	methods: {
		getData() {
			const rtnArr = [];

			for (let index = 0; index < this.propertyList.length; index++) {
				let rtnObj = {};

				this.keyArr.forEach(key => {
					rtnObj = merge(rtnObj, this.$refs[`${key}_${index}`][0].getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				adapterOperations: rtnArr
			};
		},
		addProperty() {
			const key = `cronExpression_${this.propertyList.length}`;

			this.setLengthCnt({ [key]: 0 });
			this.maxLengthCnt[key] = getRegExpInfo('cron').maxLength;

			this.propertyList.push({ idx: getUUID(), disabledYn: `N`, isDisabled: true });
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);

			this.curLengthCnt = {};
			this.maxLengthCnt = {};

			this.$nextTick(() => {
				this.propertyList.forEach((property, bodyIndex) => {
					const key = `cronExpression_${bodyIndex}`;
					const dataObj = this.$refs[key][0].getData();

					this.setLengthCnt({ [key]: isNil(dataObj.cronExpression) ? 0 : String(dataObj.cronExpression).length });
					this.maxLengthCnt[key] = getRegExpInfo('cron').maxLength;
				});
			});
		},
		async setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			if ('undefined' !== typeof this.detailData['adapterOperations']) {
				this.propertyList = [...this.detailData['adapterOperations']];

				this.propertyList.forEach((propertyInfo, index) => {
					propertyInfo.idx = getUUID();

					propertyInfo.isDisabled =
						`undefined` === typeof this.evtNameArr.find(evtName => evtName === propertyInfo.pk.adapterEvent) ? true : false;

					const key = `cronExpression_${index}`;

					this.setLengthCnt({ [key]: propertyInfo.cronExpression ? propertyInfo.cronExpression.length : 0 });
					this.maxLengthCnt[key] = getRegExpInfo('cron').maxLength;
				});
			} else {
				this.propertyList = [];
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		async openOperationModal(element) {
			const operationId = element[0].getData().operationId;

			if (!operationId) {
				this.$alert({ type: 'warn', message: this.$t('head.no.data.warn') });
				return;
			}

			this.$openModal({
				title: this.$t('igate.operation'),
				fullscreen: true,
				contentList: [
					{
						template: `<OperationModal ref="OperationModal" :config="config"/>`,
						components: {
							OperationModal: require(`@/views/modal/operation/OperationModal.vue`).default
						},
						data() {
							return {
								config: {
									operationId: operationId
								}
							};
						}
					}
				]
			});
		},
		openModalAccordingToType(modalInfoName, element) {
			this.openModal(this[modalInfoName], element);
		},
		openCronModal(element) {
			let cron = null;

			const refElement = element[0];
			const data = refElement.getData().cronExpression;
			const regexr =
				// eslint-disable-next-line no-useless-escape
				/^\s*($|#|\w+\s*=|(\?|\*|(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?(?:,(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?)*)\s(\?|\*|(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?(?:,(?:[0-5]?\d)(?:(?:-|\/|\,)(?:[0-5]?\d))?)*)\s(\?|\*|(?:[01]?\d|2[0-3])(?:(?:-|\/|\,)(?:[01]?\d|2[0-3]))?(?:,(?:[01]?\d|2[0-3])(?:(?:-|\/|\,)(?:[01]?\d|2[0-3]))?)*)\s(\?|\*|(?:0?[1-9]|[12]\d|3[01])(?:(?:-|\/|\,)(?:0?[1-9]|[12]\d|3[01]))?(?:,(?:0?[1-9]|[12]\d|3[01])(?:(?:-|\/|\,)(?:0?[1-9]|[12]\d|3[01]))?)*)\s(\?|\*|(?:[1-9]|1[012])(?:(?:-|\/|\,)(?:[1-9]|1[012]))?(?:L|W)?(?:,(?:[1-9]|1[012])(?:(?:-|\/|\,)(?:[1-9]|1[012]))?(?:L|W)?)*|\?|\*|(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC)(?:(?:-)(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC))?(?:,(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC)(?:(?:-)(?:JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC))?)*)\s(\?|\*|(?:[0-6])(?:(?:-|\/|\,|#)(?:[0-6]))?(?:,(?:[0-6])(?:(?:-|\/|\,|#)(?:[0-6]))?)*|\?|\*|(?:MON|TUE|WED|THU|FRI|SAT|SUN)(?:(?:-)(?:MON|TUE|WED|THU|FRI|SAT|SUN))?(?:,(?:MON|TUE|WED|THU|FRI|SAT|SUN)(?:(?:-)(?:MON|TUE|WED|THU|FRI|SAT|SUN))?)*))$/i;

			if (data) {
				cron = data.split(' ');

				if (!regexr.test(data) || cron[0] == '' || cron[3] == cron[5]) {
					this.$alert({ type: 'warn', message: this.$t('igate.job.error') });
					return;
				}
			} else {
				cron = ['*', '*', '*', '*', '*', '?'];
			}

			const modalInstance = this.$openModal({
				title: this.$t('igate.job.cronExpression'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
										<CronExpressionModal ref="CronExpressionModal" :cron="cron" />
									</div>`,
						components: {
							CronExpressionModal: require(`@/views/modal/job/CronExpressionModal.vue`).default
						},
						data() {
							return {
								cron: cron
							};
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" class="btn btn-primary" @click="clickOk">{{ $t('head.ok') }}</button>`,
						methods: {
							clickOk() {
								refElement.setData(modalInstance.$refs.content[0].$refs.CronExpressionModal.cronResult);

								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		changeEvt() {
			const rtnArr = [];

			for (let index = 0; index < this.propertyList.length; index++) {
				let rtnObj = {
					idx: this.propertyList[index].idx
				};

				this.keyArr.forEach(key => {
					rtnObj = merge(rtnObj, this.$refs[`${key}_${index}`][0].getData());
				});

				rtnObj.isDisabled = `undefined` === typeof this.evtNameArr.find(evtName => evtName === rtnObj[`pk.adapterEvent`]) ? true : false;

				if (rtnObj.isDisabled) {
					rtnObj.cronExpression = ``;
					rtnObj.calendarId = ``;
					rtnObj.disabledYn = `N`;
				}

				rtnArr.push(rtnObj);
			}

			this.propertyList = null;

			this.$nextTick(() => {
				this.propertyList = rtnArr;
			});
		}
	}
};
</script>
