<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('head.insert')">
					<i class="icon-plus-circle"></i>
				</button>
				<label class="col">{{ $t('common.property.key') }}</label>
				<label class="col">{{ $t('common.property.value') }}</label>
				<label class="col">{{ $t('head.description') }}</label>
			</div>
			<div class="form-table-wrap">
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="propertyObj.idx">
					<button type="button" class="btn-icon" v-if="!readonly && propertyObj.require"><i class="icon-star"></i></button>
					<button
						type="button"
						class="btn-icon"
						@click="removeProperty(bodyIndex)"
						v-if="!readonly && !propertyObj.require"
						:title="$t('head.delete')"
					>
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div :class="`detail-content-${!getReadonly(propertyObj) && !propertyObj.require ? 'regExp' : 'common'}`">
							<dataList
								:ref="`component${bodyIndex}`"
								vModel="pk.propertyKey"
								:classObject="{
									'form-control': !(!getReadonly(propertyObj) && !propertyObj.require),
									'regExp-dataList': !getReadonly(propertyObj) && !propertyObj.require,
									'view-disabled': true,
									dataKey: false
								}"
								:val="propertyObj['pk.propertyKey']"
								:readonly="readonly || propertyObj.require"
								:optionInfo="keyIdOptionInfo"
								@changeEvt="changeEvt($event, bodyIndex)"
								regExpType="id"
								@setLengthCnt="info => setLengthCnt({ [`${`pk.propertyKey`}_${bodyIndex}`]: info[`pk.propertyKey`] })"
							/>
							<span class="letterLength" v-if="!getReadonly(propertyObj) && !propertyObj.require">
								{{
									`(${curLengthCnt[`${`pk.propertyKey`}_${bodyIndex}`]}
									 /${maxLengthCnt[`${`pk.propertyKey`}_${bodyIndex}`]})`
								}}
							</span>
						</div>
					</div>

					<div class="col">
						<div :class="`detail-content-${!getReadonly(propertyObj) ? 'regExp' : 'common'}`">
							<passWord
								:ref="`component${bodyIndex}`"
								vModel="propertyValue"
								:classObject="{
									'form-control': getReadonly(propertyObj),
									'regExp-password': !getReadonly(propertyObj),
									'view-disabled': true,
									dataKey: false
								}"
								regExpType="value"
								:readonly="readonly"
								:val="propertyObj['propertyValue']"
								@setLengthCnt="info => setLengthCnt({ [`${`propertyValue`}_${bodyIndex}`]: info[`propertyValue`] })"
								v-if="propertyObj.cipher"
							/>
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="propertyValue"
								:val="propertyObj['propertyValue']"
								:classObject="{
									'form-control': getReadonly(propertyObj),
									'regExp-text': !getReadonly(propertyObj),
									'view-disabled': true,
									dataKey: false
								}"
								:readonly="readonly"
								regExpType="value"
								@setLengthCnt="info => setLengthCnt({ [`${`propertyValue`}_${bodyIndex}`]: info[`propertyValue`] })"
								v-else
							/>
							<span class="letterLength" v-if="!getReadonly(propertyObj)">
								{{
									`(${curLengthCnt[`${`propertyValue`}_${bodyIndex}`]}
									 /${maxLengthCnt[`${`propertyValue`}_${bodyIndex}`]})`
								}}
							</span>
						</div>
					</div>
					<div class="col">
						<div style="width: 100%">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="propertyDesc"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['propertyDesc']"
								:readonly="true"
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import isNil from 'lodash/isNil';
import { parseHierarchyObj, parseFlattenObj, getUUID, HttpReq, getRegExpInfo } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: 'PropertyInfo',
	extends: DetailProperty,
	props: {
		connectorType: String
	},
	data() {
		return {
			regArr: [
				{
					key: 'pk.propertyKey',
					regExpType: 'id'
				},
				{
					key: 'propertyValue',
					regExpType: 'value'
				}
			],
			keyIdOptionInfo: {
				optionFor: `option in []`,
				optionValue: `option.pk.propertyKey`,
				optionText: `option.pk.propertyKey`
			}
		};
	},
	watch: {
		detailData() {
			this.setDataList(this.detailData.connectorType);
		},
		connectorType() {
			this.setDataList(this.connectorType);
		}
	},
	methods: {
		async setDataList(connectorType) {
			if (!connectorType) return;

			const result = await new HttpReq(`/common/property/properties.json`).read({
				propertyId: `Property.Connector.${connectorType}`,
				orderByKey: true
			});

			const connectorPropertyList = JSON.stringify(result.data.object).replace(/"/g, `'`);

			this.keyIdOptionInfo.optionFor = `option in ${connectorPropertyList}`;
		},
		getData() {
			const rtnArr = [];

			for (let i = 0; i < this.propertyList.length; i++) {
				let rtnObj = {};

				this.$refs[`component${i}`].forEach(comp => {
					rtnObj = merge(rtnObj, comp.getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				connectorProperties: rtnArr
			};
		},
		addProperty() {
			const rowInfo = {
				idx: getUUID(),
				'pk.propertyKey': '',
				propertyValue: '',
				propertyDesc: ''
			};

			this.regArr.forEach(element => {
				if (element.regExpType) {
					const key = `${element.key}_${this.propertyList.length}`;
					this.setLengthCnt({ [key]: 0 });
					this.maxLengthCnt[key] = getRegExpInfo(element.regExpType).maxLength;
				}

				rowInfo[element.key] = element.val ? element.val : null;
			});

			this.propertyList.push(rowInfo);
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);

			this.$nextTick(() => {
				this.curLengthCnt = {};
				this.maxLengthCnt = {};

				this.propertyList.forEach((property, bodyIndex) => {
					let rtnObj = {};

					this.$refs[`component${bodyIndex}`].forEach(comp => {
						rtnObj = merge(rtnObj, comp.getData());
					});

					for (const key in rtnObj) {
						if (`propertyDesc` === key) continue;

						this.setLengthCnt({ [`${key}_${bodyIndex}`]: isNil(rtnObj[key]) ? 0 : String(rtnObj[key]).length });
						this.maxLengthCnt[`${key}_${bodyIndex}`] = getRegExpInfo(
							this.regArr.find(element => element.key === key).regExpType
						).maxLength;
					}
				});
			});
		},
		setPropertyList(connectorProperties) {
			if ('undefined' !== typeof connectorProperties) {
				this.propertyList = [...connectorProperties];
			} else if ('undefined' !== typeof this.detailData.connectorProperties) {
				this.propertyList = [...this.detailData.connectorProperties];
			} else {
				this.propertyList = [];
				return;
			}

			this.propertyList.forEach((propertyInfo, index) => {
				propertyInfo.idx = getUUID();
				const clonePropertyInfo = parseFlattenObj(propertyInfo);

				this.regArr.forEach(element => {
					if (!element.regExpType) return;

					const key = `${element.key}_${index}`;
					this.setLengthCnt({ [key]: clonePropertyInfo[element.key] ? String(clonePropertyInfo[element.key]).length : 0 });
					this.maxLengthCnt[key] = getRegExpInfo(element.regExpType).maxLength;
				});
			});

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		async changeEvt(info, index) {
			if (info.value.startsWith('#')) return;

			const connectorType = this.connectorType ? this.connectorType : this.detailData.connectorType;

			if (!connectorType) return;

			const httpReq = new HttpReq('/common/property/properties.json');

			const result = await httpReq.read({
				propertyId: `Property.Connector.${connectorType}`,
				propertyKey: info.value,
				orderByKey: true
			});

			if (`ok` !== result.data.result) return;

			if (0 === result.data.object.length) return;

			const connectorInfo = result.data.object[0];

			this.propertyList[index][`pk.propertyKey`] = connectorInfo.pk.propertyKey;
			this.propertyList[index][`propertyValue`] = connectorInfo.propertyValue;
			this.propertyList[index][`propertyDesc`] = connectorInfo.propertyDesc;
			this.propertyList[index][`cipher`] = `Y` === connectorInfo.cipherYn;
			this.propertyList[index][`require`] = `Y` === connectorInfo.requireYn;
		}
	}
};
</script>
