<template>
	<div style="margin-bottom: 30px">
		<div class="row frm-row" v-for="(label, index) in connectorLabelList" :key="index">
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ `${$t('igate.connector')} ${$t('head.type')}${connectorLabelList[index]}` }} </span>
						&nbsp;
						<label class="control-label warningLabel" style="padding-bottom: 0px">
							<span>{{ $t('igate.adapterwizard.connectorType.chagneWarn') }}</span>
						</label>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							:ref="`connectorType_${index}`"
							vModel="connectorType"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || !adapterType || (isFileExternal && connectorTypeReadOnly[index])"
							:optionInfo="{
								optionListName: 'connectorTypeList',
								optionFor: 'option in connectorTypeList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
							:optionListData="connectorTypeFilterList"
							@changeEvt="info => changeConnectorType(info, index)"
						/>
					</div>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ $t('head.id') }}
							<span class="letterLength" v-if="!getReadonly(`connectorId`, index)">
								{{ `(${curLengthCnt[`connectorId_${index}`]}/${maxLengthCnt[`connectorId_${index}`]})` }}
							</span>
						</span>
						<label
							class="alert alert-warning"
							v-if="!(readonly || inputReadonly[index]) && isDuplicateFail[index]"
							:style="{ margin: '0px', padding: '0px' }"
						>
							<span>{{ $t('igate.duplicate.check.id.fail') }} </span>
						</label>

						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<div class="input-group">
							<inputText
								:ref="`connectorId_${index}`"
								vModel="connectorId"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
								:readonly="readonly || inputReadonly[index]"
								regExpType="id"
								@setLengthCnt="info => setLengthCnt({ [`connectorId_${index}`]: info.connectorId })"
							/>
						</div>
					</div>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ $t('head.name') }}
							<span class="letterLength" v-if="!getReadonly(`connectorName`, index)">
								{{ `(${curLengthCnt[`connectorName_${index}`]}/${maxLengthCnt[`connectorName_${index}`]})` }}
							</span>
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(`connectorName`, index) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(`connectorName`, index) ? '8px' : '0px'}` }"
					>
						<inputText
							:ref="`connectorName_${index}`"
							vModel="connectorName"
							:classObject="{
								'form-control': getReadonly(`connectorName`, index),
								'regExp-text': !getReadonly(`connectorName`, index),
								'view-disabled': true
							}"
							:readonly="readonly || inputReadonly[index]"
							regExpType="name"
							@setLengthCnt="info => setLengthCnt({ [`connectorName_${index}`]: info.connectorName })"
						/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { getRegExpInfo, HttpReq } from '@/utils/common.js';
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'ConnectorInfo',
	extends: DetailBasic,
	props: {
		adapterType: String,
		transactions: Array
	},
	data() {
		return {
			connectorLabelList: [''], // connectorType Label
			connectorLabelObj: [], // transaction에 따라 connectorType Label 분류

			connectorTypeList: [],
			connectorTypeReadOnly: [],
			inputReadonly: [],

			isDuplicateFail: [],

			requiredKeyArr: ['connectorType', 'connectorId'],
			keyArr: ['connectorType', 'connectorId', 'connectorName'],

			regExpInfo: {
				connectorId: {
					regExpType: 'id',
					isPK: true,
					readonly: false
				},
				connectorName: {
					regExpType: 'name',
					isPK: false,
					readonly: false
				}
			}
		};
	},
	computed: {
		connectorTypeFilterList() {
			if (!this.adapterType) return [];

			if (this.adapterType.includes('External')) {
				const filterArr = this.isFileExternal ? [] : [`Online.Http.Server.Thin`, `Online.Http.Client.Thin`];

				return this.connectorTypeList.filter(
					info =>
						filterArr.includes(info.pk.propertyKey) ||
						info.pk.propertyKey.includes(`Online.Tcp`) ||
						info.pk.propertyKey.includes(`Online.X25`)
				);
			} else {
				return this.connectorTypeList.filter(info => info.pk.propertyKey.includes(this.adapterType));
			}
		},
		isFileExternal() {
			return this.adapterType ? this.adapterType.includes('File.External') : false;
		}
	},
	watch: {
		adapterType() {
			this.connectorLabelList = this.isFileExternal ? Object.values(this.connectorLabelObj) : [''];
			this.initData();
		},
		transactions() {
			if (!this.isFileExternal) return;

			let connectorPropertyKeyList = [];

			if (0 === this.transactions.length) {
				this.initData();
				connectorPropertyKeyList = Object.keys(this.connectorLabelObj);
			} else {
				const keyList = Object.keys(this.connectorLabelObj);
				const firstCharList = [...new Set(this.transactions.map(transaction => transaction.charAt(0)))];

				// connectorTypeReadOnly 해제 후 inputReadonly 해제 가능
				for (let index = 0; index < keyList.length; index++) {
					const isCheck = -1 === firstCharList.indexOf(keyList[index]);
					this.connectorTypeReadOnly.splice(index, 1, isCheck);

					if (!this.connectorTypeReadOnly[index]) continue;

					this.inputReadonly.splice(index, 1, true);

					this.keyArr.forEach(key => {
						const keyName = `${key}_${index}`;

						this.setLengthCnt({ [keyName]: 0 });

						if (!this.$refs[keyName][0].setData) return;
						this.$refs[keyName][0].setData(null);
					});

					connectorPropertyKeyList.push(keyList[index]);
				}
			}

			for (const key of connectorPropertyKeyList) {
				setTimeout(() => {
					this.$emit('changeConnectorInfo', { key: 'connectorInfo', value: { connectorType: null, transaction: key } });
				}, 0);
			}
		}
	},
	async created() {
		try {
			const httpReqList = [
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Connector.Type', orderByKey: true },
					itemName: 'connectorTypeList'
				},
				{
					url: `/common/property/properties.json`,
					param: { propertyId: `List.AdapterWizard.Transaction.File.External`, orderByKey: true },
					itemName: 'transactionList'
				}
			];

			for await (const content of httpReqList) {
				const result = await new HttpReq(content.url).read(content.param ? content.param : null);

				if ('ok' !== result.data.result) throw result.data;

				this[content.itemName] = result.data.object;
			}

			// adapterInfo > transaction 첫 글자에 따라 connectorType label 분류
			const sendTrans = this.transactionList.filter(transactionInfo => 'S' === transactionInfo.pk.propertyKey.charAt(0));
			const sendrecvMsg = sendTrans.map(transactionInfo => transactionInfo.propertyValue).join('/');

			for (const transactionInfo of this.transactionList) {
				const firstChar = transactionInfo.pk.propertyKey.charAt(0);
				const connectorTypeLabel = 'S' === firstChar ? sendrecvMsg : transactionInfo.propertyValue;
				this.connectorLabelObj[firstChar] = `(${connectorTypeLabel})`;
			}

			this.initData();
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('head.fail.notice') });
		}
	},
	methods: {
		getData() {
			let rtnObj = {};

			for (let index = 0; index < this.connectorLabelList.length; index++) {
				let connectorObj = {};
				const transaction = Object.keys(this.connectorLabelObj)[index];

				for (const key of this.keyArr) {
					const connectorInfo = this.$refs[`${key}_${index}`][0].getData();

					//null check
					if (this.requiredKeyArr.includes(key)) {
						if (this.isFileExternal && this.connectorTypeReadOnly[index]) continue;
						if (' ' === connectorInfo[key] || isEmpty(connectorInfo[key])) return {};
					}

					if (this.isFileExternal && 'S' !== transaction.charAt(0)) {
						const connector = 'connector';
						connectorInfo[`${key.slice(0, connector.length)}2nd${key.slice(connector.length)}`] = connectorInfo[key];
						delete connectorInfo[key];
					}

					connectorObj = merge(connectorObj, connectorInfo);
				}

				rtnObj = merge(rtnObj, connectorObj);
			}

			return rtnObj;
		},
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;

				if (!Object.keys(this.regExpInfo).includes(key)) return;

				const regExpType = !Object.keys(this.regExpInfo).includes(key) ? 'default' : this.regExpInfo[key].regExpType;

				for (let index = 0; index < 2; index++) {
					const keyName = `${key}_${index}`;

					this.setLengthCnt({ [keyName]: 0 });
					this.maxLengthCnt[keyName] = getRegExpInfo(regExpType).maxLength;
				}
			});

			for (const key in this.$refs) {
				if (0 === this.$refs[key].length || !this.$refs[key][0].setData) continue;
				this.$refs[key][0].setData(detailDataFormalize[this.$refs[key][0]._props.vModel]);
			}

			this.isDuplicateFail = new Array(2).fill(false);

			this.connectorTypeReadOnly = new Array(2).fill(true);
			this.inputReadonly = new Array(2).fill(true);
		},
		changeConnectorType(info, index) {
			if (isEmpty(info.value)) return;

			this.inputReadonly.splice(index, 1, false);

			this.$emit('changeConnectorInfo', {
				key: 'connectorInfo',
				value: { [info.key]: info.value, transaction: Object.keys(this.connectorLabelObj)[index] }
			});
		},
		async checkDuplicate() {
			const isDuplicateFail = [];

			for (const index in this.connectorLabelList) {
				const connectorIdInfo = this.$refs[`connectorId_${index}`][0].getData();

				if (!connectorIdInfo.connectorId) {
					isDuplicateFail[index] = false;
					continue;
				}

				const result = await new HttpReq('/igate/connector/object.json').read(connectorIdInfo, false, () => {});
				isDuplicateFail[index] = `ok` === result.data.result;
			}

			this.isDuplicateFail = [...isDuplicateFail];

			return this.isDuplicateFail.some(idState => idState);
		},
		getReadonly(key, index) {
			const colInfo = this.regExpInfo[key];
			const readonly = 'function' === typeof colInfo.readonly ? colInfo.readonly(this.detailData ? this.detailData : {}) : colInfo.readonly;
			return this.readonly || readonly || (colInfo.isPK && 'modify' === this.mode) || this.inputReadonly[index];
		}
	}
};
</script>
