<template>
	<div style="margin-bottom: 30px">
		<div class="row frm-row">
			<div :class="`col-lg-${isOnline || isOnlineExternal ? '4' : '6'}`">
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.adapter') }} {{ $t('head.type') }} </span> &nbsp;
						<label class="control-label warningLabel" style="padding-bottom: 0px">
							<span>{{ $t('igate.adapterwizard.adapterType.chagneWarn') }} </span>
						</label>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="adapterType"
							vModel="adapterType"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly"
							:optionInfo="{
								optionListName: 'adapterWizardTypeList',
								optionFor: 'option in adapterWizardTypeList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
							:optionListData="adapterWizardTypeList"
							@changeEvt="changeAdapterType"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.message') }} {{ $t('head.type') }} </span> &nbsp;
						<label class="control-label warningLabel" style="padding-bottom: 0px">
							<span>{{ $t('igate.adapterwizard.messageType.chagneWarn') }}</span>
						</label>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="messageType"
							vModel="messageType"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || messageTypeReadOnly"
							:optionInfo="{
								optionListName: 'messageTypeList',
								optionFor: 'option in messageTypeList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
							:optionListData="messageTypeList"
							:val="1 === messageTypeList.length ? messageTypeList[0].pk.propertyKey : ''"
							@changeEvt="changeMessageType"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.adapterwizard.requestDirection') }} </span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="requestDirection"
							vModel="requestDirection"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || requestDirectionReadOnly"
							:optionInfo="{
								optionListName: 'requestDirectionList',
								optionFor: 'option in requestDirectionList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
							:optionListData="requestDirectionList"
							:val="1 === requestDirectionList.length ? requestDirectionList[0].pk.propertyKey : ''"
							@changeEvt="changeRequestDirection"
						/>
					</div>
				</div>
				<div class="form-group" style="margin-top: 1rem">
					<label class="control-label">
						<span> {{ $t('igate.adapterwizard.transaction') }} </span>
						<b class="icon-star"></b>
					</label>
					<radioGroup
						ref="transactions"
						vModel="transactions"
						:readonly="readonly || transactionReadOnly"
						:radioInfo="{
							optionListName: 'transactionList',
							optionFor: `option in transactionList`,
							optionValue: `option.propertyValue`,
							optionText: `option.pk.propertyKey`
						}"
						:optionListData="transactionList"
						:class="{ 'custom-control': true, 'custom-radio single': true }"
						:val="1 === transactionList.length ? transactionList[0].pk.propertyKey : ''"
						v-if="!isFileExternal"
					/>
					<checkBoxGroup
						ref="transactions"
						vModel="transactions"
						:readonly="readonly || transactionReadOnly"
						:checkboxInfo="{
							optionListName: 'transactionList',
							optionFor: `option in transactionList`,
							optionValue: `option.propertyValue`,
							optionText: `option.pk.propertyKey`
						}"
						:optionListData="transactionList"
						:class="{ 'custom-control': true, 'custom-checkbox single': true }"
						@changeEvt="changeTransaction"
						v-else
					/>
				</div>
			</div>
			<div :class="`col-lg-${isOnline || isOnlineExternal ? '4' : '6'}`">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.id') }}
							<span class="letterLength" v-if="!getReadonly(regExpInfo[`adapterId`])">
								{{ `(${curLengthCnt[`adapterId`]}/${maxLengthCnt[`adapterId`]})` }}
							</span>
						</span>
						<label
							class="alert alert-warning"
							v-if="!(readonly || inputReadonly) && isDuplicateFail"
							:style="{ margin: '0px', padding: '0px' }"
						>
							<span>{{ $t('igate.duplicate.check.id.fail') }} </span>
						</label>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<div class="input-group">
							<inputText
								ref="adapterId"
								vModel="adapterId"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
								:readonly="readonly || inputReadonly"
								regExpType="id"
								@setLengthCnt="setLengthCnt"
							/>
						</div>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.name') }}
							<span class="letterLength" v-if="!getReadonly(regExpInfo[`adapterName`])">
								{{ `(${curLengthCnt[`adapterName`]}/${maxLengthCnt[`adapterName`]})` }}
							</span>
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`adapterName`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`adapterName`]) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="adapterName"
							vModel="adapterName"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`adapterName`]),
								'regExp-text': !getReadonly(regExpInfo[`adapterName`]),
								'view-disabled': true
							}"
							:readonly="readonly || inputReadonly"
							regExpType="name"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.adapter.charset') }}
							<span class="letterLength" v-if="!getReadonly(regExpInfo[`adapterCharset`])">
								{{ `(${curLengthCnt[`adapterCharset`]}/${maxLengthCnt[`adapterCharset`]})` }}
							</span>
						</span>
						<b class="icon-star"></b>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`adapterCharset`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`adapterCharset`]) ? '8px' : '0px'}` }"
					>
						<dataList
							ref="adapterCharset"
							vModel="adapterCharset"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`adapterCharset`]),
								'regExp-text': !getReadonly(regExpInfo[`adapterCharset`]),
								'view-disabled': true,
								dataKey: true
							}"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'Property.Adapter.Chartset',
									orderByKey: true
								},
								optionListName: 'adapterChartsetList',
								optionFor: 'option in adapterChartsetList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
							:readonly="readonly || inputReadonly"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
			</div>
			<div class="col-lg-4" v-if="isOnline || isOnlineExternal">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter.telegramHandler') }}</span>
					</label>
					<div class="input-group">
						<inputText
							ref="telegramHandler"
							vModel="telegramHandler"
							:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
							:readonly="true"
						/>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="openActivityModal"
							v-if="!readonly"
							:disabled="inputReadonly"
						>
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="$refs[`telegramHandler`].setData(null)"
							v-if="!readonly"
							:disabled="inputReadonly"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
				<div class="form-group" v-if="isOnline">
					<label class="control-label">
						<span>{{ $t('igate.adapter.structure.request') }}</span>
					</label>
					<div class="input-group">
						<inputText
							ref="requestStructure"
							vModel="requestStructure"
							:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
							:readonly="true"
						/>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="openRecordModal('I')"
							v-if="!readonly"
							:disabled="inputReadonly"
						>
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="$refs[`requestStructure`].setData(null)"
							v-if="!readonly"
							:disabled="inputReadonly"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
				<div class="form-group" v-if="isOnline">
					<label class="control-label">
						<span>{{ $t('igate.adapter.structure.response') }}</span>
					</label>
					<div class="input-group">
						<inputText
							ref="responseStructure"
							vModel="responseStructure"
							:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
							:readonly="true"
						/>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="openRecordModal('O')"
							v-if="!readonly"
							:disabled="inputReadonly"
						>
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="$refs[`responseStructure`].setData(null)"
							v-if="!readonly"
							:disabled="inputReadonly"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { getRegExpInfo, HttpReq } from '@/utils/common.js';
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'AdapterInfo',
	extends: DetailBasic,
	data() {
		return {
			httpReq: null,
			adapterType: null,

			adapterWizardTypeList: [],
			adapterTypeList: [],
			messageTypeList: [],
			requestDirectionList: [],
			transactionList: [],

			messageTypeReadOnly: true,
			requestDirectionReadOnly: true,
			transactionReadOnly: true,
			inputReadonly: true,

			isDuplicateFail: false,

			requiredKeyArr: ['adapterType', 'messageType', 'requestDirection', 'transactions', 'adapterId', 'adapterCharset'],
			keyArr: [
				'adapterType',
				'messageType',
				'requestDirection',
				'transactions',
				'adapterId',
				'adapterName',
				'adapterCharset',
				'telegramHandler',
				'requestStructure',
				'responseStructure'
			],

			regExpInfo: {
				adapterId: {
					regExpType: 'id',
					isPK: true,
					readonly: false
				},
				adapterName: {
					regExpType: 'name',
					isPK: false,
					readonly: false
				},
				adapterDesc: {
					regExpType: 'desc',
					isPK: false,
					readonly: false
				},
				adapterCharset: {
					regExpType: 'default',
					isPK: false,
					readonly: false
				}
			}
		};
	},
	computed: {
		isOnline() {
			return this.adapterType ? `Online` === this.adapterType : false;
		},
		isTable() {
			return this.adapterType ? 'DB' === this.adapterType : false;
		},
		isOnlineExternal() {
			return this.adapterType ? `Online.External` === this.adapterType : false;
		},
		isFileExternal() {
			return this.adapterType ? `File.External` === this.adapterType : false;
		}
	},
	async created() {
		try {
			this.httpReq = new HttpReq(`/common/property/properties.json`);
			const httpReqList = [
				{
					param: { propertyId: 'List.AdapterWizard.Type', orderByKey: true, convert: false },
					itemName: 'adapterWizardTypeList'
				},
				{
					param: { propertyId: 'List.Adapter.Type', orderByKey: true },
					itemName: 'adapterTypeList'
				}
			];

			for await (const content of httpReqList) {
				const result = await this.httpReq.read(content.param ? content.param : null);

				if ('ok' !== result.data.result) throw result.data;

				this[content.itemName] = result.data.object;
			}

			this.adapterWizardTypeList = this.getSortedPropertyList(this.adapterWizardTypeList);

			this.initData();
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('head.fail.notice') });
		}
	},
	methods: {
		getData() {
			let rtnObj = {};
			for (let key in this.$refs) {
				if (isEmpty(this.$refs[key])) continue;

				const adapterInfo = this.$refs[key].getData();

				// null check
				if (this.requiredKeyArr.includes(key)) {
					if (' ' === adapterInfo[key] || isEmpty(adapterInfo[key])) return {};
				}

				if (!this.isFileExternal && 'transactions' === key) {
					const transactions = Object.values(adapterInfo).filter(transaction => 0 < transaction.trim().length);
					rtnObj = merge(rtnObj, { [key]: transactions });
					continue;
				}

				rtnObj = merge(rtnObj, adapterInfo);
			}

			return rtnObj;
		},
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;

				const regExpType = !Object.keys(this.regExpInfo).includes(key) ? 'default' : this.regExpInfo[key].regExpType;

				this.setLengthCnt({ [key]: 0 });
				this.maxLengthCnt[key] = getRegExpInfo(regExpType).maxLength;
			});

			for (const key in this.$refs) {
				if (!(this.$refs[key] && this.$refs[key].setData)) continue;
				this.$refs[key].setData(detailDataFormalize[this.$refs[key]._props.vModel]);
			}

			this.messageTypeList = [];
			this.requestDirectionList = [];
			this.transactionList = [];

			this.messageTypeReadOnly = true;
			this.requestDirectionReadOnly = true;
			this.transactionReadOnly = true;
			this.inputReadonly = true;

			this.isDuplicateFail = false;
		},
		async changeAdapterType(info) {
			this.initData();

			this.adapterType = info.value;
			this.$refs.adapterType.setData(this.adapterType);
			this.messageTypeReadOnly = false;
			this.transactionReadOnly = true;

			// set messageType && select messageType
			let messageTypeList = this.adapterTypeList.filter(info => info.pk.propertyKey.includes(this.adapterType));

			if (!this.adapterType.includes('External')) {
				messageTypeList = messageTypeList.filter(info => !info.pk.propertyKey.includes('External'));
			}

			this.messageTypeList = messageTypeList;

			//  After auto select messageType
			if (1 === this.messageTypeList.length) {
				this.changeMessageType({ key: 'messageType', value: this.$refs.messageType.getData().messageType });
			}

			// set requestDirection
			let result = await this.httpReq.read({
				propertyId: `List.AdapterWizard.RequestDirection.${this.adapterType}`,
				orderByKey: true,
				convert: false
			});

			if ('ok' !== result.data.result) throw result.data;

			this.requestDirectionList = this.getSortedPropertyList(result.data.object);

			// set transactions
			if (this.isTable) {
				this.transactionList = [];
			} else {
				result = await this.httpReq.read({
					propertyId: `List.AdapterWizard.Transaction.${this.adapterType}`,
					orderByKey: true,
					convert: false
				});
				if ('ok' !== result.data.result) throw result.data;

				this.transactionList = this.getSortedPropertyList(result.data.object);
			}

			this.$emit('changeAdapterInfo', info);
		},
		async changeMessageType(info) {
			if (isEmpty(info.value)) return;

			this.requestDirectionReadOnly = false;
			this.transactionReadOnly = false;

			//  After auto select requestDirection
			if (1 === this.requestDirectionList.length) {
				this.changeRequestDirection({ key: 'requestDirection', value: this.$refs.requestDirection.getData().requestDirection });
			}

			// select transaction
			if (!this.isTable && 1 < this.transactionList.length) {
				const result = await this.httpReq.read({
					propertyId: `Select.AdapterWizard.Transaction.${info.value}`,
					orderByKey: true
				});

				if ('ok' !== result.data.result) throw result.data;

				const selectTransaction =
					0 < result.data.object.length
						? result.data.object
								.filter(transactionInfo => 'Y' === transactionInfo.propertyValue)
								.map(transactionInfo => transactionInfo.pk.propertyKey)
						: [];

				this.$refs.transactions.setData(this.isFileExternal ? selectTransaction : selectTransaction[0]);
			}

			// select telegram handler
			if (this.$refs.telegramHandler) {
				const result = await this.httpReq.read({
					propertyId: `Telegram.Adapter.${info.value}`,
					orderByKey: true
				});

				if ('ok' !== result.data.result) throw result.data;

				this.$refs.telegramHandler.setData(0 < result.data.object.length ? result.data.object[0].propertyValue : null);
			}

			// select adapterCharset
			if (this.$refs.adapterCharset) {
				const result = await this.httpReq.read({
					propertyId: `Charset.Adapter.${info.value}`,
					orderByKey: true
				});

				if ('ok' !== result.data.result) throw result.data;

				this.$refs.adapterCharset.setData(0 < result.data.object.length ? result.data.object[0].propertyValue : ``);
			}

			this.$emit('changeAdapterInfo', info);

			if (!this.isFileExternal) return;

			this.$emit('changeAdapterInfo', { key: 'transactions', value: this.$refs.transactions.getData().transactions });
		},
		async changeRequestDirection(info) {
			if (isEmpty(info.value)) return;

			this.inputReadonly = false;

			if (!this.isTable) return;

			// set transactions(DB)
			let result = await this.httpReq.read({
				propertyId: `List.AdapterWizard.Transaction.${this.adapterType}.${info.value}`,
				orderByKey: true,
				convert: false
			});

			if ('ok' !== result.data.result) throw result.data;

			this.transactionList = this.getSortedPropertyList(result.data.object);

			// select transaction
			result = await this.httpReq.read({
				propertyId: `Select.AdapterWizard.Transaction.${info.value}`,
				orderByKey: true
			});

			if ('ok' !== result.data.result) throw result.data;

			if (1 < this.transactionList.length) {
				const result = await this.httpReq.read({
					propertyId: `Select.AdapterWizard.Transaction.${info.value}`,
					orderByKey: true
				});

				if ('ok' !== result.data.result) throw result.data;

				const selectTransaction =
					0 < result.data.object.length
						? result.data.object
								.filter(transactionInfo => 'Y' === transactionInfo.propertyValue)
								.map(transactionInfo => transactionInfo.pk.propertyKey)
						: [];

				this.$refs.transactions.setData(this.isFileExternal ? selectTransaction : selectTransaction[0]);
			}
		},
		changeTransaction(info) {
			this.$emit('changeAdapterInfo', info);
		},
		getSortedPropertyList(propertyList) {
			propertyList = propertyList.sort((a, b) => {
				return a.propertyValue < b.propertyValue ? -1 : a.propertyValue > b.propertyValue ? 1 : 0;
			});

			propertyList.forEach(property => {
				property.propertyValue = this.$t(property.propertyValue);
			});

			return propertyList;
		},
		async checkDuplicate() {
			const adapterIdInfo = this.$refs.adapterId.getData();

			if (!adapterIdInfo.adapterId) {
				this.isDuplicateFail = false;
			} else {
				const result = await new HttpReq('/igate/adapter/object.json').read(this.$refs.adapterId.getData(), false, () => {});
				this.isDuplicateFail = `ok` === result.data.result;
			}

			return this.isDuplicateFail;
		},
		getReadonly(colInfo) {
			const readonly = 'function' === typeof colInfo.readonly ? colInfo.readonly(this.detailData ? this.detailData : {}) : colInfo.readonly;
			return this.readonly || readonly || (colInfo.isPK && 'modify' === this.mode) || this.inputReadonly;
		},
		openActivityModal() {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.activity'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'activityId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'select',
										vModel: 'activityType',
										label: this.$t('common.type'),
										val: 'H',
										readonly: true,
										optionInfo: {
											url: '/common/property/properties.json',
											params: {
												propertyId: 'List.Activity.ActivityType',
												orderByKey: true
											},
											optionListName: 'activityTypes',
											optionFor: 'option in activityTypes',
											optionValue: 'option.pk.propertyKey',
											optionText: 'option.propertyValue'
										}
									},
									{
										type: 'text',
										vModel: 'activityDesc',
										label: this.$t('head.description'),
										placeholder: this.$t('head.searchComment'),
										regExpType: 'desc'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/activity/searchPopup.json',
									totalCntUrl: '/igate/activity/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												name: 'activityId',
												header: this.$t('head.id')
											},
											{
												name: 'activityName',
												header: this.$t('head.name')
											},
											{
												name: 'activityType',
												header: this.$t('common.type'),
												val: 'H',
												formatter: ({ row }) => {
													const activityTypeObj = {
														F: 'igate.activity.type.control',
														A: 'igate.activity.type.activity',
														S: 'igate.activity.type.service',
														C: 'igate.activity.type.codec',
														T: 'igate.activity.type.transform',
														I: 'igate.activity.type.internal',
														H: 'igate.activity.type.telegram.handler'
													};

													return this.$t(activityTypeObj[row.activityType]);
												}
											},
											{
												name: 'activityDesc',
												header: this.$t('head.description')
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs.telegramHandler.setData(info.activityId);
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		},
		openRecordModal(type) {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.record'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'recordId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'text',
										vModel: 'recordName',
										label: this.$t('head.name'),
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/record/searchPopup.json',
									totalCntUrl: '/igate/record/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'recordId',
												width: '40%'
											},
											{
												header: this.$t('head.name'),
												name: 'recordName',
												width: '40%'
											},
											{
												header: `${this.$t('common.privilege')}`,
												name: 'privilegeId',
												width: '20%'
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs['I' === type ? 'requestStructure' : 'responseStructure'].setData(info.recordId);
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
