<template>
	<div class="speedBarChartRoot">
		<div class="transaction flex-1" ref="transaction">
			<div class="transaction-status">
				<ul>
					<li>
						<span>RPS</span>
						<span class="num">{{ rpsCnt }}</span>
					</li>
					<li>
						<span>TPS</span>
						<span class="num">{{ tpsCnt }}</span>
					</li>
				</ul>
			</div>
			<div class="transaction-info">
				<div class="rps" ref="rps" :style="rpsStyle">
					<canvas ref="canvasRps" :width="canvasRpsWidth" :height="canvasRpsHeight"></canvas>
				</div>
				<div class="tps" ref="tps" :style="tpsStyle">
					<canvas ref="canvasTps" :width="canvasTpsWidth" :height="canvasTpsHeight"></canvas>
				</div>
				<div class="activeTransactions" ref="activeTransactions" :style="activeTransactionsStyle">
					<canvas
						ref="canvasActiveTransactionsBg"
						:style="canvasActiveTransactionsBgStyle"
						:width="canvasActiveTransactionsBgWidth"
						:height="canvasActiveTransactionsBgHeight"
					></canvas>
				</div>
			</div>
		</div>
		<div class="legend transaction-legend" v-if="isShowLegend">
			<span class="status"><i class="dot bg-normal"></i>{{ $t(`head.normal`) }}</span>
			<span class="status"><i class="dot bg-warn"></i>{{ $t(`head.warn`) }}</span>
			<span class="status"><i class="dot bg-danger"></i>{{ $t(`head.serious`) }}</span>
		</div>
	</div>
</template>

<script>
import Chart from 'chart.js';
import { getNumFromStr } from '@/utils/common.js';
import { Particle, shuffle } from '@/utils/dashboard/chart_utils.js';

export default {
	name: `DashboardSpeedBarChart`,
	props: {
		options: Object,
		dashConstInfo: Object
	},
	data() {
		return {
			isShowLegend: false,

			rpsStyle: {
				position: `absolute`,
				top: `0px`,
				left: `0px`,
				width: `50%`,
				height: `100%`
			},
			tpsStyle: {
				position: `absolute`,
				top: `0px`,
				left: `50%`,
				width: `50%`,
				height: `100%`
			},
			activeTransactionsStyle: {
				position: `absolute`,
				top: `0px`,
				left: `25%`,
				width: `50%`,
				height: `100%`
			},
			canvasActiveTransactionsBgStyle: {
				position: `absolute`,
				left: `0px`
			},

			canvasRpsWidth: null,
			canvasRpsHeight: null,
			canvasTpsWidth: null,
			canvasTpsHeight: null,
			canvasActiveTransactionsBgWidth: null,
			canvasActiveTransactionsBgHeight: null,

			rpsCnt: 0,
			tpsCnt: 0,

			rpsDataArr: [],
			tpsDataArr: [],
			activeTransactionDataArr: [],

			rpsCoordObjArr: [],
			tpsCoordObjArr: [],
			activeTransactionCoordObjArr: [],

			start: null,

			canvasRpsCtx: null,
			canvasTpsCtx: null,

			activeTransactionDemo: null,

			rpsImgWidth: 95,
			rpsImgHeight: 16,
			tpsImgWidth: 95,
			tpsImgHeight: 16,

			activeTransactionEffectCntArr: [50, 100, 150]
		};
	},
	created() {
		this.activeTransactionBgImg = new Image();
		this.activeTransactionBgImg.src = require(`@/assets/img/dashboard/traffic-${`Y` === this.options.darkmodeYn ? `dark` : `light`}.png`);
	},
	mounted() {
		this.initSpeedBarChart();

		this.$nextTick(() => {
			this.initMakeActiveTransactionTag();

			this.initActiveTransactionAnimationEffect();

			this.canvasRpsCtx = this.$refs.canvasRps.getContext(`2d`);
			this.canvasTpsCtx = this.$refs.canvasTps.getContext(`2d`);

			this.resize();
		});
	},
	methods: {
		initSpeedBarChart() {
			this.isShowLegend = `Y` === this.options.remarkYn;
		},
		initMakeActiveTransactionTag() {
			this.activeTransactionDemo = Sketch.create({
				container: this.$refs.activeTransactions,
				retina: `auto`,
				autopause: false
			});

			this.activeTransactionDemo.canvas.style.position = `absolute`;
			this.activeTransactionDemo.canvas.style.left = `0px`;
		},
		initActiveTransactionAnimationEffect() {
			const MAX_PARTICLES = 100;

			const COLOURS = [`#62d36f`, `#efc402`, `#ed3137`];

			const particles = [];
			const pool = [];

			this.activeTransactionDemo.spawn = (x, y, type) => {
				let tmpCnt = 0;

				if (0 < this.activeTransactionCoordObjArr.length) {
					if (0 === type) tmpCnt = this.activeTransactionCoordObjArr[0].dataObj.cnt1;
					else if (1 === type) tmpCnt = this.activeTransactionCoordObjArr[0].dataObj.cnt2;
					else if (2 === type) tmpCnt = this.activeTransactionCoordObjArr[0].dataObj.cnt3;
				}

				if (0 === tmpCnt) return;

				let particle, theta, force;

				if (particles.length >= MAX_PARTICLES) pool.push(particles.shift());

				particle = pool.length ? pool.pop() : new Particle();

				const maxBgCircleParticleSizeRatio =
					tmpCnt >= this.activeTransactionEffectCntArr[2] ? 0.2 : tmpCnt >= this.activeTransactionEffectCntArr[1] ? 0.15 : 0.1;
				particle.init(x, y, random(5, this.canvasActiveTransactionsBgHeight * maxBgCircleParticleSizeRatio)); //원의 크기

				particle.wander = random(0.5, 2.0);
				particle.color = COLOURS[type];

				particle.drag =
					tmpCnt >= this.activeTransactionEffectCntArr[2]
						? random(0.9, 0.99)
						: tmpCnt >= this.activeTransactionEffectCntArr[1]
						? random(0.8, 0.89)
						: random(0.7, 0.79);

				theta = random(TWO_PI);
				force = random(2, 8);

				particle.vx = sin(theta) * force;
				particle.vy = cos(theta) * force;

				particles.push(particle);
			};

			this.activeTransactionDemo.update = () => {
				let i, particle;

				for (i = particles.length - 1; i >= 0; i--) {
					particle = particles[i];

					if (particle.alive) particle.move();
					else pool.push(particles.splice(i, 1)[0]);
				}
			};

			this.activeTransactionDemo.draw = () => {
				this.activeTransactionDemo.setTransform(1, 0, 0, 1, 0, 0);

				for (let i = particles.length - 1; i >= 0; i--) {
					particles[i].draw(this.activeTransactionDemo);
				}

				for (let type = 0; type < 3; type++) {
					let tmpCnt = 0;

					if (0 < this.activeTransactionCoordObjArr.length) {
						tmpCnt =
							0 === type
								? this.activeTransactionCoordObjArr[0].dataObj.cnt1
								: 1 === type
								? this.activeTransactionCoordObjArr[0].dataObj.cnt2
								: this.activeTransactionCoordObjArr[0].dataObj.cnt3;
					}

					this.activeTransactionDemo.beginPath();

					this.activeTransactionDemo.strokeStyle = COLOURS[type];
					this.activeTransactionDemo.fillStyle = COLOURS[type];

					const arcCoordX = (this.canvasActiveTransactionsBgWidth / 6) * (0 === type ? 1.5 : 1 === type ? 3.0 : 4.5);

					const bgCircleSizeRatio =
						tmpCnt >= this.activeTransactionEffectCntArr[2] ? 0.25 : tmpCnt >= this.activeTransactionEffectCntArr[1] ? 0.2 : 0.15;

					this.activeTransactionDemo.arc(
						arcCoordX,
						this.canvasActiveTransactionsBgHeight / 2,
						this.canvasActiveTransactionsBgHeight * bgCircleSizeRatio,
						0,
						2 * Math.PI
					); //원의크기

					this.activeTransactionDemo.stroke();
					this.activeTransactionDemo.fill();

					this.activeTransactionDemo.font = `13px Arial`;
					this.activeTransactionDemo.textAlign = `center`;

					this.activeTransactionDemo.fillStyle = `#ffffff`;
					this.activeTransactionDemo.fillText(tmpCnt, arcCoordX, this.canvasActiveTransactionsBgHeight / 2 + 5.0); //text 위치

					this.activeTransactionDemo.closePath();
				}
			};
		},
		addData(dataArr) {
			const dataObj = dataArr[0];

			const timestamp = Date.now();

			this.rpsDataArr.push({ timestamp: timestamp, cnt: dataObj.rps });

			this.tpsDataArr.push({ timestamp: timestamp, cnt: dataObj.tps, cnt1: dataObj.tpsShort, cnt2: dataObj.tpsMiddle, cnt3: dataObj.tpsLong });

			this.activeTransactionDataArr.push({
				timestamp: timestamp,
				cnt1: dataObj.activeShort,
				cnt2: dataObj.activeMiddle,
				cnt3: dataObj.activeLong
			});
		},
		initMakeRpsCoordObj() {
			if (0 === this.rpsDataArr.length) {
				this.rpsCnt = 0;
				return;
			}

			if (1 < this.rpsDataArr.length) this.rpsDataArr.shift();

			const coordObj = {
				timestamp: Date.now(),
				coordArr: []
			};

			const orgCnt = this.rpsDataArr[0].cnt;
			const drawCnt = orgCnt > 12 ? 12 : orgCnt;

			for (let i = 0; i < drawCnt; i++) {
				coordObj.coordArr.push({ startXCoord: -(this.rpsImgWidth * (i + 1)) });
			}

			this.rpsCoordObjArr.push(coordObj);

			this.rpsCnt = orgCnt;
		},
		initRemoveRpsCoordObj() {
			if (0 === this.rpsCoordObjArr.length) return;

			const currentTime = Date.now();

			this.rpsDataArr = this.rpsDataArr.filter(rpsData => rpsData.timestamp + this.dashConstInfo.dataIntervalSeconds * 1000 * 2 >= currentTime);

			this.rpsCoordObjArr = this.rpsCoordObjArr.filter(rpsCoordObj => rpsCoordObj.timestamp + 1000 >= currentTime);
		},
		initMakeTpsCoordObj() {
			if (0 === this.tpsDataArr.length) {
				this.tpsCnt = 0;
				return;
			}

			if (1 < this.tpsDataArr.length) this.tpsDataArr.shift();

			const coordObj = {
				timestamp: Date.now(),
				coordArr: []
			};

			let drawCnt1 = 0;
			let drawCnt2 = 0;
			let drawCnt3 = 0;

			if (12 < this.tpsDataArr[0].cnt) {
				drawCnt2 = Math.ceil((this.tpsDataArr[0].cnt2 * 12) / this.tpsDataArr[0].cnt);
				drawCnt3 = Math.ceil((this.tpsDataArr[0].cnt3 * 12) / this.tpsDataArr[0].cnt);
				drawCnt1 = 12 - (drawCnt2 + drawCnt3);
			} else {
				drawCnt2 = this.tpsDataArr[0].cnt2;
				drawCnt3 = this.tpsDataArr[0].cnt3;
				drawCnt1 = this.tpsDataArr[0].cnt1;
			}

			for (let i = 0; i < drawCnt1; i++) {
				coordObj.coordArr.push({ startXCoord: null, type: 1 });
			}

			for (let i = 0; i < drawCnt2; i++) {
				coordObj.coordArr.push({ startXCoord: null, type: 2 });
			}

			for (let i = 0; i < drawCnt3; i++) {
				coordObj.coordArr.push({ startXCoord: null, type: 3 });
			}

			coordObj.coordArr = shuffle(coordObj.coordArr);

			coordObj.coordArr.forEach((coord, idx) => {
				coord.startXCoord = this.canvasActiveTransactionsBgWidth / 2 - this.tpsImgWidth * (idx + 1);
			});

			this.tpsCoordObjArr.push(coordObj);

			this.tpsCnt = this.tpsDataArr[0].cnt;
		},
		initRemoveTpsCoordObj() {
			if (0 === this.tpsCoordObjArr.length) return;

			const currentTime = Date.now();

			this.tpsDataArr = this.tpsDataArr.filter(tpsData => tpsData.timestamp + this.dashConstInfo.dataIntervalSeconds * 1000 * 2 >= currentTime);

			this.tpsCoordObjArr = this.tpsCoordObjArr.filter(tpsCoordObj => tpsCoordObj.timestamp + 1000 >= currentTime);
		},
		initMakeActiveTransactionCoordObj() {
			if (0 === this.activeTransactionDataArr.length) return;

			if (1 < this.activeTransactionDataArr.length) this.activeTransactionDataArr.shift();

			this.activeTransactionCoordObjArr.push({
				timestamp: Date.now(),
				dataObj: {
					cnt1: this.activeTransactionDataArr[0].cnt1,
					cnt2: this.activeTransactionDataArr[0].cnt2,
					cnt3: this.activeTransactionDataArr[0].cnt3
				}
			});
		},
		initRemoveActiveTransactionData() {
			if (0 === this.activeTransactionCoordObjArr.length) return;

			const currentTime = Date.now();

			this.activeTransactionDataArr = this.activeTransactionDataArr.filter(
				activeTransactionData => activeTransactionData.timestamp + this.dashConstInfo.dataIntervalSeconds * 1000 * 2 >= currentTime
			);

			this.activeTransactionCoordObjArr = this.activeTransactionCoordObjArr.filter(
				activeTransactionCoordObj => activeTransactionCoordObj.timestamp + 1000 >= currentTime
			);
		},
		rpsRequestAnimationFrame(progress) {
			if (!this.$refs.canvasRps) return;

			this.canvasRpsCtx.clearRect(0, 0, this.canvasRpsCtx.canvas.width, this.canvasRpsCtx.canvas.height);

			const currentTime = Date.now();

			const rpsCoordObj = this.rpsCoordObjArr.find(rpsCoordObj => rpsCoordObj.timestamp + 1000 > currentTime);

			if (!rpsCoordObj) return;

			const moveXCoordAlpha =
				progress *
				((this.canvasRpsCtx.canvas.width - this.canvasActiveTransactionsBgWidth / 2 + this.rpsImgWidth * rpsCoordObj.coordArr.length) / 800);

			rpsCoordObj.coordArr.forEach(coord => {
				coord.startXCoord += moveXCoordAlpha;

				this.canvasRpsCtx.beginPath();
				this.canvasRpsCtx.save();

				this.canvasRpsCtx.moveTo(coord.startXCoord, this.canvasRpsCtx.canvas.height / 2);
				this.canvasRpsCtx.lineTo(coord.startXCoord + this.rpsImgWidth, this.canvasRpsCtx.canvas.height / 2);
				this.canvasRpsCtx.lineWidth = this.rpsImgHeight;
				this.canvasRpsCtx.lineCap = `round`;

				const grd = this.canvasRpsCtx.createLinearGradient(coord.startXCoord, 0, coord.startXCoord + this.rpsImgWidth, 0);

				grd.addColorStop(0.0, Chart.helpers.color(`#42c4e1`).alpha(0.0).rgbString());
				grd.addColorStop(0.1, Chart.helpers.color(`#42c4e1`).alpha(0.1).rgbString());
				grd.addColorStop(0.2, Chart.helpers.color(`#42c4e1`).alpha(0.2).rgbString());
				grd.addColorStop(0.3, Chart.helpers.color(`#42c4e1`).alpha(0.3).rgbString());
				grd.addColorStop(0.4, Chart.helpers.color(`#42c4e1`).alpha(0.4).rgbString());
				grd.addColorStop(0.5, Chart.helpers.color(`#42c4e1`).alpha(0.5).rgbString());
				grd.addColorStop(0.6, Chart.helpers.color(`#42c4e1`).alpha(0.6).rgbString());
				grd.addColorStop(0.7, Chart.helpers.color(`#42c4e1`).alpha(0.7).rgbString());
				grd.addColorStop(0.8, Chart.helpers.color(`#42c4e1`).alpha(0.8).rgbString());
				grd.addColorStop(0.9, Chart.helpers.color(`#42c4e1`).alpha(0.9).rgbString());
				grd.addColorStop(1.0, Chart.helpers.color(`#42c4e1`).alpha(1.0).rgbString());

				this.canvasRpsCtx.strokeStyle = grd;
				this.canvasRpsCtx.stroke();

				this.canvasRpsCtx.restore();
				this.canvasRpsCtx.closePath();
			});
		},
		tpsRequestAnimationFrame(progress) {
			if (!this.$refs.canvasTps) return;

			this.canvasTpsCtx.clearRect(0, 0, this.canvasTpsCtx.canvas.width, this.canvasTpsCtx.canvas.height);

			const currentTime = Date.now();

			const tpsCoordObj = this.tpsCoordObjArr.find(tpsCoordObj => tpsCoordObj.timestamp + 1000 > currentTime);

			if (!tpsCoordObj) return;

			const moveXCoordAlpha =
				progress *
				((this.canvasTpsCtx.canvas.width - this.canvasActiveTransactionsBgWidth / 2 + this.tpsImgWidth * tpsCoordObj.coordArr.length) / 800);

			tpsCoordObj.coordArr.forEach(coord => {
				coord.startXCoord += moveXCoordAlpha;

				this.canvasTpsCtx.beginPath();
				this.canvasTpsCtx.save();

				const tmpColor = 1 === coord.type ? `#62d36f` : 2 === coord.type ? `#efc402` : `#ed3137`;

				this.canvasTpsCtx.moveTo(coord.startXCoord, this.canvasTpsCtx.canvas.height / 2);
				this.canvasTpsCtx.lineTo(coord.startXCoord + this.tpsImgWidth, this.canvasTpsCtx.canvas.height / 2);
				this.canvasTpsCtx.lineWidth = this.tpsImgHeight;
				this.canvasTpsCtx.lineCap = `round`;

				const grd = this.canvasTpsCtx.createLinearGradient(coord.startXCoord, 0, coord.startXCoord + this.tpsImgWidth, 0);

				grd.addColorStop(0.0, Chart.helpers.color(tmpColor).alpha(0.0).rgbString());
				grd.addColorStop(0.1, Chart.helpers.color(tmpColor).alpha(0.1).rgbString());
				grd.addColorStop(0.2, Chart.helpers.color(tmpColor).alpha(0.2).rgbString());
				grd.addColorStop(0.3, Chart.helpers.color(tmpColor).alpha(0.3).rgbString());
				grd.addColorStop(0.4, Chart.helpers.color(tmpColor).alpha(0.4).rgbString());
				grd.addColorStop(0.5, Chart.helpers.color(tmpColor).alpha(0.5).rgbString());
				grd.addColorStop(0.6, Chart.helpers.color(tmpColor).alpha(0.6).rgbString());
				grd.addColorStop(0.7, Chart.helpers.color(tmpColor).alpha(0.7).rgbString());
				grd.addColorStop(0.8, Chart.helpers.color(tmpColor).alpha(0.8).rgbString());
				grd.addColorStop(0.9, Chart.helpers.color(tmpColor).alpha(0.9).rgbString());
				grd.addColorStop(1.0, Chart.helpers.color(tmpColor).alpha(1.0).rgbString());

				this.canvasTpsCtx.strokeStyle = grd;
				this.canvasTpsCtx.stroke();

				this.canvasTpsCtx.restore();
				this.canvasTpsCtx.closePath();
			});
		},
		activeTransactionRequestAnimationFrame() {
			if (!this.activeTransactionDemo) return;

			this.activeTransactionDemo.spawn((this.canvasActiveTransactionsBgWidth / 6) * 1.5, this.canvasActiveTransactionsBgHeight / 2, 0);

			this.activeTransactionDemo.spawn((this.canvasActiveTransactionsBgWidth / 6) * 3.0, this.canvasActiveTransactionsBgHeight / 2, 1);

			this.activeTransactionDemo.spawn((this.canvasActiveTransactionsBgWidth / 6) * 4.5, this.canvasActiveTransactionsBgHeight / 2, 2);
		},
		setInterval() {
			this.resize();

			this.initMakeRpsCoordObj();

			this.initRemoveRpsCoordObj();

			this.initMakeTpsCoordObj();

			this.initRemoveTpsCoordObj();

			this.initMakeActiveTransactionCoordObj();

			this.initRemoveActiveTransactionData();
		},
		setRequestAnimationFrame({ time }) {
			if (!this.start) this.start = time;

			const progress = time - this.start;

			if (progress <= 0) return;

			this.start = time;

			this.rpsRequestAnimationFrame(progress);

			this.tpsRequestAnimationFrame(progress);

			this.activeTransactionRequestAnimationFrame(progress);
		},
		resize() {
			if (!this.activeTransactionDemo) return;

			if (
				Number(this.activeTransactionDemo.canvas.getAttribute(`width`)) === this.$refs.activeTransactions.clientWidth &&
				getNumFromStr(this.activeTransactionDemo.canvas.style.width) === this.$refs.activeTransactions.clientWidth &&
				Number(this.activeTransactionDemo.canvas.getAttribute(`height`)) === this.$refs.activeTransactions.clientHeight &&
				getNumFromStr(this.activeTransactionDemo.canvas.style.height) === this.$refs.activeTransactions.clientHeight
			)
				return;

			this.canvasRpsWidth = this.$refs.rps.clientWidth;
			this.canvasRpsHeight = this.$refs.rps.clientHeight;

			this.canvasTpsWidth = this.$refs.tps.clientWidth;
			this.canvasTpsHeight = this.$refs.tps.clientHeight;

			this.canvasActiveTransactionsBgWidth = this.$refs.activeTransactions.clientWidth;
			this.canvasActiveTransactionsBgHeight = this.$refs.activeTransactions.clientHeight;

			for (const canvas of this.$refs.activeTransactions.querySelectorAll(`canvas`)) {
				if (canvas.classList.contains(`sketch`)) {
					canvas.style.width = `${this.$refs.activeTransactions.clientWidth}px`;
					canvas.style.height = `${this.$refs.activeTransactions.clientHeight}px`;

					canvas.setAttribute(`width`, this.$refs.activeTransactions.clientWidth);
					canvas.setAttribute(`height`, this.$refs.activeTransactions.clientHeight);

					break;
				}
			}

			const activeTransactionBgImg = new Image();

			activeTransactionBgImg.src = require(`@/assets/img/dashboard/traffic-${`Y` === this.options.darkmodeYn ? `dark` : `light`}.png`);

			activeTransactionBgImg.onload = () => {
				const ctx = this.$refs.canvasActiveTransactionsBg.getContext(`2d`);

				ctx.drawImage(this.activeTransactionBgImg, 0, 0, ctx.canvas.width, ctx.canvas.height);
			};
		},
		filterData() {
			this.rpsDataArr = [];
			this.rpsCoordObjArr = [];
			this.tpsDataArr = [];
			this.tpsCoordObjArr = [];
			this.activeTransactionDataArr = [];
			this.activeTransactionCoordObjArr = [];

			this.rpsCnt = 0;
			this.tpsCnt = 0;
		},
		unload() {
			this.rpsDataArr = [];
			this.tpsDataArr = [];
			this.activeTransactionDataArr = [];

			this.rpsCoordObjArr = [];
			this.tpsCoordObjArr = [];
			this.activeTransactionCoordObjArr = [];
		}
	}
};
</script>
<style lang="scss" scoped>
.speedBarChartRoot {
	height: 100%;

	.transaction-info {
		overflow: hidden;
	}
}
</style>
