<template>
	<div class="columnChartRoot">
		<canvas ref="columnChart"></canvas>
	</div>
</template>

<script>
import Chart from 'chart.js';
import { getYAxisTicksInfo, convertValue, getCanvasMousePos } from '@/utils/dashboard/chart_utils.js';

export default {
	name: `DashboardColumnChart`,
	props: {
		options: Object
	},
	computed: {
		fontColor() {
			return `Y` === this.options.darkmodeYn ? `#ffffff` : `#151826`;
		},
		filteredColor() {
			return `Y` === this.options.darkmodeYn ? `#292e47` : `#ffffff`;
		},
		axisLineColor() {
			return `Y` === this.options.darkmodeYn ? `#666666` : `#f0f1f6`;
		}
	},
	mounted() {
		this.initColumnChart();
	},
	data() {
		return {
			chartObj: null,
			chartDataArr: []
		};
	},
	methods: {
		initColumnChart() {
			const yAxisTickCount = 6;
			const yAxisTicksInfo = getYAxisTicksInfo(null, yAxisTickCount);
			const yAxisMaxValue = yAxisTicksInfo.maxValue;
			const yAxisStepSize = yAxisTicksInfo.stepSize;

			const ctx = this.$refs.columnChart.getContext(`2d`);

			const _this = this;

			this.chartObj = new Chart(ctx, {
				type: `bar`,
				data: {
					targetInfoList: [],
					labels: [],
					datasets: [
						{
							label: ``,
							backgroundColor: [],
							borderColor: [],
							borderWidth: 1,
							data: []
						}
					]
				},
				options: {
					onHover(evt) {
						if (!_this.options.isUseClickEvt) return;

						const hoverPos = getCanvasMousePos(this.ctx.canvas, evt);

						let isPointer = false;

						const chartInstance = this.chart;

						this.data.datasets.forEach((dataset, i) => {
							chartInstance.controller.getDatasetMeta(i).data.forEach(bar => {
								const startX = bar._model.x - bar._model.width / 2;
								const endX = startX + bar._model.width;

								if (startX <= hoverPos.x && endX >= hoverPos.x && 0 <= hoverPos.y && bar._chart.height >= hoverPos.y) {
									isPointer = true;
								}
							});
						});

						chartInstance.canvas.style.cursor = isPointer ? `pointer` : `default`;
					},
					onClick(evt) {
						if (!_this.options.isUseClickEvt) return;

						const clickedPos = getCanvasMousePos(this.ctx.canvas, evt);

						const chartInstance = this.chart;

						this.data.datasets.forEach((dataset, i) => {
							chartInstance.controller.getDatasetMeta(i).data.forEach(bar => {
								const startX = bar._model.x - bar._model.width / 2;
								const endX = startX + bar._model.width;

								if (startX <= clickedPos.x && endX >= clickedPos.x && 0 <= clickedPos.y && bar._chart.height >= clickedPos.y) {
									_this.$emit(`filter-target`, { targetId: _this.chartObj.data.targetInfoList[bar._index].targetId });
								}
							});
						});
					},
					legend: {
						display: false
					},
					title: {
						display: false
					},
					animation: {
						duration: 1,
						onComplete: evt => {
							const chartInstance = evt.chart;

							ctx.beginPath();

							ctx.textAlign = `center`;

							evt.chart.data.datasets.forEach((dataset, i) => {
								chartInstance.controller.getDatasetMeta(i).data.forEach((bar, index) => {
									ctx.fillStyle = this.fontColor;
									ctx.fillText(
										convertValue(
											dataset.data[index],
											this.options.unitType,
											`elapsed` === this.options.itemId ? [`ms`, `s`, `m`, `h`, `d`] : null
										),
										bar._model.x,
										bar._model.y - 5
									);

									if (this.chartObj.data.targetInfoList[index].isFiltered) {
										ctx.fillStyle = Chart.helpers.color(this.filteredColor).alpha(0.8).rgbString();
										ctx.fillRect(bar._model.x - bar._model.width / 2, 0, bar._model.width, bar._chart.height);
									}
								});
							});
						}
					},
					hover: {
						animationDuration: 0
					},
					responsive: true,
					maintainAspectRatio: false,
					responsiveAnimationDuration: 0,
					tooltips: {
						enabled: false
					},
					layout: {
						padding: {
							left: 25,
							right: 30,
							top: 20,
							bottom: 5
						}
					},
					scales: {
						yAxes: [
							{
								display: true,
								ticks: {
									fontColor: this.fontColor,
									beginAtZero: true,
									min: 0,
									max: yAxisMaxValue,
									stepSize: yAxisStepSize,
									maxTicksLimit: yAxisTickCount,
									minRotation: 0,
									maxRotation: 0,
									callback: value =>
										convertValue(
											value,
											this.options.unitType,
											`elapsed` === this.options.itemId ? [`ms`, `s`, `m`, `h`, `d`] : null
										)
								},
								gridLines: {
									color: this.axisLineColor,
									zeroLineColor: this.axisLineColor
								}
							}
						],
						xAxes: [
							{
								ticks: {
									fontColor: this.fontColor
								}
							}
						]
					}
				}
			});
		},
		addTarget(targetObj) {
			this.chartObj.data.targetInfoList.push({ targetId: targetObj.targetId, isFiltered: false, color: targetObj.color });
			this.chartObj.data.labels.push(targetObj.targetName);
			this.chartObj.data.datasets[0].backgroundColor.push(Chart.helpers.color(targetObj.color).alpha(0.6).rgbString());
			this.chartObj.data.datasets[0].borderColor.push(targetObj.color);
		},
		updateTarget(targetId, updateOption) {
			let updateIdx = null;

			for (let i = 0; i < this.chartObj.data.targetInfoList.length; i++) {
				if (targetId === this.chartObj.data.targetInfoList[i].targetId) {
					updateIdx = i;
					break;
				}
			}

			if (null === updateIdx) return;

			this.chartObj.data.targetInfoList[updateIdx].color = updateOption.color;
			this.chartObj.data.datasets[0].backgroundColor[updateIdx] = Chart.helpers.color(updateOption.color).alpha(0.6).rgbString();
			this.chartObj.data.datasets[0].borderColor[updateIdx] = updateOption.color;
		},
		deleteTarget(targetId) {
			let deleteIdx = null;

			for (let i = 0; i < this.chartObj.data.targetInfoList.length; i++) {
				if (targetId === this.chartObj.data.targetInfoList[i].targetId) {
					deleteIdx = i;
					break;
				}
			}

			if (null === deleteIdx) return;

			this.chartObj.data.targetInfoList.splice(deleteIdx, 1);
			this.chartObj.data.labels.splice(deleteIdx, 1);
			this.chartObj.data.datasets[0].backgroundColor.splice(deleteIdx, 1);
			this.chartObj.data.datasets[0].borderColor.splice(deleteIdx, 1);
			this.chartDataArr.splice(deleteIdx, 1);
			this.chartObj.data.datasets[0].data.splice(deleteIdx, 1);
		},
		addData(dataArr) {
			this.chartDataArr = dataArr;
		},
		setInterval() {
			if (0 === this.chartDataArr.length) return;

			const tmpData = [];

			this.chartObj.data.targetInfoList.forEach(targetInfo => {
				const targetId = targetInfo.targetId;

				const tmpChartDataArr = this.chartDataArr.filter(chartData => chartData.targetId === targetId);

				tmpData.push(0 < tmpChartDataArr.length ? tmpChartDataArr[0].y : null);
			});

			this.chartObj.data.datasets[0].data = tmpData;

			const currentMaxValue = Math.max.apply(
				null,
				this.chartDataArr.map(chartData => chartData.y)
			);

			const yAxisTickCount = 6;
			const yAxisTicksInfo = getYAxisTicksInfo(currentMaxValue, yAxisTickCount);
			const yAxisMaxValue = yAxisTicksInfo.maxValue;
			const yAxisStepSize = yAxisTicksInfo.stepSize;

			this.chartObj.options.scales.yAxes[0].ticks.max = yAxisMaxValue;
			this.chartObj.options.scales.yAxes[0].ticks.stepSize = yAxisStepSize;
			this.chartObj.options.scales.yAxes[0].ticks.maxTicksLimit = yAxisTickCount;

			this.chartObj.update();

			this.chartDataArr = [];
		},
		filterData({ targetId, isFiltered }) {
			const selectedTarget = this.chartObj.data.targetInfoList.find(info => info.targetId === targetId);

			if (!selectedTarget) return;

			selectedTarget.isFiltered = isFiltered;

			this.chartObj.update();
		},
		draw() {
			this.chartObj.update();
		},
		unload() {
			this.chartDataArr = [];
			if (this.chartObj) this.chartObj.destroy();
		}
	}
};
</script>

<style lang="scss" scoped>
.columnChartRoot {
	height: 100%;
}
</style>
