<template>
	<div id="ct">
		<Basic :pageInfo="pageInfo" v-if="'basic' === pageInfo.type" />
		<Custom :pageInfo="pageInfo" v-else-if="'custom' === pageInfo.type" />
		<component :pageInfo="pageInfo" v-else-if="'bundle' === pageInfo.type" :is="component" />
	</div>
</template>

<script>
import qs from 'qs';
import { mapState, mapMutations } from 'vuex';
import { HttpReq, getPrivilege, validateSession, logout } from '@/utils/common.js';
import Basic from '@/components/main/Basic.vue';
import Custom from '@/components/main/Custom.vue';

export default {
	name: 'Dispatch',
	components: {
		Basic,
		Custom
	},
	props: {
		uuid: String,
		menuId: String
	},
	data() {
		return {
			pageInfo: {},
			component: null,
			menuReq: new HttpReq(`/common/menu/object.json`),
			pageReq: new HttpReq(`/igate/page/common/pageInfo.json`)
		};
	},
	watch: {
		uuid() {
			this.initPageInfo();
		}
	},
	computed: {
		...mapState(['userInfo', 'isOnPopState', 'isTools'])
	},
	methods: {
		...mapMutations(['setIsOnPopState']),
		async initPageInfo() {
			if (!(await validateSession())) {
				this.$alert({
					type: `warn`,
					backdropMode: `full`,
					message: this.isTools ? this.$t(`igate.itools.session.expired`) : this.$t(`igate.session.expired`),
					callback: async () => {
						if (this.isTools) return;

						await logout();
					}
				});
				return;
			}

			this.pageInfo = { type: null };

			let result = await this.menuReq.read({ menuId: this.menuId });

			if (`ok` !== result.data.result) return;

			const menuInfo = result.data.object;

			const menuPrivilegeId = menuInfo.menuPrivilegeId;

			const totalUserPrivileges = this.userInfo.totalUserPrivileges.map(info => info.privilegeId);

			if (!('undefined' === typeof menuPrivilegeId || -1 < totalUserPrivileges.findIndex(privilegeId => privilegeId === menuPrivilegeId))) {
				this.$alert({
					type: 'warn',
					message: this.$t('head.unauthorized.screen.warn'),
					backdropMode: `full`,
					callback: () => {
						logout();
					}
				});

				return;
			}

			let menuUrl = menuInfo.menuUrl;

			if (-1 < menuUrl.indexOf(`?`)) {
				let searchObj = null;

				if (localStorage.getItem(`searchObj`)) {
					searchObj = {
						...JSON.parse(localStorage.getItem(`searchObj`)),
						...qs.parse(menuUrl.split(`?`)[1])
					};
				} else {
					searchObj = qs.parse(menuUrl.split(`?`)[1]);
				}

				localStorage.setItem(`searchObj`, JSON.stringify(searchObj));

				menuUrl = menuUrl.split(`?`)[0];
			}

			const splitStr = -1 < menuUrl.indexOf(`in:/`) ? `in:/` : `out:/`;
			menuUrl = `${splitStr}${menuUrl.split(splitStr)[1]}`.replace(`.html`, ``);

			if (menuUrl.startsWith('in:')) {
				this.pageInfo = {
					type: 'bundle',
					privilegeType: menuPrivilegeId ? menuPrivilegeId.replace('Viewer', '').replace('Editor', '') : null
				};

				this.component = require(`@/views/main${menuUrl.replace('in:', '')}.vue`).default;
			} else {
				result = await this.pageReq.read({ menuUrl: menuUrl.replace('out:', '') });

				if (`ok` !== result.data.result) return;

				const pageInfo = new Function(`${result.data.object} return info;`)();

				const privilegeType =
					'undefined' === typeof menuPrivilegeId ? pageInfo.privilegeType : menuPrivilegeId.replace('Viewer', '').replace('Editor', '');

				this.pageInfo = { ...pageInfo, ...getPrivilege(privilegeType) };
			}

			if (!this.isOnPopState) {
				history.pushState({ menuId: this.menuId }, null, null);
			}

			this.setIsOnPopState(false);
		}
	}
};
</script>
