<template>
	<div class="ct-header">
		<button
			type="button"
			class="btn-filter collapsed d-md-none"
			data-toggle="collapse"
			data-target=".collapse-filter"
			@click.prevent="collapseFilter"
		>
			{{ $t('head.searchFilter') }}
			<i class="icon-down"></i>
		</button>

		<div id="collapse-filter" class="collapse collapse-filter">
			<div class="filter no-gutters" style="padding-right: 0px">
				<div class="col">
					<label class="form-control-label reset">
						<b class="control-label">
							{{ $t('igate.webService') }}
							<i class="icon-srch" @click="openModal"></i>
						</b>
						<input
							:class="{ 'form-control': true }"
							v-model="address"
							:placeholder="$t('igate.webservice.searchWsdl')"
							@keyup.enter="getStruct"
						/>
						<i class="icon-close" @click="address = null"></i>
					</label>
				</div>
				<div class="col">
					<label :class="{ 'form-control-label': true, 'label-select': true, disabled: dropdownMenuInfo.service.readonly }">
						<b class="control-label">{{ $t('igate.webservice.wsdlService') }}</b>
						<span>
							<input
								:class="{ 'form-control': true }"
								v-model="dropdownMenuInfo.service.selectedName"
								:placeholder="$t('head.searchData')"
								@click="toggleDropdownItem('service')"
								readonly
							/>
							<ul :class="dropdownMenuClass.service">
								<li>
									<a
										:class="{
											'dropdown-item': true,
											active: String(object.service) === String(option.name)
										}"
										href="javascript:void(0)"
										v-for="option in serviceList"
										:key="option.name"
										@click="selectedItem('service', option.name, option.name)"
									>
										{{ option.name }}
									</a>
								</li>
							</ul>
						</span>
					</label>
				</div>
				<div class="col">
					<label :class="{ 'form-control-label': true, 'label-select': true, disabled: dropdownMenuInfo.endpoint.readonly }">
						<b class="control-label">WSDL EndPoint</b>
						<span>
							<input
								:class="{ 'form-control': true }"
								v-model="dropdownMenuInfo.endpoint.selectedName"
								:placeholder="$t('head.searchData')"
								@click="toggleDropdownItem('endpoint')"
								readonly
							/>
							<ul :class="dropdownMenuClass.endpoint" style="width: auto">
								<li>
									<a
										:class="{
											'dropdown-item': true,
											active: String(object.endpoint) === String(option.name)
										}"
										href="javascript:void(0)"
										v-for="option in endpointList"
										:key="option.name"
										@click="selectedItem('endpoint', option.name, `${option.name}(${option.endpointUrl})`)"
									>
										{{ `${option.name}(${option.endpointUrl})` }}
									</a>
								</li>
							</ul>
						</span>
					</label>
				</div>
				<div class="col">
					<label :class="{ 'form-control-label': true, 'label-select': true, disabled: dropdownMenuInfo.operation.readonly }">
						<b class="control-label">WSDL Operation</b>
						<span>
							<input
								:class="{ 'form-control': true }"
								v-model="dropdownMenuInfo.operation.selectedName"
								:placeholder="$t('head.searchData')"
								@click="toggleDropdownItem('operation')"
								readonly
							/>
							<ul :class="dropdownMenuClass.operation">
								<li>
									<a
										:class="{
											'dropdown-item': true,
											active: String(object.operation) === String(option.operationName)
										}"
										href="javascript:void(0)"
										v-for="option in operationList"
										:key="option.operationName"
										@click="
											selectedItem('operation', option.operationName, `${option.operationName}(${option.operationNameSpace})`)
										"
									>
										{{ `${option.operationName}(${option.operationNameSpace})` }}
									</a>
								</li>
							</ul>
						</span>
					</label>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import Search from '@/components/Search.vue';
import { HttpReq } from '@/utils/common.js';

export default {
	name: 'Search',
	extends: Search,
	data() {
		return {
			object: {
				service: null,
				endpoint: null,
				operation: null
			},
			address: null,
			//address: `http://www.dneonline.com/calculator.asmx?wsdl`,

			serviceList: [],
			endpointList: [],
			operationList: [],

			dropdownMenuClass: {
				service: {
					'dropdown-menu': true,
					show: false
				},
				endpoint: {
					'dropdown-menu': true,
					show: false
				},
				operation: {
					'dropdown-menu': true,
					show: false
				}
			},

			dropdownMenuInfo: {
				service: {
					selectedName: null,
					readonly: true
				},
				endpoint: {
					selectedName: null,
					readonly: true
				},
				operation: {
					selectedName: null,
					readonly: true
				}
			}
		};
	},
	methods: {
		getData() {
			return this.object;
		},
		setSearchListVal() {
			for (const key in this.object) {
				this.object[key] = null;
				this.dropdownMenuInfo[key].readonly = true;
				this.dropdownMenuClass[key].show = false;
				this.dropdownMenuInfo[key].selectedName = null;
			}
		},
		async getStruct() {
			const result = await new HttpReq('/igate/webservice/struct.json').update({ address: this.address }, true);

			if ('ok' !== result.data.result) return;

			this.serviceList = result.data.object.serviceList;

			this.dropdownMenuInfo.service.readonly = false;
		},
		toggleDropdownItem(type) {
			for (const menu in this.dropdownMenuClass) {
				if (menu === type) this.dropdownMenuClass[menu].show = !this.dropdownMenuClass[menu].show;
				else this.dropdownMenuClass[menu].show = false;
			}
		},
		selectedItem(type, value, text) {
			this.dropdownMenuClass[type].show = false;
			this.dropdownMenuInfo[type].selectedName = text;
			this.object[type] = value;

			const readOnly = 'service' === type ? 'endpoint' : 'endpoint' === type ? 'operation' : null;

			if (readOnly) this.dropdownMenuInfo[readOnly].readonly = false;

			if ('service' === type) {
				const list = this.serviceList.filter(service => service.name === this.object.service)[0];

				this.endpointList = list.endpointList;
				this.operationList = list.operationList;
			} else if ('operation' === type) {
				const list = this.operationList.filter(operation => operation.operationName === this.object.operation)[0];
				this.$emit('setAddDetailData', { searchData: this.object, structData: list });
			}
		},
		openModal() {
			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t('igate.webService'),
				modalWidth: '800px',
				contentList: [
					{
						template: `
							<inputText
								ref="wdslUrl"
								vModel="address"
								regExpType="url"
								:classObject="{ 'form-control': true }"
							/>
						`,
						components: {
							inputText: require(`@/components/element/inputText.vue`).default
						}
					}
				],
				buttonList: (() => {
					return [
						{
							template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('head.ok') }}</button>`,
							methods: {
								async confirm() {
									const info = modalInstance.$refs.content[0].$refs.wdslUrl.getData();

									_this.address = info.address;
									_this.getStruct();

									modalInstance.hideModal();
								}
							}
						}
					];
				})()
			});
		}
	}
};
</script>
