<template>
	<div class="instanceChartRoot">
		<div class="row no-gutters flex-1" :style="rowStyle">
			<div class="col" v-for="n in instanceSummaryColCnt" :key="n" :style="{ width: `${100 / instanceSummaryColCnt}%`, height: `100%` }">
				<component
					v-for="(targetInfo, idx) in targetInfoList
						? targetInfoList.filter((targetInfo, index) => n - 1 === index % instanceSummaryColCnt)
						: []"
					:is="targetInfo.component"
					:data="targetInfo.data"
					@filterTarget="filterTarget"
					:key="`${idx}_${targetInfo.targetId}`"
				/>
			</div>
		</div>

		<div class="legend legend-square" v-if="isShowLegend">
			<span class="status"><i class="dot bg-cht-6"></i>{{ $t(`dashboard.legend.square.cpu`) }}</span>
			<span class="status"><i class="dot bg-cht-7"></i>{{ $t(`dashboard.legend.square.memory`) }}</span>
			<span class="status"><i class="dot bg-cht-3"></i>{{ $t(`dashboard.legend.square.disk1`) }}</span>
			<span class="status"><i class="dot bg-cht-3"></i>{{ $t(`dashboard.legend.square.disk2`) }}</span>
		</div>
	</div>
</template>

<script>
export default {
	name: `DashboardInstanceChart`,
	props: {
		options: Object,
		dashConstInfo: Object,
		containerUserInfo: Object
	},
	data() {
		return {
			instanceSummaryColCnt: this.dashConstInfo.instanceSummaryDefaultColCnt,
			targetInfoList: [],
			isShowLegend: false,
			rowStyle: {
				overflowX: 'hidden',
				height: null
			}
		};
	},
	mounted() {
		this.initInstanceChart();
	},
	methods: {
		initInstanceChart() {
			this.isShowLegend = `Y` === this.options.remarkYn;

			this.$nextTick(() => {
				this.rowStyle.height = this.isShowLegend ? `calc(100% - ${this.$el.querySelector(`.legend-square`).offsetHeight}px)` : `100%`;
			});

			if (this.containerUserInfo.containerOptions[`instanceSummaryColCnt_${this.options.componentId}`])
				this.instanceSummaryColCnt = this.containerUserInfo.containerOptions[`instanceSummaryColCnt_${this.options.componentId}`];
		},
		addTarget(targetObj) {
			targetObj.data = {
				targetId: targetObj.targetId,
				isDown: false,
				isFiltered: false,
				cpuUsage: 0,
				heapUsage: 0,
				fileMainUsage: 0,
				fileQueueUsage: 0
			};

			targetObj.component = {
				template: `
					<div class="gate-col" :style="rootElementStyle">
						<div class="graph-caption">
							<a href="javascript:void(0)" @click="filterTarget">{{ targetId }}</a>
							<i class="iconb-warn" title="Down" v-if="isDown"></i>
						</div>
						<div class="graph-vs" style="width: 100%;">
							<div class="progress progress-vertical" style="width: 25%;height:calc(100% - 1.0rem); padding-bottom: 1.0rem;">
								<div class="progress-bar bg-cht-6" role="progressbar" data-toggle="tooltip" data-placement="top" :title="cpuUsage" :style="cpuGraphStyle" ></div>
								<span class="progress-status" style="font-size: 1.0rem; line-height: 0.875rem;"><span class="t-xs">{{ cpuUsage }}</span></span>
							</div>
							<div class="progress progress-vertical" style="width: 25%;height:calc(100% - 1.0rem); padding-bottom: 1.0rem;">
								<div class="progress-bar bg-cht-7" role="progressbar" data-toggle="tooltip" data-placement="top" :title="heapUsage" :style="heapGraphStyle"></div>
								<span class="progress-status" style="font-size: 1.0rem; line-height: 0.875rem;"><span class="t-xs">{{ heapUsage }}</span></span>
							</div>
							<div class="progress progress-vertical" style="width: 25%;height:calc(100% - 1.0rem); padding-bottom: 1.0rem;">
								<div class="progress-bar bg-cht-3" role="progressbar" data-toggle="tooltip" data-placement="top" :title="fileMainUsage" :style="fileMainGraphStyle"></div>
								<span class="progress-status" style="font-size: 1.0rem; line-height: 0.875rem;"><span class="t-xs">{{ fileMainUsage }}</span></span>
							</div>
							<div class="progress progress-vertical" style="width: 25%;height:calc(100% - 1.0rem); padding-bottom: 1.0rem;">
								<div class="progress-bar bg-cht-3" role="progressbar" data-toggle="tooltip" data-placement="top" :title="fileQueueUsage" :style="fileQueueGraphStyle"></div>
								<span class="progress-status" style="font-size: 1.0rem; line-height: 0.875rem;"><span class="t-xs">{{ fileQueueUsage }}</span></span>
							</div>
						</div>
					</div>		
				`,
				props: {
					data: {
						type: Object,
						default: () => ({
							targetId: null,
							isDown: false,
							isFiltered: false,
							cpuUsage: 0,
							heapUsage: 0,
							fileMainUsage: 0,
							fileQueueUsage: 0
						})
					}
				},
				computed: {
					targetId() {
						return this.data.targetId;
					},
					isDown() {
						return this.data.isDown;
					},
					rootElementStyle() {
						return {
							opacity: this.data.isFiltered ? 0.2 : 1.0
						};
					},
					cpuUsage() {
						return `${Math.ceil(this.data.cpuUsage)}%`;
					},
					heapUsage() {
						return `${Math.ceil(this.data.heapUsage)}%`;
					},
					fileMainUsage() {
						return `${Math.ceil(this.data.fileMainUsage)}%`;
					},
					fileQueueUsage() {
						return `${Math.ceil(this.data.fileQueueUsage)}%`;
					},
					cpuGraphStyle() {
						return {
							width: `100%`,
							height: this.cpuUsage
						};
					},
					heapGraphStyle() {
						return {
							width: `100%`,
							height: this.heapUsage
						};
					},
					fileMainGraphStyle() {
						return {
							width: `100%`,
							height: this.fileMainUsage
						};
					},
					fileQueueGraphStyle() {
						return {
							width: `100%`,
							height: this.fileQueueUsage
						};
					}
				},
				methods: {
					filterTarget() {
						this.$emit(`filterTarget`, {
							targetId: this.targetId,
							isFiltered: this.data.isFiltered
						});
					}
				}
			};

			this.targetInfoList.push(targetObj);
		},
		deleteTarget(targetId) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			this.targetInfoList = this.targetInfoList.filter(targetInfo => targetInfo.targetId !== targetId);
		},
		updateTarget(targetId, updateOption) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			if (updateOption.isDataEmpty) {
				targetInfo.data.isDown = `Y` !== updateOption.downStatus;

				targetInfo.data.cpuUsage = 0;
				targetInfo.data.heapUsage = 0;
				targetInfo.data.fileMainUsage = 0;
				targetInfo.data.fileQueueUsage = 0;
			} else {
				targetInfo.data.isDown = false;
			}
		},
		addData(dataArr) {
			dataArr.forEach(dataInfo => {
				const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === dataInfo.instanceId);

				if (!targetInfo) return;

				targetInfo.data.cpuUsage = dataInfo.cpuUsage;
				targetInfo.data.heapUsage = dataInfo.heapUsage;
				targetInfo.data.fileMainUsage = dataInfo.fileMainUsage;
				targetInfo.data.fileQueueUsage = dataInfo.fileQueueUsage;
			});
		},
		filterTarget(param) {
			this.$emit(`filter-target`, param);
		},
		filterData({ targetId, isFiltered }) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			targetInfo.data.isFiltered = isFiltered;
		},
		unload() {
			this.targetInfoList = null;
		}
	}
};
</script>
<style lang="scss" scoped>
.instanceChartRoot {
	height: 100%;
}
</style>
