<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/modelRecord/Detail.vue';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'ModelRecord',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}

		if (localStorage.getItem('selectedModelRecordInfo')) {
			const selectedModelRecordInfo = JSON.parse(localStorage.getItem('selectedModelRecordInfo'));
			localStorage.removeItem('selectedModelRecordInfo');

			this.initDetail('detail', {
				id: selectedModelRecordInfo.recordId,
				isNewTab: true
			});
		}
	},
	data() {
		return {
			cudUrl: '/igate/record/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'recordId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'recordName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'recordType',
					label: this.$t('common.type'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Record.Type',
							orderByKey: true
						},
						optionListName: 'recordTypes',
						optionFor: 'option in recordTypes',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue',
						optionIf: "'H' === option.pk.propertyKey || 'R' === option.pk.propertyKey"
					}
				},
				{
					type: 'text',
					vModel: 'recordGroup',
					label: this.$t(`head.group`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'name'
				},
				{
					type: 'dataList',
					vModel: 'privilegeId',
					label: this.$t(`common.privilege`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'searchId',
					optionInfo: {
						url: '/common/privilege/list.json',
						params: {
							privilegeType: `b`
						},
						optionListName: 'privilegeTypeList',
						optionFor: 'option in privilegeTypeList',
						optionValue: 'option.privilegeId',
						optionText: 'option.privilegeId'
					}
				},
				{
					type: 'text',
					vModel: 'recordDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: '/igate/record/search.json',
				totalCntUrl: '/igate/record/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('head.id'),
							name: 'recordId',
							width: '20%'
						},
						{
							header: this.$t('head.name'),
							name: 'recordName',
							width: '20%'
						},
						{
							header: this.$t('common.type'),
							name: 'recordType',
							align: 'center',
							width: '10%',
							formatter: ({ value }) =>
								`H` === value ? `header` : `I` === value ? `indivi` : `R` === value ? `refer` : `c` === value ? `common` : `embed`
						},
						{
							header: this.$t('head.group'),
							name: 'recordGroup',
							width: '20%'
						},
						{
							header: this.$t('common.privilege'),
							name: 'privilegeId',
							width: '10%'
						},
						{
							header: this.$t('head.description'),
							name: 'recordDesc',
							width: '30%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//detail
			detailPk: ['recordId'],
			controlUrl: '/igate/record/control.json',
			detailButtonList: [{ id: 'dump', isUse: true }],
			controlParams: detailData => ({
				recordId: detailData.recordId
			}),
			detailTabList: [
				{ type: 'bundle', url: '/modelRecord/BasicInfo', label: this.$t('head.basic.info') },
				{ type: 'bundle', url: '/modelRecord/DetailInfo', label: this.$t('head.model.info') },
				{ type: 'bundle', url: '/modelRecord/ExtendInfo', label: this.$t('head.extension.info') },
				{
					id: `useInfo`,
					type: 'basic',
					label: this.$t('head.resource.inuse.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'lockUserId',
									label: this.$t('head.lock.userId'),
									readonly: true,
									val: '0'
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: this.$t('head.updateVersion'),
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	}
};
</script>
