<template>
	<div class="ct-content">
		<NoData v-if="!isDetailView" />
		<Detail
			v-if="isDetailView"
			:buttonList="detailButtonList"
			:tabList="detailTabList"
			:info="detailInfo"
			:cudUrl="cudUrl"
			:viewer="viewer"
			:editor="editor"
			:additionalData="addDetailData"
			@changeDetailView="changeDetailView"
			ref="detail"
		/>
	</div>
</template>

<script>
import NoData from '@/components/NoData.vue';
import Detail from '@/views/detail/webService/Detail.vue';

export default {
	name: 'Main',
	props: {
		detailButtonList: Array,
		detailInfo: Object,
		detailTabList: Array,
		addDetailData: Object,
		cudUrl: String,
		viewer: Boolean,
		editor: Boolean
	},
	data() {
		return {
			isDetailView: false
		};
	},
	components: {
		NoData,
		Detail
	},
	watch: {
		addDetailData() {
			this.isDetailView = true;
		}
	},
	methods: {
		changeDetailView() {
			this.isDetailView = false;
			this.$emit('initSearchArea');
		}
	}
};
</script>
