<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.record.metaDomain') }}</span>
					</label>
					<select v-model="metaDomain" class="form-control view-disabled" :readonly="readonly">
						<option v-for="(metaDomain, idx) in metaDomainList" :key="idx" :value="metaDomain">
							{{ metaDomain }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.request.model') }}</span>
					</label>
					<div class="input-group">
						<inputText
							ref="requestRecordName"
							vModel="requestRecordName"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:val="requestRecordName"
							:readonly="true"
						/>
						<button class="btn btn-icon search-linkBtn" :title="$t('head.open')" @click="openRecordMngModal('I')">
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.response.model') }}</span>
					</label>
					<div class="input-group">
						<select v-model="selectedResponseRecordId" class="form-control view-disabled">
							<option v-for="(responseRecordInfo, idx) in responseRecordList" :key="idx" :value="responseRecordInfo.recordId">
								{{ `${responseRecordInfo.recordId} - ${responseRecordInfo.recordName}` }}
							</option>
						</select>
						<button class="btn btn-icon search-linkBtn" :title="$t('head.open')" @click="openRecordMngModal('O')">
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.list') }}</span>
					</label>
					<div class="table-responsive">
						<TuiGrid ref="tuiGrid" v-bind="options" />
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
import { Grid } from '@toast-ui/vue-grid';
import {
	HttpReq,
	getNumFromStr,
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	escapeHtml,
	makeGridOptions,
	parseFlattenObj
} from '@/utils/common.js';

import inputText from '@/components/element/inputText';

export default {
	name: 'ModelRecordInfo',
	props: {
		detailData: Object,
		mode: String
	},
	components: {
		TuiGrid: Grid,
		inputText
	},
	data() {
		return {
			metaDomainList: [],
			calendarList: [],
			selectedResponseRecordId: null,

			metaDomain: null,
			requestRecordObject: null,
			responseRecordList: [],

			options: {
				header: {
					height: 60,
					align: 'center'
				},
				columns: [
					{
						name: 'serviceObject',
						hidden: true
					},
					{
						name: 'requestRecordObject',
						hidden: true
					},
					{
						name: 'responseRecordObject',
						hidden: true
					},
					{
						name: 'requestMappingObject',
						hidden: true
					},
					{
						name: 'responseMappingObject',
						hidden: true
					},
					{
						header: this.$t('igate.service'),
						name: 'serviceId',
						formatter: ({ row, value }) => {
							return `${escapeHtml(value)}
									<span class="modal-btn")">
										<i class="icon-link" onclick="serviceClick('${row.rowKey}')"></i>
									</span>`;
						}
					},
					{
						name: 'requestMappingName',
						header: this.$t('igate.interface.requestMapping'),
						formatter: ({ row }) => {
							const requestMappingObject = row.requestMappingObject;
							const requestMappingId = requestMappingObject ? requestMappingObject.mappingId : ``;

							if (this.readonly && !requestMappingId) return ``;

							const resetHtml = this.readonly
								? ``
								: `<i class="icon-reset" onclick="serviceMappingResetClick('I', '${row.rowKey}')"></i>`;

							return `<div class="ellipsis" style="width: calc(100% - ${resetHtml ? 50 : 25}px);" title="${requestMappingId}">
                                        ${escapeHtml(requestMappingId)}
                                    </div>
									<span class="modal-btn">
										<i class="icon-mapng" onclick="serviceMappingClick('I', '${row.rowKey}')"></i>
										${resetHtml}
									</span>`;
						}
					},
					{
						name: 'responseMappingName',
						header: this.$t('igate.interface.responseMapping'),
						formatter: ({ row }) => {
							const responseMappingObject = row.responseMappingObject;
							const responseMappingId = responseMappingObject ? responseMappingObject.mappingId : ``;

							if (this.readonly && !responseMappingId) return ``;

							const resetHtml = this.readonly
								? ``
								: `<i class="icon-reset" onclick="serviceMappingResetClick('O', '${row.rowKey}')"></i>`;

							return `<div class="ellipsis" style="width: calc(100% - ${resetHtml ? 50 : 25}px);" title="${responseMappingId}">
                                        ${escapeHtml(responseMappingId)}
                                    </div>
									<span class="modal-btn">
										<i class="icon-mapng" onclick="serviceMappingClick('O', '${row.rowKey}')"></i>
										${resetHtml}
									</span>`;
						}
					},
					{
						name: 'cronExpression',
						header: this.$t('igate.interface.cronExpression'),
						escapeHTML: true
					},
					{
						name: 'calendarId',
						header: this.$t('igate.calendar'),
						formatter: ({ row }) => {
							let selectDiv = "<select class='panel-form-control' name='calendarId' style='width : 100%; text-overflow: ellipsis;'>";
							selectDiv += "		<option value=''></option>";

							this.calendarList.forEach(option => {
								selectDiv +=
									"<option value='" +
									option.calendarId +
									"' " +
									(option.calendarId == row.calendarId ? 'selected' : '') +
									'>' +
									escapeHtml(option.calendarId) +
									'</option>';
							});

							selectDiv += '</select>';

							return selectDiv;
						}
					},
					{
						name: 'conditionExpression',
						header: this.$t('igate.interface.conditionExpression'),
						escapeHTML: true
					}
				],
				draggable: false,
				onGridMounted: evt => {
					if ('undefined' === typeof evt.instance.el) return;

					evt.instance.el.removeAttribute('header');
					evt.instance.el.removeAttribute('columnoptions');
					evt.instance.el.removeAttribute('onGridMounted');
					evt.instance.el.removeAttribute('draggable');
					evt.instance.el.removeAttribute('rowheaders');
				}
			},

			modalRecordInfo: {
				title: this.$t('igate.modelRecord'),
				search: {
					list: [
						{ type: 'text', vModel: 'id', label: this.$t('igate.modelRecord.id'), placeholder: this.$t('head.searchId') },
						{
							type: 'dataList',
							vModel: 'pageSize',
							label: this.$t('head.listCount'),
							placeholder: this.$t('head.searchCoditionList'),
							val: '10',
							optionInfo: {
								optionFor: 'option in [10, 100, 1000]',
								optionValue: 'option',
								optionText: 'option'
							}
						}
					]
				},
				button: {
					list: [{ id: 'initialize', isUse: true }]
				},
				grid: {
					url: '/igate/record/searchPopup.json',
					paging: {
						isUse: true,
						side: 'server'
					},
					options: {
						columns: [
							{
								name: 'id',
								header: this.$t('igate.modelRecord.id'),
								width: '20%'
							},
							{
								name: 'name',
								header: this.$t('igate.modelRecord.name'),
								width: '40%'
							}
						]
					}
				}
			}
		};
	},
	computed: {
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		},
		requestRecordId() {
			const id = this.requestRecordObject && this.requestRecordObject.recordId ? this.requestRecordObject.recordId : '';
			return id;
		},
		requestRecordName() {
			const id = this.requestRecordObject && this.requestRecordObject.recordId ? this.requestRecordObject.recordId : '';
			const name = this.requestRecordObject && this.requestRecordObject.recordName ? this.requestRecordObject.recordName : '';

			if (id && name) {
				return `${id} - ${name}`;
			} else if (id && !name) {
				return id;
			} else if (!id && name) {
				return name;
			} else {
				return '';
			}
		}
	},
	async beforeCreate() {
		window.serviceClick = rowKey => {
			const _this = this;

			const { serviceObject } = this.$refs.tuiGrid.invoke('getRow', rowKey);

			const modalInstance = this.$openModal({
				title: this.$t('igate.service'),
				fullscreen: true,
				contentList: [
					{
						template: `<ServiceModal ref="ServiceModal" :serviceObject="serviceObject" :mode="mode" />`,
						components: {
							ServiceModal: require(`@/views/modal/service/ServiceModal.vue`).default
						},
						data() {
							return {
								serviceObject: parseFlattenObj(serviceObject),
								mode: `add` === _this.mode ? 'modify' : _this.mode
							};
						}
					}
				],
				buttonList: (() => {
					return !this.readonly
						? [
								{
									template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('head.ok') }}</button>`,
									methods: {
										async confirm() {
											const info = modalInstance.$refs.content[0].$refs.ServiceModal.getData();

											if (['name', 'privilegeId', 'privateYn'].some(key => isEmpty(info[key]))) {
												_this.$alert({ type: 'warn', message: _this.$t('head.add.service.validation.warn') });
												return;
											}

											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'serviceObject', info);
											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'serviceId', info.id);
											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'serviceName', info.name);
											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'requestRecordObject', info.requestRecordObject);
											_this.$refs.tuiGrid.invoke('setValue', rowKey, 'responseRecordObject', info.responseRecordObject);

											modalInstance.hideModal();
										}
									}
								}
						  ]
						: null;
				})()
			});
		};

		window.serviceMappingClick = (type, rowKey) => {
			this.openMappingMngModal(type, rowKey);
		};

		let result = await new HttpReq(`/igate/calendar/list.json`).read();

		if (`ok` !== result.data.result) return;

		this.calendarList = result.data.object;
	},
	async created() {
		this.options = makeGridOptions(this.options);

		let result = await new HttpReq(`/igate/fieldMeta/groups.json`).read();

		if (`ok` !== result.data.result) return;

		this.metaDomainList = result.data.object;
	},
	mounted() {
		WindowResizeEventBus.$on('MainResize', () => {
			setTimeout(this.resize, 300);
		});

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);
	},
	methods: {
		resize() {
			if (!this.$refs.tuiGrid) return;

			const panel = document.getElementById('panel');
			const panelBody = panel.getElementsByClassName('panel-body')[0];
			const panelBodyComputedStyle = getComputedStyle(panelBody);

			const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
			const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
			const panelBodyPaddingTop = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-top'));
			const panelBodyPaddingBottom = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-bottom'));

			const adjustWidth = panelBody.clientWidth - panelBodyPaddingLeft - panelBodyPaddingRight;
			const adjustHeight = panelBody.clientHeight - panelBodyPaddingTop - panelBodyPaddingBottom - (80 + 24 + 32);

			this.$refs.tuiGrid.invoke('setWidth', adjustWidth);
			this.$refs.tuiGrid.invoke('setHeight', 200 > adjustHeight ? 200 : adjustHeight);

			this.$el.style.width = `${adjustWidth}px`;
		},
		async resetData() {
			this.metaDomain = null;
			this.requestRecordObject = {};
			this.responseRecordList = [];

			for (const key in this.detailData) {
				const value = this.detailData[key];

				if (key.startsWith('requestRecordObject')) {
					this.requestRecordObject[key.replace('requestRecordObject.', '')] = value;
				} else if (`metaDomain` === key) {
					this.metaDomain = value;
				}
			}

			if (this.detailData.interfaceResponses)
				this.detailData.interfaceResponses.forEach(info => {
					if (info.recordObject) {
						this.responseRecordList.push(info.recordObject);
					} else if (this.detailData.requestRecordId === info.pk.recordId) {
						this.responseRecordList.push(cloneDeep(this.requestRecordObject));
					}
				});

			if (0 < this.responseRecordList.length) {
				this.selectedResponseRecordId = this.responseRecordList[0].recordId;
			}

			if (this.detailData.interfaceServices) {
				try {
					const promiseServiceList = [];

					this.detailData.interfaceServices.forEach(interfaceService => {
						promiseServiceList.push(new HttpReq(`/igate/service/object.json`).read({ serviceId: interfaceService.pk.serviceId }));
					});

					const values = await Promise.all(promiseServiceList);

					this.$refs.tuiGrid.invoke('clear');

					if (this.detailData.interfaceServices)
						this.$refs.tuiGrid.invoke(
							'resetData',
							this.detailData.interfaceServices.map(interfaceService => {
								const serviceInfo = values.filter(value => value.data.object.serviceId === interfaceService.pk.serviceId)[0];
								const object = serviceInfo ? serviceInfo.data.object : {};

								return {
									serviceObject: object,
									serviceId: object.serviceId,
									serviceName: object.serviceName,
									requestMappingObject: interfaceService.requestMappingObject ? interfaceService.requestMappingObject : null,
									responseMappingObject: interfaceService.responseMappingObject ? interfaceService.responseMappingObject : null,
									requestRecordObject: object.requestRecordObject ? object.requestRecordObject : null,
									responseRecordObject: object.responseRecordObject ? object.responseRecordObject : null,
									cronExpression: object.cronExpression,
									calendarId: object.calendarId,
									conditionExpression: object.conditionExpression
								};
							})
						);

					let coverDiv = this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').querySelector('#coverDiv');

					if (coverDiv) this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').removeChild(coverDiv);

					if (this.readonly) {
						coverDiv = document.createElement('div');

						coverDiv.setAttribute('id', 'coverDiv');
						coverDiv.style.width = '100%';
						coverDiv.style.height = '100%';
						coverDiv.style.backgroundColor = '#666';
						coverDiv.style.position = 'absolute';
						coverDiv.style.top = '0';
						coverDiv.style.opacity = '0.4';

						this.$refs.tuiGrid.$el.querySelector('.tui-grid-table-container').appendChild(coverDiv);
					}
				} catch (error) {
					this.$alert({ type: 'warn', message: this.$t('head.no.data.warn') });
				}
			} else {
				this.$refs.tuiGrid.invoke('clear');
			}
		},
		async openMappingMngModal(type, rowKey) {
			const { serviceId, serviceName, requestRecordObject, responseRecordObject, requestMappingObject, responseMappingObject } =
				this.$refs.tuiGrid.invoke('getRow', rowKey);

			if (`I` === type) {
				if (!this.requestRecordObject) return;
				if (!this.requestRecordObject.recordId) return;
				if (!requestRecordObject) return;
			}

			if (`O` === type) {
				if (0 === this.responseRecordList.length) return;
				if (!this.selectedResponseRecordId) return;
				if (!responseRecordObject) return;
			}

			let validateDataInfo = {},
				validateList = [];

			if (`add` === this.mode) {
				validateList = [`name`, `privilegeId`];

				const result = await this.$parent.checkValidate();

				if (!result.isValidate) return;

				validateDataInfo.id = result.id;

				this.$parent.$refs.component.forEach(info => {
					if (`DetailCustom` === info.$options.name) return;

					if (`ModelRecordInfo` === info.$options.name) return;

					if (!info.getData) return;

					const data = info.getData();

					validateList.forEach(validateKey => {
						if (!data[validateKey]) return;
						validateDataInfo[validateKey] = data[validateKey];
					});
				});
			} else {
				validateList = [`interfaceId`, `interfaceName`, `privilegeId`];

				this.$parent.$refs.component.forEach(info => {
					if (`ModelRecordInfo` === info.$options.name) return;

					if (!info.getData) return;

					const data = info.getData();

					validateList.forEach(validateKey => {
						if (!data[validateKey]) return;
						validateDataInfo[validateKey] = data[validateKey];
					});
				});
			}

			if (validateList.some(key => isEmpty(validateDataInfo[key]))) {
				this.$alert({ type: `warn`, message: this.$t('head.add.interface.validation.warn') });
				return;
			}

			const interfaceRecordInfo = `I` === type ? [this.requestRecordObject] : this.responseRecordList;
			const serviceRecordInfo = `I` === type ? [requestRecordObject] : [responseRecordObject, requestRecordObject, this.requestRecordObject];
			const mappingObject = `I` === type ? requestMappingObject : responseMappingObject;

			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t('igate.mapping'),
				fullscreen: true,
				contentList: [
					{
						template: `<MappingModal ref="MappingModal" :data="data" />`,
						components: {
							MappingModal: require(`@/views/modal/mapping/MappingModal.vue`).default
						},
						data() {
							return {
								data: {
									type,
									interfaceId: validateDataInfo.id,
									interfaceName: validateDataInfo.name,
									privilegeId: validateDataInfo.privilegeId,
									serviceId,
									serviceName,
									interfaceRecordInfo,
									serviceRecordInfo,
									mappingObject,
									readonly: _this.readonly
								}
							};
						}
					}
				],
				buttonList: (() => {
					return !this.readonly
						? [
								{
									template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('head.ok') }}</button>`,
									methods: {
										confirm() {
											const mappingInfo = modalInstance.$refs.content[0].$refs.MappingModal.getData();

											_this.$refs.tuiGrid.invoke(
												'setValue',
												rowKey,
												`I` === type ? `requestMappingObject` : `responseMappingObject`,
												mappingInfo
											);

											modalInstance.hideModal();
										}
									}
								}
						  ]
						: null;
				})()
			});
		},
		async openRecordMngModal(type, mod, recordObject) {
			mod = 'update';

			if ('update' === mod && ((`I` === type && !this.requestRecordId) || (`O` === type && 0 === this.responseRecordList.length))) {
				this.$alert({ type: `warn`, message: this.$t(this.readonly ? 'head.no.data.warn' : 'head.no.update.record.warn') });
				return;
			}

			if ('copy' === mod) {
				if (!recordObject) return;
			}

			if (('create' === mod || 'copy' === mod) && `I` === type && this.requestRecordObject) {
				const dataList = this.$refs.tuiGrid.invoke('getData');

				const interfaceRecordId = this.requestRecordObject.recordId;

				const useMappingIdArr = [];

				dataList.forEach(data => {
					const mappingObject = data.requestMappingObject;

					if (mappingObject && mappingObject.mappingDetails)
						mappingObject.mappingDetails.forEach(mappingDetail => {
							if (interfaceRecordId === mappingDetail.mappingRuleObject.recordId)
								useMappingIdArr.push(mappingDetail.mappingRuleObject.pk.mappingId);
						});
				});

				if (0 < useMappingIdArr.length) {
					this.$alert({ type: `warn`, message: `${useMappingIdArr.join(', ')} ${this.$t('head.mapping.referenced.warn')}` });
					return;
				}
			}

			//set Modal Data
			let validateDataInfo = {};
			const validateList = [`id`, `privateYn`, `privilegeId`, `metaDomain`];

			if (`add` === this.mode) {
				const result = await this.$parent.checkValidate();

				if (!result.isValidate) return;

				validateDataInfo.id = result.id;
			}

			const recordObj =
				'I' === type
					? cloneDeep(this.requestRecordObject)
					: cloneDeep(this.responseRecordList.filter(responseRecord => responseRecord.recordId === this.selectedResponseRecordId)[0]);

			if ('update' === mod && recordObj && !recordObj.mode) {
				// 검색을 통해 얻은 데이터 모델 정보 수정
				validateList.forEach(validateKey => {
					if (!recordObj[validateKey]) return;
					validateDataInfo[validateKey] = recordObj[validateKey];
				});
			} else {
				this.$parent.$refs.component.forEach(info => {
					if (`DetailCustom` === info.$options.name) return;

					if (`ModelRecordInfo` === info.$options.name) return;

					if (!info.getData) return;

					const data = info.getData();

					validateList.forEach(validateKey => {
						if (!data[validateKey]) return;
						validateDataInfo[validateKey] = data[validateKey];
					});
				});

				validateDataInfo.metaDomain = this.metaDomain;
			}

			if ('update' === mod) {
				validateDataInfo.recordObject = recordObj;
			} else if ('copy' === mod) {
				validateDataInfo.recordObject = { fields: recordObject.fields };
			}

			// modal open or not
			if ('create' === mod && validateList.some(key => isEmpty(validateDataInfo[key]))) {
				this.$alert({ type: `warn`, message: this.$t('head.add.interface.validation.warn') });
				return;
			}

			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t('igate.modelRecord'),
				fullscreen: true,
				contentList: [
					{
						template: (() => {
							return _this.readonly || `I` === type || (`O` === type && ('create' === mod || 'update' === mod))
								? `<RecordModal ref="RecordModal" :config="config" :mode="mode" />`
								: `<div style="height: 100%">
							   			<div class="row" v-if="!isNext">
							       			<div class="col-lg-12">
								       			<div class="form-group">
									       			<label class="control-label">
										       			<span>Response Form</span>
										   			</label>
										   			<input type="text" v-model="responseForm" class="form-control">
												</div>
								   			</div>
							   			</div>
						       			<RecordModal ref="RecordModal" :config="config" v-else />
						   			</div>`;
						})(),
						components: {
							RecordModal: require(`@/views/modal/modelRecord/RecordModal.vue`).default
						},
						data() {
							return {
								mode: _this.mode,
								config: {
									modalType: `I${type}`,
									modalData: validateDataInfo
								},

								isNext: false,
								responseForm: null
							};
						},
						watch: {
							isNext() {
								if (this.responseForm) this.config.modalData.id += `_${this.responseForm}`;
							}
						}
					}
				],
				buttonList: (() => {
					if (this.readonly) return null;
					else
						return [
							{
								template: `<button type="button" @click="confirm" class="btn btn-primary">{{ $t('head.ok') }}</button>`,
								methods: {
									confirm() {
										if (`O` === type && `copy` === mod && !modalInstance.$refs.content[0].isNext) {
											modalInstance.$refs.content[0].isNext = true;
										} else {
											const modalComp = modalInstance.$refs.content[0].$refs.RecordModal;

											let tabAllData = {};

											for (const key in modalComp.$refs) {
												if (!modalComp.$refs[key].getData) continue;
												tabAllData = merge(tabAllData, modalComp.$refs[key].getData());
											}

											const data = merge('create' === mod ? { mode: mod } : {}, cloneDeep(tabAllData));

											if (`I` === type) {
												_this.requestRecordObject = data;
											} else {
												const idx = _this.responseRecordList.findIndex(responseRecord => responseRecord.id === tabAllData.id);

												if (-1 < idx) _this.responseRecordList[idx] = data;
												else _this.responseRecordList.push(data);

												_this.selectedResponseRecordId = tabAllData.id;
											}

											modalInstance.hideModal();
										}
									}
								}
							}
						];
				})()
			});
		}
	},
	watch: {
		detailData() {
			this.resetData();
		}
	}
};
</script>

<style lang="scss">
.tui-grid-body-area {
	.modal-btn {
		float: right;
		cursor: pointer;
	}
}
</style>
