<template>
	<div class="lineChartRoot">
		<div class="graph">
			<canvas ref="lineChart"></canvas>
		</div>
		<div class="legend"></div>
		<div class="chartjs-tooltip" :style="tooltipStyle" v-show="isShowTooltip">
			<div>{{ tooltipDateTime }}</div>
			<div v-for="(target, idx) in tooltipTargetList" :key="idx">
				<div :style="target.style"></div>
				{{ target.name }} : {{ target.value }}
			</div>
		</div>
	</div>
</template>

<script>
import Chart from 'chart.js';
import moment from 'moment';
import { getYAxisTicksInfo, convertValue, getCanvasMousePos, binanrySearch } from '@/utils/dashboard/chart_utils.js';
import { downloadCSV } from '@/utils/common.js';

export default {
	name: `DashboardLineChart`,
	props: {
		options: Object,
		dashConstInfo: Object
	},
	computed: {
		fontColor() {
			return `Y` === this.options.darkmodeYn ? `#ffffff` : `#151826`;
		},
		filteredColor() {
			return `Y` === this.options.darkmodeYn ? `#292e47` : `#ffffff`;
		},
		axisLineColor() {
			return `Y` === this.options.darkmodeYn ? `#666666` : `#f0f1f6`;
		}
	},
	mounted() {
		this.initLineChart();
	},
	data() {
		return {
			chartObj: null,
			chartDataArr: [],

			isShowTooltip: false,
			tooltipStyle: {
				left: null,
				top: null,
				padding: null
			},
			tooltipDateTime: null,
			tooltipStartTime: null,
			tooltipTargetList: []
		};
	},
	methods: {
		initLineChart() {
			const _this = this;

			const yAxisTickCount = 6;
			const yAxisTicksInfo = getYAxisTicksInfo(null, yAxisTickCount);
			const yAxisMaxValue = yAxisTicksInfo.maxValue;
			const yAxisStepSize = yAxisTicksInfo.stepSize;

			const xAxisMaxValue = Math.floor(Date.now() / 1000) * 1000;
			const xAxisMinValue = xAxisMaxValue - this.dashConstInfo.lineChartTimeDuration * 60 * 1000;

			const ctx = this.$refs.lineChart.getContext(`2d`);

			this.chartObj = new Chart(ctx, {
				type: `line`,
				data: {
					datasets: []
				},
				options: {
					legend: {
						display: false
					},
					title: {
						display: false
					},
					animation: {
						duration: 0
					},
					hover: {
						mode: `point`,
						animationDuration: 0
					},
					responsive: true,
					maintainAspectRatio: false,
					responsiveAnimationDuration: 0,
					chartHoverLastEvt: null,
					onHover(evt) {
						this.options.chartHoverLastEvt = evt;
					},
					tooltips: {
						enabled: false,
						custom(tooltipModel) {
							_this.isShowTooltip = false;

							if (`undefined` === typeof tooltipModel.dataPoints) return;

							if (this._chart.options.chartHoverLastEvt.isTrusted) {
								const mousePos = getCanvasMousePos(this._chart.canvas, this._chart.options.chartHoverLastEvt);
								const mousePosX = mousePos.x;
								// const mousePosY = mousePos.y;

								const chartArea = this._chart.chartArea;
								const left = chartArea.left;
								const right = chartArea.right;

								const xAxesMax = this._chart.config.options.scales.xAxes[0].ticks.max;
								const xAxesMin = this._chart.config.options.scales.xAxes[0].ticks.min;

								const xCoorAlpha = (xAxesMax - xAxesMin) / (right - left);

								const tmpTimeMillis = (mousePosX - left) * xCoorAlpha + xAxesMin;

								const tmpDataset = this._chart.data.datasets[tooltipModel.dataPoints[0].datasetIndex];

								let findDataObj = binanrySearch(tmpDataset.data, Math.floor(tmpTimeMillis / 1000) * 1000);

								if (!findDataObj) findDataObj = binanrySearch(tmpDataset.data, Math.floor((tmpTimeMillis - 1000) / 1000) * 1000);

								if (!findDataObj) findDataObj = binanrySearch(tmpDataset.data, Math.floor((tmpTimeMillis + 1000) / 1000) * 1000);

								if (!findDataObj) return;

								if (null === findDataObj.dataInfo.y) return;

								_this.$emit(`line-chart-tooltip-sync`, {
									standardTimeMillis: Math.floor(findDataObj.dataInfo.x / 1000) * 1000,
									datasetIndex: tooltipModel.dataPoints[0].datasetIndex,
									chartObj: _this.chartObj,
									dataIndex: findDataObj.idx
								});
							} else {
								_this.tooltipTargetList = [];

								let standardTimeMillis =
									_this.chartObj.data.datasets[tooltipModel.dataPoints[0].datasetIndex].data[tooltipModel.dataPoints[0].index].x;

								standardTimeMillis = Math.floor(standardTimeMillis / 1000) * 1000;

								_this.tooltipDateTime = moment(new Date(standardTimeMillis)).format(`HH:mm:ss`);
								_this.tooltipStartTime = Date.now();

								this._chart.data.datasets.forEach(dataset => {
									const findDataObj = binanrySearch(dataset.data, standardTimeMillis);

									if (!findDataObj) return;

									const name = dataset.uniqueName;
									const value = convertValue(
										findDataObj.dataInfo.y,
										_this.options.unitType,
										`elapsed` === _this.options.itemId ? [`ms`, `s`, `m`, `h`, `d`] : null
									);

									_this.tooltipTargetList.push({
										style: {
											width: `10px`,
											height: `10px`,
											backgroundColor: dataset.borderColor,
											display: `inline-block`,
											marginRight: `3px`
										},
										name,
										value
									});
								});

								const position = this._chart.canvas.getBoundingClientRect();

								_this.tooltipStyle.left = `${position.left + window.pageXOffset + tooltipModel.caretX + 10}px`;
								_this.tooltipStyle.top = `${position.top + window.pageYOffset + tooltipModel.caretY}px`;
								_this.tooltipStyle.padding = `${tooltipModel.yPadding}px ${tooltipModel.xPadding}px`;
							}

							_this.isShowTooltip = true;
						}
					},
					layout: {
						padding: {
							left: 25,
							right: 30,
							top: 20,
							bottom: 30
						}
					},
					scales: {
						xAxes: [
							{
								display: false,
								type: `time`,
								time: {
									unit: `millisecond`,
									stepSize: this.dashConstInfo.dataIntervalSeconds * 1000
								},
								ticks: {
									fontColor: this.fontColor,
									beginAtZero: false,
									min: xAxisMinValue,
									max: xAxisMaxValue,
									minRotation: 0,
									maxRotation: 0
								}
							}
						],
						yAxes: [
							{
								display: true,
								ticks: {
									fontColor: this.fontColor,
									beginAtZero: true,
									min: 0,
									max: yAxisMaxValue,
									stepSize: yAxisStepSize,
									maxTicksLimit: yAxisTickCount,
									minRotation: 0,
									maxRotation: 0,
									callback: value =>
										convertValue(
											value,
											this.options.unitType,
											`elapsed` === this.options.itemId ? [`ms`, `s`, `m`, `h`, `d`] : null
										)
								},
								gridLines: {
									color: this.axisLineColor,
									zeroLineColor: this.axisLineColor
								}
							}
						]
					}
				},
				plugins: [
					{
						beforeDraw: chartObj => {
							const chartArea = chartObj.chartArea;

							const left = chartArea.left;
							const right = chartArea.right;

							const xAxesMax = chartObj.config.options.scales.xAxes[0].ticks.max;
							const xAxesMin = chartObj.config.options.scales.xAxes[0].ticks.min;

							const xCoorAlpha = (xAxesMax - xAxesMin) / (right - left);

							ctx.beginPath();

							ctx.strokeStyle = this.axisLineColor;

							ctx.font = `12px Arial`;
							ctx.fillStyle = this.fontColor;
							ctx.textAlign = `center`;

							for (let i = 0; i < this.dashConstInfo.lineChartTimeDuration; i++) {
								const majorTickTime = Math.floor(xAxesMin / (60 * 1000)) * (60 * 1000) + (i + 1) * 60 * 1000;
								const majorTickTimeXCoord = (majorTickTime - xAxesMin) / xCoorAlpha;

								ctx.moveTo(left + majorTickTimeXCoord, chartObj.chartArea.top);
								ctx.lineTo(left + majorTickTimeXCoord, chartObj.chartArea.bottom + 10);

								ctx.fillText(
									moment(new Date(majorTickTime)).format(`HH : mm`),
									left + majorTickTimeXCoord,
									chartObj.chartArea.bottom + 20
								);
							}

							ctx.stroke();

							ctx.closePath();
						}
					},
					{
						afterDraw: chartObj => {
							const maxDataArr = [];

							const xAxes = chartObj.config.options.scales.xAxes[0];

							chartObj.data.datasets.forEach(dataset => {
								const maxDataObj = { x: null, y: null };

								dataset.data
									.filter(o => xAxes.ticks.min <= o.x && xAxes.ticks.max >= o.x)
									.forEach(dataObj => {
										if (!(null === maxDataObj.y || dataObj.y > maxDataObj.y)) return;

										maxDataObj.x = dataObj.x;
										maxDataObj.y = dataObj.y;
									});

								if (null != maxDataObj.y) maxDataArr.push(maxDataObj);
							});

							if (0 === maxDataArr.length) return;

							const maxDataObj = { x: null, y: null };

							maxDataArr.forEach(dataObj => {
								if (!(null === maxDataObj.y || dataObj.y > maxDataObj.y)) return;

								maxDataObj.x = dataObj.x;
								maxDataObj.y = dataObj.y;
							});

							const xAxesMax = xAxes.ticks.max;
							const xAxesMin = xAxes.ticks.min;

							const chartArea = chartObj.chartArea;

							const left = chartArea.left;
							const right = chartArea.right;
							const top = chartArea.top;
							const bottom = chartArea.bottom;

							const xCoordAlpha = (xAxesMax - xAxesMin) / (right - left);

							const xCoord = left + (maxDataObj.x - xAxesMin) / xCoordAlpha;

							ctx.beginPath();

							ctx.save();
							ctx.strokeStyle = this.axisLineColor;
							ctx.setLineDash([2, 2]);
							ctx.moveTo(xCoord, top - 8);
							ctx.lineTo(xCoord, bottom);
							ctx.lineWidth = 2;
							ctx.stroke();

							ctx.font = `9px Arial`;
							ctx.fillStyle = this.fontColor;
							ctx.textAlign = `center`;
							ctx.fillText(
								`Max:${convertValue(
									maxDataObj.y,
									this.options.unitType,
									`elapsed` === this.options.itemId ? [`ms`, `s`, `m`, `h`, `d`] : null
								)}`,
								xCoord,
								top - 8
							);
							ctx.restore();

							ctx.closePath();
						}
					}
				]
			});
		},
		addTarget(targetObj) {
			this.chartObj.data.datasets.push({
				uniqueKey: targetObj.targetId,
				uniqueName: targetObj.targetName,
				color: targetObj.color,
				label: targetObj.targetId,
				backgroundColor: Chart.helpers.color(targetObj.color).alpha(0.5).rgbString(),
				borderColor: targetObj.color,
				borderWidth: 2,
				fill: false,
				pointRadius: 1,
				data: []
			});
		},
		updateTarget(targetId, updateOption) {
			this.chartObj.data.datasets.forEach(seriesOption => {
				if (seriesOption.uniqueKey !== targetId) return;

				seriesOption.color = updateOption.color;
				seriesOption.backgroundColor = Chart.helpers.color(updateOption.color).alpha(0.5).rgbString();
				seriesOption.borderColor = updateOption.color;
			});
		},
		deleteTarget(targetId) {
			this.chartObj.data.datasets.forEach((seriesInfo, index) => {
				if (seriesInfo.uniqueKey === targetId) this.chartObj.data.datasets.splice(index, 1);
			});
		},
		addData(dataArr) {
			this.chartDataArr = this.chartDataArr.concat(dataArr);
		},
		setInterval({ time }) {
			//x축
			const xAxisMaxValue = Math.floor(time / 1000) * 1000;
			const xAxisMinValue = xAxisMaxValue - this.dashConstInfo.lineChartTimeDuration * 60 * 1000;

			this.chartObj.options.scales.xAxes[0].ticks.min = xAxisMinValue;
			this.chartObj.options.scales.xAxes[0].ticks.max = xAxisMaxValue;

			//y축
			this.chartObj.data.datasets.forEach(dataset => {
				this.chartDataArr.forEach(chartData => {
					if (dataset.uniqueKey !== chartData.targetId) return;

					if (
						0 < dataset.data.length &&
						this.dashConstInfo.lineChartDrawDataCycle < chartData.x - dataset.data[dataset.data.length - 1].x
					) {
						dataset.data.push({ x: chartData.x - (chartData.x - dataset.data[dataset.data.length - 1].x) / 2, y: null });
					}

					dataset.data.push({ x: chartData.x, y: chartData.y });
				});
			});

			this.chartDataArr = [];

			let tmpMaxValue = 0;

			this.chartObj.data.datasets.forEach(dataset => {
				const seriesMaxValue = Math.max.apply(
					Math,
					dataset.data.filter(o => xAxisMinValue <= o.x && xAxisMaxValue >= o.x).map(o => o.y)
				);

				if (seriesMaxValue > tmpMaxValue) tmpMaxValue = seriesMaxValue;
			});

			const yAxisTickCount = 6;
			const yAxisTicksInfo = getYAxisTicksInfo(tmpMaxValue, yAxisTickCount);
			const yAxisMaxValue = yAxisTicksInfo.maxValue;
			const yAxisStepSize = yAxisTicksInfo.stepSize;

			this.chartObj.options.scales.yAxes[0].ticks.max = yAxisMaxValue;
			this.chartObj.options.scales.yAxes[0].ticks.stepSize = yAxisStepSize;
			this.chartObj.options.scales.yAxes[0].ticks.maxTicksLimit = yAxisTickCount;

			//차트 update
			this.chartObj.update();

			//data 정리
			this.chartObj.data.datasets.forEach(dataset => {
				dataset.data = dataset.data.filter(obj => xAxisMinValue - 60 * 1000 < obj.x);
			});

			if (3000 < Date.now() - this.tooltipStartTime) this.isShowTooltip = false;
		},
		filterData({ targetId, isFiltered }) {
			const selectedTarget = this.chartObj.data.datasets.find(info => info.uniqueKey === targetId);

			if (!selectedTarget) return;

			selectedTarget.backgroundColor = Chart.helpers
				.color(isFiltered ? this.filteredColor : selectedTarget.color)
				.alpha(0.5)
				.rgbString();

			selectedTarget.borderColor = isFiltered ? this.filteredColor : selectedTarget.color;

			this.chartObj.update();
		},
		unload() {
			this.chartDataArr = [];
			if (this.chartObj) this.chartObj.destroy();
		},
		draw() {
			this.chartObj.update();
		},
		exportData() {
			this.$startSpinner();

			const xAxisMinValue = this.chartObj.options.scales.xAxes[0].ticks.min;
			const xAxisMaxValue = this.chartObj.options.scales.xAxes[0].ticks.max;

			let csvData = `,`;

			csvData += this.chartObj.data.datasets.map(dataset => dataset.uniqueName).join(`,`);
			csvData += `\r\n`;

			const exportDataObj = {};

			this.chartObj.data.datasets.forEach((dataset, idx) => {
				for (const data of dataset.data) {
					if (xAxisMinValue > data.x) continue;

					if (xAxisMaxValue < data.x) continue;

					const timeStamp = Math.floor(data.x / 1000) * 1000;

					if (!exportDataObj[timeStamp]) {
						exportDataObj[timeStamp] = [];

						for (let i = 0; i < this.chartObj.data.datasets.length; i++) {
							exportDataObj[timeStamp].push(``);
						}
					}

					exportDataObj[timeStamp][idx] = convertValue(
						data.y,
						this.options.unitType,
						`elapsed` === this.options.itemId ? [`ms`, `s`, `m`, `h`, `d`] : null
					);
				}
			});

			for (const time in exportDataObj) {
				csvData += `${moment(new Date(Number(time))).format(`HH:mm:ss`)},`;
				csvData += exportDataObj[time].join(`,`);
				csvData += `\r\n`;
			}

			downloadCSV(`${this.options.componentName}_${Date.now()}`, csvData);

			this.$stopSpinner();
		}
	}
};
</script>

<style lang="scss" scoped>
.lineChartRoot {
	height: 100%;
}

.graph {
	height: 100%;
}

.chartjs-tooltip {
	position: fixed;
	background-color: rgba(0, 0, 0, 0.7);
	border-radius: 3px;
	color: #fff;
	z-index: 9999;
}
</style>
