<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<input type="text" v-model="object.queryId" class="form-control view-disabled dataKey" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.name') }}</span>
					</label>
					<input type="text" v-model="object.queryName" class="form-control view-disabled" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.private') }}</span>
					</label>
					<select v-model="object.privateYn" class="form-control view-disabled" :readonly="readonly">
						<option
							v-for="option in [
								{ value: 'Y', name: 'Y' },
								{ value: 'N', name: 'N' }
							]"
							:key="option.value"
							:value="option.value"
						>
							{{ option.name }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.type') }}</span>
					</label>
					<select v-model="object.queryType" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in queryTypes" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.query.datasource') }}</span>
					</label>
					<select v-model="object.dataSource" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in dataSourceList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.pk.propertyKey }} - {{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.query.input.dataModel') }}</span>
					</label>
					<div class="input-group" :title="inputRecordName">
						<input type="text" class="form-control view-disabled ellipsis" :value="inputRecordName" readonly="readonly" />
						<button class="btn btn-icon search-linkBtn" :title="$t('head.open')" @click="openDataModelModal('I')">
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.query.output.dataModel') }}</span>
					</label>
					<div class="input-group" :title="outputRecordName">
						<input type="text" class="form-control view-disabled ellipsis" :value="outputRecordName" readonly="readonly" />
						<button class="btn btn-icon search-linkBtn" :title="$t('head.open')" @click="openDataModelModal('O')">
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.log') }}</span>
					</label>
					<input type="text" v-model="object.queryLogYn" class="form-control view-disabled" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.group') }}</span>
					</label>
					<input type="text" v-model="object.queryGroup" class="form-control view-disabled" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.privilege') }}</span>
					</label>
					<select v-model="object.privilegeId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in privilegeIds" :key="option" :value="option">
							{{ option }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.query.statement') }}</span>
					</label>
					<div class="input-group">
						<textarea v-model="object.queryStatement" class="form-control view-disabled" :readonly="readonly" style="min-height: 200px" />
					</div>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.description') }}</span>
					</label>
					<div class="input-group">
						<textarea v-model="object.queryDesc" class="form-control view-disabled" :readonly="readonly" style="min-height: 200px" />
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';
import { parseHierarchyObj, HttpReq } from '@/utils/common.js';

export default {
	name: 'QueryBasicInfo',
	props: {
		detailData: Object,
		mode: String
	},
	data() {
		return {
			privilegeIds: [],
			queryTypes: [],
			dataSourceList: [],

			inputRecordObject: {},
			outputRecordObject: {},

			object: {
				queryId: null,
				queryName: null,
				privilegeId: null,
				privateYn: null,
				queryGroup: null,
				queryType: null,
				queryLogYn: null,
				dataSource: null,
				inputDataModel: null,
				outputDataModel: null,
				queryStatement: null,
				queryDesc: null
			},

			httpReqList: [
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Query.DataSource', orderByKey: true },
					itemName: 'dataSourceList'
				},
				{
					url: '/common/auth/businessPrivileges.json',
					itemName: 'privilegeIds'
				},
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Query.QueryType', orderByKey: true },
					itemName: 'queryTypes'
				}
			]
		};
	},
	async created() {
		try {
			for await (const content of this.httpReqList) {
				const result = await new HttpReq(content.url).read(content.param ? content.param : null);

				if ('ok' !== result.data.result) throw result.data;

				this[content.itemName] = result.data.object;
			}
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('head.error.load.data.warn') });
		}
	},
	computed: {
		...mapState(['menuList', 'userInfo']),
		...mapGetters(['isUserAdmin']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		},
		inputRecordName() {
			const id = this.inputRecordObject && this.inputRecordObject.recordId ? this.inputRecordObject.recordId : '';
			const name = this.inputRecordObject && this.inputRecordObject.recordName ? this.inputRecordObject.recordName : '';

			return id + (name ? ` - ${name}` : name);
		},
		outputRecordName() {
			const id = this.outputRecordObject && this.outputRecordObject.recordId ? this.outputRecordObject.recordId : '';
			const name = this.outputRecordObject && this.outputRecordObject.recordName ? this.outputRecordObject.recordName : '';

			return id + (name ? ` - ${name}` : name);
		}
	},
	watch: {
		detailData() {
			//clear data
			for (const key in this.object) {
				this.object[key] = null;
			}

			this.inputRecordObject = {};
			this.outputRecordObject = {};

			if ('add' === this.mode) return;

			//set data

			const isSameRecordObj = this.detailData.inputRecordId && this.detailData.inputRecordId === this.detailData.outputRecordId;

			for (const key in this.detailData) {
				const value = this.detailData[key];

				if (key.startsWith('inputRecordObject')) {
					this.inputRecordObject[key.replace('inputRecordObject.', '')] = value;
					if (isSameRecordObj) this.object[key.replace('input', 'output')] = value;
				} else if (key.startsWith('outputRecordObject')) {
					this.outputRecordObject[key.replace('outputRecordObject.', '')] = value;
				}

				this.object[key] = value;
			}

			if (isSameRecordObj) this.outputRecordObject = cloneDeep(this.inputRecordObject);
		}
	},
	methods: {
		openDataModelModal(type) {
			const recordObj =
				`I` === type ? { id: `inputRecordObject`, name: this.inputRecordName } : { id: `outputRecordObject`, name: this.outputRecordName };

			if (!recordObj.name) {
				this.$alert({ type: 'warn', message: this.$t('head.no.data.warn') });
				return;
			}

			const _this = this;
			const validateList = ['queryId', 'privateYn', 'privilegeId'];
			let validateDataInfo = parseHierarchyObj(cloneDeep(this.object));

			for (const validateKey in validateDataInfo) {
				if (recordObj.id === validateKey) validateDataInfo['recordObject'] = validateDataInfo[validateKey];
				if (!this[recordObj.id].mode) validateDataInfo[validateKey] = this[recordObj.id][validateKey];
				if (-1 === validateList.indexOf(validateKey)) delete validateDataInfo[validateKey];
			}

			this.$openModal({
				title: this.$t('igate.record'),
				fullscreen: true,
				contentList: [
					{
						template: `<RecordModal ref="RecordModal" :mode="mode" :config="config"/>`,
						components: {
							RecordModal: require(`@/views/modal/modelRecord/RecordModal.vue`).default
						},
						data() {
							return {
								mode: _this.mode,
								config: {
									modalType: `S${type}`,
									modalData: validateDataInfo
								}
							};
						}
					}
				]
			});
		}
	}
};
</script>
