<template>
	<div class="wrap">
		<header id="hd">
			<div class="hd-brand">
				<h1 class="logo text-hide">
					<a href="javascript:void(0);" style="cursor: default">
						<img :src="logoFile" alt="INZENT" />
					</a>
				</h1>
			</div>
			<ul class="breadcrumb">
				<li
					:class="{ 'breadcrumb-item': true, active: index === selectedMenuPathNameList.length - 1 }"
					v-for="(menuName, index) in selectedMenuPathNameList"
					:key="index"
				>
					{{ menuName }}
				</li>
			</ul>
			<div class="hd-side">
				<a href="javascript:void(0);" @click="userInfo" :title="$t('head.mypage')"><i class="icon-profile"></i></a>
			</div>
		</header>
		<Dispatch :uuid="uuid" :menuId="menuId" />
	</div>
</template>

<script>
import { mapMutations } from 'vuex';
import { getHttpUrl, getUUID, MenuClickEventBus, HttpReq, logout } from '@/utils/common.js';
import Dispatch from '@/components/main/Dispatch.vue';

export default {
	name: 'LayoutExternal',
	components: {
		Dispatch
	},
	data() {
		return {
			uuid: null,
			menuId: null,
			selectedMenuPathIdList: [],
			selectedMenuPathNameList: [],
			isOutLogo: false,
			logoFileName: null,
			logoFileExtension: null,
			logoHttpReq: new HttpReq(`/igate/page/common/logoFileName.json`)
		};
	},
	computed: {
		logoFile() {
			if (!this.logoFileName || !this.logoFileExtension) return;

			const fileName = `${this.logoFileName}.${this.logoFileExtension}`;

			return this.isOutLogo ? getHttpUrl(`/custom/img/${fileName}`) : require(`@/assets/img/${fileName}`);
		}
	},
	beforeCreate() {
		document.body.classList.add(`external-menu-url`);
		document.body.classList.add(`sidebar-toggled`);
		document.body.classList.remove(`moon`);
	},
	async created() {
		try {
			this.setIsTools(true);

			let result = await this.logoHttpReq.read();

			if (`ok` !== result.data.result || !result.data.object) {
				this.isOutLogo = false;

				this.logoFileExtension = `svg`;
				this.logoFileName = `logo`;
			} else {
				this.isOutLogo = true;

				const fileNameArr = result.data.object.split(`.`);

				this.logoFileExtension = fileNameArr[fileNameArr.length - 1];
				this.logoFileName = result.data.object.replace(`.${this.logoFileExtension}`, ``);
			}

			const httpReq = new HttpReq(`/common/menu/list.json`);

			result = await httpReq.read();

			if (`ok` === result.data.result) {
				const menuList = result.data.object;

				this.setMenuList(menuList);

				let externalMenuUrl = sessionStorage.getItem('externalMenuUrl');

				const splitStr = -1 < externalMenuUrl.indexOf(`in:/`) ? `in:/` : `out:/`;
				externalMenuUrl = `${splitStr}${externalMenuUrl.split(splitStr)[1]}`;

				let externalMenuList = null;

				if (localStorage.getItem('selectedMenuPathIdListNewTab')) {
					const menuId = JSON.parse(localStorage.getItem('selectedMenuPathIdListNewTab')).pop();
					localStorage.removeItem('selectedMenuPathIdListNewTab');
					externalMenuList = menuList.filter(menuInfo => menuInfo.menuId === menuId);
				} else {
					externalMenuList = menuList.filter(menuInfo => {
						return !menuInfo.menuUrl ? false : -1 < menuInfo.menuUrl.indexOf(externalMenuUrl);
					});
				}

				if (0 < externalMenuList.length) {
					let externalMenuInfo = null;

					const externalMenuParam = JSON.parse(sessionStorage.getItem(`externalMenuParam`));

					if (externalMenuParam) {
						for (const externalMenu of externalMenuList) {
							let isFindParam = true;

							for (const key in externalMenuParam) {
								if (-1 === externalMenu.menuUrl.indexOf(`${key}=${externalMenuParam[key]}`)) {
									isFindParam = false;
									break;
								}
							}

							if (isFindParam) {
								externalMenuInfo = externalMenu;
								break;
							}
						}
					}

					if (null === externalMenuInfo) externalMenuInfo = externalMenuList[0];

					while (true) {
						this.selectedMenuPathIdList.unshift(externalMenuInfo.menuId);
						this.selectedMenuPathNameList.unshift(this.$t(externalMenuInfo.menuName));

						const parentMenuList = menuList.filter(menu => menu.menuId === externalMenuInfo.parentMenuId);

						if (0 === parentMenuList.length) break;

						externalMenuInfo = parentMenuList[0];
					}

					this.loadContent();
				} else {
					this.$alert({
						type: `warn`,
						message: this.$t(`head.menu.not.exist.warn`),
						callback: () => {
							logout();
						}
					});
				}
			} else {
				throw result.data.error[0].message;
			}
		} catch (error) {
			this.$alert({ type: `warn`, message: `${this.$t(`head.fail.notice`)} ${error}` });
		}
	},
	methods: {
		...mapMutations(['setMenuList', 'setIsTools']),
		loadContent() {
			sessionStorage.setItem('selectedMenuPathIdList', JSON.stringify(this.selectedMenuPathIdList));

			this.uuid = getUUID();
			this.menuId = this.selectedMenuPathIdList[this.selectedMenuPathIdList.length - 1];
			MenuClickEventBus.$emit('ModalDelete');
		},
		userInfo() {
			const modalInstance = this.$openModal({
				title: `${this.$t(`common.user`)} ${this.$t(`head.info`)}`,
				modalWidth: `800px`,
				contentList: [
					{
						template: `<div>
								     <UserInfo/>
								   </div>
						`,
						components: {
							UserInfo: require('@/views/modal/user/UserInfo.vue').default
						}
					}
				],
				buttonList: [
					{
						template: `
							<div>
								<button type="button" class="btn btn-primary" v-if="'detail' === modalMode" @click="chgModalMode('modify')">
									<i class="icon-edit"></i>{{ $t('head.update') }}
								</button>
								<button type="button" class="btn" v-if="'detail' !== modalMode" @click="chgModalMode('detail')">
									{{ $t('head.cancel') }}
								</button>
								<button type="button" class="btn btn-primary" v-if="'detail' !== modalMode" @click="updateUserInfo">
									{{ $t('head.save') }}
								</button>
							</div>
						`,
						data() {
							return {
								modalMode: `detail`
							};
						},
						methods: {
							chgModalMode(mode) {
								this.modalMode = mode;
								modalInstance.$refs.content[0].$children[0].chgModalMode(mode);
							},
							updateUserInfo() {
								modalInstance.$refs.content[0].$children[0].updateUserInfo();
							}
						}
					}
				]
			});
		}
	}
};
</script>

<style lang="scss">
.external-menu-url #ct .panel.panel-bottom {
	width: 100% !important;
	margin-left: unset !important;
}
</style>
