<template>
	<div class="externalLineChartRoot">
		<div class="row row-label" :style="rowStyle">
			<component
				v-for="(targetInfo, idx) in targetInfoList"
				:is="targetInfo.component"
				:data="targetInfo.data"
				:key="`${idx}_${targetInfo.targetId}`"
			/>
		</div>

		<div class="legend" v-if="isShowLegend">
			<span class="status"><i class="dot bg-normal"></i>{{ $t(`dashboard.legend.status.normal`) }}</span>
			<span class="status"><i class="dot bg-warn"></i>{{ $t(`dashboard.legend.status.warn`) }}</span>
			<span class="status"><i class="dot bg-cht-1"></i>{{ $t(`dashboard.legend.status.error`) }}</span>
			<span class="status"><i class="dot bg-danger"></i>{{ $t(`dashboard.legend.status.fetal`) }}</span>
			<span class="status"><i class="dot bg-down"></i>{{ $t(`dashboard.legend.status.down`) }}</span>
		</div>
	</div>
</template>

<script>
export default {
	name: `DashboardExternalLineChart`,
	props: {
		options: Object
	},
	data() {
		return {
			isShowLegend: false,
			targetInfoList: [],
			rowStyle: {
				height: null
			}
		};
	},
	mounted() {
		this.initExternalLineChart();
	},
	methods: {
		initExternalLineChart() {
			this.isShowLegend = `Y` === this.options.remarkYn;

			this.$nextTick(() => {
				this.rowStyle.height = this.isShowLegend ? `calc(100% - ${this.$el.querySelector(`.legend`).offsetHeight}px)` : `100%`;
			});
		},
		addTarget(targetObj) {
			targetObj.data = {
				targetId: targetObj.targetId,
				targetName: targetObj.targetName,
				status: `Normal`,
				active: 0,
				total: 0
			};

			targetObj.component = {
				template: `
					<div class="col-auto" style="flex-basis: 4%">
						<span class="label" data-toggle="tooltip" data-placement="top" title="">
							<i :class="[statusDefaultClass, statusDataClass]"></i><span>{{ targetName }}</span>
						</span>
					</div>				
				`,
				props: {
					data: {
						type: Object,
						default: () => ({
							targetId: null,
							targetName: null,
							status: `Normal`,
							active: 0,
							total: 0
						})
					}
				},
				computed: {
					targetId() {
						return this.data.targetId;
					},
					targetName() {
						return `${this.data.targetName} (${this.data.active} / ${this.data.total})`;
					},
					statusDataClass() {
						const status = this.data.status;

						if (`Normal` === status) return `bg-normal`;
						else if (`Warn` === status) return `bg-warn`;
						else if (`Error` === status) return `bg-cht-1`;
						else if (`Fatal` === status) return `bg-danger`;
						else if (`Down` === status) return `bg-down`;
						else return `bg-normal`;
					}
				},
				data() {
					return {
						statusDefaultClass: `status`
					};
				}
			};

			this.targetInfoList.push(targetObj);
		},
		deleteTarget(targetId) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			this.targetInfoList = this.targetInfoList.filter(targetInfo => targetInfo.targetId !== targetId);
		},
		addData(dataArr) {
			dataArr.forEach(dataInfo => {
				const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === dataInfo.externalLineId);

				if (!targetInfo) return;

				targetInfo.data.status = dataInfo.status;
				targetInfo.data.active = dataInfo.active;
				targetInfo.data.total = dataInfo.total;
			});
		},
		unload() {
			this.targetInfoList = null;
		}
	}
};
</script>
<style lang="scss" scoped>
.externalLineChartRoot {
	height: 100%;
	overflow: hidden;

	.row {
		overflow-y: auto;
	}
}
</style>
