<template>
	<div id="modelRecordModal">
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 0 === selectedIdx }"
					@click="changeTab(0)"
					v-text="$t('head.basic.info')"
				></a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 1 === selectedIdx }"
					@click="changeTab(1)"
					v-text="$t('head.detailInfo')"
				></a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 2 === selectedIdx }"
					@click="changeTab(2)"
					v-text="$t('head.extension.info')"
				></a>
			</li>
		</ul>

		<div class="panel-body tab-content" style="padding-bottom: 0px">
			<div :class="{ [mode]: true, 'tab-pane': true, active: 0 === selectedIdx }">
				<component
					ref="basicInfo"
					:is="require(`@/views/modal${basicInfo.url}.vue`).default"
					:detailInfo="detailInfo"
					:detailData="detailData"
					:modalType="modalType"
					:mode="mode"
				/>
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 1 === selectedIdx }" :style="detailInfoStyle">
				<component
					ref="detailInfo"
					:is="require(`@/views/modal${detailInfo.url}.vue`).default"
					:detailInfo="detailInfo"
					:detailData="detailData"
					:mode="mode"
				/>
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 2 === selectedIdx }">
				<component
					ref="extendInfo"
					:is="require(`@/views/detail${extendInfo.url}.vue`).default"
					:detailInfo="extendInfo"
					:detailData="detailData"
					:mode="mode"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';

export default {
	name: 'RecordModal',
	props: {
		mode: String,
		config: Object
	},
	components: {
		DetailBasic
	},
	data() {
		return {
			modalType: this.config.modalType,
			modalData: this.config.modalData,

			selectedIdx: 0,
			modalMode: 'detail',

			basicInfo: { url: '/modelRecord/BasicInfo', label: this.$t('head.detailInfo') },
			detailInfo: { url: '/modelRecord/DetailInfo', label: this.$t('head.detailInfo') },
			extendInfo: { url: '/modelRecord/ExtendInfo', label: this.$t('head.extension.info') },
			detailData: {}
		};
	},
	methods: {
		changeTab(idx) {
			this.selectedIdx = idx;

			for (const key in this.$refs) {
				if (this.$refs[key].resize) this.$refs[key].resize();
				if (this.$refs[key].resetData) this.$refs[key].resetData();
			}
		}
	},
	computed: {
		detailInfoStyle() {
			return { 'margin-top': `${'detail' === this.mode || 'done' === this.mode ? '0px' : '-1.5rem'}` };
		}
	},
	mounted() {
		this.detailData = this.modalData.recordObject ? this.modalData.recordObject : {};
	}
};
</script>
<style lang="scss">
.modal-fullscreen {
	.panel-body {
		.form-table-wrap {
			max-height: calc(100vh - 72px - 14rem);
		}
	}
}
</style>
