<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('head.insert')">
					<i class="icon-plus-circle"></i>
				</button>
				<label class="col">{{ $t(`common.property.key`) }}</label>
				<label class="col">{{ $t(`common.property.value`) }}</label>
				<label class="col">{{ $t(`head.description`) }}</label>
			</div>
			<div class="form-table-wrap">
				<div class="form-table-body" v-for="(detailObj, bodyIndex) in propertyList" :key="`${detailObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('head.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<input
								type="text"
								list="propertyInfoList"
								:class="{
									'form-control': readonly,
									'regExp-dataList': !readonly,
									'view-disabled': true
								}"
								:readonly="readonly"
								v-model="detailObj['pk.propertyKey']"
								@blur="propertyKeyChange(detailObj)"
								@input="inputEvt($event, bodyIndex, `pk.propertyKey`)"
								:maxlength="keyMaxLength"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ detailObj['pk.propertyKey'] ? detailObj['pk.propertyKey'].length : 0 }} / {{ keyMaxLength }})
							</span>
							<datalist id="propertyInfoList">
								<option
									v-for="(propertyInfo, idx) in propertyInfoList"
									:key="idx"
									:value="propertyInfo.pk.propertyKey"
									v-text="propertyInfo.pk.propertyKey"
								></option>
							</datalist>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<input
								type="text"
								v-model="detailObj['propertyValue']"
								:class="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true
								}"
								@input="inputEvt($event, bodyIndex, `propertyValue`)"
								:maxlength="valueMaxLength"
								:readonly="readonly"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ detailObj['propertyValue'] ? detailObj['propertyValue'].length : 0 }} / {{ valueMaxLength }})
							</span>
						</div>
					</div>
					<div class="col">
						<div :class="`detail-content-${readonly ? `common` : `regExp`}`">
							<input
								type="text"
								v-model="detailObj['propertyDesc']"
								:class="{
									'form-control': readonly,
									'regExp-text': !readonly,
									'view-disabled': true
								}"
								:maxlength="descMaxLength"
								@input="inputEvt($event, bodyIndex, `propertyDesc`)"
								:readonly="readonly"
							/>
							<span class="letterLength" v-if="!readonly">
								({{ detailObj['propertyDesc'] ? detailObj['propertyDesc'].length : 0 }} / {{ descMaxLength }})
							</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';
import { parseFlattenObj, getUUID, HttpReq, getRegExpInfo } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: `instanceProperty`,
	extends: DetailProperty,
	async created() {
		this.keyMaxLength = getRegExpInfo(`id`).maxLength;
		this.valueMaxLength = getRegExpInfo(`value`).maxLength;
		this.descMaxLength = getRegExpInfo(`desc`).maxLength;

		const result = await new HttpReq(`/igate/instance/propertyKeys.json`).read();

		if (`ok` !== result.data.result) return;

		this.propertyInfoList = result.data.object;
	},
	data() {
		return {
			propertyInfoList: [],
			keyMaxLength: null,
			valueMaxLength: null,
			descMaxLength: null,

			regExpInfo: {
				'pk.propertyKey': 'id',
				propertyValue: 'name',
				propertyDesc: 'desc'
			}
		};
	},
	methods: {
		addProperty() {
			this.propertyList.push({
				idx: getUUID(),
				'pk.propertyKey': null,
				propertyValue: null,
				propertyDesc: null
			});
		},
		async setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			if ('undefined' !== typeof this.detailData[`instanceProperties`]) {
				this.propertyList = [...this.detailData[`instanceProperties`]];

				this.propertyList.forEach(propertyInfo => {
					propertyInfo.idx = getUUID();
				});
			} else {
				this.propertyList = [];
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		getData() {
			return {
				instanceProperties: cloneDeep(this.propertyList)
			};
		},
		inputEvt(event, bodyIndex, key) {
			this.propertyList[bodyIndex][key] = event.target.value;
			this.propertyList[bodyIndex][key] = this.propertyList[bodyIndex][key].replace(
				new RegExp(getRegExpInfo(this.regExpInfo[key] ? this.regExpInfo[key] : 'default').regExp, `g`),
				``
			);
		},
		propertyKeyChange(detailObj) {
			if (null !== detailObj.propertyValue && 0 < String(detailObj.propertyValue).trim().length) return;

			const propertyInfo = this.propertyInfoList.find(info => info.pk.propertyKey === detailObj[`pk.propertyKey`]);

			if (!propertyInfo) return;

			const { propertyValue, propertyDesc } = propertyInfo;

			detailObj.propertyValue = propertyValue;
			detailObj.propertyDesc = propertyDesc;
		}
	}
};
</script>
