<template>
	<div class="basic">
		<div class="ct-content">
			<Detail ref="detail" :info="detailInfo" :buttonList="detailButtonList" :cudUrl="cudUrl" :viewer="viewer" :editor="editor" />
		</div>
	</div>
</template>
<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/adapterWizard/Detail.vue';
import { getPrivilege } from '@/utils/common.js';

export default {
	name: 'AdapterWizard',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege('AdapterWizard');

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		this.initNewTabSearchObj();
	},
	methods: {},
	data() {
		return {
			cudUrl: '/igate/adapterWizard/regist.json',

			//auth
			viewer: null,
			editor: null,

			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [
				{ id: 'newTab', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'insert', isUse: true }
			]
		};
	}
};
</script>
