<template>
	<div>
		<section class="tab-section">
			<header class="sub-bar">
				<h3 class="sub-bar-text">{{ $t('head.basic.info') }}</h3>
			</header>
			<div class="row frm-row">
				<div class="col-lg-6">
					<div class="form-group">
						<label class="control-label">
							<span>{{ `${$t('head.transaction')} ${$t('head.id')}` }}</span>
						</label>
						<div :class="object.exceptionCode ? 'input-group' : ''">
							<input type="text" v-model="object.transactionId" class="form-control view-disabled" disabled />
							<button
								class="btn btn-icon search-linkBtn"
								:title="$t('head.open')"
								@click="clickExceptionInfo()"
								v-if="object.exceptionCode && !this.isTools"
							>
								<i class="icon-link"></i>
							</button>
						</div>
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ `${$t('igate.message')} ${$t('head.id')}` }}</span>
						</label>
						<input type="text" v-model="object.messageId" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ `${$t('head.log')} ${$t('head.classification')}` }}</span>
						</label>
						<input type="text" v-model="object.logCode" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ `${$t('igate.instance')} ${$t('head.id')}` }}</span>
						</label>
						<input type="text" v-model="object.instanceId" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ $t('igate.externalTransaction') }}</span>
						</label>
						<input type="text" v-model="object.externalTransaction" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ $t('igate.externalMessage') }}</span>
						</label>
						<input type="text" v-model="object.externalMessage" class="form-control view-disabled" disabled />
					</div>
				</div>
				<div class="col-lg-6">
					<div class="form-group">
						<label class="control-label">
							<span>{{ $t('igate.adapter') }}</span>
						</label>
						<input type="text" v-model="object.adapterId" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ $t('igate.connector') }}</span>
						</label>
						<input type="text" v-model="object.connectorId" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ `${$t('igate.connectorControl.session')} ${$t('head.id')}` }}</span>
						</label>
						<input type="text" v-model="object.sessionId" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ $t('head.ip') }}</span>
						</label>
						<input type="text" v-model="object.remoteAddr" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ $t('igate.traceLog.requestTimestamp') }}</span>
						</label>
						<input type="text" v-model="object.requestTimestamp" class="form-control view-disabled" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">
							<span>{{ $t('igate.traceLog.responseTimestamp') }}</span>
						</label>
						<input type="text" v-model="object.responseTimestamp" class="form-control view-disabled" disabled />
					</div>
				</div>
			</div>
		</section>
		<section class="tab-section">
			<header class="sub-bar">
				<h3 class="sub-bar-text">{{ $t('head.extensionInfo') }}</h3>
			</header>
			<div class="row frm-row">
				<div class="col-lg-6">
					<div class="form-group">
						<label class="control-label">{{ `${$t('igate.interface')} ${$t('head.id')}` }}</label>
						<input type="text" class="form-control view-disabled" v-model="object.interfaceId" disabled />
					</div>
					<div class="form-group">
						<label class="control-label">{{ `${$t('igate.service')} ${$t('head.id')}` }}</label>
						<input type="text" class="form-control view-disabled" v-model="object.serviceId" disabled />
					</div>
				</div>
				<div class="col-lg-6">
					<div class="form-group">
						<label class="control-label">{{ $t('igate.traceLog.responseCode') }}</label>
						<input type="text" class="form-control view-disabled" v-model="object.responseCode" disabled />
					</div>
				</div>
			</div>
		</section>
		<section class="tab-section">
			<header class="sub-bar">
				<h3 class="sub-bar-text">{{ $t('head.errorInfo') }}</h3>
			</header>
			<div class="row frm-row">
				<div class="col-lg-6">
					<div class="form-group">
						<label class="control-label"> {{ $t('head.exception.code') }}</label>
						<input type="text" v-model="object.exceptionCode" class="form-control view-disabled" disabled />
					</div>

					<div class="form-group">
						<label class="control-label">{{ $t('head.exception.message') }}</label>
						<input type="text" class="form-control view-disabled" v-model="object.exceptionMessage" disabled />
					</div>
				</div>
				<div class="col-lg-6">
					<div class="form-group">
						<label class="control-label">{{ $t('head.timeoutYn') }}</label>
						<input type="text" class="form-control view-disabled" :value="$t('head.yes')" v-if="`Y` === object.timeoutYn" disabled />
						<input type="text" class="form-control view-disabled" :value="$t('head.no')" v-else disabled />
					</div>
				</div>
			</div>
		</section>
		<section class="tab-section">
			<header class="sub-bar">
				<h3 class="sub-bar-text">{{ $t('head.logInfo') }}</h3>
			</header>
			<div class="row frm-row">
				<div class="col-lg-6">
					<div class="form-group">
						<label class="control-label"> {{ `${$t('head.log')} ${$t('head.id')} ` }}</label>
						<input type="text" class="form-control view-disabled" v-model="object.logId" disabled />
					</div>
					<div class="form-group">
						<label class="control-label"> {{ `${$t('head.log')} ${$t('head.date')} ` }}</label>
						<input type="text" class="form-control view-disabled" v-model="object.logDateTime" disabled />
					</div>
					<div class="form-group">
						<label class="control-label"> {{ `${$t('head.file')} ${$t('head.name')} ` }}</label>
						<input type="text" class="form-control view-disabled" v-model="object.fileName" disabled />
					</div>
				</div>
				<div class="col-lg-6">
					<div class="form-group">
						<label class="control-label"> {{ `${$t('head.file')} ${$t('head.offset')} ` }}</label>
						<input type="text" class="form-control view-disabled" v-model="object.fileOffset" disabled />
					</div>
					<div class="form-group">
						<label class="control-label"> {{ `${$t('head.file')} ${$t('head.size')} ` }}</label>
						<div class="input-group viewGroup">
							<input type="text" class="form-control view-disabled" v-model="object.bodySize" disabled />
							<div class="input-group-append">
								<button type="button" class="btn viewGroup" @click="downloadFile">{{ $t('head.download') }}</button>
							</div>
						</div>
					</div>
				</div>
				<section class="tab-section">
					<header class="sub-bar">
						<h3 class="sub-bar-text">{{ $t('igate.traceLog.relatedLog') }}</h3>
					</header>
					<div class="table-responsive">
						<TuiGrid ref="tuiGrid" v-bind="options" @click="onClick" />
						<ul class="pagination">
							<li class="page-item" v-for="(page, index) in pages" :key="index">
								<a class="page-link active" href="javascript:void(0);" v-if="page.current">{{ page.pageNo }}</a>
								<a class="page-link" href="javascript:void(0);" @click="getDataList(`_pageNo=${page.pageNo}&${page.keyURI}`)" v-else>
									{{ page.pageNo }}
								</a>
							</li>
						</ul>
					</div>
				</section>
			</div>
		</section>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import moment from 'moment';
import {
	makeGridOptions,
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	getNumFromStr,
	parseFlattenObj,
	HttpReq,
	openNewTab,
	getHttpUrl,
	getUUIDStr
} from '@/utils/common.js';
import { Grid } from '@toast-ui/vue-grid';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'BasicInfo',
	props: {
		mode: String,
		detailData: Object
	},
	components: {
		...mapState(['isTools']),
		TuiGrid: Grid
	},
	data() {
		return {
			object: {
				fromLogDateTime: null,
				toLogDateTime: null,
				adapterId: null,
				connectorId: null,
				interfaceId: null,
				serviceId: null,
				transactionId: null,
				remoteAddr: null,
				logCode: null,
				instanceId: ' ',
				timeoutYn: ' ',
				responseCode: null
			},

			totalList: [],
			pages: [],

			options: {
				columnOptions: {
					resizable: true
				},
				scrollX: true,
				draggable: false,
				columns: [
					{
						name: 'logCode',
						header: `${this.$t('head.log')} ${this.$t('head.classification')}`
					},
					{
						name: 'requestTimestamp',
						header: this.$t('igate.traceLog.requestTimestamp')
					},
					{
						name: 'responseTimestamp',
						header: this.$t('igate.traceLog.responseTimestamp')
					},
					{
						name: 'transactionTime',
						header: this.$t('igate.traceLog.transactionTimestamp'),
						formatter: ({ row }) => {
							if ('INI' == row.logCode) return '';

							if (null != row.responseTimestamp) {
								if ('I' == row.logCode[2] || 'O' == row.logCode[2])
									return (
										moment(row.responseTimestamp, 'YYYY-MM-DD HH:mm:ss.SSS').diff(
											moment(row.requestTimestamp, 'YYYY-MM-DD HH:mm:ss.SSS')
										) + ' ms'
									);
							} else {
								if (!this.totalList) return '';

								const findIndex = this.totalList.findIndex(
									info => info.pk.logDateTime == row['pk.logDateTime'] && info.pk.logId == row['pk.logId']
								);

								if (-1 === findIndex) return '';

								if (0 == findIndex && 'SFS' == row.logCode) return '';
								if ('I' == row.logCode[2] || 'O' == row.logCode[2])
									return (
										moment(row.requestTimestamp, 'YYYY-MM-DD HH:mm:ss.SSS').diff(
											moment(this.totalList[findIndex - 1].requestTimestamp, 'YYYY-MM-DD HH:mm:ss.SSS')
										) + ' ms'
									);
							}
						}
					}
				],
				onGridMounted: evt => {
					evt.instance.on('expand', () => {
						this.$nextTick(this.resize);
					});

					evt.instance.on('collapse', () => {
						this.$nextTick(this.resize);
					});

					this.resize();
				},
				onGridUpdated: () => {
					this.resize();
				}
			}
		};
	},
	computed: {
		...mapState(['userInfo']),
		...mapGetters(['isUserAdmin']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	watch: {
		detailData() {
			for (const key in this.object) {
				this.object[key] = null;
			}

			for (const key in this.detailData) {
				this.object[key] = 'undefined' === typeof this.detailData[key] ? null : this.detailData[key];
			}

			if (!isEmpty(this.detailData)) this.resetData();
		}
	},
	async created() {
		this.options = makeGridOptions(this.options);

		WindowResizeEventBus.$on('MainResize', this.mainResize);
		SideMenuToggleEventBus.$on('MainResize', this.mainResize);
	},
	methods: {
		async resetData() {
			const listResult = await new HttpReq('/igate/traceLog/list.json').read({ transactionId: this.object.transactionId });

			this.totalList = listResult.data.object;

			const result = await new HttpReq('/igate/traceLog/search.json').read({
				transactionId: this.object.transactionId,
				_pageSize: 5,
				'NEW-WINDOW-ID': getUUIDStr()
			});

			this.$refs.tuiGrid.invoke(
				'resetData',
				(result.data.object.page ? result.data.object.page : result.data.object ? result.data.object : []).map(info => parseFlattenObj(info))
			);

			this.pages = result.data.object.pages;
		},
		async getDataList(params) {
			if (!this.$refs.tuiGrid) return;

			params = `${params}&transactionId=${this.object.transactionId}`;

			const result = await new HttpReq('/igate/traceLog/search.json').read(params);

			if (`ok` !== result.data.result) return;

			this.$refs.tuiGrid.invoke(
				'resetData',
				(result.data.object.page ? result.data.object.page : result.data.object ? result.data.object : []).map(info => parseFlattenObj(info))
			);

			this.pages = result.data.object.pages;

			this.$nextTick(() => {
				this.resize();
			});
		},
		async downloadFile() {
			let makeData = `?`;

			for (const key in this.object) {
				if (`object` === typeof this.object[key]) {
					for (const subkey in this.object[key]) {
						makeData += `${key}.${subkey}=${this.object[key][subkey]}&`;
					}
				} else {
					makeData += `${key}=${this.object[key]}&`;
				}
			}

			const url = encodeURI(`${getHttpUrl(`/igate/traceLog/body.json`)}${makeData}`);
			const popup = window.open(url, `_parent`, `width=0, height=0, top=0, statusbar=no, scrollbars=no, toolbar=no`);
			popup.focus();
		},
		clickExceptionInfo() {
			openNewTab('103010', () => {
				localStorage.removeItem('searchObj');
				localStorage.setItem('searchObj', JSON.stringify({ transactionId: this.object.transactionId }));
			});
		},
		onClick(evt) {
			if ('undefined' === typeof evt.rowKey || `rowHeader` === evt.targetType) return;

			const grid = evt.instance;

			grid.getData().forEach(data => {
				if (data.rowKey === evt.rowKey) return;

				grid.removeRowClassName(data.rowKey, 'row-selected');
			});

			grid.addRowClassName(evt.rowKey, 'row-selected');

			const rowInfo = grid.getRow(evt.rowKey);

			const filterList = ['sortKey', 'rowKey', 'rowSpanMap', 'uniqueKey', '_attributes', '_disabledPriority', '_relationListItemMap'];

			const paramRowInfo = {};

			for (const key in rowInfo) {
				if (-1 < filterList.indexOf(key)) continue;
				paramRowInfo[key] = rowInfo[key];
			}

			openNewTab(JSON.parse(sessionStorage.getItem('selectedMenuPathIdList')).pop(), () => {
				localStorage.removeItem('selectedTraceLogInfo');
				localStorage.setItem('selectedTraceLogInfo', JSON.stringify(paramRowInfo));
			});
		},
		mainResize() {
			setTimeout(() => {
				this.resize();
			}, 250);
		},
		resize() {
			if (!this.$refs.tuiGrid) return;

			const panel = document.getElementById('panel');

			const panelBody = panel.getElementsByClassName('panel-body')[0];

			const panelBodyComputedStyle = getComputedStyle(panelBody);

			const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
			const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));

			const panelWidth = panelBody.clientWidth - (panelBodyPaddingLeft + panelBodyPaddingRight) - 8;

			this.$refs.tuiGrid.invoke('setWidth', panelWidth);
		}
	}
};
</script>
