import store from '@/store';
import state from '@/store/state';
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import moment from 'moment';

export const WindowResizeEventBus = new Vue();
export const SideMenuToggleEventBus = new Vue();
export const DetailToggleEventBus = new Vue();
export const SearchToggleEventBus = new Vue();
export const MenuClickEventBus = new Vue();
export const MenuContextMenuEventBus = new Vue();
export const AntiDuplicateEventBus = new Vue();
export const ModalKeyupEventBus = new Vue();

export const http = axios.create({ withCredentials: true });

export function getHttpUrl(url) {
	let rtnUrl = null;

	if ('production' === process.env.NODE_ENV) {
		rtnUrl = process.env.BASE_URL.substring(0, process.env.BASE_URL.length - 1);
	} else {
		rtnUrl = `${process.env.VUE_APP_DEVELOPMENT_BACKEND_ADDR}${process.env.VUE_APP_DEVELOPMENT_BACKEND_CONTEXT_PATH}`;
	}

	return rtnUrl + url;
}

export class HttpReq {
	orgUrl;
	url;
	constructor(url) {
		this.orgUrl = url;
		this.url = getHttpUrl(url);
	}
	async submit(mode, param, isViewSpinner, errorCallback) {
		if (isViewSpinner) {
			window.$startSpinner();
		}

		const config = {
			headers: {}
		};

		let csrfToken = localStorage.getItem(`csrfToken`);

		if (csrfToken) {
			csrfToken = JSON.parse(csrfToken);
			config.headers[csrfToken.headerName] = csrfToken.token;
		}

		if (param) {
			if (`string` === typeof param) {
				if (-1 < param.indexOf(`NEW-WINDOW-ID`)) {
					const splitParam = param.split(`&`);

					for (const info of splitParam) {
						if (-1 < info.indexOf(`NEW-WINDOW-ID`)) {
							config.headers['X-IMANAGER-WINDOW'] = info.split(`=`)[1];
							break;
						}
					}

					param = splitParam.filter(info => -1 === info.indexOf(`NEW-WINDOW-ID`)).join(`&`);
				} else {
					config.headers['X-IMANAGER-WINDOW'] = state.windowId;
				}
			} else {
				if (param['NEW-WINDOW-ID']) {
					config.headers['X-IMANAGER-WINDOW'] = param['NEW-WINDOW-ID'];
					delete param['NEW-WINDOW-ID'];
				} else {
					config.headers['X-IMANAGER-WINDOW'] = state.windowId;
				}
			}
		} else {
			config.headers['X-IMANAGER-WINDOW'] = state.windowId;
		}

		const data = this.initParam(mode, param);

		let result = null;

		try {
			if ('read' === mode) {
				result = await http.get(`${this.url}${-1 < this.url.indexOf('?') ? '&' : '?'}${data}`, config);
			} else {
				result = await http.post(this.url, data, config);
			}
		} catch (e) {
			result = {
				data: {
					result: 'error',
					error: [
						{
							message: e
						}
					]
				}
			};
		} finally {
			if (isViewSpinner) {
				window.$stopSpinner();
			}

			if (`error` === result.data.result) {
				if (!(await validateSession())) {
					MenuClickEventBus.$emit(`ModalDelete`);

					window.$alert({
						type: `warn`,
						backdropMode: `full`,
						message: state.isTools ? i18n.t(`igate.itools.session.expired`) : i18n.t(`igate.session.expired`),
						callback: async () => {
							if (state.isTools) return;

							await logout();
						}
					});
				} else {
					if (errorCallback) errorCallback(result);
					else window.$alert({ type: `warn`, message: result.data.error.map(info => info.message).join(`,`) });
				}
			}
		}

		return result;
	}
	async read(param, isViewSpinner, errorCallback) {
		return await this.submit('read', param, isViewSpinner, errorCallback);
	}
	async create(param, isViewSpinner, errorCallback) {
		return await this.submit('create', param, isViewSpinner, errorCallback);
	}
	async update(param, isViewSpinner, errorCallback) {
		return await this.submit('update', param, isViewSpinner, errorCallback);
	}
	async delete(param, isViewSpinner, errorCallback) {
		return await this.submit('delete', param, isViewSpinner, errorCallback);
	}
	initParam(mode, param) {
		if (!param) return '';

		let data = null;

		if ('string' === typeof param) {
			data = param;
		} else {
			const tmpParam = JSON.parse(JSON.stringify(param));

			if ('read' != mode) {
				tmpParam._method = 'create' === mode ? 'PUT' : 'update' === mode ? 'POST' : 'DELETE';
			}

			data = serialize(tmpParam);
		}

		return data;
	}
}

export class HttpStreamReq extends HttpReq {
	initParam(mode, param) {
		let rtnParam = null;

		this.url = getHttpUrl(this.orgUrl);

		if ('read' === mode || 'delete' === mode) {
			rtnParam = super.initParam(mode, param);
		} else {
			this.url += `${-1 < this.url.indexOf('?') ? '&' : '?'}_method=${'create' === mode ? 'PUT' : 'update' === mode ? 'POST' : 'DELETE'}`;
			rtnParam = param;
		}

		return rtnParam;
	}
}

export class HttpFileReq extends HttpReq {
	initParam(mode, param) {
		const formData = new FormData();

		for (const key in param) {
			formData.append(key, param[key]);
		}

		//파일 업로드만 있음..
		this.url = getHttpUrl(this.orgUrl);
		this.url += `${-1 < this.url.indexOf('?') ? '&' : '?'}_method=POST`;

		return formData;
	}
}

export function parseHierarchyObj(obj) {
	const rtnObj = {};

	for (const key in obj) {
		parseHierarchyObjSub(key, obj[key], rtnObj);
	}

	return rtnObj;
}

function parseHierarchyObjSub(key, finalValue, resultObj) {
	let tmpKeyArr = key.split('.');

	if (1 === tmpKeyArr.length) {
		if ('undefined' !== typeof resultObj[tmpKeyArr[0]]) return;
		resultObj[tmpKeyArr[0]] = finalValue;
	} else {
		const tmpKey = tmpKeyArr.splice(0, 1);

		if ('object' !== typeof resultObj[tmpKey]) resultObj[tmpKey] = {};

		parseHierarchyObjSub(tmpKeyArr.join('.'), finalValue, resultObj[tmpKey]);
	}
}

export function parseFlattenObj(obj, roots = [], sep = '.') {
	return Object.keys(obj).reduce(
		(memo, prop) =>
			Object.assign(
				{},
				memo,
				Object.prototype.toString.call(obj[prop]) === '[object Object]'
					? parseFlattenObj(obj[prop], roots.concat([prop]))
					: { [roots.concat([prop]).join(sep)]: obj[prop] }
			),
		{}
	);
}

export function getNumFromStr(str) {
	return Number(str.replace(/[^0-9]/g, ''));
}

export function getUUID() {
	return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
		let r = (mathRandom() * 16) | 0,
			v = c == 'x' ? r : (r & 0x3) | 0x8;
		return v.toString(16);
	});
}

export function getUUIDStr() {
	return `GUID-${getUUID()}`;
}

export function serialize(obj) {
	const array = serialize_sub(null, obj, []);
	return array ? array.join('&') : '';
}

function serialize_sub(name, orgObject, newObject) {
	for (const key in orgObject) {
		const value = orgObject[key];
		const subname = name ? `${name}.${key}` : key;

		if (value instanceof Array)
			value.forEach((item, idx) => {
				if (`object` !== typeof item) newObject.push(`${encodeURIComponent(`${subname}[${idx}]`)}=${encodeURIComponent(item)}`);
				else serialize_sub(`${subname}[${idx}]`, item, newObject);
			});
		else {
			const vType = typeof value;

			if (!vType) continue;
			else if ('object' === vType) serialize_sub(subname, value, newObject);
			else newObject.push(`${encodeURIComponent(subname)}=${encodeURIComponent(value)}`);
		}
	}

	return newObject;
}

export function escapeHtml(text) {
	const span = document.createElement('span');
	span.innerText = text;
	return span.innerHTML;
}

export function unEscapeHtml(text) {
	const span = document.createElement('span');
	span.innerHTML = text;
	return span.innerText;
}

export function encryptPassword(password) {
	if (!password) return null;

	let key = (function () {
		let characters = 'ABCDEF0123456789';

		let result = '';

		for (let i = 0; i < 32; i++) {
			result += characters.charAt(Math.floor(mathRandom() * characters.length));
		}

		return result;
	})();

	const encrypt = CryptoJS.AES.encrypt(password, CryptoJS.enc.Hex.parse(key), { iv: CryptoJS.enc.Hex.parse(key) });

	return '{jst}' + btoa(key + encrypt.toString());
}

export function mathRandom() {
	let cryptoObj = window.crypto || window.msCrypto;

	let arr = new Uint32Array(1);

	cryptoObj.getRandomValues(arr);

	return arr[0] / Math.pow(2, 32); //4294967296
}

export function openNewTab(menuId, beforeCallBack) {
	if (beforeCallBack) beforeCallBack();

	const selectedMenuPathIdListNewTab = [];

	if (-1 < menuId.indexOf(`home`)) {
		selectedMenuPathIdListNewTab.push(menuId);
	} else if (-1 < menuId.indexOf(`bookmark`)) {
		selectedMenuPathIdListNewTab.push(`bookMark`);
		selectedMenuPathIdListNewTab.push(menuId);
	} else {
		selectedMenuPathIdListNewTab.push(menuId);

		while (true) {
			const filterMenuList = state.menuList.filter(menuInfo => selectedMenuPathIdListNewTab[0] === menuInfo.menuId);

			if (0 === filterMenuList.length) break;

			const menuInfo = filterMenuList[0];

			if ('undefined' === typeof menuInfo.parentMenuId) break;

			selectedMenuPathIdListNewTab.unshift(menuInfo.parentMenuId);
		}
	}

	localStorage.removeItem('selectedMenuPathIdListNewTab');
	localStorage.setItem('selectedMenuPathIdListNewTab', JSON.stringify(selectedMenuPathIdListNewTab));

	window.open(location.href);
}

export function numberWithComma(number) {
	return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
}

export function makeGridOptions(gridOptions = {}, formatterData) {
	gridOptions = cloneDeep(gridOptions);

	const options = merge(
		{
			width: null,
			columns: [],
			header: {
				height: 32,
				align: 'center'
			},
			columnOptions: {
				resizable: true
			},
			contextMenu: () => [],
			scrollX: false,
			data: []
		},
		gridOptions
	);

	options.columns.forEach(column => {
		if (column.formatter) {
			const formatterFunc = column.formatter;

			column.formatter = info => {
				if (formatterData) info.formatterData = formatterData;

				return formatterFunc(info);
			};
		} else {
			column.escapeHTML = true;
		}

		if (column.width && -1 < String(column.width).indexOf('%')) {
			if (!column.copyOptions) column.copyOptions = {};

			column.copyOptions.widthRatio = column.width.replace('%', '');

			delete column.width;
		}
	});

	const onGridMounted = gridOptions.onGridMounted;
	delete options.onGridMounted;
	delete gridOptions.onGridMounted;

	options.onGridMounted = evt => {
		if (!evt.instance) return;

		if (!evt.instance.el) return;

		for (const attributeKey in options) {
			evt.instance.el.removeAttribute(attributeKey);
		}

		evt.instance.on('mouseover', mouseEvt => {
			if ('cell' !== mouseEvt.targetType) return;

			const element = mouseEvt.instance.getElement(mouseEvt.rowKey, mouseEvt.columnName);
			const value = unEscapeHtml(mouseEvt.instance.getFormattedValue(mouseEvt.rowKey, mouseEvt.columnName));

			element.setAttribute('title', value);
		});

		if (onGridMounted) onGridMounted(evt);
	};
	const onGridUpdated = gridOptions.onGridUpdated;
	delete options.onGridUpdated;
	delete gridOptions.onGridUpdated;

	options.onGridUpdated = evt => {
		for (const attributeKey in options) {
			evt.instance.el.removeAttribute(attributeKey);
		}

		const resetColumnWidths = [];

		evt.instance.getColumns().forEach(columnInfo => {
			if (!columnInfo.copyOptions) return;

			if (columnInfo.copyOptions.widthRatio) {
				resetColumnWidths.push(evt.instance.el.offsetWidth * (columnInfo.copyOptions.widthRatio / 100));
			}
		});

		if (0 < resetColumnWidths.length) evt.instance.resetColumnWidths(resetColumnWidths);

		evt.instance.refreshLayout();

		if (onGridUpdated) onGridUpdated(evt);
	};

	return options;
}

export function getPrivilege(privilegeType) {
	const totalUserPrivileges = state.userInfo.totalUserPrivileges.map(privilegeInfo => privilegeInfo.privilegeId);
	const isAdmin = store.getters.isUserAdmin;

	const selectedMenuList = JSON.parse(sessionStorage.getItem('selectedMenuPathIdList'));
	const selectMenu = String(getNumFromStr(selectedMenuList.pop()));
	const currentMenuPrivilege = state.menuList.find(menuInfo => menuInfo.menuId === selectMenu).menuPrivilegeId;

	return {
		viewer: isAdmin || !currentMenuPrivilege ? true : totalUserPrivileges.includes(`${privilegeType}Viewer`),
		editor: isAdmin ? true : totalUserPrivileges.includes(`${privilegeType}Editor`)
	};
}

export function downloadCSV(pCsvFileName, pCsvData) {
	const csvFileName = `${pCsvFileName}.csv`;

	const csvData = '\ufeff' + pCsvData;

	if (window.navigator && window.navigator.msSaveOrOpenBlob) {
		const blob = new Blob([csvData], { type: `text/csv;charset=utf8` });
		window.navigator.msSaveOrOpenBlob(blob, csvFileName);
	} else {
		const downloadLink = document.createElement(`a`);

		const blob = new Blob([csvData], { type: `text/csv;charset=utf-8;` });

		downloadLink.href = URL.createObjectURL(blob);
		downloadLink.download = csvFileName;

		document.body.appendChild(downloadLink);

		downloadLink.click();

		document.body.removeChild(downloadLink);
	}
}

export function downloadJson(pJsonFileName, pJsonData) {
	const jsonFileName = `${pJsonFileName}.json`;

	const jsonData = pJsonData;

	if (window.navigator && window.navigator.msSaveOrOpenBlob) {
		const blob = new Blob([jsonData], { type: `text/json;charset=utf8` });
		window.navigator.msSaveOrOpenBlob(blob, jsonFileName);
	} else {
		const downloadLink = document.createElement('a');

		const blob = new Blob([jsonData], { type: `text/json;charset=utf-8;` });

		downloadLink.href = URL.createObjectURL(blob);
		downloadLink.download = jsonFileName;

		document.body.appendChild(downloadLink);

		downloadLink.click();

		document.body.removeChild(downloadLink);
	}
}

export function getRegExpInfo(type) {
	return state.regExpList[type];
}

export async function validateSession() {
	const config = {
		headers: {}
	};

	let csrfToken = localStorage.getItem(`csrfToken`);

	if (csrfToken) {
		csrfToken = JSON.parse(csrfToken);
		config.headers[csrfToken.headerName] = csrfToken.token;
	}

	const X_IMANAGER_WINDOW = state.windowId;

	if (X_IMANAGER_WINDOW) {
		config.headers['X-IMANAGER-WINDOW'] = X_IMANAGER_WINDOW;
	}

	const result = await http.get(getHttpUrl(`/igate/page/common/validateSession.json`), config);

	return `error` === result.data.result ? false : result.data.object;
}

export function goLoginPage() {
	localStorage.removeItem(`searchObj`);
	localStorage.removeItem(`selectedMenuPathIdListNewTab`);
	localStorage.removeItem(`isOpenNewTab`);
	localStorage.removeItem(`csrfToken`);
	localStorage.removeItem(`selectedRowMapping`);
	localStorage.removeItem(`selectedMenuPathIdListNewTab`);

	sessionStorage.removeItem(`externalMenuUrl`);
	sessionStorage.removeItem(`externalMenuParam`);
	sessionStorage.removeItem(`selectedMenuPathIdList`);

	document.body.classList.remove(`moon`);

	require(`@/router`)
		.default.replace({ name: `login` })
		.catch(() => {});
}

export async function logout() {
	const config = {
		headers: {}
	};

	let csrfToken = localStorage.getItem(`csrfToken`);

	if (csrfToken) {
		csrfToken = JSON.parse(csrfToken);
		config.headers[csrfToken.headerName] = csrfToken.token;
	}

	const X_IMANAGER_WINDOW = state.windowId;

	if (X_IMANAGER_WINDOW) {
		config.headers['X-IMANAGER-WINDOW'] = X_IMANAGER_WINDOW;
	}

	await http.get(getHttpUrl(`/common/auth/logout.json`), config);

	goLoginPage();
}

export function getContextPath() {
	const hostIndex = location.href.indexOf(location.host) + location.host.length;
	return location.href.substring(hostIndex, location.href.indexOf(`/`, hostIndex + 1));
}

export function changeDateFormat(date, format = `YYYY-MM-DD HH:mm:ss`) {
	if (!date) return date;

	return moment(new Date(date.split('.')[0].replace('-', '/'))).format(format);
}
