<script>
import { HttpStreamReq } from '@/utils/common.js';
import Detail from '@/components/detail/Detail.vue';
import BasicInfo from '@/views/detail/mapping/BasicInfo.vue';
import MappingInfo from '@/views/detail/mapping/MappingInfo.vue';

export default {
	name: 'Detail',
	extends: Detail,
	props: {
		mode: {
			type: String,
			default: 'detail'
		},
		pk: Array,
		buttonList: Array,
		info: Object,
		cudUrl: String,
		viewer: Boolean,
		editor: Boolean
	},
	components: {
		BasicInfo,
		MappingInfo
	},
	watch: {
		cudUrl: {
			immediate: true,
			handler() {
				this.httpReq = new HttpStreamReq(`${this.cudUrl}?ext=1`);
			}
		}
	},
	methods: {
		changeTab(idx) {
			for (const component of this.$refs.component) {
				if (!component.resize) continue;
				component.resize();
			}

			this.selectedIdx = idx;
		}
	}
};
</script>
