<script>
import { mapState } from 'vuex';
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/activity/Detail.vue';
import { getPrivilege, HttpReq } from '@/utils/common.js';

export default {
	name: 'Activity',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	async mounted() {
		if (this.isTools) {
			const externalMenuParam = JSON.parse(sessionStorage.getItem(`externalMenuParam`));

			if (externalMenuParam && externalMenuParam.activityId) {
				const result = await new HttpReq(this.cudUrl).read({ activityId: externalMenuParam.activityId });

				delete externalMenuParam.activityId;
				sessionStorage.setItem(`externalMenuParam`, JSON.stringify(externalMenuParam));

				if (`ok` !== result.data.result) return;

				this.initDetail('detail', result.data.object);
			} else {
				this.$nextTick(() => {
					setTimeout(() => {
						this.initDetail(`add`);
					}, 0);
				});
			}
		}

		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			cudUrl: '/igate/activity/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'activityId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'activityName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'activityType',
					label: this.$t('common.type'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Activity.ActivityType',
							orderByKey: true
						},
						optionListName: 'activityTypes',
						optionFor: 'option in activityTypes',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'text',
					vModel: 'activityGroup',
					label: this.$t('head.group'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'activityDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: '/igate/activity/search.json',
				totalCntUrl: '/igate/activity/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'activityId',
							header: this.$t('head.id'),
							width: `20%`
						},
						{
							name: 'activityName',
							header: this.$t('head.name'),
							width: `20%`
						},
						{
							name: 'activityType',
							header: this.$t('common.type'),
							align: `center`,
							width: `15%`,
							formatter: ({ row }) => {
								const activityTypeObj = {
									F: 'igate.activity.type.control',
									A: 'igate.activity.type.activity',
									S: 'igate.activity.type.service',
									C: 'igate.activity.type.codec',
									T: 'igate.activity.type.transform',
									I: 'igate.activity.type.internal',
									H: 'igate.activity.type.telegram.handler'
								};

								return this.$t(activityTypeObj[row.activityType]);
							}
						},
						{
							name: 'activityGroup',
							header: this.$t('head.group'),
							width: `20%`
						},
						{
							name: 'activityDesc',
							header: this.$t('head.description'),
							width: `25%`
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['activityId'],
			controlUrl: '/igate/activity/control.json',
			controlParams: detailData => ({
				activityId: detailData.activityId
			}),
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{
					id: 'delete',
					isUse: !this.isTools
				},
				{
					id: 'load',
					isUse: info => {
						return info.loadable;
					}
				},
				{ id: 'dump', isUse: true }
			],
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('head.basic.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'activityId',
									label: this.$t('head.id'),
									isPK: true,
									regExpType: 'id'
								},
								{
									type: 'text',
									vModel: 'activityName',
									label: this.$t('head.name'),
									regExpType: 'name'
								},
								{
									type: 'select',
									vModel: 'activityType',
									label: this.$t('common.type'),
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Activity.ActivityType',
											orderByKey: true
										},
										optionListName: 'activityTypes',
										optionFor: 'option in activityTypes',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue',
										optionDisabled: `option.pk.propertyKey === 'F' && 'detail' !== '${this.detailMode}'`
									},
									isRequired: true
								}
							],
							[
								{
									type: 'text',
									vModel: 'activityClassName',
									label: this.$t('igate.activity.className'),
									regExpType: 'className',
									isRequired: true
								},
								{
									type: 'select',
									vModel: 'activityLogLevel',
									label: this.$t('head.log.level'),
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.LogLevel',
											orderByKey: false
										},
										optionListName: 'logLevels',
										optionFor: 'option in logLevels',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									},
									isRequired: true
								}
							],
							[
								{
									type: 'modal',
									vModel: 'activityProject',
									label: this.$t('igate.javaProject'),
									modalInfo: {
										title: this.$t('igate.javaProject'),
										search: {
											list: [
												{
													type: 'text',
													vModel: 'projectId',
													label: this.$t('head.id'),
													placeholder: this.$t('head.searchId'),
													regExpType: 'id'
												},
												{
													type: 'dataList',
													vModel: 'pageSize',
													label: this.$t('head.listCount'),
													val: '10',
													optionInfo: {
														optionFor: 'option in [10, 100, 1000]',
														optionValue: 'option',
														optionText: 'option'
													}
												}
											]
										},
										button: {
											list: [{ id: 'initialize', isUse: true }]
										},
										grid: {
											url: '/igate/javaProject/searchPopup.json',
											paging: {
												isUse: true,
												side: 'server'
											},
											options: {
												columns: [
													{
														header: this.$t('head.id'),
														name: 'projectId'
													},
													{
														header: this.$t('head.name'),
														name: 'projectName'
													},
													{
														header: this.$t('head.description'),
														name: 'projectDesc'
													}
												]
											}
										},
										rowClickedCallback: rowInfo => rowInfo.projectId
									}
								},
								{
									type: 'text',
									vModel: 'activityGroup',
									label: this.$t('head.group'),
									isRequired: true
								},
								{
									type: 'modal',
									vModel: 'executePrivilegeId',
									label: this.$t('common.privilege'),
									modalInfo: {
										title: this.$t('common.privilege'),
										search: {
											list: [
												{
													type: 'text',
													vModel: 'privilegeId',
													label: this.$t('head.id'),
													placeholder: this.$t('head.searchId'),
													regExpType: 'id'
												},
												{
													type: 'text',
													vModel: 'privilegeDesc',
													label: this.$t('head.description'),
													placeholder: this.$t('head.searchComment'),
													regExpType: 'desc'
												},
												{
													type: 'select',
													vModel: 'privilegeType',
													label: this.$t('common.type'),
													val: 'b',
													optionInfo: {
														url: '/common/property/properties.json',
														params: {
															propertyId: 'List.Privilege.Type',
															orderByKey: true
														},
														optionListName: 'privilegeType',
														optionFor: 'option in privilegeType',
														optionValue: 'option.pk.propertyKey',
														optionText: 'option.propertyValue',
														optionIf: "'b' === option.pk.propertyKey",
														isViewAll: false
													}
												},
												{
													type: 'dataList',
													vModel: 'pageSize',
													label: this.$t('head.listCount'),
													val: '10',
													optionInfo: {
														optionFor: 'option in [10, 100, 1000]',
														optionValue: 'option',
														optionText: 'option'
													}
												}
											]
										},
										button: {
											list: [{ id: 'initialize', isUse: true }]
										},
										grid: {
											url: '/common/privilege/searchPopup.json',
											paging: {
												isUse: true,
												side: 'server'
											},
											options: {
												columns: [
													{
														header: this.$t('head.id'),
														name: 'privilegeId'
													},
													{
														header: this.$t('common.type'),
														name: 'privilegeType',
														formatter: ({ row }) => {
															return 'S' === row.privilegeType
																? this.$t('common.privilege.type.system')
																: this.$t('common.privilege.type.business');
														}
													},
													{
														header: this.$t('head.description'),
														name: 'privilegeDesc'
													}
												]
											}
										},
										rowClickedCallback: rowInfo => rowInfo.privilegeId
									}
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'activityDesc',
									label: this.$t('head.description'),
									regExpType: 'desc'
								}
							]
						]
					]
				},
				{
					type: 'property',
					label: 'Parameter',
					dataKey: 'activityParameters',
					content: [
						{
							type: 'dataList',
							vModel: 'parameterType',
							regExpType: 'id',
							label: this.$t('common.type'),
							optionInfo: {
								url: '/igate/activity/parameterTypes.json',
								optionListName: 'parameterTypeList',
								optionFor: 'option in parameterTypeList',
								optionValue: 'option',
								optionText: 'option'
							}
						},
						{ type: 'text', vModel: 'parameterDesc', label: this.$t('head.description'), regExpType: 'desc' }
					]
				}
			]
		};
	},
	computed: {
		...mapState([`isTools`])
	}
};
</script>
