<template>
	<div :class="classObj" :style="styleObj">
		<Search :list="searchList" @search="search" ref="search" />
		<Main
			:buttonList="buttonList"
			:gridInfo="gridInfo"
			:gridSearchObj="gridSearchObj"
			:gridType="gridType"
			:viewer="viewer"
			:editor="editor"
			@initialize="initialize"
			@newTab="newTab"
			@fileMake="fileMake"
			@fileImport="fileImport"
			ref="main"
		/>
		<Detail
			:mode="detailMode"
			:viewer="viewer"
			:editor="editor"
			v-show="isDetailView"
			:selectedDetailData="detailData"
			:buttonList="[]"
			@detailHorizonResize="detailHorizonResize"
			ref="detail"
		/>
	</div>
</template>
<script>
import { mapState } from 'vuex';
import { getPrivilege, HttpReq, escapeHtml } from '@/utils/common.js';
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/migration/query/Detail.vue';

export default {
	name: `migrationQuery`,
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		const migrationEditor = this.userInfo.totalUserPrivileges.find(privilege => `MigrationEditor` === privilege.privilegeId) ? true : false;

		this.viewer = privileges.viewer;
		this.editor = `undefined` === typeof migrationEditor ? false : migrationEditor && privileges.editor;

		this.gridInfo.options.rowHeaders = this.editor ? ['checkbox'] : [];
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			searchList: [
				{
					type: 'text',
					vModel: 'queryId',
					label: this.$t(`head.id`),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'queryName',
					label: this.$t(`head.name`),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'queryType',
					label: this.$t(`common.type`),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Query.QueryType',
							orderByKey: true
						},
						optionListName: 'queryTypes',
						optionFor: 'option in queryTypes',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'text',
					vModel: 'queryGroup',
					label: this.$t(`head.group`),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'dataList',
					vModel: 'privilegeId',
					label: `${this.$t(`common.privilege`)} ${this.$t(`head.id`)}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					optionInfo: {
						url: '/common/privilege/list.json',
						params: {
							privilegeType: `b`
						},
						optionListName: 'privilegeTypeList',
						optionFor: 'option in privilegeTypeList',
						optionValue: 'option.privilegeId',
						optionText: 'option.privilegeId'
					}
				},
				{
					type: 'text',
					vModel: 'queryDesc',
					label: this.$t(`head.description`),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				}
			],
			buttonList: [
				{ id: 'fileImport', isUse: true },
				{ id: 'fileMake', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],
			gridInfo: {
				url: '/igate/migration/query/list.json',
				paging: {
					isUse: false
				},
				options: {
					rowHeaders: null,
					columns: [
						{
							name: 'queryId',
							header: this.$t(`head.id`),
							align: 'left',
							width: '20%'
						},
						{
							name: 'queryName',
							header: this.$t(`head.name`),
							align: 'left',
							width: '20%'
						},
						{
							name: 'queryType',
							header: this.$t(`common.type`),
							align: 'center',
							width: '10%',
							formatter: ({ row }) =>
								`F` === row.queryType
									? 'Single Select'
									: `S` === row.queryType
									? 'Multi Select'
									: `U` === row.queryType
									? 'Single Update/Delete'
									: `B` === row.queryType
									? 'Multi Update/Delete'
									: escapeHtml(row.queryType)
						},
						{
							name: 'queryGroup',
							header: this.$t(`head.group`),
							align: 'left',
							width: '15%'
						},
						{
							name: 'privilegeId',
							header: `${this.$t(`common.privilege`)} ${this.$t(`head.id`)}`,
							align: 'left',
							width: '15%'
						},
						{
							name: 'queryDesc',
							header: this.$t(`head.description`),
							align: 'left',
							width: '20%'
						}
					]
				}
			},
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.make'),
					url: `/migration/Make`
				},
				{
					type: 'bundle',
					label: this.$t('head.import'),
					url: `/migration/Import`
				}
			],
			isDetailView: false,
			detailData: null,
			detailMode: null,
			viewer: false,
			editor: false
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		initDetail() {
			return false;
		},
		async fileMake({ gridObj }) {
			if (!gridObj) return;

			const checkedRows = gridObj.invoke(`getCheckedRows`);

			if (0 === checkedRows.length) {
				this.$alert({
					type: `warn`,
					message: this.$t(`igate.migration.selectError`)
				});
			}

			const targetIds = checkedRows.map(info => info.queryId);

			const result = await new HttpReq(`/igate/migration/query/make.json`).update({ targetIds }, true);

			if (`ok` !== result.data.result) return;

			this.$alert({
				type: `normal`,
				message: this.$t(`igate.migration.make`)
			});

			this.openPanel(`make`, {
				...result.data.object,
				targetIds
			});
		},
		fileImport() {
			this.openPanel(`import`);
		},
		openPanel(mode, detailData) {
			if (this.isDetailView) {
				this.isDetailView = false;
				this.detailData = null;
				this.detailMode = null;
			}

			this.$nextTick(() => {
				this.isDetailView = true;
				this.detailData = detailData;
				this.detailMode = mode;
			});
		}
	}
};
</script>
