<script>
import DetailGrid from '@/components/detail/DetailGrid';

import {
	WindowResizeEventBus,
	SideMenuToggleEventBus,
	DetailToggleEventBus,
	SearchToggleEventBus,
	parseFlattenObj,
	makeGridOptions,
	HttpReq
} from '@/utils/common.js';
import isEmpty from 'lodash/isEmpty';
import merge from 'lodash/merge';

export default {
	name: 'DetailGrid',
	extends: DetailGrid,
	data() {
		return {
			options: {},
			url: null,
			HttpReq: null
		};
	},
	watch: {
		async detailData() {
			if (isEmpty(this.detailData)) return;

			const result = await this.HttpReq.read(this.detailData);
			let gridData = [];

			if ('ok' !== result.data.result) {
				this.$alert({ type: 'warn', message: `${result.data.error[0].message}` });
			} else {
				gridData = result.data.object.map(dataObj => parseFlattenObj(dataObj));
			}

			this.$refs.tuiGrid.invoke('resetData', gridData);

			this.resize();
		}
	},
	created() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		DetailToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);

		this.options = makeGridOptions(
			merge(this.detailInfo.options, {
				onGridUpdated: evt => {
					evt.instance.expandAll();
				}
			})
		);

		this.resize();

		this.url = this.detailInfo.url;
		this.HttpReq = new HttpReq(this.url);
	}
};
</script>
