<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<input type="text" v-model="object.serviceId" class="form-control view-disabled dataKey" :readonly="'add' !== this.mode" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.name') }}</span>
						<b class="icon-star"></b>
					</label>
					<input type="text" v-model="object.serviceName" class="form-control view-disabled" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.type') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.serviceType" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in serviceTypeList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter') }} {{ $t('head.id') }}</span>
					</label>
					<input type="text" v-model="object.adapterId" class="form-control view-disabled dataKey" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.operation') }} </span>
					</label>
					<div class="input-group" :title="object.operationId">
						<input type="text" v-model="object.operationId" class="form-control view-disabled" :readonly="readonly" />
						<button class="btn btn-icon search-linkBtn" :title="$t('head.open')" @click="openOperationModal()">
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.activity') }}</span>
					</label>
					<input type="text" v-model="object.serviceActivity" class="form-control view-disabled" :readonly="readonly" />
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.fieldMeta.metaDomain') }}</span>
					</label>
					<select v-model="object.metaDomain" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in metaDomainList" :key="option" :value="option">
							{{ option }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.request.model') }}</span>
					</label>
					<div class="input-group" :title="requestRecordName">
						<input type="text" class="form-control view-disabled ellipsis" :value="requestRecordName" readonly="readonly" />
						<button class="btn btn-icon search-linkBtn" :title="$t('head.open')" @click="openRecordMngModal('I')">
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.response.model') }}</span>
					</label>
					<div class="input-group" :title="responseRecordName">
						<input type="text" class="form-control view-disabled ellipsis" :value="responseRecordName" readonly="readonly" />
						<button class="btn btn-icon search-linkBtn" :title="$t('head.open')" @click="openRecordMngModal('O')">
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
			</div>
			<div class="col-lg-3">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.private') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privateYn" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in privateYnList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.group') }}</span>
					</label>
					<input type="text" v-model="object.serviceGroup" class="form-control view-disabled" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.privilege') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privilegeId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in privilegeTypeList" :key="option.privilegeId" :value="option.privilegeId" :disabled="option.disabled">
							{{ option.privilegeId }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.description') }}</span>
					</label>
					<textarea v-model="object.serviceDesc" class="form-control view-disabled" :readonly="readonly" style="min-height: 150px" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';
import { parseHierarchyObj, HttpReq } from '@/utils/common.js';

export default {
	name: 'ServiceBasicInfo',
	props: {
		detailData: Object,
		mode: String
	},
	data() {
		return {
			serviceTypeList: [],
			privateYnList: [],
			privilegeTypeList: [],
			metaDomainList: [],

			requestRecordObject: {},
			responseRecordObject: {},

			object: {
				serviceId: null,
				serviceName: null,
				serviceType: null,
				privilegeId: null,
				serviceGroup: null,
				privateYn: null,
				adapterId: null,
				operationId: null,
				serviceActivity: null,
				metaDomain: null,
				requestRecordId: null,
				responseRecordId: null,
				serviceDesc: null
			},
			modalInfo: {
				title: this.$t('igate.record'),
				search: {
					list: [
						{
							type: 'text',
							vModel: 'id',
							label: `${this.$t('igate.record')} ${this.$t('head.id')}`,
							placeholder: this.$t('head.searchId')
						},
						{
							type: 'dataList',
							vModel: 'pageSize',
							label: this.$t('head.listCount'),
							val: '10',
							optionInfo: {
								optionFor: 'option in [10, 100, 1000]',
								optionValue: 'option',
								optionText: 'option'
							}
						}
					]
				},
				button: {
					list: [{ id: 'initialize', isUse: true }]
				},
				grid: {
					url: '/igate/record/searchPopup.json',
					paging: {
						isUse: true,
						side: 'server'
					},
					options: {
						columns: [
							{
								name: 'id',
								label: `${this.$t('igate.record')} ${this.$t('head.id')}`,
								width: '20%'
							},
							{
								name: 'name',
								label: `${this.$t('igate.record')} ${this.$t('head.name')}`,
								width: '40%'
							}
						]
					}
				}
			},

			httpReqList: [
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Service.ServiceType', orderByKey: true },
					itemName: 'serviceTypeList'
				},
				{
					url: `/igate/fieldMeta/groups.json`,
					itemName: 'metaDomainList'
				},
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Yn', orderByKey: true },
					itemName: 'privateYnList'
				},
				{
					url: `/common/privilege/list.json`,
					param: { privilegeType: 'b' },
					itemName: 'privilegeTypeList'
				}
			]
		};
	},
	computed: {
		...mapState(['menuList', 'userInfo']),
		...mapGetters(['isUserAdmin']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		},
		requestRecordName() {
			const id = this.requestRecordObject && this.requestRecordObject.recordId ? this.requestRecordObject.recordId : '';
			const name = this.requestRecordObject && this.requestRecordObject.recordName ? this.requestRecordObject.recordName : '';

			return id + (name ? ` - ${name}` : name);
		},
		responseRecordName() {
			const id = this.responseRecordObject && this.responseRecordObject.recordId ? this.responseRecordObject.recordId : '';
			const name = this.responseRecordObject && this.responseRecordObject.recordName ? this.responseRecordObject.recordName : '';

			return id + (name ? ` - ${name}` : name);
		}
	},
	watch: {
		detailData() {
			//clear data
			for (const key in this.object) {
				this.object[key] = null;
			}

			this.requestRecordObject = {};
			this.responseRecordObject = {};

			if ('add' === this.mode) return;

			//set data
			const isSameRecordObj = this.detailData.requestRecordId && this.detailData.requestRecordId === this.detailData.responseRecordId;

			for (const key in this.detailData) {
				const value = this.detailData[key];

				if ('serviceProperties' === key) {
					continue;
				} else if (key.startsWith('requestRecordObject')) {
					this.requestRecordObject[key.replace('requestRecordObject.', '')] = value;
					if (isSameRecordObj) this.object[key.replace('request', 'response')] = value;
				} else if (key.startsWith('responseRecordObject')) {
					this.responseRecordObject[key.replace('responseRecordObject.', '')] = value;
				}

				this.object[key] = value;
			}

			if (isSameRecordObj) this.responseRecordObject = cloneDeep(this.requestRecordObject);
		}
	},
	async created() {
		const promiseAllList = [];

		this.httpReqList.forEach(content => {
			promiseAllList.push(
				new Promise((resolve, reject) => {
					(async () => {
						const result = await new HttpReq(content.url).read(content.param ? content.param : null);

						if ('ok' === result.data.result) {
							if (`privilegeTypeList` === content.itemName) {
								const privilegeTypeList = result.data.object;

								const bTotalUserPrivileges = this.userInfo.totalUserPrivileges.filter(
									userPrivilege => `b` === userPrivilege.privilegeType
								);

								for (const info of privilegeTypeList) {
									if (this.isUserAdmin) {
										info.disabled = false;
									} else {
										info.disabled =
											`undefined` ===
											typeof bTotalUserPrivileges.find(userPrivilege => userPrivilege.privilegeId == info.privilegeId);
									}
								}

								this.privilegeTypeList = privilegeTypeList;
							} else {
								this[content.itemName] = result.data.object;
							}

							resolve(result);
						} else {
							reject(result);
						}
					})();
				})
			);
		});

		try {
			const resultList = await Promise.all(promiseAllList);

			for (let result of resultList) {
				if ('error' === result.data.result) throw result.data;
			}
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('head.error.load.data.warn') });
		}
	},
	methods: {
		async openOperationModal() {
			const operationId = this.object.operationId;

			if (!operationId) {
				this.$alert({ type: 'warn', message: this.$t('head.no.data.warn') });
				return;
			}

			this.$openModal({
				title: this.$t('igate.operation'),
				fullscreen: true,
				modalHeight: '50%',
				contentList: [
					{
						template: `<OperationModal ref="OperationModal" :config="config"/>`,
						components: {
							OperationModal: require(`@/views/modal/operation/OperationModal.vue`).default
						},
						data() {
							return {
								config: {
									operationId: operationId
								}
							};
						}
					}
				]
			});
		},
		async openRecordMngModal(type) {
			const recordObj =
				'I' === type
					? { id: 'requestRecordObject', name: this.requestRecordName }
					: { id: 'responseRecordObject', name: this.responseRecordName };

			if (!recordObj.name) {
				this.$alert({ type: 'warn', message: this.$t('head.no.data.warn') });
				return;
			}

			const _this = this;
			const validateList = ['serviceId', 'privateYn', 'metaDomain', 'privilegeId'];
			let validateDataInfo = parseHierarchyObj(cloneDeep(this.object));

			for (const validateKey in validateDataInfo) {
				if (recordObj.id === validateKey) validateDataInfo['recordObject'] = validateDataInfo[validateKey];
				if (!this[recordObj.id].mode) validateDataInfo[validateKey] = this[recordObj.id][validateKey];
				if (-1 === validateList.indexOf(validateKey)) delete validateDataInfo[validateKey];
			}

			this.$openModal({
				title: this.$t('igate.record'),
				fullscreen: true,

				contentList: [
					{
						template: `<RecordModal ref="RecordModal" :mode="mode" :config="config"/>`,
						components: {
							RecordModal: require(`@/views/modal/modelRecord/RecordModal.vue`).default
						},
						data() {
							return {
								mode: _this.mode,
								config: {
									modalType: `S${type}`,
									modalData: validateDataInfo
								}
							};
						}
					}
				]
			});
		}
	}
};
</script>
