							:readonly="readonly"
						/>
						<button
							class="btn btn-icon search-linkBtn"
							:title="$t('head.open')"
							@click="clickExceptionInfo()"
							v-if="detailData.exceptionCode && !this.isTools"
						>
							<i class="icon-link"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.adapter') }} {{ $t('head.id') }}</span>
					</label>
					<inputText
						ref="adapterId"
						vModel="adapterId"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('common.metaHistory.updateRemoteAddress') }}</span>
					</label>
					<inputText
						ref="remoteAddr"
						vModel="remoteAddr"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('head.file') }} {{ $t('head.name') }}</span>
					</label>
					<inputText ref="fileName" vModel="fileName" :classObject="{ 'form-control': true, 'view-disabled': true }" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.batchLog.tableLastKey') }}</span>
					</label>
					<inputText
						ref="tableLastKey"
						vModel="tableLastKey"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('head.exception.message') }}</span>
					</label>
					<inputText
						ref="exceptionMessage"
						vModel="exceptionMessage"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('head.log') }} {{ $t('head.classification') }}</span>
					</label>
					<inputText ref="logCode" vModel="logCode" :classObject="{ 'form-control': true, 'view-disabled': true }" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.connector') }} {{ $t('head.id') }}</span>
					</label>
					<inputText
						ref="connectorId"
						vModel="connectorId"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.interface') }} {{ $t('igate.service') }} {{ $t('head.id') }}</span>
					</label>
					<inputText
						ref="interfaceServiceId"
						vModel="interfaceServiceId"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('head.file') }} {{ $t('head.sequence') }}</span>
					</label>
					<inputText
						ref="fileSequence"
						vModel="fileSequence"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.saf') }} {{ $t('head.id') }}</span>
					</label>
					<inputText ref="safId" vModel="safId" :classObject="{ 'form-control': true, 'view-disabled': true }" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.transactionRestriction.time.start') }} </span>
					</label>
					<inputText
						ref="startTimestamp"
						vModel="startTimestamp"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.instance') }} {{ $t('head.id') }} </span>
					</label>
					<inputText
						ref="instanceId"
						vModel="instanceId"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.connectorControl.session') }} {{ $t('head.id') }} </span>
					</label>
					<inputText
						ref="sessionId"
						vModel="sessionId"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('head.file') }} {{ $t('head.date') }} </span>
					</label>
					<inputText ref="fileDate" vModel="fileDate" :classObject="{ 'form-control': true, 'view-disabled': true }" :readonly="readonly" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('head.table') }} {{ $t('head.sequence') }} </span>
					</label>
					<inputText
						ref="tableSequence"
						vModel="tableSequence"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.exceptionCode') }} </span>
					</label>
					<inputText
						ref="exceptionCode"
						vModel="exceptionCode"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span> {{ $t('igate.transactionRestriction.time.end') }} </span>
					</label>
					<inputText
						ref="endTimestamp"
						vModel="endTimestamp"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { openNewTab, changeDateFormat } from '@/utils/common.js';
import { mapState } from 'vuex';

export default {
	name: 'BasicInfo',
	extends: DetailBasic,
	components: {
		...mapState(['isTools'])
	},
	data() {
		return {
			keyArr: [
				'transactionId',
				'adapterId',
				'remoteAddr',
				'fileName',
				'tableLastKey',
				'exceptionMessage',
				'logCode',
				'connectorId',
				'interfaceServiceId',
				'fileSequence',
				'safId',
				'startTimestamp',
				'instanceId',
				'sessionId',
				'fileDate',
				'tableSequence',
				'exceptionCode',
				'endTimestamp'
			],
			dateArr: ['startTimestamp', 'endTimestamp']
		};
	},
	methods: {
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				this.dateArr.forEach(dateKey => {
					if (this.detailData[dateKey]) changeDateFormat(this.detailData[dateKey]);
				});

				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;
			});

			for (const key in this.$refs) {
				if (!this.$refs[key].setData) continue;
				this.$refs[key].setData(detailDataFormalize[this.$refs[key]._props.vModel]);
			}
		},
		clickExceptionInfo() {
			openNewTab('103010', () => {
				localStorage.removeItem('searchObj');
				localStorage.setItem('searchObj', JSON.stringify({ transactionId: this.detailData.transactionId }));
			});
		}
	}
};
</script>
