<template>
	<div class="ct-content">
		<NoData v-if="!isDetailView" />
		<Detail
			v-if="isDetailView"
			:buttonList="detailButtonList"
			:tabList="detailTabList"
			:info="detailInfo"
			:cudUrl="cudUrl"
			:viewer="viewer"
			:editor="editor"
			:recordData="recordData"
			@changeDetailView="changeDetailView"
			ref="detail"
		/>
	</div>
</template>

<script>
import NoData from '@/components/NoData.vue';
import Detail from '@/views/detail/sapService/Detail.vue';
import { HttpReq } from '@/utils/common';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'Main',
	props: {
		detailButtonList: Array,
		detailInfo: Object,
		detailTabList: Array,
		searchObj: Object,
		isExpand: Boolean,
		cudUrl: String,
		viewer: Boolean,
		editor: Boolean
	},
	data() {
		return {
			isDetailView: false,
			recordData: {}
		};
	},
	components: {
		NoData,
		Detail
	},
	watch: {
		async searchObj() {
			await this.getDataList(this.searchObj);
		}
	},
	async mounted() {
		await this.getDataList(this.searchObj);
	},
	methods: {
		async getDataList(searchObj) {
			if (isEmpty(searchObj)) return;

			const addressObj = {};

			for (let key in searchObj) {
				if ('gwserv' === key || 'gwhost' === key) continue;
				addressObj[key] = searchObj[key];
			}

			if (searchObj.gwserv) {
				addressObj.gwserv = searchObj.gwserv;
				addressObj.gwhost = searchObj.gwhost;
			}

			const result = await new HttpReq(`/igate/sapService/struct.json`).update(addressObj, true);

			if (`ok` !== result.data.result) {
				this.isDetailView = false;
			} else {
				this.isDetailView = true;
				this.recordData = result.data.object;
			}
		},
		changeDetailView() {
			this.isDetailView = !this.isDetailView;
		}
	}
};
</script>
