<template>
	<div id="operationModal" style="height: calc(100% - 68px)">
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 0 === selectedIdx }"
					@click="changeTab(0)"
					v-text="$t('head.basic.info')"
				></a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 1 === selectedIdx }"
					@click="changeTab(1)"
					v-text="$t(`igate.operation.workflow`)"
				></a>
			</li>
			<li class="nav-item">
				<a href="javascript:void(0);" :class="{ 'nav-link': true, active: 2 === selectedIdx }" @click="changeTab(2)" v-text="`XML`"></a>
			</li>
			<li class="nav-item">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: 3 === selectedIdx }"
					@click="changeTab(3)"
					v-text="$t('head.resource.inuse.info')"
				></a>
			</li>
		</ul>

		<div class="panel-body tab-content" style="padding-bottom: 0px">
			<div :class="{ [mode]: true, 'tab-pane': true, active: 0 === selectedIdx }">
				<DetailBasic ref="basicInfo" :detailInfo="tabList[0]" :detailData="detailData" :mode="mode" />
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 1 === selectedIdx }">
				<component
					ref="workflowInfo"
					:is="require(`@/views/detail${tabList[1].url}.vue`).default"
					:detailInfo="tabList[1]"
					:detailData="detailData"
					:mode="mode"
				/>
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 2 === selectedIdx }" style="height: 100%">
				<component
					ref="detailInfo"
					:is="require(`@/views/detail${tabList[2].url}.vue`).default"
					:detailInfo="tabList[2]"
					:detailData="detailData"
					:mode="mode"
				/>
			</div>
			<div :class="{ [mode]: true, 'tab-pane': true, active: 3 === selectedIdx }">
				<DetailBasic ref="useInfo" :detailInfo="tabList[3]" :detailData="detailData" :mode="mode" />
			</div>
		</div>
	</div>
</template>

<script>
import { HttpReq, changeDateFormat } from '@/utils/common.js';
import DetailBasic from '@/components/detail/DetailBasic.vue';
import DetailCustom from '@/components/detail/DetailCustom.vue';

export default {
	name: 'OperationModal',
	components: {
		DetailBasic,
		DetailCustom
	},
	props: {
		config: Object
	},
	async created() {
		const result = await new HttpReq(`/igate/operation/object.json`).read({ operationId: this.config.operationId });

		this.detailData = result.data.object;
	},
	data() {
		return {
			selectedIdx: 0,
			mode: 'detail',
			detailData: {},
			tabList: [
				{
					type: 'basic',
					label: this.$t('head.basic.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'operationId',
									label: this.$t('head.id'),
									isPK: true
								},
								{
									type: 'text',
									vModel: 'operationName',
									label: this.$t('head.name')
								},
								{
									type: 'select',
									vModel: 'operationType',
									label: this.$t('common.type'),
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Operation.OperationType',
											orderByKey: true
										},
										optionListName: 'operationTypeList',
										optionFor: 'option in operationTypeList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue',
										optionIf: `option.pk.propertyKey !== 'S'`
									}
								},
								{
									type: 'text',
									vModel: 'privilegeId',
									label: this.$t('common.privilege')
								}
							],
							[
								{
									type: 'select',
									vModel: 'privateYn',
									label: `Private`,
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Yn',
											orderByKey: true
										},
										optionListName: 'privateYnList',
										optionFor: 'option in privateYnList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'select',
									vModel: 'operationLogLevel',
									label: `Log Level`,
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.LogLevel',
											orderByKey: false
										},
										optionListName: 'logLevelList',
										optionFor: 'option in logLevelList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'select',
									vModel: 'xaTransactionAttribute',
									label: `XA-Transaction`,
									optionInfo: {
										optionFor: `option in [{value: 'S', name: 'Supports'}, {value: 'M', name: 'Mandatory'}, {value: 'N', name: 'Requires New'}, {value: 'X', name: 'Not Supported'}]`,
										optionValue: 'option.value',
										optionText: 'option.name'
									}
								},
								{
									type: 'text',
									vModel: 'operationGroup',
									label: this.$t('head.group')
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'operationDesc',
									label: this.$t('head.description'),
									height: '200px'
								}
							]
						]
					]
				},
				{
					type: `bundle`,
					label: this.$t(`igate.operation.workflow`),
					url: `/operation/DetailWorkFlow`
				},
				{
					type: `bundle`,
					label: `XML`,
					url: `/operation/DetailXML`
				},
				{
					id: `useInfo`,
					type: 'basic',
					label: this.$t('head.resource.inuse.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'lockUserId',
									label: this.$t('head.lock.userId'),
									readonly: true,
									val: '0'
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: this.$t('head.updateVersion'),
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			]
		};
	},
	methods: {
		changeTab(idx) {
			this.selectedIdx = idx;
		}
	}
};
</script>
