<script>
import Main from '@/components/Main.vue';
import { openNewTab } from '@/utils/common.js';
import { mapState } from 'vuex';

export default {
	name: 'Main',
	extends: Main,
	computed: {
		...mapState(['userInfo', 'isTools'])
	},
	methods: {
		hasNewTabPrivilege(privilegeId) {
			return this.userInfo.totalUserPrivileges.find(userPrivilegeInfo => privilegeId === userPrivilegeInfo.privilegeId);
		},
		rowClicked(info) {
			const columnName = this.$refs.grid.getGrid().invoke('getFocusedCell').columnName;
			let menuInfo = null;

			if ('traceLog' === columnName && this.hasNewTabPrivilege(`TraceLogViewer`)) {
				menuInfo = {
					menuId: '103020',
					param: {
						transactionId: info.transactionId
					}
				};
			} else if ('safMessage' === columnName && info.exceptionCode && this.hasNewTabPrivilege(`SafMessageViewer`)) {
				menuInfo = {
					menuId: '103060',
					param: {
						transactionId: info.transactionId
					}
				};
			}

			if (menuInfo && !this.isTools) {
				openNewTab(menuInfo.menuId, () => {
					localStorage.removeItem('searchObj');
					localStorage.setItem('searchObj', JSON.stringify(menuInfo.param));
				});
			}

			this.$emit('rowClicked', info);
		}
	}
};
</script>
