<template>
	<div :class="classObj" :style="styleObj">
		<Search
			:list="searchList"
			@search="
				searchObj => {
					$refs.detail.closePanel();
					search(searchObj);
				}
			"
			ref="search"
		/>
		<Main
			:buttonList="buttonList"
			:gridInfo="gridInfo"
			:gridSearchObj="gridSearchObj"
			:gridType="gridType"
			:viewer="viewer"
			:editor="editor"
			@rowClicked="rowClicked"
			@initialize="initialize"
			@add="add"
			@newTab="newTab"
			@excelExport="excelExport"
			@excelImport="excelImport"
			@refresh="refresh"
			@updateStatus="updateStatus"
			@gridControl="gridControl"
			@reorder="reorder"
			ref="main"
		/>
		<Detail
			v-show="isDetailView && 0 < detailTabList.length"
			:mode="detailMode"
			:pk="detailPk"
			:buttonList="detailButtonList"
			:tabList="detailTabList"
			:info="detailInfo"
			:cudUrl="cudUrl"
			:controlUrl="controlUrl"
			:controlParams="controlParams"
			:viewer="viewer"
			:editor="editor"
			@search="
				() => {
					search($refs.search.getSearchObj());
				}
			"
			@detailHorizonResize="detailHorizonResize"
			ref="detail"
		/>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import merge from 'lodash/merge';
import cloneDeep from 'lodash/cloneDeep';
import Search from '@/components/Search.vue';
import Main from '@/components/Main.vue';
import Detail from '@/components/detail/Detail.vue';
import { HttpFileReq, openNewTab, getHttpUrl, HttpReq, WindowResizeEventBus, getNumFromStr } from '@/utils/common.js';
export default {
	name: 'Basic',
	components: {
		Search,
		Main,
		Detail
	},
	props: {
		pageInfo: {
			type: Object,
			default: () => ({})
		}
	},
	data() {
		return {
			cudUrl: null,

			//auth
			viewer: false,
			editor: false,

			//Search
			searchList: [],

			//Button
			buttonList: [],

			//Update Button Function
			updateStatusFunc: null,

			// Grid
			gridInfo: {},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: [],
			detailButtonList: [],
			detailTabList: [],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			controlUrl: null,
			controlParams: () => {},

			classObj: {
				basic: true,
				horizon: false
			},
			styleObj: {
				width: null
			}
		};
	},
	mounted() {
		this.initBasicInfo();
	},
	computed: {
		...mapState([`minPageSize`])
	},
	methods: {
		initBasicInfo() {
			if ('basic' !== this.pageInfo.type) return;

			//initComponent
			this.cudUrl = null;
			this.viewer = false;
			this.editor = false;
			this.searchList = [];
			this.buttonList = [];
			this.gridInfo = {};
			this.detailPk = [];
			this.detailButtonList = [];
			this.detailTabList = [];
			this.controlUrl = null;
			this.controlParams = () => {};
			this.isDetailView = false;
			this.$refs.main.isGridView = false;

			//initPageInfo
			const { cudUrl, search, button, grid, detail, viewer, editor, updateStatusFunc } = this.pageInfo;

			this.cudUrl = cudUrl;

			//auth
			this.viewer = viewer;
			this.editor = editor;

			//Search
			this.searchList = search.list;

			//Button
			this.buttonList = button.list;

			//Update Button Function
			this.updateStatusFunc = updateStatusFunc;

			//Grid
			this.gridInfo = grid;

			//Detail
			if (detail) {
				this.detailPk = detail.pk ? detail.pk : [];
				this.detailButtonList = detail.button ? detail.button.list : [];
				this.detailTabList = detail.tabList ? detail.tabList : [];
				this.controlUrl = detail.controlUrl;
				this.controlParams = detail.controlParams;
			}

			if (localStorage.getItem('searchObj')) {
				this.initNewTabSearchObj();
			} else if (search.load) {
				this.$nextTick(() => {
					this.search(this.$refs.search.getSearchObj());
				});
			}
		},
		add() {
			this.initDetail('add');
		},
		initialize() {
			this.$refs.search.initialize();
		},
		newTab() {
			const searchObjStr = JSON.stringify(this.$refs.search.getSearchObj());

			openNewTab(JSON.parse(sessionStorage.getItem(`selectedMenuPathIdList`)).pop(), () => {
				localStorage.removeItem(`searchObj`);
				localStorage.setItem(`searchObj`, searchObjStr);
			});
		},
		async excelImport() {
			const importUrl = this.buttonList.filter(btn => 'excelImport' === btn.id)[0].importUrl;
			const searchFunc = this.$refs.search.search;

			const modalInstane = this.$openModal({
				title: this.$t('head.excel.upload'),
				contentList: [
					{
						template: `<div class="input-group">
								<input class="form-control" type="text" v-model="fileName" placeholder="${this.$t('igate.migration.fileSelectError')}" readonly/>
								<button type="button" class="btn" @click="selectFile" style="margin-left: 5px; margin-right: 5px;">
									${this.$t('head.file')} ${this.$t('head.choice')}
								</button>
								<button type="button" class="btn btn-primary" @click="uploadFile">
									${this.$t('head.upload')}
								</button>
						   </div>`,
						data() {
							return {
								fileInfo: null,
								fileName: null
							};
						},
						methods: {
							selectFile() {
								const fileInput = document.createElement('input');
								fileInput.setAttribute('type', 'file');
								fileInput.addEventListener('change', evt => {
									const fileName = evt.target.files[0].name;
									if ('xlsx' != fileName.substring(fileName.length - 4)) {
										this.$alert({ type: 'warn', message: this.$t('head.xlsx.file.format.only.warn') });
										return;
									}
									this.fileInfo = evt.target.files[0];
									this.fileName = fileName;
								});

								fileInput.click();
							},
							async uploadFile() {
								if (!this.fileName) {
									this.$alert({ type: 'warn', message: this.$t('igate.migration.fileSelectError') });
									return;
								}

								let result = await new HttpFileReq(importUrl).update({ uploadFile: this.fileInfo }, true);

								if ('error' === result.data.result) {
									this.$alert({ type: 'warn', message: `${result.data.error[0].message}` });
									return;
								}

								this.$alert({ type: 'normal', message: this.$t('head.success.notice') });

								searchFunc();

								modalInstane.hideModal();
							},
							getData() {
								return this.fileInfo;
							}
						}
					}
				]
			});
		},
		async excelExport() {
			const exportBtnInfo = this.buttonList.filter(btn => 'excelExport' === btn.id)[0];

			if (!exportBtnInfo) return;

			this.$startSpinner();

			const req = new XMLHttpRequest();
			req.open('POST', getHttpUrl(exportBtnInfo.exportUrl), true);

			let csrfToken = localStorage.getItem(`csrfToken`);

			csrfToken = JSON.parse(csrfToken);
			req.setRequestHeader(csrfToken.headerName, csrfToken.token);

			req.withCredentials = true;

			req.responseType = 'blob';

			const excelForm = document.createElement('form');

			const searchObj = this.$refs.search.getSearchObj();

			for (const key in cloneDeep(searchObj)) {
				if (!searchObj[key]) continue;

				const hiddenField = document.createElement('input');

				hiddenField.setAttribute('type', 'hidden');
				hiddenField.setAttribute('name', key);
				hiddenField.setAttribute('value', searchObj[key]);

				excelForm.appendChild(hiddenField);
			}

			req.send(new FormData(excelForm));

			req.onload = event => {
				this.$stopSpinner();

				const blob = req.response;
				const file_name = exportBtnInfo.fileName ? `${exportBtnInfo.fileName}_${Date.now()}.xlsx` : `ExcelExport_${Date.now()}.xlsx`;

				if (blob.size <= 0 || event.target.status != '200') {
					this.$alert({ type: 'warn', message: this.$t(`head.fail.notice`) });
					return;
				}

				if (window.navigator && window.navigator.msSaveOrOpenBlob) {
					window.navigator.msSaveOrOpenBlob(blob, file_name);
				} else {
					let link = document.createElement('a');
					link.href = window.URL.createObjectURL(blob);
					link.download = file_name;
					link.click();
					URL.revokeObjectURL(link.href);
					link.remove();
				}
			};
		},
		search(searchObj) {
			this.gridSearchObj = searchObj;
		},
		refresh() {
			this.search(cloneDeep(this.gridSearchObj));
		},
		rowClicked(info) {
			this.initDetail('detail', info);
		},
		initDetail(mode, info) {
			if (0 === this.detailTabList.length) {
				return;
			}

			if (!this.isDetailView) this.isDetailView = true;

			this.detailInfo = info;
			this.detailMode = mode;
			this.detailMode = null;

			this.$nextTick(() => {
				this.detailMode = mode;
			});
		},
		initNewTabSearchObj() {
			if (!localStorage.getItem('searchObj')) return;

			const tmpSearchObj = JSON.parse(localStorage.getItem('searchObj'));
			localStorage.removeItem('searchObj');

			this.gridSearchObj = merge(this.$refs.search.getSearchObj(), tmpSearchObj ? tmpSearchObj : {});

			this.searchList.forEach(searchInfo => {
				const key = 'pageSize' === searchInfo.vModel ? `_${searchInfo.vModel}` : searchInfo.vModel;

				if ('undefined' === typeof this.gridSearchObj[key]) {
					return;
				}

				if (`pageSize` === searchInfo.vModel && null === this.gridSearchObj[key]) {
					this.gridSearchObj[key] = this.minPageSize;
				}

				this.$set(searchInfo, 'val', this.gridSearchObj[key]);
			});
		},
		async updateStatus({ gridObj, mod }) {
			if (!gridObj) return;

			const updateList = gridObj.invoke(`getCheckedRows`);

			if (0 === updateList.length) {
				this.$alert({ type: 'warn', message: this.$t(`common.no.target.selected`) });
				return;
			}

			this.$confirm({
				type: 'warn',
				message: `ready` === mod ? this.$t(`common.redo.it`) : this.$t(`common.cancel.it`),
				callback: async () => {
					const promiseList = [];

					updateList.forEach(async row => {
						const { url, params, columnName } = this.updateStatusFunc(row, mod);

						this.detailPk.forEach(pkInfo => {
							params[pkInfo] = row[pkInfo];
						});

						const result = await new Promise((resolve, reject) => {
							(async () => {
								const result = await new HttpReq(url).update(params, true);

								if (`ok` === result.data.result) {
									gridObj.invoke('setValue', row.rowKey, columnName, `ready` === mod ? `R` : `C`);
									resolve(result);
								} else {
									reject();
								}
							})();
						});

						promiseList.push(result);
					});

					try {
						await Promise.all(promiseList);
					} catch (error) {
						console.log(error);
					}
				}
			});
		},
		async gridControl({ gridObj, mod }) {
			try {
				if (!gridObj) return;

				const checkedRows = gridObj.invoke('getCheckedRows');

				if (0 === checkedRows.length) {
					this.$alert({ type: 'warn', message: this.$t('head.selectError') });
					return;
				}

				this.$startSpinner();

				for await (const row of checkedRows) {
					gridObj.invoke('setValue', row.rowKey, 'processResult', this.$t('common.running'));

					const params = this.controlParams(row);

					let url = `${this.controlUrl}?command=${mod}`;

					if (!params.instance) url += `&instance=${params.instance}`;

					const result = await new HttpReq(url).update(params);

					if ('ok' === result.data.result) {
						if (result.data.response[0].success) {
							gridObj.invoke('setValue', row.rowKey, 'processResult', `${mod} was successed`);
							gridObj.invoke('setValue', row.rowKey, 'status', result.data.object.status);
						} else {
							gridObj.invoke('setValue', row.rowKey, 'processResult', result.data.response[0].response);
						}
					} else {
						gridObj.invoke('setValue', row.rowKey, 'processResult', `${mod} was failed`);
					}
				}
			} catch (error) {
				this.$alert({ type: `warn`, message: this.$t('head.fail.notice') });
			} finally {
				this.$stopSpinner();
			}
		},
		async reorder() {
			try {
				const reorderBtnInfo = this.buttonList.filter(btn => 'reorder' === btn.id)[0];

				if (!reorderBtnInfo) return;

				this.$startSpinner();

				const result = await new HttpReq(reorderBtnInfo.reorderUrl).read();

				if ('ok' !== result.data.result) return;

				this.$refs.search.search();
			} catch (error) {
				console.log(error);
				this.$alert({ type: `warn`, message: this.$t('head.fail.notice') });
			} finally {
				this.$stopSpinner();
			}
		},
		detailHorizonResize({ detailWidth }) {
			if (null === detailWidth) {
				this.classObj.horizon = false;
				this.styleObj.width = null;
			} else {
				let adjustWidth = window.innerWidth;

				if (992 <= window.innerWidth && document.getElementById('sidebar') && !document.body.classList.contains('sidebar-toggled')) {
					const sidebar = document.getElementById('sidebar');

					const sidebarComputedStyle = getComputedStyle(sidebar);

					const sidebarMarginLeft = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-left'));
					const sidebarMarginRight = getNumFromStr(sidebarComputedStyle.getPropertyValue('margin-right'));

					adjustWidth -= sidebar.offsetWidth + sidebarMarginLeft + sidebarMarginRight;
				}

				if (document.querySelector(`.horizon`) && `block` === document.querySelector(`#panel`).style.display) {
					adjustWidth -= getNumFromStr(getComputedStyle(document.querySelector(`.horizon .panel-content`)).width);
				}

				this.classObj.horizon = true;
				this.styleObj.width = `${adjustWidth - 48}px`;
			}

			this.$nextTick(() => {
				WindowResizeEventBus.$emit(`MainResize`);
			});
		}
	}
};
</script>
