<template>
	<DashboardStructure
		ref="dashboardStructure"
		@initDashboardStructure="initDashboardView"
		:containerInfo="selectedContainerInfo"
		@initContextmenu="initContextmenu"
		@dashboardClick="dashboardClick"
	>
		<template slot="dashboardLbtnArea">
			<div class="form-inline">
				<select
					v-model="selectedContainerId"
					class="form-control"
					:style="{ maxWidth: `300px`, textOverflow: `ellipsis` }"
					@change="initDashboardView({ containerId: selectedContainerId })"
				>
					<option value="-">{{ $t('dashboard.select.container') }}</option>
					<option v-for="(container, idx) in containerList" :value="container.containerId" :key="idx">
						{{ container.containerName }}
					</option>
				</select>
			</div>
		</template>
		<template slot="dashboardRbtnArea">
			<button type="button" :class="viewNoticeBtnClass" @click="viewNotice" v-if="viewer">
				<span class="custom-control-label" style="width: 105px">
					{{ $t('dashboard.notification.settings') }}
				</span>
			</button>
			<a href="javascript:void(0);" class="btn" @click="noticeHistoryList" v-if="viewer">
				<i class="icon-list"></i>
				{{ $t('dashboard.notification.history.list') }}
			</a>
			<a href="javascript:void(0);" class="btn" @click="previewContainer" v-if="viewer && !isSafari">
				<i class="icon-eye"></i>
				{{ $t('dashboard.head.full.screen') }}
			</a>
			<a href="javascript:void(0);" class="btn" @click="shareContainer" v-if="editor">
				<i class="icon-share"></i>
				{{ $t('head.share') }}
			</a>
			<a href="javascript:void(0);" class="btn" @click="migrationContainer" v-if="editor">
				<i class="icon-model"></i>
				{{ $t('head.migration') }}
			</a>
			<a href="javascript:void(0);" class="btn" @click="copyContainer" v-if="editor">
				<i class="icon-plus"></i>
				{{ $t('head.copy') }}
			</a>
			<a href="javascript:void(0);" class="btn" @click="modifyContainerMode" v-if="editor">
				<i class="icon-edit"></i>
				{{ $t('head.update') }}
			</a>
			<a href="javascript:void(0);" class="btn" @click="deleteContainer" v-if="editor">
				<i class="icon-delete"></i>
				{{ $t('head.delete') }}
			</a>
			<a href="javascript:void(0);" class="btn btn-primary" @click="saveContainerMode" v-if="editor">
				<i class="icon-plus"></i>
				{{ $t('dashboard.head.add.dashboard') }}
			</a>
		</template>
		<template slot="dashboardBodyArea">
			<DashboardViewBody
				ref="dashboardViewBody"
				:containerInfo="selectedContainerInfo"
				:containerUserInfo="selectedContainerUserInfo"
				:perfItemList="perfItemList"
				:instanceList="instanceList"
				:adapterList="adapterList"
				:connectorList="connectorList"
				:queueList="queueList"
				:threadList="threadList"
				:externalLineList="externalLineList"
				:dashConstInfo="dashConstInfo"
				@filterTarget="filterTarget"
			/>
		</template>
		<template slot="dashboardContextMenuArea">
			<div id="dashContextMenu" ref="dashContextMenu" v-if="isViewContextmenu" :style="dashContextMenuStyle">
				<ul ref="menu">
					<li id="dashChangeMenu" @mouseenter="dashChangeMenuMouseEnter">
						<i class="icon-result"></i> {{ $t(`dashboard.context.dashChange`) }}
						<div style="float: right"><i class="icon-right"></i></div>
					</li>
					<li id="fullscreenExitMenu" @mouseenter="fullscreenExitMenuMouseEnter" @click="fullscreenExitMenuClick">
						<i class="icon-eye"></i> {{ $t(`dashboard.context.exit.fullscreen`) }}
					</li>
				</ul>
				<div
					id="dashContextSetting"
					ref="dashContextSetting"
					class="row frm-row"
					v-if="isViewDashContextSetting"
					:style="dashContextSettingStyle"
				>
					<div class="col-lg-12">
						<div class="form-group">
							<label class="control-label">
								<span>{{ $t(`head.dashboard`) }}</span>
								<button
									name="close"
									type="button"
									class="btn-icon"
									style="position: absolute; right: 9px; height: 1.3rem"
									@click="closeDashContextSetting"
								>
									<i name="close" class="icon-close" style="font-size: 0.75rem" @click="closeDashContextSetting"></i>
								</button>
							</label>
							<div class="input-group">
								<select v-model="selectedcontextMenuContainerId" class="form-control" size="9">
									<option v-for="(container, idx) in containerList" :value="container.containerId" :key="idx">
										{{ container.containerName }}
									</option>
								</select>
							</div>
						</div>
						<div class="form-group">
							<label class="control-label">
								<span>{{ $t(`dashboard.head.movement.form`) }}</span>
							</label>
							<div class="input-group">
								<select v-model="contextMenuMoveType" class="form-control">
									<option value="current">{{ $t(`dashboard.moveType.current`) }}</option>
									<option value="new">{{ $t(`dashboard.moveType.new`) }}</option>
								</select>
								<div class="input-group-append">
									<button type="button" class="btn" @click="containerMove">{{ $t(`head.move`) }}</button>
								</div>
							</div>
						</div>
						<div class="form-group">
							<div class="input-group">
								<div class="input-group-append" style="width: 100%; margin-left: 0px">
									<button type="button" class="btn" @click="containerClose" style="width: 100%">
										{{ $t(`dashboard.head.close.current.dashboard`) }}
									</button>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</template>
		<template slot="dashboardNoticeArea">
			<section id="noticeArea" class="card" :style="noticeAreaStyle" v-show="isViewNoticeArea">
				<h2 class="card-header" style="padding: 0px">
					<ul>
						<li class="nav-link">
							<i class="gicon-txns"></i><span>{{ $t(`dashboard.notification`) }}</span>
						</li>
						<li class="nav-link export-nav-link close" style="padding: 0 0.8rem 0 0; cursor: pointer">
							<i class="icon-close" @click="noticeAreaClose"></i>
						</li>
					</ul>
				</h2>
				<div class="card-body" style="padding-top: 0px; overflow-y: auto">
					<TuiGrid ref="noticeGrid" v-bind="noticeGridOptions" />
				</div>
			</section>
		</template>
	</DashboardStructure>
</template>
<script>
import { mapState } from 'vuex';
import { Grid } from '@toast-ui/vue-grid';
import cloneDeep from 'lodash/cloneDeep';
import moment from 'moment';
import { HttpReq, getUUID, makeGridOptions, escapeHtml, downloadJson, getNumFromStr, openNewTab } from '@/utils/common.js';
import DashboardStructure from '@/views/main/dashboard/DashboardStructure.vue';
import DashboardViewBody from '@/views/main/dashboard/DashboardViewBody.vue';

export default {
	name: `DashboardView`,
	components: {
		DashboardStructure,
		DashboardViewBody,
		TuiGrid: Grid
	},
	props: {
		viewer: {
			type: Boolean,
			default: false
		},
		editor: {
			type: Boolean,
			default: false
		},
		dashConstInfo: Object
	},
	data() {
		return {
			webSocketUrl: null,
			webSocket: null,

			containerList: [],
			selectedContainerId: `-`,
			selectedContainerInfo: null,
			selectedContainerUserInfo: null,
			selectedcontextMenuContainerId: null,
			contextMenuMoveType: `current`,

			perfItemList: [],

			instanceList: [],
			adapterList: [],
			connectorList: [],
			queueList: [],
			threadList: [],
			externalLineList: [],

			intervalObj: { time: null, measurementTime: null, isRecvTime: false },

			intervalId: null,
			rafId: null,
			refreshTimeoutId: null,

			minContainerWidth: 1280,
			minContainerHeight: 1024,

			isViewContextmenu: false,
			isViewDashContextSetting: false,

			dashContextMenuStyle: {
				position: `absolute`,
				top: null,
				left: null
			},
			dashContextSettingStyle: {
				position: `fixed`,
				paddingTop: `10px`,
				top: null,
				left: null
			},

			viewNoticeBtnClass: {
				'custom-control': true,
				'custom-switch': true,
				'btn-widget': true,
				checked: false
			},

			noticeDataList: [],
			noticeDataHistoryList: [],

			isViewNoticeArea: false,
			noticeAreaStyle: {
				width: `${this.dashConstInfo.noticeAreaWidth}px`,
				height: `${this.dashConstInfo.noticeAreaHeight}px`
			},
			temporaryCloseStartTime: null,

			noticeGridOptions: {
				columns: [
					{
						name: `noticeDateTime`,
						header: `${this.$t(`dashboard.notification`)} ${this.$t(`head.time`)}`,
						align: `center`,
						width: this.dashConstInfo.noticeDateTimeColWidth
					},
					{
						name: `instanceId`,
						header: `${this.$t(`igate.instance`)} ID`,
						width: this.dashConstInfo.noticeInstanceIdColWidth
					},
					{
						name: `message`,
						header: this.$t(`igate.message`),
						width: this.dashConstInfo.noticeMessageColWidth
					},
					{
						name: `status`,
						header: this.$t(`head.status`),
						align: `center`,
						width: this.dashConstInfo.noticeStatusColWidth
					}
				]
			},

			targetSyncRefreshTime: null,

			isDestroy: false,

			isSafari: /^((?!chrome|android).)*safari/i.test(navigator.userAgent)
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	async created() {
		this.noticeGridOptions = makeGridOptions(this.noticeGridOptions);
	},
	methods: {
		async getContainerList() {
			try {
				const httpReq = new HttpReq(`/igate/monitoring/dashboard/container.json`);

				const result = await httpReq.read();

				if (`ok` !== result.data.result) throw `Error Init Container List`;

				this.containerList = result.data.object;
			} catch (error) {
				console.log(error);
			}
		},
		async getTargetInfo() {
			try {
				const httpReq = new HttpReq(`/igate/monitoring/dashboard/targetInfo.json`);

				const result = await httpReq.read();

				if (`ok` !== result.data.result) throw `Error Init Target Info`;

				const { instanceList, adapterList, connectorList, queueList, threadList, externalLineList } = result.data.object;

				this.instanceList = instanceList.map((instance, idx) => {
					instance.color = this.dashConstInfo.COLORS[idx];
					instance.isFiltered = false;
					return instance;
				});
				this.adapterList = adapterList;
				this.connectorList = connectorList;
				this.queueList = queueList;
				this.threadList = threadList;
				this.externalLineList = externalLineList;
			} catch (error) {
				console.log(error);
			}
		},
		async getWebSocketUrl() {
			try {
				const httpReq = new HttpReq(`/igate/monitoring/dashboard.json`);

				const result = await httpReq.read();

				if (`ok` !== result.data.result) throw `Error get message url`;

				this.webSocketUrl = `${result.data.object}/message`;
			} catch (error) {
				console.log(error);
			}
		},
		async initDashboardView({ parentData, containerId }) {
			await this.getContainerList();

			if (parentData) {
				await this.getTargetInfo();

				await this.getWebSocketUrl();

				const { perfItemList } = parentData;

				this.perfItemList = perfItemList;

				if (localStorage.getItem(`paramSelectedContainerId`)) {
					this.selectedContainerId = localStorage.getItem(`paramSelectedContainerId`);
					localStorage.removeItem(`paramSelectedContainerId`);
				} else {
					for (let i = 0; i < this.containerList.length; i++) {
						const { selectYn } = this.containerList[i].monitorContainerUsers.find(
							monitorContainerUser => monitorContainerUser.pk.containerUserId === this.userInfo.userId
						);

						if (`Y` === selectYn) {
							this.selectedContainerId = this.containerList[i].containerId;
							break;
						}
					}

					if (`-` === this.selectedContainerId) {
						this.selectedContainerId = this.containerList[0].containerId;
					}
				}
			} else {
				this.unloadContainer();
				this.selectedContainerId = containerId;
			}

			this.selectedContainerInfo = cloneDeep(this.containerList.find(containerInfo => containerInfo.containerId === this.selectedContainerId));

			this.selectedContainerUserInfo = this.selectedContainerInfo.monitorContainerUsers.find(
				monitorContainerUser => monitorContainerUser.pk.containerUserId === this.userInfo.userId
			);

			this.selectedContainerUserInfo.selectYn = `Y`;
			this.selectedContainerUserInfo.containerOptions = this.selectedContainerUserInfo.containerOptions
				? JSON.parse(this.selectedContainerUserInfo.containerOptions)
				: {};

			this.updateContainer();

			this.viewNoticeBtnClass.checked = this.selectedContainerUserInfo.containerOptions[`isViewNotice_${this.selectedContainerId}`]
				? true
				: false;

			document.querySelector(`.wrap`).classList[`Y` === this.selectedContainerInfo.darkmodeYn ? `add` : `remove`](`dark`);
			document.querySelector(`#ct`).classList.add(`ct-dashboard`);

			this.targetSyncRefreshTime = Date.now();

			this.initInterval();

			this.initRequestAnimationFrame();

			this.initWebSocket();

			this.initRefresh();
		},
		initInterval() {
			this.intervalId = setInterval(() => {
				if (!document.querySelector(`#dashboard`)) {
					this.unloadContainer();
					return;
				}

				if (!this.intervalObj.isRecvTime) {
					this.intervalObj.time = Date.now();
				} else {
					this.intervalObj.time += Date.now() - this.intervalObj.measurementTime;
					this.intervalObj.measurementTime = Date.now();
				}

				this.$refs.dashboardViewBody.setInterval(cloneDeep(this.intervalObj));

				//대상 동기화.
				this.setSyncTarget();

				//알림 부분.
				this.setNotice();
			}, 1000);
		},
		initRequestAnimationFrame() {
			const step = time => {
				if (!document.querySelector(`#dashboard`)) {
					this.unloadContainer();
					return;
				}

				this.$refs.dashboardViewBody.setRequestAnimationFrame({ time });

				this.rafId = requestAnimationFrame(step);
			};

			this.rafId = requestAnimationFrame(step);
		},
		initWebSocket() {
			this.webSocket = new WebSocket(this.webSocketUrl);

			this.webSocket.onopen = () => {
				this.webSocket.send(this.getWebsocketSendParam());
			};

			this.webSocket.onerror = () => {
				console.log(`WebSocket onerror`);
			};

			this.webSocket.onmessage = event => {
				if (!document.querySelector(`#dashboard`)) {
					this.unloadContainer();
					return;
				}

				const bodyObj = JSON.parse(event.data);

				this.intervalObj.isInterval = false;

				if (!this.intervalObj.isRecvTime) {
					this.intervalObj.time = bodyObj.standardTime;
					this.intervalObj.measurementTime = Date.now();
					this.intervalObj.isRecvTime = true;
				} else {
					this.intervalObj.time += Date.now() - this.intervalObj.measurementTime;
					this.intervalObj.measurementTime = Date.now();
				}

				const time = this.intervalObj.time;

				if (1 === Object.keys(bodyObj).length) return;

				bodyObj.noticeMessage.forEach(data => {
					if (this.selectedContainerUserInfo.containerOptions[`isViewNotice_${this.selectedContainerId}`]) {
						this.noticeDataList.push(data);
					}

					this.noticeDataHistoryList.push(data);
				});

				if (this.dashConstInfo.noticeDataHistoryListMaxCnt < this.noticeDataHistoryList.length) {
					this.noticeDataHistoryList.splice(0, this.noticeDataHistoryList.length - this.dashConstInfo.noticeDataHistoryListMaxCnt);
				}

				let dataObj = {};

				this.selectedContainerInfo.monitorComponents.forEach(monitorComponent => {
					const { componentId, itemId, targetType, chartType, inoutType, monitorComponentTargets } = monitorComponent;

					let dataArr = [];

					if (`elapsed` === itemId || `deal` === itemId) {
						const seriesArr = `elapsed` === itemId ? [`elapsedMax`, `elapsedMin`, `elapsedAvg`] : [`success`, `error`, `timeout`];

						if (`INSTANCE` === targetType) {
							let dataObj = `IN` === inoutType ? bodyObj.instance_trafficIn : bodyObj.instance_trafficOut;

							if (`LINE` === chartType || `COLUMN` === chartType) {
								seriesArr.forEach(seriesId => {
									dataArr.push({ targetId: seriesId, x: time, y: dataObj[seriesId] });
								});
							}

							dataObj = null;
						} else if (`ADAPTER` === targetType) {
							if (0 < monitorComponentTargets.length) {
								let recvDataArr = `IN` === inoutType ? bodyObj.adapter_trafficIn : bodyObj.adapter_trafficOut;

								let dataObj = recvDataArr.find(recvData => recvData.adapterId === monitorComponentTargets[0].pk.componentTargetId);

								if (`LINE` === chartType || `COLUMN` === chartType) {
									seriesArr.forEach(seriesId => {
										dataArr.push({ targetId: seriesId, x: time, y: dataObj[seriesId] });
									});
								}

								dataObj = null;
								recvDataArr = null;
							}
						}
					} else if (`INSTANCE` === targetType) {
						if (`LINE` === chartType || `COLUMN` === chartType) {
							bodyObj.instance.forEach(data => {
								dataArr.push({ targetId: data.instanceId, x: time, y: data[itemId] });
							});
						} else if (`INSTANCE` === chartType) {
							bodyObj.instance.forEach(data => {
								dataArr.push(data);
							});
						} else if (`SPEEDBAR` === chartType) {
							let speedbar_traffic_obj = `IN` === inoutType ? bodyObj.instance_trafficIn : bodyObj.instance_trafficOut;

							dataArr.push({
								rps: speedbar_traffic_obj.rps,
								tps: speedbar_traffic_obj.tps,
								tpsShort: speedbar_traffic_obj.tpsShort,
								tpsMiddle: speedbar_traffic_obj.tpsMiddle,
								tpsLong: speedbar_traffic_obj.tpsLong,
								activeShort: speedbar_traffic_obj.activeShort,
								activeMiddle: speedbar_traffic_obj.activeMiddle,
								activeLong: speedbar_traffic_obj.activeLong
							});

							speedbar_traffic_obj = null;
						} else if (`DATATABLE` === chartType) {
							let transaction_obj = `IN` === inoutType ? bodyObj.instance_activeTransactionIn : bodyObj.instance_activeTransactionOut;

							let datatableRowCnt = this.selectedContainerUserInfo.containerOptions[`datatableRowCnt_${componentId}`];

							datatableRowCnt = datatableRowCnt ? datatableRowCnt : this.dashConstInfo.datatableDefaultRowCnt;

							transaction_obj.forEach((data, index) => {
								if (index > datatableRowCnt - 1) return;

								dataArr.push({
									status: data.elapsedType,
									uuid: data.transactionId,
									transactionId: data.interfaceServiceId,
									transactionName: data.interfaceServiceName,
									adapterId: data.adapterId,
									instanceId: data.instanceId,
									snapshotTimestamp: data.snapshotTimestamp
								});
							});

							transaction_obj = null;
						} else if (`XVIEW` === chartType) {
							let drawMinData = this.selectedContainerUserInfo.containerOptions[`xViewMinData_${componentId}`];

							drawMinData =
								null === drawMinData || `undefined` === typeof drawMinData ? this.dashConstInfo.xViewDefaultxMinData : drawMinData;

							let data = null;

							for (let tmpIdx = 0; tmpIdx < bodyObj.instance_finishedTransaction.length; tmpIdx++) {
								data = bodyObj.instance_finishedTransaction[tmpIdx];

								if (!data.snapshotTimestamp) continue;

								if (drawMinData > data.elapsedTime) continue;

								dataArr.push({
									targetId: data.instanceId,
									x: data.snapshotTimestamp,
									y: data.elapsedTime,
									type: `Y` === data.errorYn ? 2 : `Y` === data.timeoutYn ? 1 : 0,
									uuid: data.transactionId,
									transactionId: data.interfaceServiceId,
									transactionName: data.interfaceServiceName,
									adapterId: data.adapterId,
									instanceId: data.instanceId,
									snapshotTimestamp: data.snapshotTimestamp
								});
							}

							dataArr.sort((a, b) => a.x - b.x);
						}
					} else if (`ADAPTER` === targetType) {
						if (`SPEEDBAR` === chartType) {
							if (0 < monitorComponentTargets.length) {
								let speedbar_traffic_arr = `IN` === inoutType ? bodyObj.adapter_trafficIn : bodyObj.adapter_trafficOut;

								let speedbar_traffic_obj = speedbar_traffic_arr.find(
									speedbar_traffic_data => speedbar_traffic_data.adapterId === monitorComponentTargets[0].pk.componentTargetId
								);

								dataArr.push({
									rps: speedbar_traffic_obj.rps,
									tps: speedbar_traffic_obj.tps,
									tpsShort: speedbar_traffic_obj.tpsShort,
									tpsMiddle: speedbar_traffic_obj.tpsMiddle,
									tpsLong: speedbar_traffic_obj.tpsLong,
									activeShort: speedbar_traffic_obj.activeShort,
									activeMiddle: speedbar_traffic_obj.activeMiddle,
									activeLong: speedbar_traffic_obj.activeLong
								});

								speedbar_traffic_obj = null;
								speedbar_traffic_arr = null;
							}
						} else if (`DATATABLE` === chartType) {
							let transaction_obj = `IN` === inoutType ? bodyObj.adapter_activeTransactionIn : bodyObj.adapter_activeTransactionOut;

							let datatableRowCnt = this.selectedContainerUserInfo.containerOptions[`datatableRowCnt_${componentId}`];

							datatableRowCnt = datatableRowCnt ? datatableRowCnt : this.dashConstInfo.datatableDefaultRowCnt;

							transaction_obj.forEach((data, index) => {
								if (index > datatableRowCnt - 1) return;

								dataArr.push({
									status: data.elapsedType,
									uuid: data.transactionId,
									transactionId: data.interfaceServiceId,
									transactionName: data.interfaceServiceName,
									adapterId: data.adapterId,
									instanceId: data.instanceId,
									snapshotTimestamp: data.snapshotTimestamp
								});
							});

							transaction_obj = null;
						}
					} else if (`THREAD` === targetType) {
						if (`THREAD` === chartType) {
							if (bodyObj.thread && 0 < monitorComponentTargets.length) {
								bodyObj.thread[monitorComponentTargets[0].pk.componentTargetId].forEach(data => {
									dataArr.push({
										status: data.status,
										threadActive: data.threadActive,
										threadIdle: data.threadIdle,
										instanceId: data.instanceId
									});
								});
							}
						}
					} else if (`CONNECTOR` === targetType) {
						if (`CONNECTOR` === chartType) {
							if (bodyObj.connector && 0 < monitorComponentTargets.length) {
								bodyObj.connector[monitorComponentTargets[0].pk.componentTargetId].forEach(data => {
									dataArr.push({
										instanceId: data.instanceId,
										sessionInuse: data.sessionInuse,
										sessionWait: data.sessionWait,
										threadInuse: data.threadInuse,
										threadWait: data.threadWait,
										idleWarningYn: data.idleWarningYn,
										sessionCountWarningYn: data.sessionCountWarningYn,
										sessionInuseWarningYn: data.sessionInuseWarningYn,
										sessionWaitWarningYn: data.sessionWaitWarningYn,
										threadCountWarningYn: data.threadCountWarningYn,
										threadInuseWarningYn: data.threadInuseWarningYn,
										status: data.status
									});
								});
							}
						}
					} else if (`QUEUE` === targetType) {
						if (`QUEUE` === chartType) {
							if (bodyObj.queue && 0 < monitorComponentTargets.length) {
								bodyObj.queue[monitorComponentTargets[0].pk.componentTargetId].forEach(data => {
									dataArr.push({
										instanceId: data.instanceId,
										messageCount: data.messageCount,
										consumerCount: data.consumerCount,
										messageWarningYn: data.messageWarningYn,
										consumerWarningYn: data.consumerWarningYn,
										status: data.status
									});
								});
							}
						}
					} else if (`EXTERNALLINE` === targetType) {
						bodyObj.externalLine.forEach(data => {
							dataArr.push({
								externalLineId: data.externalLineId,
								externalLineName: data.externalLineName,
								status: data.status,
								active: data.active,
								total: data.total
							});
						});
					}

					dataObj[componentId] = dataArr;
				});

				this.$refs.dashboardViewBody.addData(dataObj);

				for (const key in dataObj) {
					dataObj[key] = null;
					delete dataObj[key];
				}

				dataObj = null;
			};
		},
		getWebsocketSendParam() {
			const adapters = [];
			const threads = [];
			const connectors = [];
			const queues = [];
			const externalLine = 1;

			let topCount = this.dashConstInfo.datatableDefaultRowCnt;

			this.selectedContainerInfo.monitorComponents.forEach(monitorComponent => {
				const { chartType, targetType, monitorComponentTargets, componentId } = monitorComponent;

				if (`ADAPTER` === targetType) {
					monitorComponentTargets.forEach(monitorComponentTarget => {
						adapters.push(monitorComponentTarget.pk.componentTargetId);
					});
				} else if (`THREAD` === targetType) {
					monitorComponentTargets.forEach(monitorComponentTarget => {
						threads.push({
							threadId: monitorComponentTarget.pk.componentTargetId,
							instanceIds: this.instanceList.filter(instance => `T` === instance.instanceType).map(instance => instance.instanceId)
						});
					});
				} else if (`CONNECTOR` === targetType) {
					monitorComponentTargets.forEach(monitorComponentTarget => {
						connectors.push({
							connectorId: monitorComponentTarget.pk.componentTargetId,
							instanceIds: (() => {
								const connectorInfo = this.connectorList.find(
									connectorInfo => connectorInfo.connectorId == monitorComponentTarget.pk.componentTargetId
								);

								return connectorInfo ? connectorInfo.instanceList.map(instanceInfo => instanceInfo.pk.instanceId) : null;
							})()
						});
					});
				} else if (`QUEUE` == targetType) {
					monitorComponentTargets.forEach(monitorComponentTarget => {
						queues.push({
							queueId: monitorComponentTarget.pk.componentTargetId,
							instanceIds: (() => {
								const queueInfo = this.queueList.find(
									queueInfo => queueInfo.adapterId == monitorComponentTarget.pk.componentTargetId
								);

								return queueInfo ? queueInfo.instanceList.map(instanceInfo => instanceInfo.pk.instanceId) : null;
							})()
						});
					});
				}

				if (`DATATABLE` === chartType) {
					let datatableRowCnt = this.selectedContainerUserInfo.containerOptions[`datatableRowCnt_${componentId}`];

					datatableRowCnt = datatableRowCnt ? datatableRowCnt : this.dashConstInfo.datatableDefaultRowCnt;

					if (topCount < datatableRowCnt) topCount = datatableRowCnt;
				}
			});

			return JSON.stringify({
				duration: this.dashConstInfo.dataIntervalSeconds,
				filter: {
					instances: this.instanceList.filter(instanceInfo => !instanceInfo.isFiltered).map(instanceInfo => instanceInfo.instanceId)
				},
				adapters: 0 < adapters.length ? adapters : null,
				threads: 0 < threads.length ? threads : null,
				connectors: 0 < connectors.length ? connectors : null,
				queues: 0 < queues.length ? queues : null,
				externalLine: externalLine,
				topCount: topCount
			});
		},
