		},
		deleteContainer() {
			if (!this.selectedContainerId || `-` === this.selectedContainerId) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.select.deleteDashboard`)
				});
				return;
			}

			this.$confirm({
				type: `warn`,
				message: this.$t(`dashboard.delete.warn`),
				callback: async () => {
					const httpReq = new HttpReq(`/igate/monitoring/dashboard/container.json`);

					const result = await httpReq.delete({ containerId: this.selectedContainerId }, true);

					if (`ok` !== result.data.result) return;

					this.$alert({
						type: `normal`,
						message: this.$t(`dashboard.delete.success`)
					});

					this.changeContainer(`view`);
				}
			});
		},
		copyContainer() {
			if (!this.selectedContainerId || `-` === this.selectedContainerId) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.select.copyDashboard`)
				});
				return;
			}

			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t(`dashboard.head.copy.dashboard`),
				modalWidth: `350px`,
				contentList: [
					{
						template: `
							<div>
								<div class="form-group">
									<label class="control-label">${this.$t(`dashboard.head.name`)}<b class="icon-star"></b></label>
									<input type="text" v-model="containerName" class="form-control" placeholder="${this.$t(`dashboard.enter.name`)}" required>
								</div>
								<div class="form-group">
									<label class="control-label">${this.$t(`dashboard.head.resolution`)}<b class="icon-star"></b></label>
									<div class="input-group">
										<input type="text" v-model="containerWidth" class="form-control" maxlength="4" placeholder="${this.minContainerWidth}" required>
										<i class="icon-close mx-1"></i>
										<input type="text" v-model="containerHeight" class="form-control" maxlength="4" placeholder="${this.minContainerHeight}" required>
									</div>
								</div>
							</div> 							
						`,
						data() {
							return {
								containerName: _this.selectedContainerInfo.containerName,
								containerWidth: _this.selectedContainerInfo.containerWidth,
								containerHeight: _this.selectedContainerInfo.containerHeight
							};
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" @click="confirm" class="btn btn-primary">${this.$t(`head.ok`)}</button>`,
						methods: {
							confirm() {
								if (
									!_this.validationDashConfigModal({
										containerName: modalInstance.$refs.content[0].containerName,
										containerWidth: modalInstance.$refs.content[0].containerWidth,
										containerHeight: modalInstance.$refs.content[0].containerHeight
									})
								)
									return;

								const containerName = modalInstance.$refs.content[0].containerName.trim();
								const containerWidth = Number(modalInstance.$refs.content[0].containerWidth);
								const containerHeight = Number(modalInstance.$refs.content[0].containerHeight);

								_this.existEqualsContainerName(
									{
										containerName: modalInstance.$refs.content[0].containerName.trim()
									},
									async () => {
										const copyContainerInfo = {
											containerId: getUUID(),
											containerName,
											containerWidth,
											containerHeight,
											remarkYn: _this.selectedContainerInfo.remarkYn,
											darkmodeYn: _this.selectedContainerInfo.darkmodeYn
										};

										const containerOptions = cloneDeep(_this.selectedContainerUserInfo.containerOptions);

										const basicComponentStruct = _this.$refs.dashboardStructure.getBasicComponentStruct();

										const parentComponentIdList = _this.selectedContainerInfo.monitorComponents.map(component => ({
											beforeChangeId: component.componentId
										}));

										const copyComponentList = _this.selectedContainerInfo.monitorComponents.map(component => {
											const beforeComponentId = component.componentId;

											component.componentId = getUUID();
											component.containerId = copyContainerInfo.containerId;

											parentComponentIdList.forEach(parentComponentObj => {
												if (parentComponentObj.beforeChangeId !== beforeComponentId) return;

												parentComponentObj.afterChangeId = component.componentId;
											});

											const newComponent = {};

											Object.keys(basicComponentStruct).forEach(key => {
												newComponent[key] = component[key];
											});

											if (`C` === newComponent.componentType) {
												[
													`xViewYAxisMax`,
													`xViewTrans`,
													`xViewMinData`,
													`xViewInstanceFilter`,
													`xViewadapterFilter`,
													`xViewtransactionFilter`,
													`datatableRowCnt`,
													`instanceSummaryColCnt`
												].forEach(key => {
													if ('undefined' === typeof containerOptions[`${key}_${beforeComponentId}`]) return;

													containerOptions[`${key}_${newComponent.componentId}`] =
														containerOptions[`${key}_${beforeComponentId}`];

													delete containerOptions[`${key}_${beforeComponentId}`];
												});
											}

											return newComponent;
										});

										copyComponentList.forEach(component => {
											parentComponentIdList.forEach(parentComponentObj => {
												if (parentComponentObj.beforeChangeId !== component.pComponentId) return;

												component.pComponentId = parentComponentObj.afterChangeId;
											});

											component.monitorComponentTargets.forEach(monitorComponentTarget => {
												monitorComponentTarget.pk.componentId = component.componentId;
											});
										});

										copyContainerInfo.monitorComponents = copyComponentList;

										if (`undefined` !== typeof containerOptions[`isViewNotice_${_this.selectedContainerId}`]) {
											containerOptions[`isViewNotice_${copyContainerInfo.containerId}`] =
												containerOptions[`isViewNotice_${_this.selectedContainerId}`];

											delete containerOptions[`isViewNotice_${_this.selectedContainerId}`];
										}

										copyContainerInfo.monitorContainerUsers = [
											{
												pk: {
													containerId: copyContainerInfo.containerId,
													containerUserId: _this.userInfo.userId
												},
												selectYn: `Y`,
												containerOptions: JSON.stringify(containerOptions)
											}
										];

										const httpReq = new HttpReq(`/igate/monitoring/dashboard/container.json`);

										const result = await httpReq.update(copyContainerInfo, true);

										if (`ok` !== result.data.result) return;

										this.$alert({
											type: `normal`,
											message: this.$t(`dashboard.add.success`)
										});

										_this.changeContainer(`view`);

										modalInstance.hideModal();
									}
								);
							}
						}
					}
				]
			});
		},
		async shareContainer() {
			if (!this.selectedContainerId || `-` === this.selectedContainerId) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.select.shareDashboard`)
				});
				return;
			}

			const httpReq = new HttpReq(`/igate/monitoring/dashboard/user.json`);

			const result = await httpReq.read({ containerId: this.selectedContainerId });

			const { allUserList, registerDashUserList } = result.data.object;

			allUserList.forEach(info => {
				info._attributes = {
					checked: registerDashUserList.find(registerDashUser => registerDashUser.pk.containerUserId === info.userId) ? true : false
				};
			});

			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t(`head.share`),
				modalWidth: `350px`,
				contentList: [
					{
						template: `
							<div>
								<div class="sub-bar" style="padding-top: 0px;">
									<div class="form-inline m-full" style="width: 100%">
										<input v-model="searchShareUser" type="text" class="form-control input-search" @keypress.enter="search"
											placeholder="${this.$t(`dashboard.share.search`)}" autocomplete="off" style="width: 100%">
									</div>
								</div>
								<TuiGrid ref="tuiGrid" v-bind="options" />
							</div> 							
						`,
						data() {
							return {
								list: [],
								searchShareUser: '',
								perPage: 20,
								scrollEndIdx: 1,
								options: {
									bodyHeight: 250,
									rowHeaders: [{ type: 'checkbox', header: ' ' }],
									columns: [
										{
											name: `userId`,
											header: this.$t(`common.user.id`),
											formatter: info => escapeHtml(info.row.userId)
										}
									],
									onGridMounted: evt => {
										evt.instance.on(`scrollEnd`, mouseEvt => {
											if (this.scrollEndIdx > Math.ceil(this.list.length / this.perPage)) return;

											mouseEvt.instance.appendRows(
												this.list.slice(this.perPage * this.scrollEndIdx, this.perPage * (this.scrollEndIdx + 1))
											);

											this.scrollEndIdx += 1;
										});
									}
								}
							};
						},
						methods: {
							search() {
								this.perPage = 20;
								this.scrollEndIdx = 1;

								this.list = allUserList.filter(info =>
									0 === this.searchShareUser.trim().length
										? true
										: -1 < info.userId.toLowerCase().indexOf(this.searchShareUser.toLowerCase())
								);

								this.$refs.tuiGrid.invoke(`resetData`, this.list.slice(0, this.perPage));
							}
						},
						components: {
							TuiGrid: Grid
						},
						created() {
							this.list = cloneDeep(allUserList);
							this.options = makeGridOptions(this.options);
						},
						mounted() {
							this.search();
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" @click="confirm" class="btn btn-primary">${this.$t(`head.ok`)}</button>`,
						methods: {
							async confirm() {
								const containerInfo = cloneDeep(_this.selectedContainerInfo);

								modalInstance.$refs.content[0].$refs.tuiGrid.invoke(`getCheckedRows`).forEach(info => {
									containerInfo.monitorContainerUsers.push({
										pk: {
											containerId: containerInfo.containerId,
											containerUserId: info.userId
										},
										selectYn: `N`,
										containerOptions: JSON.stringify(_this.selectedContainerUserInfo.containerOptions)
									});
								});

								const httpReq = new HttpReq(`/igate/monitoring/dashboard/container.json`);

								const result = await httpReq.create(containerInfo, true);

								if (`ok` !== result.data.result) return;

								this.$alert({
									type: `normal`,
									message: this.$t(`dashboard.share.success`)
								});

								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		migrationContainer() {
			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t(`head.migration`),
				modalWidth: `800px`,
				contentList: [
					{
						template: `<div>
								       <ul class="nav nav-tabs flex-shrink-0">
									       <li class="nav-item"><a class="nav-link active" href="#download" data-toggle="tab">{{ $t("head.download") }}</a></li>
									       <li class="nav-item"><a class="nav-link" href="#migration" data-toggle="tab">{{ $t("head.migration") }}</a></li>
									   </ul>					
									   <div class="modal-body tab-content py-4" style="padding-top: 1.0rem !important;">	
										   <div id="download" class="tab-pane active">
											   <div class="sub-bar" style="padding-top: 0px;">
												   <div class="ml-auto form-inline m-full">
													   <a href="javascript:void(0);" class="btn btn-m" @click="download">
														   <i class="icon-export"></i><span class="hide">{{ $t('head.download') }}</span>
													   </a>
												   </div>
											   </div>
											   <TuiGrid ref="tuiGrid" v-bind="options" />
										   </div>
										   <div id="migration" class="tab-pane">
											   <div class="row frm-row">
												   <div class="col-lg-12">
													   <div class="form-group">
														   <label class="control-label">{{ $t('head.file') }} {{ $t('head.select') }}</label>
														   <div class="input-group">
															   <input class="form-control view-disabled" v-model="fileName" type="text" readonly="readonly" :placeholder="$t('igate.migration.fileSelectError')" />
															   <button type="button" class="btn" @click="fileSelect" style="margin-left: 5px; margin-right: 5px;">{{ $t('head.file') }} {{ $t('head.select') }}</button>
															   <button type="button" class="btn btn-primary" @click="fileUpload" >{{ $t('head.upload') }}</button>
														   </div>
													   </div>
												   </div>	        
											   </div>
										   </div>
									   </div>
								   </div>							
						`,
						components: {
							TuiGrid: Grid
						},
						data() {
							return {
								fileName: null,
								importContainerList: null,
								options: {
									rowHeaders: [`checkbox`],
									bodyHeight: 300,
									columns: [
										{
											name: `containerName`,
											header: `${this.$t(`head.dashboard`)} ${this.$t(`dashboard.head.name`)}`,
											width: `70%`
										},
										{
											name: `resolution`,
											header: this.$t(`dashboard.head.resolution`),
											align: `center`,
											width: `30%`,
											formatter: ({ row: { containerWidth, containerHeight } }) => `${containerWidth} X ${containerHeight}`
										},
										{
											name: `containerId`,
											hidden: true
										}
									]
								}
							};
						},
						created() {
							this.options = makeGridOptions(this.options);
						},
						mounted() {
							this.resetData();
						},
						methods: {
							resetData() {
								this.$refs.tuiGrid.invoke(`resetData`, cloneDeep(_this.containerList));
							},
							download() {
								if (0 === this.$refs.tuiGrid.invoke(`getCheckedRowKeys`).length) {
									this.$alert({
										type: `warn`,
										message: this.$t(`dashboard.select.noDashboard`)
									});
									return;
								}

								const exportContainerList = [];

								this.$refs.tuiGrid.invoke(`getCheckedRowKeys`).forEach(rowKey => {
									const exportContainerInfo = cloneDeep(
										_this.containerList.find(
											containerInfo => containerInfo.containerId === this.$refs.tuiGrid.invoke(`getRow`, rowKey).containerId
										)
									);

									delete exportContainerInfo.monitorContainerUsers;

									for (let i = 0; i < exportContainerInfo.monitorComponents.length; i++) {
										delete exportContainerInfo.monitorComponents[i].monitorComponentTargets;
									}

									exportContainerList.push(exportContainerInfo);
								});

								const jsonFileName = `DashBoardMigration_${moment(new Date()).format('YYYY-MM-DD hh-mm-ss')}_${getUUID()}`;
								const jsonData = JSON.stringify(exportContainerList);

								downloadJson(jsonFileName, jsonData);
							},
							fileSelect() {
								this.importContainerList = null;

								const fileEle = document.createElement(`input`);
								fileEle.setAttribute(`type`, `file`);
								fileEle.style.display = `none`;

								fileEle.addEventListener('change', evt => {
									const fileName = evt.target.files[0].name;

									if ('json' != fileName.substring(fileName.length - 4)) {
										this.$alert({
											type: `warn`,
											message: this.$t(`dashboard.json.file.format.only`)
										});
										return;
									}

									this.fileName = escapeHtml(fileName);

									const reader = new FileReader();

									reader.readAsText(evt.target.files[0], `utf-8`);

									reader.onload = () => {
										this.importContainerList = JSON.parse(reader.result);
									};
								});

								fileEle.click();
							},
							async fileUpload() {
								if (null === this.importContainerList) {
									this.$alert({
										type: `warn`,
										message: this.$t(`igate.migration.fileSelectError`)
									});
									return;
								}

								const promiseList = [];

								const newContainerId = getUUID();

								this.importContainerList.forEach(importContainerInfo => {
									importContainerInfo.containerId = newContainerId;

									importContainerInfo.monitorComponents.forEach((component, index) => {
										component.containerId = newContainerId;

										for (const key in component) {
											importContainerInfo[`monitorComponents[${index}].${key}`] = component[key];
										}
									});

									delete importContainerInfo.monitorComponents;

									importContainerInfo[`monitorContainerUsers[0].pk.containerId`] = newContainerId;
									importContainerInfo[`monitorContainerUsers[0].pk.containerUserId`] = _this.userInfo.userId;
									importContainerInfo[`monitorContainerUsers[0].selectYn`] = `N`;

									promiseList.push(
										new Promise(resolve => {
											(async () => {
												let result = await new HttpReq(`/igate/monitoring/dashboard/existEqualsContainerName.json`).read({
													containerName: importContainerInfo.containerName.trim()
												});

												if (`ok` !== result.data.result) return;

												if (result.data.object) {
													resolve(`${importContainerInfo.containerName}: ${this.$t(`dashboard.overlap.warn`)}`);
												} else {
													result = await new HttpReq(`/igate/monitoring/dashboard/container.json`).update(
														{
															_method: `POST`,
															...importContainerInfo
														},
														true
													);

													if (`ok` !== result.data.result) return;

													resolve(
														`${importContainerInfo.containerName}: ${
															`ok` === result.data.result ? this.$t(`igate.migration.make`) : result.error[0].message
														}`
													);
												}
											})();
										})
									);
								});

								const result = await Promise.all(promiseList);

								let message = ``;

								result.forEach(msg => {
									message += `${escapeHtml(msg)} <br/>`;
								});

								this.$alert({
									type: `normal`,
									isXSSMode: true,
									message
								});

								this.fileName = null;
								this.importContainerList = null;

								await _this.getContainerList();

								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		previewContainer() {
			if (!this.selectedContainerId || `-` === this.selectedContainerId) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.select.noDashboard`)
				});
				return;
			}

			const element = document.documentElement;

			if (element.requestFullScreen) element.requestFullScreen();
			else if (element.webkitRequestFullScreen) element.webkitRequestFullScreen();
			else if (element.mozRequestFullScreen) element.mozRequestFullScreen();
			else if (element.msRequestFullscreen) element.msRequestFullscreen();
		},
		async existEqualsContainerName(param, callBackFunc) {
			const httpReq = new HttpReq(`/igate/monitoring/dashboard/existEqualsContainerName.json`);

			const result = await httpReq.read(param);

			if (result.data.object) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.overlap.warn`)
				});
				return;
			}

			if (callBackFunc) callBackFunc();
		},
		dashboardClick(evt) {
			if (`OPTION` === evt.target.tagName || `BUTTON` === evt.target.tagName || `SELECT` === evt.target.tagName) return;

			this.selectedcontextMenuContainerId = null;
			this.isViewDashContextSetting = false;
			this.isViewContextmenu = false;
		},
		initContextmenu(evt) {
			if (!(screen.width == window.innerWidth && screen.height == window.innerHeight)) {
				this.isViewContextmenu = false;
			} else {
				this.isViewContextmenu = true;

				this.$nextTick(() => {
					this.dashContextMenuStyle.top = `${
						window.innerHeight <= evt.clientY + this.$refs.dashContextMenu.offsetHeight
							? evt.clientY - this.$refs.dashContextMenu.offsetHeight
							: evt.clientY
					}px`;

					this.dashContextMenuStyle.left = `${
						window.innerWidth <= evt.clientX + this.$refs.dashContextMenu.offsetWidth
							? window.innerWidth - this.$refs.dashContextMenu.offsetWidth
							: evt.clientX
					}px`;
				});
			}
		},
		dashChangeMenuMouseEnter(evt) {
			this.isViewDashContextSetting = true;

			this.selectedcontextMenuContainerId = this.selectedContainerId;

			this.$nextTick(() => {
				this.dashContextSettingStyle.top = `${
					window.innerHeight <= evt.clientY + this.$refs.dashContextSetting.offsetHeight
						? getNumFromStr(this.dashContextMenuStyle.top) - this.$refs.dashContextSetting.offsetHeight + 30
						: getNumFromStr(this.dashContextMenuStyle.top)
				}px`;

				this.dashContextSettingStyle.left = `${
					window.innerWidth <= evt.clientX + this.$refs.menu.offsetWidth + this.$refs.dashContextSetting.offsetWidth
						? getNumFromStr(this.dashContextMenuStyle.left) - this.$refs.dashContextSetting.offsetWidth + 5
						: getNumFromStr(this.dashContextMenuStyle.left) + this.$refs.menu.offsetWidth + 20
				}px`;
			});
		},
		fullscreenExitMenuClick() {
			this.isViewDashContextSetting = false;
			this.isViewContextmenu = false;

			if (!document.fullscreenElement && screen.width == window.innerWidth && screen.height == window.innerHeight) {
				this.$alert({ type: `warn`, message: this.$t(`dashboard.context.fullscreenMsg`) });
				return false;
			}

			if (document.exitFullscreen) document.exitFullscreen();
			else if (document.webkitExitFullscreen) document.webkitExitFullscreen();
			else if (document.mozCancelFullScreen) document.mozCancelFullScreen();
			else if (document.msExitFullscreen) document.msExitFullscreen();
		},
		fullscreenExitMenuMouseEnter() {
			this.isViewDashContextSetting = false;
		},
		closeDashContextSetting() {
			this.isViewDashContextSetting = false;
			this.isViewContextmenu = false;
		},
		containerMove() {
			if (`current` === this.contextMenuMoveType) {
				localStorage.setItem(`paramSelectedContainerId`, this.selectedcontextMenuContainerId);
				this.changeContainer(`view`);
			} else {
				openNewTab(401010, () => {
					localStorage.setItem(`paramSelectedContainerId`, this.selectedcontextMenuContainerId);
				});
			}
		},
		containerClose() {
			if (!window.opener) this.$alert({ type: `warn`, message: this.$t(`dashboard.close.new.window`) });
			else window.close();
		},
		filterTarget({ targetId }) {
			for (const instanceInfo of this.instanceList) {
				if (instanceInfo.instanceId === targetId) {
					instanceInfo.isFiltered = !instanceInfo.isFiltered;
					break;
				}
			}

			this.$refs.dashboardViewBody.filterTargetAfter();

			if (this.webSocket) this.webSocket.send(this.getWebsocketSendParam());
		},
		initRefresh() {
			const refreshDay = new Date(Date.now());

			refreshDay.setDate(refreshDay.getDate() + 1);
			refreshDay.setHours(4);
			refreshDay.setMinutes(0);
			refreshDay.setSeconds(0);
			refreshDay.setMilliseconds(0);

			this.refreshTimeoutId = setTimeout(() => {
				if (!document.querySelector(`#dashboard`)) {
					this.unloadContainer();
					return;
				}

				location.reload();
			}, refreshDay.getTime() - Date.now());
		},
		changeContainer(mod, containerInfo) {
			this.unloadContainer();

			this.$emit(`changeContainer`, mod, containerInfo);
		},
		unloadContainer() {
			if (null !== this.intervalId) {
				clearInterval(this.intervalId);
				this.intervalId = null;
			}

			if (null !== this.rafId) {
				cancelAnimationFrame(this.rafId);
				this.rafId = null;
			}

			if (this.webSocket) {
				this.webSocket.close();
				this.webSocket = null;
			}

			if (null != this.refreshTimeoutId) {
				clearTimeout(this.refreshTimeoutId);
				this.refreshTimeoutId = null;
			}

			this.noticeDataList = [];
			this.noticeDataHistoryList = [];

			this.isViewNoticeArea = false;

			if (this.$refs.dashboardViewBody) this.$refs.dashboardViewBody.unload();

			document.querySelector(`.wrap`).classList.remove(`dark`);
			document.querySelector(`#ct`).classList.remove(`ct-dashboard`);
		},
		viewNotice() {
			this.viewNoticeBtnClass.checked = !this.viewNoticeBtnClass.checked;

			if (!this.viewNoticeBtnClass.checked) this.isViewNoticeArea = false;

			this.temporaryCloseStartTime = null;

			this.selectedContainerUserInfo.containerOptions[`isViewNotice_${this.selectedContainerId}`] = this.viewNoticeBtnClass.checked;

			this.updateContainer();
		},
		noticeAreaClose() {
			this.isViewNoticeArea = false;
			this.temporaryCloseStartTime = Date.now();
		},
		setNotice() {
			if (this.selectedContainerUserInfo.containerOptions[`isViewNotice_${this.selectedContainerId}`]) {
				if (this.$refs.noticeGrid) {
					const noticeGrid = this.$refs.noticeGrid;

					noticeGrid.invoke(`getData`).forEach(dataInfo => {
						if (Date.now() - dataInfo.showTime <= this.dashConstInfo.noticeDataHoldingTime * 1000) return;
						noticeGrid.invoke(`removeRow`, dataInfo.rowKey);
					});

					const sortNoticeDataList = this.noticeDataList.sort(
						(a, b) => new Date(a.noticeDateTime).getTime() - new Date(b.noticeDateTime).getTime()
					);

					if (0 < sortNoticeDataList.length)
						sortNoticeDataList.forEach(dataInfo => {
							dataInfo.showTime = Date.now();
							noticeGrid.invoke(`prependRow`, cloneDeep(dataInfo));
						});

					if (
						!this.temporaryCloseStartTime ||
						(this.temporaryCloseStartTime &&
							Date.now() - this.temporaryCloseStartTime >= this.dashConstInfo.noticeTemporaryCloseStartTime * 1000)
					) {
						if (this.temporaryCloseStartTime) this.temporaryCloseStartTime = null;

						if (0 < noticeGrid.invoke(`getData`).length) {
							if (!this.isViewNoticeArea) {
								this.isViewNoticeArea = true;

								this.$nextTick(() => {
									noticeGrid.invoke(`resetData`, noticeGrid.invoke(`getData`));
								});
							}
						} else {
							this.isViewNoticeArea = false;
						}
					}
				}

				this.noticeDataList = [];
			} else {
				if (this.$refs.noticeGrid && 0 < this.$refs.noticeGrid.invoke(`getData`).length) this.$refs.noticeGrid.invoke(`clear`);

				if (this.isViewNoticeArea) this.isViewNoticeArea = false;

				if (0 < this.noticeDataList.length) this.noticeDataList = [];
			}
		},
		noticeHistoryList() {
			if (!this.selectedContainerId || `-` === this.selectedContainerId) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.select.noDashboard`)
				});
				return;
			}

			const _this = this;

			let noticeDataHistoryList = cloneDeep(this.noticeDataHistoryList).reverse();

			this.$openModal({
				title: this.$t(`dashboard.notification.history.list`),
				modalWidth: `900px`,
				contentList: [
					{
						template: `<div>
									   <div class="sub-bar" style="padding-top: 0px;">
									       <span>Last Updated Time: {{ refreshTime }}</span>
										   <div class="ml-auto form-inline m-full">
										       <a href="javascript:void(0);"  title="${this.$t(`igate.refresh`)}" class="btn btn-m" @click="resetData">
											       <i class="icon-reset"></i>
												   <span class="hide">${this.$t(`igate.refresh`)}</span>
											   </a>
										   </div>
									   </div>
						               <TuiGrid ref="tuiGrid" v-bind="options" />
						           </div>`,
						components: {
							TuiGrid: Grid
						},
						data() {
							return {
								refreshTime: null,
								perPage: 20,
								scrollEndIdx: 1,
								options: {
									bodyHeight: 250,
									columns: [
										{
											name: `noticeDateTime`,
											header: `${this.$t(`dashboard.notification`)} ${this.$t(`head.time`)}`,
											align: `center`,
											width: _this.dashConstInfo.noticeDateTimeColWidth
										},
										{
											name: `instanceId`,
											header: `${this.$t(`igate.instance`)} ID`,
											width: _this.dashConstInfo.noticeInstanceIdColWidth
										},
										{
											name: `message`,
											header: this.$t(`igate.message`),
											width: _this.dashConstInfo.noticeMessageColWidth
										},
										{
											name: `status`,
											header: this.$t(`head.status`),
											align: `center`,
											width: _this.dashConstInfo.noticeStatusColWidth
										}
									],
									onGridMounted: evt => {
										evt.instance.on(`scrollEnd`, mouseEvt => {
											if (this.scrollEndIdx > Math.ceil(noticeDataHistoryList.length / this.perPage)) return;

											mouseEvt.instance.appendRows(
												noticeDataHistoryList.slice(this.perPage * this.scrollEndIdx, this.perPage * (this.scrollEndIdx + 1))
											);

											this.scrollEndIdx += 1;
										});
									}
								}
							};
						},
						created() {
							this.options = makeGridOptions(this.options);
						},
						mounted() {
							this.resetData();
						},
						methods: {
							resetData() {
								this.scrollEndIdx = 1;

								noticeDataHistoryList = cloneDeep(_this.noticeDataHistoryList).reverse();

								this.$refs.tuiGrid.invoke(`resetData`, noticeDataHistoryList ? noticeDataHistoryList.slice(0, this.perPage) : null);

								this.refreshTime = moment(new Date()).format(`YYYY-MM-DD hh:mm:ss`);
							}
						},
						beforeDestroy() {
							noticeDataHistoryList = null;
						}
					}
				]
			});
		},
		async setSyncTarget() {
			const dashboardViewBody = this.$refs.dashboardViewBody;

			const dashboardBodyArea = dashboardViewBody.$refs.dashboardBodyArea;

			if (null != this.targetSyncRefreshTime && Date.now() - this.targetSyncRefreshTime < this.dashConstInfo.targetSyncRefreshStandardTime)
				return;

			this.targetSyncRefreshTime = Date.now();

			let oldInstanceList = [];
			let oldAdapterList = [];
			let oldConnectorList = [];
			let oldQueueList = [];
			let oldExternalLineList = [];

			Array.prototype.push.apply(oldInstanceList, this.instanceList);
			Array.prototype.push.apply(oldAdapterList, this.adapterList);
			Array.prototype.push.apply(oldConnectorList, this.connectorList);
			Array.prototype.push.apply(oldQueueList, this.queueList);
			Array.prototype.push.apply(oldExternalLineList, this.externalLineList);

			await this.getTargetInfo();

			this.instanceList.forEach((instance, index) => {
				instance.color = this.dashConstInfo.COLORS[index];

				let isFiltered = false;

				for (let i = 0; i < oldInstanceList.length; i++) {
					if (oldInstanceList[i].instanceId === instance.instanceId) {
						isFiltered = oldInstanceList[i].isFiltered;
						break;
					}
				}

				instance.isFiltered = isFiltered;
			});

			if (!this.selectedContainerInfo.monitorComponents) return;

			if (0 === this.selectedContainerInfo.monitorComponents.length) return;

			let isChangeFlag = false;

			//instnace, thread도 같이.
			const addInstanceList = this.instanceList.filter(
				instanceInfo => -1 === oldInstanceList.map(oldInstanceInfo => oldInstanceInfo.instanceId).indexOf(instanceInfo.instanceId)
			);

			const deleteInstanceList = oldInstanceList.filter(
				oldInstanceInfo => -1 === this.instanceList.map(instanceInfo => instanceInfo.instanceId).indexOf(oldInstanceInfo.instanceId)
			);

			//adapter
			// const addAdapterList = this.adapterList.filter(
			// 	adapterInfo => -1 === oldAdapterList.map(oldAdapterInfo => oldAdapterInfo.adapterId).indexOf(adapterInfo.adapterId),
			// );

			// const deleteAdapterList = oldAdapterList.filter(
			// 	oldAdapterInfo => -1 === this.adapterList.map(adapterInfo => adapterInfo.adapterId).indexOf(oldAdapterInfo.adapterId),
			// );

			//externalLine
			const addExternalLineList = this.externalLineList.filter(
				externalInfo => -1 === oldExternalLineList.map(oldExternalInfo => oldExternalInfo.externalLineId).indexOf(externalInfo.externalLineId)
			);

			const deleteExternalLineList = oldExternalLineList.filter(
				oldExternalInfo =>
					-1 === this.externalLineList.map(externalInfo => externalInfo.externalLineId).indexOf(oldExternalInfo.externalLineId)
			);

			for (const component of this.selectedContainerInfo.monitorComponents) {
				if (`elapsed` === component.itemId || `deal` === component.itemId) continue;

				if (!dashboardBodyArea.$refs[component.componentId]) continue;

				const chart = dashboardBodyArea.$refs[component.componentId];

				if (`INSTANCE` === component.targetType) {
					if (0 < deleteInstanceList.length)
						deleteInstanceList.forEach(instanceInfo => {
							if (chart.deleteTarget) chart.deleteTarget(instanceInfo.instanceId);
						});

					if (0 < addInstanceList.length)
						addInstanceList.forEach(instanceInfo => {
							if (chart.addTarget)
								chart.addTarget({ targetId: instanceInfo.instanceId, targetName: instanceInfo.instanceId, color: `` });
						});

					if (0 < deleteInstanceList.length || 0 < addInstanceList.length) {
						isChangeFlag = true;

						this.instanceList.forEach(instanceInfo => {
							if (chart.updateTarget) chart.updateTarget(instanceInfo.instanceId, { color: instanceInfo.color });
						});

						if (chart.draw) chart.draw();
					}
				} else if (`CONNECTOR` === component.targetType) {
					component.monitorComponentTargets.forEach(componentTargetInfo => {
						if (
							-1 ===
							this.connectorList.map(connectorInfo => connectorInfo.connectorId).indexOf(componentTargetInfo.pk.componentTargetId)
						) {
							isChangeFlag = true;

							if (chart.deleteTargetAll) chart.deleteTargetAll();
						} else {
							const filterConnectorInfo = this.connectorList.find(
								connectorInfo => connectorInfo.connectorId == componentTargetInfo.pk.componentTargetId
							);

							const filterOldConnectorInfo = oldConnectorList.find(
								oldConnectorInfo => oldConnectorInfo.connectorId == componentTargetInfo.pk.componentTargetId
							);

							const currInstanceList = filterConnectorInfo.instanceList;
							const oldInstanceList = filterOldConnectorInfo.instanceList;

							const deleteInstanceList = oldInstanceList.filter(
								oldInstanceInfo =>
									-1 === currInstanceList.map(instanceInfo => instanceInfo.pk.instanceId).indexOf(oldInstanceInfo.pk.instanceId)
							);

							const addInstanceList = currInstanceList.filter(
								instanceInfo =>
									-1 === oldInstanceList.map(oldInstanceInfo => oldInstanceInfo.pk.instanceId).indexOf(instanceInfo.pk.instanceId)
							);

							if (0 < deleteInstanceList.length)
								deleteInstanceList.forEach(instanceObj => {
									if (chart.deleteTarget) chart.deleteTarget(instanceObj.pk.instanceId);
								});

							if (0 < addInstanceList.length)
								addInstanceList.forEach(instanceObj => {
									if (chart.addTarget)
										chart.addTarget({ targetId: instanceObj.pk.instanceId, targetName: instanceObj.pk.instanceId });
								});

							if (0 < deleteInstanceList.length || 0 < addInstanceList.length) {
								isChangeFlag = true;

								if (chart.draw) chart.draw();
							}
						}
					});
				} else if (`QUEUE` === component.targetType) {
					component.monitorComponentTargets.forEach(componentTargetInfo => {
						if (-1 === this.queueList.map(queueInfo => queueInfo.adapterId).indexOf(componentTargetInfo.pk.componentTargetId)) {
							isChangeFlag = true;

							if (chart.deleteTargetAll) chart.deleteTargetAll();
						} else {
							const filterQueueInfo = this.queueList.find(
								queueInfo => queueInfo.adapterId === componentTargetInfo.pk.componentTargetId
							);

							const filterOldQueueInfo = oldQueueList.find(
								oldQueueInfo => oldQueueInfo.adapterId == componentTargetInfo.pk.componentTargetId
							);

							const currInstanceList = filterQueueInfo.instanceList;
							const oldInstanceList = filterOldQueueInfo.instanceList;

							const deleteInstanceList = oldInstanceList.filter(
								oldInstanceInfo =>
									-1 === currInstanceList.map(instanceInfo => instanceInfo.pk.instanceId).indexOf(oldInstanceInfo.pk.instanceId)
							);

							const addInstanceList = currInstanceList.filter(
								instanceInfo =>
									-1 === oldInstanceList.map(oldInstanceInfo => oldInstanceInfo.pk.instanceId).indexOf(instanceInfo.pk.instanceId)
							);

							if (0 < deleteInstanceList.length)
								deleteInstanceList.forEach(instanceObj => {
									if (chart.deleteTarget) chart.deleteTarget(instanceObj.pk.instanceId);
								});

							if (0 < addInstanceList.length)
								addInstanceList.forEach(instanceObj => {
									if (chart.addTarget)
										chart.addTarget({ targetId: instanceObj.pk.instanceId, targetName: instanceObj.pk.instanceId });
								});

							if (0 < deleteInstanceList.length || 0 < addInstanceList.length) {
								isChangeFlag = true;

								if (chart.draw) chart.draw();
							}
						}
					});
				} else if (`THREAD` === component.targetType) {
					if (0 < deleteInstanceList.length)
						deleteInstanceList
							.filter(instance => `T` === instance.instanceType)
							.forEach(instanceInfo => {
								if (chart.deleteTarget) chart.deleteTarget(instanceInfo.instanceId);
							});

					if (0 < addInstanceList.length)
						addInstanceList
							.filter(instance => 'T' === instance.instanceType)
							.forEach(instanceInfo => {
								if (chart.addTarget) chart.addTarget({ targetId: instanceInfo.instanceId, targetName: instanceInfo.instanceId });
							});

					if (0 < deleteInstanceList.length || 0 < addInstanceList.length) {
						isChangeFlag = true;

						if (chart.draw) chart.draw();
					}
				} else if (`EXTERNALLINE` === component.targetType) {
					deleteExternalLineList.forEach(externalInfo => {
						if (chart.deleteTarget) chart.deleteTarget(externalInfo.externalLineId);
					});

					addExternalLineList.forEach(externalInfo => {
						if (chart.addTarget) chart.addTarget({ targetId: externalInfo.externalLineId, targetName: externalInfo.externalLineName });
					});

					if (0 < addExternalLineList.length) {
						if (chart.draw) chart.draw();
					}
				}
			}

			if (isChangeFlag) {
				this.instanceList.forEach(instanceInfo => {
					instanceInfo.isFiltered = false;
				});

				dashboardViewBody.filterTargetAfter();

				if (this.webSocket) this.webSocket.send(this.getWebsocketSendParam());
			}

			oldInstanceList.splice(0, oldInstanceList.length);
			oldAdapterList.splice(0, oldAdapterList.length);
			oldConnectorList.splice(0, oldConnectorList.length);
			oldQueueList.splice(0, oldQueueList.length);
			oldExternalLineList.splice(0, oldExternalLineList.length);

			oldInstanceList = null;
			oldAdapterList = null;
			oldConnectorList = null;
			oldQueueList = null;
			oldExternalLineList = null;
		},
		validationDashConfigModal({ containerName, containerWidth, containerHeight }) {
			if (!containerName || 0 === containerName.trim().length) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.enter.name`)
				});
				return false;
			}

			if (!containerWidth || !containerHeight || 0 === String(containerWidth).trim().length || 0 === String(containerHeight).trim().length) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.enter.resolution`)
				});
				return false;
			}

			if (
				0 ===
					String(containerWidth)
						.replace(/[^0-9]/g, '')
						.trim().length ||
				0 ===
					String(containerHeight)
						.replace(/[^0-9]/g, '')
						.trim().length
			) {
				this.$alert({
					type: `warn`,
					message: this.$t(`dashboard.enter.number`)
				});
				return false;
			}

			return true;
		},
		updateContainer() {
			const selectedContainerInfo = cloneDeep(this.selectedContainerInfo);

			const selectedContainerUserInfo = selectedContainerInfo.monitorContainerUsers.find(
				monitorContainerUser => monitorContainerUser.pk.containerUserId === this.userInfo.userId
			);

			selectedContainerUserInfo.containerOptions = JSON.stringify(selectedContainerUserInfo.containerOptions);

			const httpReq = new HttpReq(`/igate/monitoring/dashboard/container.json`);

			httpReq.create(selectedContainerInfo, true);
		}
	},
	beforeDestroy() {
		this.isDestroy = true;
		this.unloadContainer();
	}
};
</script>
