<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.id') }}

							<span class="letterLength" v-if="!(readonly || 'modify' === this.mode)">
								({{ object['threadPoolId'] ? object['threadPoolId'].length : 0 }}/{{ keyMaxLength }})
							</span>
						</span>
						<b class="icon-star"></b>
					</label>
					<div
						:class="`detail-content-${!(readonly || 'modify' === this.mode) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!(readonly || 'modify' === this.mode) ? '8px' : '0px'}` }"
					>
						<input
							type="text"
							v-model="object.threadPoolId"
							:class="{
								'form-control': readonly || 'modify' === this.mode,
								'regExp-text': !(readonly || 'modify' === this.mode),
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly || 'modify' === this.mode"
							@input="inputEvt(`id`, `threadPoolId`, $event)"
							:maxlength="keyMaxLength"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.threadPool.min') }}

							<span class="letterLength" v-if="!readonly">
								({{ null !== object['threadMin'] ? object['threadMin'].toString().trim().length : 0 }}/{{ numMaxLength }})
							</span>
						</span>
					</label>
					<div :class="`detail-content-${!readonly ? 'regExp' : 'common'}`" :style="{ 'padding-left': `${!readonly ? '8px' : '0px'}` }">
						<input
							type="text"
							v-model="object.threadMin"
							:class="{
								'form-control': readonly,
								'regExp-text': !readonly,
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly"
							@input="inputEvt(`num`, `threadMin`, $event)"
							:maxlength="numMaxLength"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.threadPool.max') }}

							<span class="letterLength" v-if="!readonly">
								({{ null !== object['threadMax'] ? object['threadMax'].toString().trim().length : 0 }}/{{ numMaxLength }})
							</span>
						</span>
					</label>
					<div :class="`detail-content-${!readonly ? 'regExp' : 'common'}`" :style="{ 'padding-left': `${!readonly ? '8px' : '0px'}` }">
						<input
							type="text"
							v-model="object.threadMax"
							:class="{
								'form-control': readonly,
								'regExp-text': !readonly,
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly"
							@input="inputEvt(`num`, `threadMax`, $event)"
							:maxlength="numMaxLength"
							@blur="blurEvt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.threadPool.idle') }}

							<span class="letterLength" v-if="!readonly">
								({{ null !== object['threadIdle'] ? object['threadIdle'].toString().trim().length : 0 }}/{{ numMaxLength }})
							</span>
						</span>
					</label>
					<div :class="`detail-content-${!readonly ? 'regExp' : 'common'}`" :style="{ 'padding-left': `${!readonly ? '8px' : '0px'}` }">
						<input
							type="text"
							v-model="object.threadIdle"
							:class="{
								'form-control': readonly,
								'regExp-text': !readonly,
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly"
							@input="inputEvt(`num`, `threadIdle`, $event)"
							:maxlength="numMaxLength"
						/>
					</div>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.threadPool.rejectPolicy') }}
						</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<select v-model="object.rejectPolicy" class="form-control view-disabled" :readonly="readonly">
							<option v-for="option in threadPoolRejectPolicy" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
								{{ option.propertyValue }}
							</option>
						</select>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.threadPool.rejectWarnYn') }}
						</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<select v-model="object.rejectWarnYn" class="form-control view-disabled" :readonly="readonly">
							<option v-for="option in rejectWarnYns" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
								{{ option.propertyValue }}
							</option>
						</select>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.threadPool.threshold') }}

							<span class="letterLength" v-if="!readonly">
								({{ null !== object['threadThreshold'] ? object['threadThreshold'].toString().trim().length : 0 }}/{{ numMaxLength }})
							</span>
						</span>
					</label>
					<div :class="`detail-content-${!readonly ? 'regExp' : 'common'}`" :style="{ 'padding-left': `${!readonly ? '8px' : '0px'}` }">
						<input
							type="text"
							v-model="object.threadThreshold"
							:class="{
								'form-control': readonly,
								'regExp-text': !readonly,
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly"
							@input="inputEvt(`num`, `threadThreshold`, $event)"
							:maxlength="numMaxLength"
						/>
					</div>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.description') }}

							<span class="letterLength" v-if="!readonly">
								({{ object['threadPoolDesc'] ? object['threadPoolDesc'].length : 0 }}/{{ descMaxLength }})
							</span>
						</span>
					</label>
					<div :class="`detail-content-${!readonly ? 'regExp' : 'common'}`" :style="{ 'padding-left': `${!readonly ? '8px' : '0px'}` }">
						<textarea
							v-model="object.threadPoolDesc"
							:class="{
								'form-control': readonly,
								'regExp-text': !readonly,
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly"
							@input="inputEvt(`desc`, `threadPoolDesc`, $event)"
							:maxlength="descMaxLength"
						/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import { HttpReq, getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'BasicInfo',
	props: {
		detailData: Object,
		mode: String
	},
	data() {
		return {
			rejectWarnYns: [],
			threadPoolRejectPolicy: [],

			httpReqList: [
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Yn', orderByKey: true },
					itemName: 'rejectWarnYns'
				},
				{
					url: `/common/property/properties.json`,
					param: { propertyId: 'List.Threadpool.Rejectpolicy', orderByKey: true },
					itemName: 'threadPoolRejectPolicy'
				}
			],

			object: {
				threadPoolId: null,
				threadMin: null,
				threadMax: null,
				rejectWarnYn: null,
				rejectPolicy: null,
				threadIdle: null,
				threadThreshold: null,
				threadPoolDesc: null
			},

			keyMaxLength: null,
			numMaxLength: null,
			descMaxLength: null
		};
	},
	computed: {
		...mapState(['menuList', 'userInfo']),
		...mapGetters(['isUserAdmin']),
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode;
		}
	},
	watch: {
		detailData() {
			//clear data
			for (const key in this.object) {
				this.object[key] = null;
			}

			if ('add' === this.mode) return;

			//set data
			for (const key in this.detailData) {
				const value = this.detailData[key];

				this.object[key] = value;
			}
		}
	},
	async created() {
		this.keyMaxLength = getRegExpInfo(`id`).maxLength;
		this.numMaxLength = getRegExpInfo(`num`).maxLength;
		this.descMaxLength = getRegExpInfo(`desc`).maxLength;

		try {
			for await (const content of this.httpReqList) {
				const result = await new HttpReq(content.url).read(content.param ? content.param : null);

				if ('ok' !== result.data.result) throw result.data;

				this[content.itemName] = result.data.object;
			}
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('head.error.load.data.warn') });
		}
	},
	methods: {
		inputEvt(type, key, e) {
			this.object[key] = e.target.value;
			this.object[key] = this.object[key].replace(new RegExp(getRegExpInfo(type).regExp, `g`), ``);
		},
		blurEvt(e) {
			if (0 === Number(e.target.value) && 0 !== e.target.value.length) {
				this.$alert({ type: 'warn', message: this.$t('igate.threadPool.max.warn') });

				e.target.value = null;
			}
		},
		getData() {
			return this.object;
		}
	}
};
</script>
