<template>
	<div id="panel" ref="panel">
		<header id="panel-header" class="panel-header sub-bar">
			<h2 class="sub-bar-tit"></h2>
		</header>

		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item" v-for="(tabObj, index) in tabList" :key="tabObj.id">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: selectedIdx === index }"
					@click="changeTab(index)"
					v-text="tabObj.label"
				>
				</a>
			</li>
			<li class="nav-item" v-if="toShowResult">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: selectedIdx === tabList.length }"
					@click="changeTab(tabList.length)"
				>
					{{ $t('head.executionResult') }}
				</a>
			</li>
		</ul>
		<!-- tab-pane -->
		<div class="panel-body tab-content" :style="{ height: panelBodyHeight }">
			<div :class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === 0 }">
				<component
					ref="BasicInfo"
					:is="require(`@/views/detail${tabList[0].url}.vue`).default"
					:detailInfo="tabList[0]"
					:detailData="detailData"
					:mode="panelMode"
				/>
			</div>
			<div :class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === 1 }">
				<component
					ref="requestModel"
					:is="require(`@/views/detail${tabList[1].url}.vue`).default"
					:detailInfo="tabList[1]"
					:detailData="inputStructData"
					:mode="panelMode"
				/>
			</div>
			<div :class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === 2 }">
				<component
					ref="responseModel"
					:is="require(`@/views/detail${tabList[2].url}.vue`).default"
					:detailInfo="tabList[2]"
					:detailData="outputStructData"
					:mode="panelMode"
				/>
			</div>
			<DetailResult
				:class="{ 'tab-pane': true, active: selectedIdx === tabList.length && showResultTab }"
				:httpReqResult="httpReqResult"
				v-if="toShowResult"
			/>
		</div>

		<footer id="panel-footer" class="panel-footer sub-bar">
			<div class="ml-auto" v-if="'add' === panelMode || 'clone' === panelMode">
				<a href="javascript:void(0);" class="btn btn-primary" @click="saveInfo" v-if="insertBtn && editor">
					<i class="icon-plus"></i>{{ $t('head.insert') }}
				</a>
			</div>
		</footer>
	</div>
</template>
<script>
import Detail from '@/components/detail/Detail.vue';
import { HttpReq, SearchToggleEventBus, WindowResizeEventBus, SideMenuToggleEventBus, getNumFromStr } from '@/utils/common.js';
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'Detail',
	extends: Detail,
	data() {
		return {
			panelMode: 'add',
			inputStructData: {},
			outputStructData: {},
			panelBodyHeight: null
		};
	},
	props: {
		mode: {
			type: String,
			default: 'detail'
		},
		pk: Array,
		buttonList: Array,
		tabList: Array,
		info: Object,
		cudUrl: String,
		viewer: Boolean,
		editor: Boolean,
		recordData: Object
	},
	watch: {
		cudUrl: {
			immediate: true,
			handler() {
				this.httpReq = new HttpReq(`${this.cudUrl}`);
			}
		},
		recordData: {
			immediate: true,
			handler() {
				if (isEmpty(this.recordData)) return;
				this.detailData = {};
				this.inputStructData = this.initTreeData(this.recordData.requestRecordObject);
				this.outputStructData = this.initTreeData(this.recordData.responseRecordObject);
			}
		},
		selectedIdx() {
			if (1 === this.selectedIdx) {
				this.$refs.requestModel.getDataList();
			} else if (2 === this.selectedIdx) {
				this.$refs.responseModel.getDataList();
			}
		}
	},
	mounted() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		SideMenuToggleEventBus.$on('MainResize', this.resize);

		SearchToggleEventBus.$on('MainResize', this.resize);

		this.resize();
	},
	methods: {
		resize() {
			const ct = document.getElementById('ct');
			const ctComputedStyle = getComputedStyle(ct);
			const ctPaddingTop = getNumFromStr(ctComputedStyle.getPropertyValue('padding-top'));
			const ctPaddingBottom = getNumFromStr(ctComputedStyle.getPropertyValue('padding-bottom'));

			const ctHeaderHeight = document.querySelector(`.ct-header`).offsetHeight;
			const panelHeaderHeight = document.querySelector(`.panel-header`).offsetHeight;
			const navHeight = document.querySelector(`#panel`).querySelector(`.nav`).offsetHeight;
			const panelFooterHeight = document.querySelector(`.panel-footer`).offsetHeight;

			this.panelBodyHeight = `${
				window.innerHeight - ctPaddingTop - ctPaddingBottom - ctHeaderHeight - panelHeaderHeight - navHeight - panelFooterHeight
			}px`;
		},
		changeTab(idx) {
			for (const key in this.$refs) {
				if (!this.$refs[key].resize) continue;

				this.$nextTick(() => {
					this.$refs[key].resize();
				});
			}

			this.selectedIdx = idx;
		},
		async saveInfo() {
			this.tabAllData = merge(this.tabAllData, this.$refs.BasicInfo.getData());

			const result = await this.httpReq.update(this.tabAllData, true);

			this.setData(result);
		},
		async setData(result) {
			const resultState = 'ok' === result.data.result;

			this.httpReqResult = {
				state: resultState,
				content: resultState ? result.data.response : result.data.error
			};

			this.selectedIdx = this.tabList.length;
			this.showResultTab = true;

			if (!resultState) {
				this.tabAllData = {};
				return;
			}

			this.panelMode = 'add';

			this.$alert({
				type: 'normal',
				isXSSMode: true,
				message: `${this.$t('head.insert.notice')}<hr>${this.$t('igate.sap.insert.msg')}`
			});

			this.$emit('changeDetailView');
		},
		initTreeData(record) {
			const treeData = {
				fieldId: record.recordId,
				fieldName: record.recordId,
				fieldType: record.recordType,
				_children: []
			};

			record.fields.forEach(field => {
				const subTreeData = {
					fieldId: field.pk.fieldId,
					fieldName: field.fieldName,
					fieldType: field.fieldType
				};

				if (field.recordObject) {
					const subRecord = this.initTreeData(field.recordObject);
					subTreeData._children = [...subRecord._children];
				}

				treeData._children.push(subTreeData);
			});

			return treeData;
		}
	}
};
</script>
