<template>
	<div class="basic">
		<Search :list="searchList" @search="search" ref="search" />
		<Main
			:detailButtonList="detailButtonList"
			:detailTabList="detailTabList"
			:detailInfo="detailInfo"
			:searchObj="searchObj"
			:cudUrl="cudUrl"
			:viewer="viewer"
			:editor="editor"
			ref="main"
		/>
	</div>
</template>
<script>
import Basic from '@/components/main/Basic.vue';
import Main from '@/views/main/sapService/Main.vue';
import { getPrivilege } from '@/utils/common.js';

export default {
	name: 'SapService',
	extends: Basic,
	components: {
		Main
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		this.initNewTabSearchObj();
	},
	methods: {
		search(searchObj) {
			this.searchObj = searchObj;
		}
	},
	data() {
		return {
			cudUrl: '/igate/sapService/regist.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchObj: {},
			searchList: [
				{
					type: 'text',
					vModel: 'ashost',
					label: this.$t('igate.sap.ashost'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'sysnr',
					label: this.$t('igate.sap.sysnr'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'client',
					label: this.$t('igate.sap.client'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'user',
					label: this.$t('igate.sap.user'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'passwd',
					label: this.$t('igate.sap.passwd'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'gwhost',
					label: this.$t('igate.sap.gwhost'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'gwserv',
					label: this.$t('igate.sap.gwserv'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'lang',
					label: this.$t('igate.sap.lang'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'functionName',
					label: this.$t('igate.sap.functionName'),
					placeholder: this.$t('head.searchData')
				}
			],

			//Detail
			detailPk: ['serviceId'],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [{ id: 'insert', isUse: true }],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/sapService/BasicInfo'
				},
				{
					type: 'grid',
					label: this.$t('igate.service.record.request'),
					url: '/sapService/DetailGrid',
					options: {
						rowHeight: 30,
						minRowHeight: 30,
						treeColumnOptions: {
							name: 'fieldId'
						},
						columns: [
							{
								name: 'fieldId',
								header: `${this.$t('head.field')} ${this.$t('head.id')}`,
								width: '30%'
							},
							{
								name: 'fieldType',
								header: `${this.$t('head.field')} ${this.$t('common.type')}`,
								width: '30%',
								formatter: value => {
									const fieldTypeStrObj = {
										B: this.$t('igate.fieldMeta.fieldType.byte'),
										S: this.$t('igate.fieldMeta.fieldType.short'),
										I: this.$t('igate.fieldMeta.fieldType.int'),
										L: this.$t('igate.fieldMeta.fieldType.long'),
										F: this.$t('igate.fieldMeta.fieldType.float'),
										D: this.$t('igate.fieldMeta.fieldType.double'),
										N: this.$t('igate.fieldMeta.fieldType.numeric'),
										p: this.$t('igate.fieldMeta.fieldType.timeStamp'),
										b: this.$t('igate.fieldMeta.fieldType.boolean'),
										v: this.$t('igate.fieldMeta.fieldType.individual'),
										A: this.$t('igate.fieldMeta.fieldType.raw'),
										P: this.$t('igate.fieldMeta.fieldType.packedDecimal'),
										R: this.$t('igate.fieldMeta.fieldType.record'),
										T: this.$t('igate.fieldMeta.fieldType.string')
									};

									return fieldTypeStrObj[value.row.fieldType];
								}
							},
							{
								name: 'fieldName',
								header: `${this.$t('head.field')} ${this.$t('head.name')}`,
								width: '35%'
							}
						]
					}
				},
				{
					type: 'grid',
					label: this.$t('igate.service.record.response'),
					url: '/sapService/DetailGrid',
					options: {
						rowHeight: 30,
						minRowHeight: 30,
						treeColumnOptions: {
							name: 'fieldId'
						},
						columns: [
							{
								name: 'fieldId',
								header: `${this.$t('head.field')} ${this.$t('head.id')}`,
								width: '30%'
							},
							{
								name: 'fieldType',
								header: `${this.$t('head.field')} ${this.$t('common.type')}`,
								width: '30%',
								formatter: value => {
									const nodeTypeStrObj = {
										B: this.$t('igate.fieldMeta.fieldType.byte'),
										S: this.$t('igate.fieldMeta.fieldType.short'),
										I: this.$t('igate.fieldMeta.fieldType.int'),
										L: this.$t('igate.fieldMeta.fieldType.long'),
										F: this.$t('igate.fieldMeta.fieldType.float'),
										D: this.$t('igate.fieldMeta.fieldType.double'),
										N: this.$t('igate.fieldMeta.fieldType.numeric'),
										p: this.$t('igate.fieldMeta.fieldType.timeStamp'),
										b: this.$t('igate.fieldMeta.fieldType.boolean'),
										v: this.$t('igate.fieldMeta.fieldType.individual'),
										A: this.$t('igate.fieldMeta.fieldType.raw'),
										P: this.$t('igate.fieldMeta.fieldType.packedDecimal'),
										R: this.$t('igate.fieldMeta.fieldType.record'),
										T: this.$t('igate.fieldMeta.fieldType.string')
									};

									return nodeTypeStrObj[value.row.fieldType];
								}
							},
							{
								name: 'fieldName',
								header: `${this.$t('head.field')} ${this.$t('head.name')}`,
								width: '35%'
							}
						]
					}
				}
			]
		};
	}
};
</script>
