<template>
	<div class="basic">
		<Search :list="searchList" @setAddDetailData="setAddDetailData" ref="search" />
		<Main
			:detailButtonList="detailButtonList"
			:detailTabList="detailTabList"
			:detailInfo="detailInfo"
			:addDetailData="addDetailData"
			:cudUrl="cudUrl"
			:viewer="viewer"
			:editor="editor"
			@initSearchArea="initSearchArea"
			ref="main"
		/>
	</div>
</template>
<script>
import Basic from '@/components/main/Basic.vue';
import Search from '@/views/main/webService/Search.vue';
import Main from '@/views/main/webService/Main.vue';
import { getPrivilege } from '@/utils/common.js';

export default {
	name: 'WebSerivce',
	extends: Basic,
	components: {
		Search,
		Main
	},
	created() {
		const privileges = getPrivilege('Service');

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		this.initNewTabSearchObj();
	},
	methods: {
		initSearchArea() {
			this.$refs.search.setSearchListVal();
		},
		setAddDetailData(addDetailData) {
			this.addDetailData = addDetailData;
		}
	},
	data() {
		return {
			cudUrl: '/igate/webservice/regist.json',
			addDetailData: {},

			//auth
			viewer: null,
			editor: null,

			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [{ id: 'insert', isUse: true }],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/webService/BasicInfo'
				},
				{
					type: 'grid',
					label: this.$t('igate.service.record.request'),
					url: '/webService/DetailGrid',
					options: {
						rowHeight: 30,
						minRowHeight: 30,
						treeColumnOptions: {
							name: 'nodeId'
						},
						columns: [
							{
								name: 'nodeId',
								header: `${this.$t('head.node')} ${this.$t('head.id')}`
							},
							{
								name: 'nodeType',
								header: `${this.$t('head.node')} ${this.$t('common.type')}`,
								formatter: value => {
									const nodeTypeStrObj = {
										B: this.$t('igate.fieldMeta.fieldType.byte'),
										S: this.$t('igate.fieldMeta.fieldType.short'),
										I: this.$t('igate.fieldMeta.fieldType.int'),
										L: this.$t('igate.fieldMeta.fieldType.long'),
										F: this.$t('igate.fieldMeta.fieldType.float'),
										D: this.$t('igate.fieldMeta.fieldType.double'),
										N: this.$t('igate.fieldMeta.fieldType.numeric'),
										p: this.$t('igate.fieldMeta.fieldType.timeStamp'),
										b: this.$t('igate.fieldMeta.fieldType.boolean'),
										v: this.$t('igate.fieldMeta.fieldType.individual'),
										A: this.$t('igate.fieldMeta.fieldType.raw'),
										P: this.$t('igate.fieldMeta.fieldType.packedDecimal'),
										R: this.$t('igate.fieldMeta.fieldType.record'),
										T: this.$t('igate.fieldMeta.fieldType.string')
									};

									return nodeTypeStrObj[value.row.nodeType];
								}
							},
							{
								name: 'nodeName',
								header: `${this.$t('head.node')} ${this.$t('head.name')}`
							}
						]
					}
				},
				{
					type: 'grid',
					label: this.$t('igate.service.record.response'),
					url: '/webService/DetailGrid',
					options: {
						rowHeight: 30,
						minRowHeight: 30,
						treeColumnOptions: {
							name: 'nodeId'
						},
						columns: [
							{
								name: 'nodeId',
								header: `${this.$t('head.node')} ${this.$t('head.id')}`
							},
							{
								name: 'nodeType',
								header: `${this.$t('head.node')} ${this.$t('common.type')}`,
								formatter: value => {
									const nodeTypeStrObj = {
										B: this.$t('igate.fieldMeta.fieldType.byte'),
										S: this.$t('igate.fieldMeta.fieldType.short'),
										I: this.$t('igate.fieldMeta.fieldType.int'),
										L: this.$t('igate.fieldMeta.fieldType.long'),
										F: this.$t('igate.fieldMeta.fieldType.float'),
										D: this.$t('igate.fieldMeta.fieldType.double'),
										N: this.$t('igate.fieldMeta.fieldType.numeric'),
										p: this.$t('igate.fieldMeta.fieldType.timeStamp'),
										b: this.$t('igate.fieldMeta.fieldType.boolean'),
										v: this.$t('igate.fieldMeta.fieldType.individual'),
										A: this.$t('igate.fieldMeta.fieldType.raw'),
										P: this.$t('igate.fieldMeta.fieldType.packedDecimal'),
										R: this.$t('igate.fieldMeta.fieldType.record'),
										T: this.$t('igate.fieldMeta.fieldType.string')
									};

									return nodeTypeStrObj[value.row.nodeType];
								}
							},
							{
								name: 'nodeName',
								header: `${this.$t('head.node')} ${this.$t('head.name')}`
							}
						]
					}
				}
			]
		};
	}
};
</script>
<style lang="scss">
.basic {
	overflow-y: auto;
	overflow-x: hidden;
	.ct-content {
		overflow-y: auto;
		#panel {
			height: calc(100% - 2.5rem);
			.panel-body {
				height: calc(100% - 6.5rem);
			}
			.panel-footer {
				margin: 0.5rem;
			}
		}
	}
}
</style>
