<template>
	<div id="serviceModal">
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item" v-for="(tabInfo, index) in detailTabList" :key="`menu_${index}`">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: index === selectedIdx }"
					@click="changeTab(index)"
					v-text="tabInfo.label"
				>
				</a>
			</li>
		</ul>
		<div class="panel-body tab-content">
			<div :class="{ 'tab-pane': true, active: index === selectedIdx }" v-for="(tabInfo, index) in detailTabList" :key="`content_${index}`">
				<component
					ref="component"
					:is="require(`@/views/detail${tabInfo.url}.vue`).default"
					:detailInfo="tabInfo"
					:detailData="detailData"
					:mode="mode"
					v-if="'bundle' === tabInfo.type"
				/>
				<DetailBasic ref="component" :detailInfo="tabInfo" :detailData="detailData" :mode="mode" v-else-if="'basic' === tabInfo.type" />
			</div>
		</div>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import merge from 'lodash/merge';
import DetailBasic from '@/components/detail/DetailBasic';
import { changeDateFormat } from '@/utils/common.js';

export default {
	name: `ServiceModal`,
	props: {
		serviceObject: Object,
		mode: String
	},
	components: {
		DetailBasic
	},
	data() {
		return {
			detailData: {},
			selectedIdx: 0,
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/service/BasicInfo'
				},
				{
					type: 'bundle',
					url: '/service/ExtendInfo',
					label: this.$t('head.extensionInfo')
				},
				{
					type: 'basic',
					label: this.$t('head.useInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'updateVersion',
									label: 'Version',
									readonly: true,
									val: '0'
								},
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('igate.notice.writer'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('igate.notice.created.date'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			]
		};
	},
	mounted() {
		this.detailData = cloneDeep(this.serviceObject);
	},
	methods: {
		changeTab(index) {
			this.selectedIdx = index;
		},
		getData() {
			let tabAllData = {};

			this.$refs.component.forEach(component => {
				if (!component.getData) return;
				tabAllData = merge(tabAllData, component.getData());
			});

			return tabAllData;
		}
	}
};
</script>
