<template>
	<div id="panel" ref="panel">
		<header id="panel-header" class="panel-header sub-bar">
			<h2 class="sub-bar-tit"></h2>
		</header>

		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item" v-for="(tabObj, index) in tabList" :key="tabObj.id">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: selectedIdx === index }"
					@click="changeTab(index)"
					v-text="tabObj.label"
				>
				</a>
			</li>
			<li class="nav-item" v-if="toShowResult">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: selectedIdx === tabList.length }"
					@click="changeTab(tabList.length)"
				>
					{{ $t('head.executionResult') }}
				</a>
			</li>
		</ul>
		<!-- tab-pane -->
		<div class="panel-body tab-content">
			<div :class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === 0 }">
				<component
					ref="BasicInfo"
					:is="require(`@/views/detail${tabList[0].url}.vue`).default"
					:detailInfo="tabList[0]"
					:detailData="detailData"
					:mode="panelMode"
				/>
			</div>
			<div :class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === 1 }">
				<component
					ref="requestModel"
					:is="require(`@/views/detail${tabList[1].url}.vue`).default"
					:detailInfo="tabList[1]"
					:detailData="inputStructData"
					:mode="panelMode"
				/>
			</div>
			<div :class="{ [panelMode]: true, 'tab-pane': true, active: selectedIdx === 2 }">
				<component
					ref="responseModel"
					:is="require(`@/views/detail${tabList[2].url}.vue`).default"
					:detailInfo="tabList[2]"
					:detailData="outputStructData"
					:mode="panelMode"
				/>
			</div>
			<DetailResult
				:class="{ 'tab-pane': true, active: selectedIdx === tabList.length && showResultTab }"
				:httpReqResult="httpReqResult"
				v-if="toShowResult"
			/>
		</div>

		<footer id="panel-footer" class="panel-footer sub-bar">
			<div class="ml-auto" v-if="'add' === panelMode || 'clone' === panelMode">
				<a href="javascript:void(0);" class="btn btn-primary" @click="saveInfo" v-if="insertBtn && editor">
					<i class="icon-plus"></i>{{ $t('head.insert') }}
				</a>
			</div>
		</footer>
	</div>
</template>

<script>
import Detail from '@/components/detail/Detail.vue';
import { HttpReq } from '@/utils/common';
import merge from 'lodash/merge';
import cloneDeep from 'lodash/cloneDeep';

export default {
	name: 'Detail',
	extends: Detail,
	props: {
		buttonList: Array,
		tabList: Array,
		info: Object,
		cudUrl: String,
		viewer: Boolean,
		editor: Boolean,
		additionalData: Object
	},
	data() {
		return {
			panelMode: 'add',
			searchData: {},
			inputStructData: {},
			outputStructData: {}
		};
	},
	watch: {
		cudUrl: {
			immediate: true,
			handler() {
				this.httpReq = new HttpReq(`${this.cudUrl}`);
			}
		},
		additionalData: {
			immediate: true,
			deep: true,
			handler() {
				this.searchData = cloneDeep(this.additionalData.searchData);
				this.inputStructData = this.initTreeData(this.additionalData.structData.input.record);
				this.outputStructData = this.initTreeData(this.additionalData.structData.output.record);
			}
		},
		selectedIdx() {
			if (1 === this.selectedIdx) {
				this.$refs.requestModel.getDataList();
			} else if (2 === this.selectedIdx) {
				this.$refs.responseModel.getDataList();
			}
		}
	},
	methods: {
		changeTab(idx) {
			for (const key in this.$refs) {
				if (!this.$refs[key].resize) continue;

				this.$nextTick(() => {
					this.$refs[key].resize();
				});
			}

			this.selectedIdx = idx;
		},
		async saveInfo() {
			this.tabAllData = merge(this.tabAllData, this.$refs.BasicInfo.getData());
			this.tabAllData = merge(this.tabAllData, this.searchData);

			const result = await this.httpReq.update(merge({ _method: 'Post' }, this.tabAllData), true);

			this.setData(result);
		},
		async setData(result) {
			const resultState = 'ok' === result.data.result;

			this.httpReqResult = {
				state: resultState,
				content: resultState ? result.data.response : result.data.error
			};

			this.selectedIdx = this.tabList.length;
			this.showResultTab = true;

			if (!resultState) {
				this.tabAllData = {};
				return;
			}

			this.panelMode = 'add';

			this.$alert({
				type: 'normal',
				isXSSMode: true,
				message: `${this.$t('head.insert.notice')}<hr>${this.$t('igate.webService.insert.msg')}`
			});

			this.$emit('changeDetailView');
		},
		initTreeData(record) {
			const treeData = {
				nodeId: record.recordId,
				nodeName: record.recordId,
				nodeType: record.recordType,
				_children: []
			};

			record.fields.forEach(field => {
				const subTreeData = {
					nodeId: field.pk.fieldId,
					nodeName: field.fieldName,
					nodeType: field.fieldType
				};

				if (field.recordObject) {
					const subRecord = this.initTreeData(field.recordObject);
					subTreeData._children = [...subRecord._children];
				}

				treeData._children.push(subTreeData);
			});

			return treeData;
		}
	}
};
</script>
