<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/calendar/Detail.vue';
import { getPrivilege } from '@/utils/common.js';

export default {
	name: 'Adapter',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			cudUrl: '/igate/calendar/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'calendarId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'calendarName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'text',
					vModel: 'calendarDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'add', isUse: true },
				{ id: `excelImport`, isUse: true, importUrl: `/igate/calendar/importList.json` },
				{ id: `excelExport`, isUse: true, exportUrl: `/igate/calendar/exportList.json` }
			],

			// Grid
			gridInfo: {
				url: '/igate/calendar/search.json',
				totalCntUrl: '/igate/calendar/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'calendarId',
							header: this.$t('head.id')
						},
						{
							name: 'calendarName',
							header: this.$t('head.name')
						},
						{
							name: 'calendarDesc',
							header: this.$t('head.description')
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['calendarId', 'holidayDate'],

			controlUrl: '/igate/calendar/control.json',
			controlParams: detailData => {
				return {
					calendarId: detailData.calendarId,
					holidayDate: detailData.holidayDate
				};
			},

			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'dump', isUse: true }
			],
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('head.basic.info'),
					url: '/calendar/Detail',
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'calendarId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									isPK: true,
									regExpType: 'id'
								},
								{
									type: 'text',
									vModel: 'calendarName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'select',
									vModel: 'saturdayYn',
									isPK: true,
									label: this.$t('igate.calendar.saturday'),
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Yn',
											orderByKey: true
										},
										optionListName: 'calendarSaturday',
										optionFor: 'option in calendarSaturday',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'select',
									vModel: 'sundayYn',
									isPK: true,
									label: this.$t('igate.calendar.sunday'),
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Yn',
											orderByKey: true
										},
										optionListName: 'calendarSunday',
										optionFor: 'option in calendarSunday',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								}
							],
							[],
							[]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'calendarDesc',
									label: this.$t('head.description'),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc'
								}
							]
						]
					]
				},
				{
					type: 'bundle',
					label: this.$t('igate.calendar.date.detail'),
					url: '/calendar/CalendarHoliday'
				}
			]
		};
	}
};
</script>
