<template>
	<div :class="classObj" :style="styleObj">
		<Search :list="searchList" @search="search" ref="search" />
		<Main
			:buttonList="buttonList"
			:gridInfo="gridInfo"
			:gridSearchObj="gridSearchObj"
			:gridType="gridType"
			:viewer="viewer"
			:editor="editor"
			@initialize="initialize"
			@newTab="newTab"
			@fileMake="fileMake"
			@fileImport="fileImport"
			ref="main"
		/>
		<Detail
			:mode="detailMode"
			:viewer="viewer"
			:editor="editor"
			v-show="isDetailView"
			:selectedDetailData="detailData"
			:buttonList="[]"
			@detailHorizonResize="detailHorizonResize"
			ref="detail"
		/>
	</div>
</template>
<script>
import { mapState } from 'vuex';
import { getPrivilege, HttpReq } from '@/utils/common.js';
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/migration/interface/Detail.vue';

export default {
	name: `migrationInterface`,
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		const migrationEditor = this.userInfo.totalUserPrivileges.find(privilege => `MigrationEditor` === privilege.privilegeId) ? true : false;

		this.viewer = privileges.viewer;
		this.editor = `undefined` === typeof migrationEditor ? false : migrationEditor && privileges.editor;

		this.gridInfo.options.rowHeaders = this.editor ? ['checkbox'] : [];
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			searchList: [
				{
					type: 'text',
					vModel: 'interfaceId',
					label: this.$t(`head.id`),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'interfaceName',
					label: this.$t(`head.name`),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'modal',
					vModel: 'adapterId',
					label: `${this.$t(`igate.adapter`)} ${this.$t(`head.id`)}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.adapter'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'adapterId',
									label: this.$t(`head.id`),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'adapterName',
									label: this.$t(`head.name`),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'text',
									vModel: 'adapterDesc',
									label: this.$t(`head.description`),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/adapter/searchPopup.json',
							totalCntUrl: '/igate/adapter/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'adapterId',
										header: this.$t(`head.id`),
										align: 'left'
									},
									{
										name: 'adapterName',
										header: this.$t(`head.name`),
										align: 'left'
									},
									{
										name: 'requestStructure',
										header: this.$t('igate.adapter.structure.request'),
										align: 'left'
									},
									{
										name: 'responseStructure',
										header: this.$t('igate.adapter.structure.response'),
										align: 'left'
									},
									{
										name: 'adapterDesc',
										header: this.$t(`head.description`),
										align: 'left'
									}
								]
							}
						},
						rowClickedCallback: info => info.adapterId
					}
				},
				{
					type: 'select',
					vModel: 'interfaceType',
					label: this.$t(`common.type`),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Interface.InterfaceType',
							orderByKey: true
						},
						optionListName: 'interfaceTypes',
						optionFor: 'option in interfaceTypes',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'select',
					vModel: 'scheduleType',
					label: `${this.$t('head.schedule')} ${this.$t(`common.type`)}`,
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Interface.ScheduleType',
							orderByKey: true
						},
						optionListName: 'scheduleTypes',
						optionFor: 'option in scheduleTypes',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'select',
					vModel: 'usedYn',
					label: this.$t('igate.interface.usedYn'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Yn',
							orderByKey: true
						},
						optionListName: 'useYnList',
						optionFor: 'option in useYnList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'text',
					vModel: 'interfaceGroup',
					label: this.$t(`head.group`),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'dataList',
					vModel: 'privilegeId',
					label: `${this.$t(`common.privilege`)} ${this.$t(`head.id`)}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					optionInfo: {
						url: '/common/privilege/list.json',
						params: {
							privilegeType: `b`
						},
						optionListName: 'privilegeTypeList',
						optionFor: 'option in privilegeTypeList',
						optionValue: 'option.privilegeId',
						optionText: 'option.privilegeId'
					}
				},
				{
					type: 'text',
					vModel: 'interfaceDesc',
					label: this.$t(`head.description`),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				}
			],
			buttonList: [
				{ id: 'fileImport', isUse: true },
				{ id: 'fileMake', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],
			gridInfo: {
				url: '/igate/migration/interface/list.json',
				paging: {
					isUse: false
				},
				options: {
					rowHeaders: null,
					columns: [
						{
							name: 'interfaceId',
							header: this.$t(`head.id`),
							align: 'left',
							width: '15%'
						},
						{
							name: 'interfaceName',
							header: this.$t(`head.name`),
							align: 'left',
							width: '20%'
						},
						{
							name: 'adapterId',
							header: `${this.$t(`igate.adapter`)} ${this.$t(`head.id`)}`,
							align: 'left',
							width: '15%'
						},
						{
							name: 'interfaceType',
							header: this.$t(`common.type`),
							align: 'center',
							width: '10%',
							formatter: ({ row }) => (`Online` === row.interfaceType ? this.$t(`head.online`) : this.$t(`head.file`))
						},
						{
							name: 'scheduleType',
							header: `${this.$t('head.schedule')} ${this.$t(`common.type`)}`,
							align: 'center',
							width: '10%',
							formatter: ({ row }) =>
								`B` === row.scheduleType
									? `Batched`
									: `D` === row.scheduleType
									? `Deferred`
									: `O` === row.scheduleType
									? `Online`
									: `T` === row.scheduleType
									? `Triggered`
									: ``
						},
						{
							name: 'interfaceGroup',
							header: this.$t(`head.group`),
							align: 'left',
							width: '15%'
						},

						{
							name: 'privilegeId',
							header: `${this.$t(`common.privilege`)} ${this.$t(`head.id`)}`,
							align: 'left',
							width: '15%'
						},
						{
							name: 'interfaceDesc',
							header: this.$t(`head.description`),
							align: 'left',
							width: '20%'
						}
					]
				}
			},
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.make'),
					url: `/migration/Make`
				},
				{
					type: 'bundle',
					label: this.$t('head.import'),
					url: `/migration/Import`
				}
			],
			isDetailView: false,
			detailData: null,
			detailMode: null,
			viewer: false,
			editor: false
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		initDetail() {
			return false;
		},
		async fileMake({ gridObj }) {
			if (!gridObj) return;

			const checkedRows = gridObj.invoke(`getCheckedRows`);

			if (0 === checkedRows.length) {
				this.$alert({
					type: `warn`,
					message: this.$t(`igate.migration.selectError`)
				});
			}

			const targetIds = checkedRows.map(info => info.interfaceId);

			const result = await new HttpReq(`/igate/migration/interface/make.json`).update({ targetIds }, true);

			if (`ok` !== result.data.result) return;

			this.$alert({
				type: `normal`,
				message: this.$t(`igate.migration.make`)
			});

			this.openPanel(`make`, {
				...result.data.object,
				targetIds
			});
		},
		fileImport() {
			this.openPanel(`import`);
		},
		openPanel(mode, detailData) {
			if (this.isDetailView) {
				this.isDetailView = false;
				this.detailData = null;
				this.detailMode = null;
			}

			this.$nextTick(() => {
				this.isDetailView = true;
				this.detailData = detailData;
				this.detailMode = mode;
			});
		}
	}
};
</script>
