<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ $t('igate.service') }} {{ $t('head.id') }}

							<span class="letterLength"> ({{ object['serviceId'] ? object['serviceId'].length : 0 }}/{{ keyMaxLength }}) </span>
						</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-regExp" style="padding-left: 8px">
						<input
							type="text"
							v-model="object.serviceId"
							class="regExp-text view-disabled dataKey"
							:readonly="readonly"
							@input="inputEvt(`id`, `serviceId`, $event)"
							:maxlength="keyMaxLength"
							@change="changeServiceId"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service') }} {{ $t('common.type') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.serviceType" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in serviceTypeList" :key="option.pk.propertyKey" :value="option.pk.propertyKey">
							{{ option.propertyValue }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ $t('igate.service') }} {{ $t('head.name') }}

							<span class="letterLength"> ({{ object['serviceName'] ? object['serviceName'].length : 0 }}/{{ nameMaxLength }}) </span>
						</span>
					</label>
					<div class="detail-content-regExp" style="padding-left: 8px">
						<input
							type="text"
							v-model="object.serviceName"
							class="regExp-text view-disabled"
							:readonly="readonly"
							@input="inputEvt(`name`, `serviceName`, $event)"
							:maxlength="nameMaxLength"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ $t('igate.service') }} {{ $t('head.description') }}

							<span class="letterLength"> ({{ object['serviceDesc'] ? object['serviceDesc'].length : 0 }}/{{ descMaxLength }}) </span>
						</span>
					</label>
					<div class="detail-content-regExp" style="padding-left: 8px">
						<input
							type="text"
							v-model="object.serviceDesc"
							class="regExp-text view-disabled"
							:readonly="readonly"
							@input="inputEvt(`desc`, `serviceDesc`, $event)"
							:maxlength="descMaxLength"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.metaDomain') }}</span>
					</label>
					<select v-model="object.metaDomain" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in metaDomainList" :key="option" :value="option">
							{{ option }}
						</option>
					</select>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.record.request') }}</span>
					</label>
					<input type="text" v-model="object.requestRecordId" class="form-control view-disabled dataKey" :readonly="true" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.record.response') }}</span>
					</label>
					<input type="text" v-model="object.responseRecordId" class="form-control view-disabled" :readonly="true" />
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.group') }}</span>
						<span class="letterLength"> ({{ serviceGroupEdit ? serviceGroupEdit.length : 0 }}/{{ defaultMaxLength }}) </span>
						<b class="icon-star"></b>
					</label>
					<div class="input-group">
						<select v-model="serviceGroupSelect" class="form-control view-disabled" :readonly="readonly">
							<option v-for="option in serviceGroupList" :key="option" :value="option">
								{{ option }}
							</option>
						</select>
						<input
							type="text"
							v-model="serviceGroupEdit"
							class="form-control view-disabled"
							:readonly="readonly"
							@input="serviceGroupEditEvt"
							:maxlength="defaultMaxLength"
							style="margin-left: 5px"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.service.execute.privilege') }}</span>
						<b class="icon-star"></b>
					</label>
					<select v-model="object.privilegeId" class="form-control view-disabled" :readonly="readonly">
						<option v-for="option in executePrivilegeIdList" :key="option" :value="option">
							{{ option }}
						</option>
					</select>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter') }} {{ $t('head.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="input-group">
						<input type="text" v-model="object.adapterId" class="form-control view-disabled" :readonly="true" />
						<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openAdapterModal" v-if="!readonly">
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="object.adapterId = null"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { HttpReq, getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'BasicInfo',
	extends: DetailBasic,
	data() {
		return {
			object: {
				service: null,
				endpoint: null,
				operation: null,
				serviceType: null,
				serviceId: null,
				serviceName: null,
				serviceDesc: null,
				metaDomain: null,
				requestRecordId: null,
				responseRecordId: null,
				serviceGroup: null,
				privilegeId: null,
				adapterId: null
			},

			serviceTypeList: [],
			serviceGroupList: [],
			executePrivilegeIdList: [],
			metaDomainList: [],

			serviceGroupSelect: null,
			serviceGroupEdit: null,

			keyMaxLength: null,
			nameMaxLength: null,
			descMaxLength: null,
			defaultMaxLength: null
		};
	},
	async created() {
		this.keyMaxLength = getRegExpInfo(`id`).maxLength;
		this.nameMaxLength = getRegExpInfo(`name`).maxLength;
		this.descMaxLength = getRegExpInfo(`desc`).maxLength;
		this.defaultMaxLength = getRegExpInfo(`default`).maxLength;

		const httpReqList = [
			{
				url: `/common/property/properties.json`,
				param: { propertyId: 'List.Service.ServiceType', orderByKey: true },
				itemName: 'serviceTypeList'
			},
			{
				url: `/igate/fieldMeta/groups.json`,
				itemName: 'metaDomainList'
			},
			{
				url: `/igate/service/groups.json`,
				itemName: 'serviceGroupList'
			},
			{
				url: `/common/auth/businessPrivileges.json`,
				itemName: 'executePrivilegeIdList'
			}
		];

		try {
			for await (const content of httpReqList) {
				const result = await new HttpReq(content.url).read(content.param ? content.param : null);

				if ('ok' !== result.data.result) throw result.data;

				this[content.itemName] = result.data.object;
			}
		} catch (error) {
			this.$alert({ type: 'warn', message: this.$t('head.fail.notice') });
		}
	},
	methods: {
		inputEvt(type, key, e) {
			this.object[key] = e.target.value;
			this.object[key] = this.object[key].replace(new RegExp(getRegExpInfo(type).regExp, `g`), ``);
		},
		serviceGroupEditEvt() {
			this.serviceGroupEdit = event.target.value;
			this.serviceGroupEdit = this.serviceGroupEdit.replace(new RegExp(getRegExpInfo('default').regExp, `g`), ``);
		},
		getData() {
			this.object.serviceGroup = this.serviceGroupSelect;

			if (this.serviceGroupEdit) {
				if (this.object.serviceGroup) this.object.serviceGroup += `.${this.serviceGroupEdit}`;
				else this.object.serviceGroup = this.serviceGroupEdit;
			}

			return this.object;
		},
		changeServiceId() {
			this.object.requestRecordId = `SFD_${this.object.serviceId}_I`;
			this.object.responseRecordId = `SFD_${this.object.serviceId}_O`;
		},
		openAdapterModal() {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.adapter'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'adapterId',
										label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'text',
										vModel: 'adapterName',
										label: `${this.$t('igate.adapter')} ${this.$t('head.name')}`,
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'text',
										vModel: 'adapterDesc',
										label: `${this.$t('igate.adapter')} ${this.$t('head.description')}`,
										placeholder: this.$t('head.searchComment'),
										regExpType: 'desc'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										placeholder: this.$t('head.listCount'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/adapter/searchPopup.json',
									totalCntUrl: '/igate/adapter/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
												name: 'adapterId',
												width: '20%'
											},
											{
												header: `${this.$t('igate.adapter')} ${this.$t('head.name')}`,
												name: 'adapterName',
												width: '20%'
											},
											{
												header: `${this.$t('igate.adapter')} ${this.$t('head.description')}`,
												name: 'adapterDesc',
												width: '20%'
											},
											{
												header: this.$t('igate.adapter.structure.request'),
												name: 'requestStructure',
												width: '20%'
											},
											{
												header: this.$t('igate.adapter.structure.response'),
												name: 'responseStructure',
												width: '20%'
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.object.adapterId = info.adapterId;
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
