<template>
	<div :style="rootStyle">
		<div id="dashBar" class="sub-bar">
			<slot name="dashboardLbtnArea"></slot>
			<div class="ml-auto form-inline">
				<slot name="dashboardRbtnArea"></slot>
			</div>
		</div>
		<div id="dashboard" @click="dashboardClick" @contextmenu="initContextmenu">
			<div class="dashboard-inner">
				<div id="containerBody" :style="containerBodyStyle">
					<slot name="dashboardBodyArea"></slot>
				</div>
			</div>
			<slot name="dashboardWidgetArea"></slot>
			<slot name="dashboardContextMenuArea"></slot>
			<slot name="dashboardNoticeArea"></slot>
		</div>
	</div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import { WindowResizeEventBus, HttpReq } from '@/utils/common.js';

export default {
	name: `DashboardStructure`,
	props: {
		containerInfo: Object
	},
	data() {
		return {
			perfItemList: [],
			rootStyle: {
				display: `contents`
			},
			containerBodyStyle: {
				width: `100%`,
				height: `100%`
			},
			isViewContextmenu: false
		};
	},
	async created() {
		WindowResizeEventBus.$on('MainResize', this.resize);

		await this.getPerfItemList();

		this.$emit(`initDashboardStructure`, { parentData: cloneDeep(this.$data) });
	},
	watch: {
		containerInfo() {
			this.resize();
		}
	},
	methods: {
		async getPerfItemList() {
			try {
				const httpReq = new HttpReq(`/igate/monitoring/dashboard/perfItem.json`);

				const result = await httpReq.read();

				if (`ok` !== result.data.result) throw `Error Init PerfItem List`;

				this.perfItemList = result.data.object;
			} catch (error) {
				console.log(error);
			}
		},
		getTypeList(perfItemObj) {
			const chartTypeList = [];
			const targetTypeList = [];
			const inoutTypeList = [];

			//chart
			const monitorChartGroup = perfItemObj.monitorChartGroup;

			const chartTypeArr = [
				{ key: 'LINE', value: 'LINE', name: this.$t(`dashboard.chart.lineChart`) },
				{ key: 'COLUMN', value: 'COLUMN', name: this.$t(`dashboard.chart.columnChart`) },
				{ key: 'INSTANCE', value: 'INSTANCE', name: this.$t(`dashboard.chart.instanceSummaryChart`) },
				{ key: 'CONNECTOR', value: 'CONNECTOR', name: this.$t(`dashboard.chart.connectorSummaryChart`) },
				{ key: 'QUEUE', value: 'QUEUE', name: this.$t(`dashboard.chart.queueSummaryChart`) },
				{ key: 'THREAD', value: 'THREAD', name: this.$t(`dashboard.chart.threadSummaryChart`) },
				{ key: 'SPEEDBAR', value: 'SPEEDBAR', name: this.$t(`dashboard.chart.speedBarChart`) },
				{ key: 'DATATABLE', value: 'DATATABLE', name: this.$t(`dashboard.chart.dataTable`) },
				{ key: 'EXTERNALLINE', value: 'EXTERNALLINE', name: this.$t(`dashboard.target.externalLine`) },
				{ key: 'XVIEW', value: 'XVIEW', name: this.$t(`dashboard.chart.xview`) }
			];

			monitorChartGroup.forEach(chartGroupInfo => {
				for (let i = 0; i < chartTypeArr.length; i++) {
					if (chartGroupInfo.pk.chartType == chartTypeArr[i].key) {
						chartTypeList.push({ value: chartTypeArr[i].value, name: chartTypeArr[i].name });
						break;
					}
				}
			});

			//target
			const monitorTargetGroup = perfItemObj.monitorTargetGroup;

			const targetTypeArr = [
				{ key: 'INSTANCE', value: 'INSTANCE', name: this.$t(`dashboard.target.instance`) },
				{ key: 'ADAPTER', value: 'ADAPTER', name: this.$t(`dashboard.target.adaptor`) },
				{ key: 'CONNECTOR', value: 'CONNECTOR', name: this.$t(`dashboard.target.connector`) },
				{ key: 'QUEUE', value: 'QUEUE', name: this.$t(`dashboard.target.queue`) },
				{ key: 'THREAD', value: 'THREAD', name: this.$t(`dashboard.target.thread`) },
				{ key: 'EXTERNALLINE', value: 'EXTERNALLINE', name: this.$t(`dashboard.target.externalLine`) }
			];

			monitorTargetGroup.forEach(targetGroupInfo => {
				for (let i = 0; i < targetTypeArr.length; i++) {
					if (targetGroupInfo.pk.targetType == targetTypeArr[i].key) {
						targetTypeList.push({ value: targetTypeArr[i].value, name: targetTypeArr[i].name });
						break;
					}
				}
			});

			//in out
			if ('Y' == perfItemObj.inOutExistYn) {
				inoutTypeList.push({ value: 'IN', name: 'IN' });
				inoutTypeList.push({ value: 'OUT', name: 'OUT' });
			}

			return {
				chartTypeList,
				targetTypeList,
				inoutTypeList
			};
		},
		dashboardClick(evt) {
			this.$emit(`dashboardClick`, evt);
		},
		initContextmenu(evt) {
			evt.preventDefault();
			this.$emit(`initContextmenu`, evt);
		},
		getBasicComponentStruct() {
			return {
				containerId: null,
				componentId: null,
				itemId: null,
				pComponentId: null,
				componentName: null,
				componentWidth: null,
				componentHeight: null,
				chartType: null,
				targetType: null,
				unitType: null,
				inoutType: null,
				componentOrder: null,
				componentType: null,
				componentTypeDirection: null,
				monitorComponentTargets: []
			};
		},
		resize() {
			const dashBar = document.querySelector(`#dashBar`);

			if (!dashBar) return;

			if (0 < dashBar.offsetWidth || 0 < dashBar.offsetHeight) {
				this.containerBodyStyle.width = `100%`;
				this.containerBodyStyle.height = `100%`;
			} else {
				this.containerBodyStyle.width = `${this.containerInfo.containerWidth}px`;
				this.containerBodyStyle.height = `${this.containerInfo.containerHeight}px`;
			}
		}
	}
};
</script>
