<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('head.insert')">
					<i class="icon-plus-circle"></i>
				</button>
				<label class="col">{{ $t('head.id') }}</label>
				<label class="col">{{ $t('igate.job.jobParameter') }}</label>
				<label class="col">{{ $t('head.description') }}</label>
			</div>
			<div class="form-table-wrap">
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('head.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div style="width: 100%">
							<dataList
								:ref="`component${bodyIndex}`"
								vModel="pk.propertyKey"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['pk.propertyKey']"
								:readonly="readonly"
								:optionInfo="keyIdOptionInfo"
							/>
						</div>
					</div>

					<div class="col">
						<div style="width: 100%">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="propertyValue"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['propertyValue']"
								:readonly="readonly"
							/>
						</div>
					</div>

					<div class="col">
						<div style="width: 100%">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="propertyDesc"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['propertyDesc']"
								:readonly="readonly"
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';
import { parseHierarchyObj, parseFlattenObj, getUUID, HttpReq } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: 'InterfaceExtendInfo',
	extends: DetailProperty,
	data() {
		return {
			interfacePropertyList: [],
			keyIdOptionInfo: {
				url: '/common/property/properties.json',
				params: {
					propertyId: 'Property.Interface',
					orderByKey: true
				},
				optionListName: 'interfacePropertyList',
				optionFor: 'option in interfacePropertyList',
				optionValue: 'option.pk.propertyKey',
				optionText: 'option.pk.propertyKey'
			}
		};
	},
	async created() {
		const httpReq = new HttpReq('/common/property/properties.json');

		const result = await httpReq.read({ propertyId: 'Property.Interface', orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.interfacePropertyList = result.data.object;
	},
	methods: {
		getData() {
			const rtnArr = [];

			for (let i = 0; i < this.propertyList.length; i++) {
				let rtnObj = {};

				this.$refs[`component${i}`].forEach(comp => {
					rtnObj = merge(rtnObj, comp.getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				interfaceProperties: rtnArr
			};
		},
		async setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			if ('undefined' !== typeof this.detailData.interfaceProperties) {
				this.propertyList = [...this.detailData.interfaceProperties];

				this.propertyList.forEach(propertyInfo => {
					propertyInfo.idx = getUUID();

					const property = this.interfacePropertyList.filter(property => propertyInfo.pk.propertyKey == property.pk.propertyKey)[0];
					if (property) propertyInfo.propertyDesc = property.propertyDesc;
				});
			} else {
				this.propertyList = [];
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		}
	}
};
</script>
