<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.id') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`connectorId`])">
								{{ `(${curLengthCnt[`connectorId`]}/${maxLengthCnt[`connectorId`]})` }}
							</span>
						</span>
						<b class="icon-star"></b>
					</label>

					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`connectorId`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`connectorId`]) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="connectorId"
							vModel="connectorId"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`connectorId`]),
								'regExp-text': !getReadonly(regExpInfo[`connectorId`]),
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly || getReadonly(regExpInfo[`connectorId`])"
							regExpType="id"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.name') }}
							<span class="letterLength" v-if="!getReadonly(regExpInfo[`connectorName`])">
								{{ `(${curLengthCnt[`connectorName`]}/${maxLengthCnt[`connectorName`]})` }}
							</span>
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`connectorName`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`connectorName`]) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="connectorName"
							vModel="connectorName"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`connectorName`]),
								'regExp-text': !getReadonly(regExpInfo[`connectorName`]),
								'view-disabled': true
							}"
							:readonly="readonly"
							regExpType="name"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('common.type') }}</span>
					</label>
					<label class="control-label warningLabel" v-if="!readonly">
						<span>{{ this.$t('igate.connector.alert.type.empty') }}</span>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="connectorType"
							vModel="connectorType"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Connector.Type',
									orderByKey: true
								},
								optionListName: 'connectorTypes',
								optionFor: 'option in connectorTypes',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
							@changeEvt="changeEvt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.log.level') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="connectorLogLevel"
							vModel="connectorLogLevel"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.LogLevel',
									orderByKey: false
								},
								optionListName: 'connectorLogLevel',
								optionFor: 'option in connectorLogLevel',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
						/>
					</div>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.connector.startYn') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="connectorStartYn"
							vModel="connectorStartYn"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || getReadonly({ isPK: false })"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Yn',
									orderByKey: true
								},
								optionListName: 'connectorStartYns',
								optionFor: 'option in connectorStartYns',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.connector.requestDirection') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="detail-content-common">
						<selectBox
							ref="requestDirection"
							vModel="requestDirection"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'List.Connector.RequestDirection',
									orderByKey: true
								},
								optionListName: 'connectorRequestdirections',
								optionFor: 'option in connectorRequestdirections',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter') }} {{ $t('head.id') }}</span>
					</label>
					<div class="detail-content-common">
						<div class="input-group">
							<inputText
								ref="adapterId"
								vModel="adapterId"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
								:readonly="true"
							/>
							<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openAdapterModal" v-if="!readonly">
								<i class="icon-srch"></i>{{ $t('head.search') }}
							</button>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="$refs[`adapterId`].setData(null)"
								v-if="!readonly"
								:title="$t('head.initialize')"
							>
								<i class="icon-reset" style="margin-right: 0px"></i>
							</button>
						</div>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.threadPool') }} {{ $t('head.id') }}</span>
					</label>
					<div class="detail-content-common">
						<div class="input-group">
							<inputText
								ref="threadPoolId"
								vModel="threadPoolId"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
								:readonly="true"
							/>
							<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openThreadPoolModal" v-if="!readonly">
								<i class="icon-srch"></i>{{ $t('head.search') }}
							</button>
							<button
								type="button"
								class="btn"
								style="margin-left: 5px; min-width: 0px"
								@click="$refs[`threadPoolId`].setData(null)"
								v-if="!readonly"
								:title="$t('head.initialize')"
							>
								<i class="icon-reset" style="margin-right: 0px"></i>
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ $t('head.description') }}
							<span class="letterLength" v-if="!getReadonly(regExpInfo[`connectorDesc`])">
								{{ `(${curLengthCnt[`connectorDesc`]}/${maxLengthCnt[`connectorDesc`]})` }}
							</span>
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`connectorDesc`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`connectorDesc`]) ? '8px' : '0px'}` }"
					>
						<textArea
							ref="connectorDesc"
							vModel="connectorDesc"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`connectorDesc`]),
								'regExp-textarea': !getReadonly(regExpInfo[`connectorDesc`]),
								'view-disabled': true
							}"
							:readonly="readonly"
							regExpType="desc"
							:height="'200px'"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { numberWithComma, getRegExpInfo } from '@/utils/common.js';
import merge from 'lodash/merge';

export default {
	name: 'BasicInfo',
	props: {
		detailData: Object,
		mode: String,
		pk: Array
	},
	extends: DetailBasic,
	data() {
		return {
			keyArr: [
				'connectorId',
				'connectorName',
				'connectorType',
				'connectorLogLevel',
				'connectorStartYn',
				'requestDirection',
				'adapterId',
				'threadPoolId',
				'connectorDesc'
			],

			regExpInfo: {
				connectorId: {
					regExpType: 'id',
					isPK: true,
					readonly: false
				},
				connectorName: {
					regExpType: 'name',
					isPK: false,
					readonly: false
				},
				connectorDesc: {
					regExpType: 'desc',
					isPK: false,
					readonly: false
				}
			}
		};
	},
	methods: {
		getReadonly(colInfo) {
			return this.readonly || (colInfo.isPK && 'modify' === this.mode);
		},
		getData() {
			let rtnObj = {};

			for (let key in this.$refs) {
				if (0 === this.$refs[key].length) continue;

				rtnObj = merge(rtnObj, this.$refs[key].getData());
			}

			return rtnObj;
		},
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;

				const regExpType = !Object.keys(this.regExpInfo).includes(key) ? 'default' : this.regExpInfo[key].regExpType;

				this.setLengthCnt({ [key]: 0 });
				this.maxLengthCnt[key] = getRegExpInfo(regExpType).maxLength;
			});

			for (const key in this.$refs) {
				if (!this.$refs[key].setData) continue;
				this.$refs[key].setData(detailDataFormalize[this.$refs[key]._props.vModel]);
			}
		},
		changeEvt(info) {
			this.$emit('changeProperties', info.value);
		},
		openAdapterModal() {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.adapter'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'adapterId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'text',
										vModel: 'adapterName',
										label: this.$t('head.name'),
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'text',
										vModel: 'adapterDesc',
										label: this.$t('head.description'),
										placeholder: this.$t('head.searchComment'),
										regExpType: 'desc'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										placeholder: this.$t('head.searchCoditionList'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/adapter/searchPopup.json',
									totalCntUrl: '/igate/adapter/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'adapterId',
												width: '20%'
											},
											{
												header: this.$t('head.name'),
												name: 'adapterName',
												width: '20%'
											},
											{
												header: this.$t('head.description'),
												name: 'adapterDesc',
												width: '20%'
											},
											{
												header: this.$t('igate.adapter.structure.request'),
												name: 'requestStructure',
												width: '20%'
											},
											{
												header: this.$t('igate.adapter.structure.response'),
												name: 'responseStructure',
												width: '20%'
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs.adapterId.setData(info.adapterId);
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		},
		openThreadPoolModal() {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.threadPool'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'entityName',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/threadPool/searchPopup.json',
									totalCntUrl: '/igate/threadPool/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												name: 'threadPoolId',
												header: this.$t('head.id'),
												align: 'left',
												width: '20%'
											},
											{
												name: 'rejectPolicy',
												header: this.$t('igate.threadPool.rejectPolicy'),
												align: 'center',
												width: '16%',
												formatter: value => {
													switch (value.value) {
														case 'A': {
															return 'abort';
														}
														case 'C': {
															return 'caller runs';
														}
														case 'D': {
															return 'discard';
														}
														case 'O': {
															return 'discard oldest';
														}
													}
												}
											},
											{
												name: 'threadMin',
												header: this.$t('igate.threadPool.min'),
												align: 'right',
												width: '16%',
												formatter: info => {
													return numberWithComma(info.row.threadMin);
												}
											},
											{
												name: 'threadMax',
												header: this.$t('igate.threadPool.max'),
												align: 'right',
												width: '16%',
												formatter: info => {
													return numberWithComma(info.row.threadMax);
												}
											},
											{
												name: 'threadIdle',
												header: this.$t('igate.threadPool.idle'),
												align: 'right',
												width: '16%',
												formatter: info => {
													return numberWithComma(info.row.threadIdle);
												}
											},
											{
												name: 'threadThreshold',
												header: this.$t('igate.threadPool.threshold'),
												align: 'right',
												width: '16%',
												formatter: info => {
													return numberWithComma(info.row.threadThreshold);
												}
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs.threadPoolId.setData(info.threadPoolId);
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
