<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<label class="col" v-for="(headerObj, headerIndex) in detailInfo.content" :key="headerIndex">{{ headerObj.label }}</label>
			</div>
			<div class="form-table-wrap">
				<div class="form-table-body" v-for="(detailObj, bodyIndex) in propertyList" :key="`${detailObj.idx}`">
					<div class="col" v-for="(detailSubObj, index) in detailInfo.content" :key="`${detailObj.idx}_${index}`">
						<div
							:class="`detail-content-${
								regExpTypeList.includes(detailSubObj.type) && !getReadonly(detailSubObj) ? 'regExp' : 'common'
							}`"
						>
							<inputText
								:ref="`component_${bodyIndex}_${index}`"
								:vModel="detailSubObj.vModel"
								:classObject="{
									'form-control': getReadonly(detailSubObj),
									[`regExp-${detailSubObj.type}`]: !getReadonly(detailSubObj),
									'view-disabled': true,
									dataKey: detailSubObj.isRequired
								}"
								:val="detailObj[detailSubObj.vModel]"
								:regExpType="detailSubObj.regExpType"
								:readonly="readonly || detailSubObj.readonly"
								@setLengthCnt="info => setLengthCnt({ [`${detailSubObj.vModel}_${bodyIndex}`]: info[detailSubObj.vModel] })"
								v-if="'text' === detailSubObj.type"
							></inputText>
							<span
								class="letterLength"
								v-if="regExpTypeList.includes(detailSubObj.type) && !getReadonly(detailSubObj)"
								style="margin: 0px"
							>
								{{
									`(${curLengthCnt[`${detailSubObj.vModel}_${bodyIndex}`]}
									 /${maxLengthCnt[`${detailSubObj.vModel}_${bodyIndex}`]})`
								}}
							</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import DetailProperty from '@/components/detail/DetailProperty.vue';
import { getUUID, getRegExpInfo, parseFlattenObj, HttpReq } from '@/utils/common.js';
import merge from 'lodash/merge';
import cloneDeep from 'lodash/cloneDeep';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'PropertyInfo',
	extends: DetailProperty,
	props: {
		adapterType: String,
		messageType: String,
		connectorInfo: Object
	},
	data() {
		return {
			propertyList: [],
			mergePropertyList: {}
		};
	},
	watch: {
		adapterType() {
			this.setPropertyList();
		},
		messageType() {
			if (!this.messageType) return;
			this.setPropertyList(`Property.Adapter.${this.messageType}`);
		},
		connectorInfo: {
			deep: true,
			handler() {
				if (isEmpty(this.connectorInfo)) return;

				if (this.connectorInfo.connectorType) {
					this.setPropertyList(`Property.Connector.${this.connectorInfo.connectorType}`, true);
				} else {
					this.mergePropertyList[this.connectorInfo.transaction] = [];

					this.propertyList = cloneDeep([].concat(...Object.values(this.mergePropertyList)));
					this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
				}
			}
		}
	},
	methods: {
		getData(type) {
			const rtnArr = [];

			for (let bodyIndex in this.propertyList) {
				let rtnObj = {};

				for (let index in this.detailInfo.content) {
					rtnObj = merge(rtnObj, this.$refs[`component_${bodyIndex}_${index}`][0].getData());
				}

				rtnArr.push(rtnObj);
			}

			return {
				[`${type}Properties`]: rtnArr
			};
		},
		async setPropertyList(propertyId, mergeFlag) {
			if (!propertyId) {
				this.propertyList = [];
				return;
			}

			const httpReq = new HttpReq('/common/property/properties.json');
			const result = await httpReq.read({ propertyId: propertyId, orderByKey: true });

			if ('ok' !== result.data.result) {
				this.$alert({ type: 'warn', message: `${result.data.error[0].message}` });
				return;
			}

			this.regexpElmList = this.detailInfo.content.filter(element => this.regExpTypeList.includes(element.type));

			let propertyList = result.data.object.filter(propertyInfo => '-' === propertyInfo.propertyValue && 'Y' === propertyInfo.requireYn);

			if (!isEmpty(this.connectorInfo)) propertyList = propertyList.filter(propertyInfo => !propertyInfo.pk.propertyKey.includes('timeout'));

			propertyList.forEach((propertyInfo, index) => {
				propertyInfo.idx = getUUID();

				this.regexpElmList.forEach(element => {
					const key = `${element.vModel}_${this.propertyList.length + index}`;

					this.setLengthCnt({ [key]: propertyInfo[element.vModel] ? propertyInfo[element.vModel].length : 0 });
					this.maxLengthCnt[key] = getRegExpInfo(element.regExpType ? element.regExpType : 'default').maxLength;
				});
			});

			if (mergeFlag) {
				this.mergePropertyList[this.connectorInfo.transaction] = cloneDeep(propertyList);

				let mergePropertyList = [].concat(...Object.values(this.mergePropertyList));

				this.propertyList = mergePropertyList.filter((property, index) => {
					return (
						mergePropertyList.findIndex(findProperty => {
							return property.pk.propertyKey === findProperty.pk.propertyKey;
						}) === index
					);
				});
			} else {
				this.propertyList = propertyList;
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		}
	}
};
</script>
