<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('igate.adapter') }} {{ this.$t('head.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<label class="control-label warningLabel" v-if="!readonly">
						<span>{{ this.$t('igate.interfaceRecognize.changeWarn') }}</span>
					</label>
					<div class="input-group">
						<inputText
							ref="pk.adapterId"
							vModel="pk.adapterId"
							:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
							:readonly="true"
						/>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="openAdapterModal"
							v-if="!(readonly || `modify` === mode)"
						>
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="$refs[`pk.adapterId`].setData(null)"
							v-if="!(readonly || `modify` === mode)"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
			</div>
			<div class="col-lg-6"></div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('igate.telegramValue') }}</span>
						<span class="letterLength" v-if="!getReadonly({ isPK: true })">
							{{ `(${curLengthCnt[`pk.telegramValue`]}/${maxLengthCnt[`pk.telegramValue`]})` }}
						</span>
						<b class="icon-star"></b>
					</label>
					<div
						:class="`detail-content-${!getReadonly({ isPK: true }) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly({ isPK: true }) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="pk.telegramValue"
							vModel="pk.telegramValue"
							:classObject="{
								'form-control': getReadonly({ isPK: true }),
								'regExp-text': !getReadonly({ isPK: true }),
								'view-disabled': true
							}"
							:readonly="readonly || getReadonly({ isPK: true })"
							regExpType="name"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
			</div>
			<div class="col-lg-6"></div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('igate.interface.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<div class="input-group">
						<inputText
							ref="interfaceId"
							vModel="interfaceId"
							:classObject="{ 'form-control': true, 'view-disabled': true }"
							:readonly="true"
						/>
						<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openInterfaceModal" v-if="!readonly">
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="$refs[`interfaceId`].setData(null)"
							v-if="!readonly"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
			</div>
			<div class="col-lg-6"></div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { getRegExpInfo } from '@/utils/common.js';

export default {
	name: 'InterfaceRecognizeBasicInfo',
	extends: DetailBasic,
	data() {
		return {
			keyArr: ['pk.adapterId', 'pk.telegramValue', 'interfaceId']
		};
	},
	methods: {
		getData() {
			let rtnObj = {};

			for (let key in this.$refs) {
				if (0 === this.$refs[key].length) continue;

				rtnObj = merge(rtnObj, this.$refs[key].getData());
			}

			return rtnObj;
		},
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;
			});

			for (const key in this.$refs) {
				if (!this.$refs[key].setData) continue;
				this.$refs[key].setData(detailDataFormalize[this.$refs[key]._props.vModel]);
			}

			this.setLengthCnt({ 'pk.telegramValue': detailDataFormalize[`pk.telegramValue`] ? detailDataFormalize[`pk.telegramValue`].length : 0 });
			this.maxLengthCnt['pk.telegramValue'] = getRegExpInfo('value').maxLength;
		},
		openAdapterModal() {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.adapter'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'adapterId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'text',
										vModel: 'adapterName',
										label: this.$t('head.name'),
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'text',
										vModel: 'adapterDesc',
										label: this.$t('head.description'),
										placeholder: this.$t('head.searchComment'),
										regExpType: 'desc'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										placeholder: this.$t('head.searchCoditionList'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/adapter/searchPopup.json',
									totalCntUrl: '/igate/adapter/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'adapterId',
												width: '20%'
											},
											{
												header: this.$t('head.name'),
												name: 'adapterName',
												width: '20%'
											},
											{
												header: this.$t('head.description'),
												name: 'adapterDesc',
												width: '20%'
											},
											{
												header: this.$t('igate.adapter.structure.request'),
												name: 'requestStructure',
												width: '20%'
											},
											{
												header: this.$t('igate.adapter.structure.response'),
												name: 'responseStructure',
												width: '20%'
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs[`pk.adapterId`].setData(info.adapterId);
								_this.$refs[`interfaceId`].setData(null);
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		},
		openInterfaceModal() {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.interface'),
				modalWidth: `1000px`,
				contentList: [
					{
						template: `<div>
						       <Search :list="searchList" @search="search" ref="search"/>
							     <Main
							   	   :buttonList="buttonList"
								   :gridInfo="gridInfo"
								   :gridSearchObj="gridSearchObj"
								   :gridType="'modalBasic'"
								   :viewer="true"
								   @rowClicked="rowClicked"
								   @initialize="initialize"
							   />
						   </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'interfaceId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'text',
										vModel: 'interfaceName',
										label: this.$t('head.name'),
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										placeholder: this.$t('head.searchCoditionList'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/interface/searchPopup.json',
									totalCntUrl: '/igate/interface/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'interfaceId',
												width: '25%'
											},
											{
												header: this.$t('head.name'),
												name: 'interfaceName',
												width: '25%'
											},
											{
												header: this.$t('head.description'),
												name: 'interfaceDesc',
												width: '25%'
											},
											{
												header: this.$t('common.type'),
												name: 'interfaceType',
												width: '25%'
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs[`interfaceId`].setData(info.interfaceId);
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
