<template>
	<div class="xViewChartRoot">
		<div class="graph" style="height: 100%">
			<div ref="background" :style="backgroundStyle">
				<canvas ref="canvasBackground" :width="canvasWidth" :height="canvasHeight"></canvas>
			</div>
			<div ref="body" :style="bodyStyle">
				<canvas ref="canvasBody" :width="canvasWidth" :height="canvasHeight"></canvas>
			</div>
		</div>
		<div class="legend" v-if="isShowLegend">
			<span class="status"><i class="dot bg-normal"></i>{{ $t(`dashboard.head.success`) }}</span>
			<span class="status"><i class="dot bg-warn"></i>{{ $t(`dashboard.head.time.out`) }}</span>
			<span class="status"><i class="dot bg-danger"></i>{{ $t(`dashboard.head.error`) }}</span>
		</div>
	</div>
</template>

<script>
import Chart from 'chart.js';
import moment from 'moment';
import { Grid } from '@toast-ui/vue-grid';
import cloneDeep from 'lodash/cloneDeep';
import { mapState } from 'vuex';
import { convertValue, getCanvasMousePos, WORKER_RESOURCE, cloneObject } from '@/utils/dashboard/chart_utils.js';
import { makeGridOptions, HttpReq, openNewTab, downloadCSV, getNumFromStr, SideMenuToggleEventBus, WindowResizeEventBus } from '@/utils/common.js';

export default {
	name: `DashboardXviewChart`,
	props: {
		options: Object,
		intervalParam: Object,
		rafParam: Object,
		dataObj: Object,
		dashConstInfo: Object,
		containerInfo: Object,
		containerUserInfo: Object
	},
	data() {
		return {
			isShowLegend: false,
			targetInfoList: [],

			backgroundStyle: {
				position: `absolute`,
				top: `0px`,
				left: `0px`,
				width: `100%`,
				height: `100%`
			},
			bodyStyle: {
				position: `absolute`,
				top: `0px`,
				left: `0px`,
				width: `100%`,
				height: `100%`
			},
			adapterFilterObj: {},
			instanceFilterObj: {},
			transactionFilter: ``,
			canvasWidth: null,
			canvasHeight: null,

			chartTopPadding: 20,
			chartBottomPadding: 20,
			chartLeftPadding: 90,
			chartRightPadding: 40,

			fontColor: null,
			axisLineColor: null,

			yAxisTickCount: 6,
			yAxisMaxValue: null,
			yAxisStepSize: null,

			xAxisMinValue: null,
			xAxisMaxValue: null,

			trans: null,

			isFirstChartRecvStandardTime: false,

			chartMoveXRemainder: 0,
			chartDrawXRemainder: 0,

			COLOURS: [`#62d36f`, `#efc402`, `#ed3137`, `#666666`],

			COLOURS_0: null,
			COLOURS_1: null,
			COLOURS_2: null,
			COLOURS_FILTER: null,

			countStart: null,
			deleteStart: null,

			dragStartMousePosInfo: null,
			dragStopMousePosInfo: null,

			dataFilterWorker: null,

			canvasBackgroundCtx: null,
			canvasBodyCtx: null
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	watch: {
		intervalParam: {
			deep: true,
			handler() {
				this.setInterval(this.intervalParam);
			}
		},
		rafParam: {
			deep: true,
			handler() {
				this.setRequestAnimationFrame(this.rafParam);
			}
		}
	},
	created() {
		if (!window.xViewDataObj) window.xViewDataObj = {};

		window.xViewDataObj[this.options.componentId] = {
			saveDataArr: [],
			drawDataArr: []
		};
	},
	mounted() {
		this.initXviewChart();

		this.$nextTick(() => {
			this.canvasBackgroundCtx = this.$refs.canvasBackground.getContext(`2d`);
			this.canvasBodyCtx = this.$refs.canvasBody.getContext(`2d`);

			this.initYAxis();

			this.initSelectableEvt();
		});
	},
	methods: {
		initXviewChart() {
			this.canvasWidth = this.$refs.background.clientWidth;
			this.canvasHeight = this.$refs.background.clientHeight;

			this.isShowLegend = `Y` === this.options.remarkYn;

			this.fontColor = `Y` === this.options.darkmodeYn ? `#ffffff` : Chart.helpers.color(`#212529`).alpha(0.6).rgbString();
			this.axisLineColor = `Y` === this.options.darkmodeYn ? `#666666` : `#f0f1f6`;

			const containerOptions = this.containerUserInfo.containerOptions;

			this.adapterFilterObj = containerOptions[`xViewadapterFilter_${this.options.componentId}`]
				? JSON.parse(containerOptions[`xViewadapterFilter_${this.options.componentId}`])
				: {};

			this.instanceFilterObj = containerOptions[`xViewInstanceFilter_${this.options.componentId}`]
				? JSON.parse(containerOptions[`xViewInstanceFilter_${this.options.componentId}`])
				: {};

			this.transactionFilter = containerOptions[`xViewtransactionFilter_${this.options.componentId}`]
				? containerOptions[`xViewtransactionFilter_${this.options.componentId}`]
				: ``;

			this.yAxisMaxValue = containerOptions[`xViewYAxisMax_${this.options.componentId}`]
				? containerOptions[`xViewYAxisMax_${this.options.componentId}`]
				: this.dashConstInfo.xViewDefaultYAxisMax;

			this.trans =
				null === containerOptions[`xViewTrans_${this.options.componentId}`]
					? containerOptions[`xViewTrans_${this.options.componentId}`]
					: this.dashConstInfo.xViewDefaultTrans;

			this.yAxisStepSize = (this.yAxisMaxValue / (this.yAxisTickCount - 1)).toFixed(2);

			this.COLOURS_0 = Chart.helpers.color(this.COLOURS[0]).alpha(this.trans).rgbString();
			this.COLOURS_1 = Chart.helpers.color(this.COLOURS[1]).alpha(this.trans).rgbString();
			this.COLOURS_2 = Chart.helpers.color(this.COLOURS[2]).alpha(this.trans).rgbString();
			this.COLOURS_FILTER = Chart.helpers.color(this.COLOURS[3]).alpha(this.trans).rgbString();

			const blob = new Blob([WORKER_RESOURCE], { type: `application/javascript` });
			const blobURL = URL.createObjectURL(blob);

			this.dataFilterWorker = new Worker(blobURL);

			this.dataFilterWorker.onmessage = e => {
				if (`drag` === e.data.mod) {
					this.openXLogDetailModalGrid(e);
				} else if (`export` === e.data.mod) {
					this.exportDataCallBack(e);
				}
			};
		},
		initXAxis() {
			const xCoorAlpha =
				(this.xAxisMaxValue - this.xAxisMinValue) / (this.canvasBodyCtx.canvas.width - (this.chartLeftPadding + this.chartRightPadding));

			this.canvasBodyCtx.clearRect(0, 0, this.canvasBodyCtx.canvas.width, this.canvasBodyCtx.canvas.height);

			this.canvasBodyCtx.font = `12px Arial`;
			this.canvasBodyCtx.textAlign = `center`;

			for (let i = 0; i < this.dashConstInfo.xViewChartTimeDuration; i++) {
				this.canvasBodyCtx.beginPath();

				const majorTickTime = Math.floor(this.xAxisMinValue / (60 * 1000)) * (60 * 1000) + (i + 1) * 60 * 1000;
				const majorTickTimeXCoord = (majorTickTime - this.xAxisMinValue) / xCoorAlpha;

				this.canvasBodyCtx.save();
				this.canvasBodyCtx.strokeStyle = Chart.helpers.color(this.axisLineColor).alpha(0.5).rgbString();
				this.canvasBodyCtx.lineWidth = 2;
				this.canvasBodyCtx.moveTo(this.chartLeftPadding + majorTickTimeXCoord, this.chartTopPadding);
				this.canvasBodyCtx.lineTo(
					this.chartLeftPadding + majorTickTimeXCoord,
					this.canvasBodyCtx.canvas.height - this.chartBottomPadding + 10
				);
				this.canvasBodyCtx.stroke();
				this.canvasBodyCtx.restore();

				this.canvasBodyCtx.fillStyle = this.fontColor;
				this.canvasBodyCtx.fillText(
					moment(new Date(majorTickTime)).format(`HH : mm`),
					this.chartLeftPadding + majorTickTimeXCoord,
					this.canvasBodyCtx.canvas.height - this.chartBottomPadding + 20
				);

				this.canvasBodyCtx.closePath();
			}
		},
		initYAxis() {
			const yCoordAlpha = (this.canvasBackgroundCtx.canvas.height - (this.chartTopPadding + this.chartBottomPadding)) / this.yAxisMaxValue;

			this.canvasBackgroundCtx.beginPath();

			this.canvasBackgroundCtx.clearRect(0, 0, this.canvasBackgroundCtx.canvas.width, this.canvasBackgroundCtx.canvas.height);

			//y축 오른쪽 세로 라인
			this.canvasBackgroundCtx.moveTo(this.chartLeftPadding, this.chartTopPadding);
			this.canvasBackgroundCtx.lineTo(this.chartLeftPadding, this.canvasBackgroundCtx.canvas.height - this.chartBottomPadding);
			this.canvasBackgroundCtx.strokeStyle = this.axisLineColor;
			this.canvasBackgroundCtx.stroke();

			this.canvasBackgroundCtx.font = `12px Arial`;
			this.canvasBackgroundCtx.textAlign = `right`;
			this.canvasBackgroundCtx.textBaseline = `middle`;

			for (let i = 0; i < this.yAxisTickCount; i++) {
				const value = this.yAxisStepSize * i;

				const yCoord =
					this.canvasBackgroundCtx.canvas.height -
					(this.chartTopPadding + this.chartBottomPadding) -
					yCoordAlpha * value +
					this.chartTopPadding;

				//y축 값
				this.canvasBackgroundCtx.fillStyle = this.fontColor;
				this.canvasBackgroundCtx.fillText(convertValue(value, `NUMBER`), this.chartLeftPadding - 15, yCoord);

				//y축 가로 라인
				this.canvasBackgroundCtx.moveTo(this.chartLeftPadding - 7, yCoord);
				this.canvasBackgroundCtx.lineTo(this.canvasBackgroundCtx.canvas.width - this.chartRightPadding, yCoord);
				this.canvasBackgroundCtx.strokeStyle = this.axisLineColor;
				this.canvasBackgroundCtx.stroke();
			}

			this.canvasBackgroundCtx.closePath();
		},
		addTarget(targetObj) {
			targetObj.isFiltered = false;
			this.targetInfoList.push(targetObj);
		},
		deleteTarget(targetId) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			this.targetInfoList = this.targetInfoList.filter(targetInfo => targetInfo.targetId !== targetId);
		},
		addData(dataArr) {
			Array.prototype.push.apply(window.xViewDataObj[this.options.componentId].saveDataArr, dataArr);

			Array.prototype.push.apply(
				window.xViewDataObj[this.options.componentId].drawDataArr,
				dataArr.map(data => cloneObject(data))
			);

			dataArr = null;

			const adapterFilterObj = this.adapterFilterObj;
			const instanceFilterObj = this.instanceFilterObj;
			const transactionFilter = this.transactionFilter;

			if (!(0 < Object.keys(adapterFilterObj).length || 0 < Object.keys(instanceFilterObj).length || 0 < transactionFilter.length)) return;

			window.xViewDataObj[this.options.componentId].drawDataArr = window.xViewDataObj[this.options.componentId].drawDataArr.filter(data => {
				const isAdapterFilter = 0 < Object.keys(adapterFilterObj).length ? adapterFilterObj[data.adapterId] : true;

				const isInstanceFilter = 0 < Object.keys(instanceFilterObj).length ? instanceFilterObj[data.instanceId] : true;

				const isTransactionFilter = 0 < transactionFilter.trim().length ? transactionFilter.trim() === data.transactionId : true;

				return isAdapterFilter && isInstanceFilter && isTransactionFilter;
			});
		},
		setInterval({ time, isRecvTime }) {
			//shift chart
			if (null === this.xAxisMaxValue || (!this.isFirstChartRecvStandardTime && isRecvTime)) {
				this.xAxisMaxValue = Math.floor(time / 1000) * 1000;
				this.xAxisMinValue = this.xAxisMaxValue - this.dashConstInfo.xViewChartTimeDuration * 60 * 1000;

				this.initXAxis();

				this.isFirstChartRecvStandardTime = true;
			} else {
				let isChangeXAxisMaxValue = false;

				const beforeDate = new Date(this.xAxisMaxValue);
				const currentDate = new Date(Math.floor(time / 1000) * 1000);

				const diffTime = currentDate.getTime() - beforeDate.getTime();

				isChangeXAxisMaxValue = beforeDate.getMinutes() != currentDate.getMinutes();

				this.xAxisMaxValue = Math.floor(time / 1000) * 1000;
				this.xAxisMinValue = this.xAxisMaxValue - this.dashConstInfo.xViewChartTimeDuration * 60 * 1000;

				if (0 === this.canvasBodyCtx.canvas.width || 0 === this.canvasBodyCtx.canvas.height) return;

				const xCoorAlpha =
					(this.xAxisMaxValue - this.xAxisMinValue) / (this.canvasBodyCtx.canvas.width - (this.chartLeftPadding + this.chartRightPadding));

				let tmpX = Math.floor(diffTime / xCoorAlpha);

				this.chartMoveXRemainder += (diffTime / xCoorAlpha) % 1;
				this.chartDrawXRemainder = (diffTime / xCoorAlpha) % 1;

				if (1 <= this.chartMoveXRemainder) {
					tmpX += Math.floor(this.chartMoveXRemainder / 1);
					this.chartMoveXRemainder -= Math.floor(this.chartMoveXRemainder / 1);
					this.chartDrawXRemainder = this.chartMoveXRemainder;
				}

				const imgData = this.canvasBodyCtx.getImageData(
					this.chartLeftPadding + tmpX,
					0,
					this.canvasBodyCtx.canvas.width,
					this.canvasBodyCtx.canvas.height
				);

				this.canvasBodyCtx.beginPath();

				this.canvasBodyCtx.clearRect(0, 0, this.canvasBodyCtx.canvas.width, this.canvasBodyCtx.canvas.height);

				this.canvasBodyCtx.putImageData(imgData, this.chartLeftPadding, 0);

				this.canvasBodyCtx.closePath();

				if (isChangeXAxisMaxValue) {
					let majorTickTime =
						Math.floor(this.xAxisMinValue / (60 * 1000)) * (60 * 1000) + this.dashConstInfo.xViewChartTimeDuration * 60 * 1000;
					let majorTickTimeXCoord = (majorTickTime - this.xAxisMinValue) / xCoorAlpha;

					if (this.chartDrawXRemainder) {
						majorTickTimeXCoord += this.chartDrawXRemainder;
					}

					this.canvasBodyCtx.beginPath();

					this.canvasBodyCtx.save();
					this.canvasBodyCtx.strokeStyle = Chart.helpers.color(this.axisLineColor).alpha(0.5).rgbString();
					this.canvasBodyCtx.lineWidth = 2;
					this.canvasBodyCtx.moveTo(this.chartLeftPadding + majorTickTimeXCoord, this.chartTopPadding);
					this.canvasBodyCtx.lineTo(
						this.chartLeftPadding + majorTickTimeXCoord,
						this.canvasBodyCtx.canvas.height - this.chartBottomPadding + 10
					);
					this.canvasBodyCtx.stroke();
					this.canvasBodyCtx.restore();

					this.canvasBodyCtx.font = `12px Arial`;
					this.canvasBodyCtx.textAlign = `center`;
					this.canvasBodyCtx.fillStyle = this.fontColor;
					this.canvasBodyCtx.fillText(
						moment(new Date(majorTickTime)).format(`HH : mm`),
						this.chartLeftPadding + majorTickTimeXCoord,
						this.canvasBodyCtx.canvas.height - this.chartBottomPadding + 20
					);

					this.canvasBodyCtx.closePath();
				}
			}

			//delete data
			const deleteCurrentTime = Date.now();

			if (!this.deleteStart) this.deleteStart = deleteCurrentTime;

			const deleteProgress = deleteCurrentTime - this.deleteStart;

			if (deleteProgress >= 30 * 1000) {
				this.deleteStart = deleteCurrentTime;

				this.initDeleteDataInterval();
			}

			//chart data count
			const countCurrentTime = Date.now();

			if (!this.countStart) this.countStart = countCurrentTime;

			const countProgress = countCurrentTime - this.countStart;

			if (countProgress >= 15 * 1000) {
				this.countStart = countCurrentTime;

				this.initCountDataInterval();
			}

			//resize
			if (this.canvasWidth !== this.$refs.background.clientWidth || this.canvasHeight !== this.$refs.background.clientHeight) {
				this.canvasWidth = this.$refs.background.clientWidth;
				this.canvasHeight = this.$refs.background.clientHeight;

				this.chartMoveXRemainder = 0;
				this.chartDrawXRemainder = 0;

				this.$nextTick(() => {
					this.initYAxis();
					this.initXAxis();

					this.filterDrawData();
				});
			}
		},
		setRequestAnimationFrame() {
			if (!this.$refs.canvasBody) return;

			if (0 === window.xViewDataObj[this.options.componentId].drawDataArr.length) return;

			const xCoorAlpha =
				(this.xAxisMaxValue - this.xAxisMinValue) / (this.canvasBodyCtx.canvas.width - (this.chartLeftPadding + this.chartRightPadding));
			const yCoordAlpha = (this.canvasBodyCtx.canvas.height - (this.chartTopPadding + this.chartBottomPadding)) / this.yAxisMaxValue;

			const beforeDrawTime = Date.now();

			for (const data of window.xViewDataObj[this.options.componentId].drawDataArr) {
				if (data.isDraw || data.x > this.xAxisMaxValue) {
					continue;
				}

				if (Date.now() - beforeDrawTime >= 3) {
					break;
				}

				if (data.x < this.xAxisMinValue) {
					data.isDraw = true;
					continue;
				}

				let tmpXCoord = this.chartLeftPadding + (data.x - this.xAxisMinValue) / xCoorAlpha;
				let tmpYCoord =
					this.canvasBodyCtx.canvas.height - (this.chartTopPadding + this.chartBottomPadding) - yCoordAlpha * data.y + this.chartTopPadding;

				if (this.chartDrawXRemainder) tmpXCoord += this.chartDrawXRemainder;

				if (tmpYCoord < this.chartTopPadding) tmpYCoord = this.chartTopPadding;

				this.canvasBodyCtx.beginPath();

				this.canvasBodyCtx.lineWidth = 3;

				const targetInfoObj = this.targetInfoList.find(targetInfo => targetInfo.targetId === data.targetId);

				this.canvasBodyCtx.strokeStyle =
					targetInfoObj && targetInfoObj.isFiltered
						? this.COLOURS_FILTER
						: 0 === data.type
						? this.COLOURS_0
						: 1 === data.type
						? this.COLOURS_1
						: this.COLOURS_2;

				this.canvasBodyCtx.moveTo(tmpXCoord - 5, tmpYCoord - 5);
				this.canvasBodyCtx.lineTo(tmpXCoord + 5, tmpYCoord + 5);

				this.canvasBodyCtx.moveTo(tmpXCoord - 5, tmpYCoord + 5);
				this.canvasBodyCtx.lineTo(tmpXCoord + 5, tmpYCoord - 5);

				this.canvasBodyCtx.stroke();

				this.canvasBodyCtx.closePath();

				data.isDraw = true;
			}

			const findIdx = window.xViewDataObj[this.options.componentId].drawDataArr.findIndex(data => !data.isDraw);

			if (-1 < findIdx) {
				window.xViewDataObj[this.options.componentId].drawDataArr = window.xViewDataObj[this.options.componentId].drawDataArr.slice(findIdx);
			} else if (0 < window.xViewDataObj[this.options.componentId].drawDataArr.length) {
				window.xViewDataObj[this.options.componentId].drawDataArr = [];
			}
		},
		initDeleteDataInterval() {
			let findIdx = window.xViewDataObj[this.options.componentId].saveDataArr.findIndex(data => data.x >= this.xAxisMinValue);

			if (-1 < findIdx) {
				window.xViewDataObj[this.options.componentId].saveDataArr = window.xViewDataObj[this.options.componentId].saveDataArr.slice(findIdx);
			} else if (0 < window.xViewDataObj[this.options.componentId].saveDataArr.length) {
				window.xViewDataObj[this.options.componentId].saveDataArr = [];
			}

			findIdx = window.xViewDataObj[this.options.componentId].drawDataArr.findIndex(data => data.x >= this.xAxisMinValue);

			if (-1 < findIdx) {
				window.xViewDataObj[this.options.componentId].drawDataArr = window.xViewDataObj[this.options.componentId].drawDataArr.slice(findIdx);
			} else if (0 < window.xViewDataObj[this.options.componentId].drawDataArr.length) {
				window.xViewDataObj[this.options.componentId].drawDataArr = [];
			}
		},
		initCountDataInterval() {
			const countInfo = { total: 0, 0: 0, 1: 0, 2: 0 };

			for (const data of window.xViewDataObj[this.options.componentId].saveDataArr) {
				if (this.xAxisMinValue > data.x) continue;

				if (this.xAxisMaxValue < data.x) break;

				if (0 < Object.keys(this.adapterFilterObj).length && !this.adapterFilterObj[data.adapterId]) continue;

				if (0 < Object.keys(this.instanceFilterObj).length && !this.instanceFilterObj[data.instanceId]) continue;

				if (0 < this.transactionFilter.trim().length && !(this.transactionFilter.trim() === data.transactionId)) continue;

				++countInfo[data.type];
			}

			countInfo[`total`] = countInfo[`0`] + countInfo[`1`] + countInfo[`2`];

			for (const key in countInfo) {
				countInfo[key] = 1000 > countInfo[key] ? countInfo[key] : convertValue(countInfo[key], `NUMBER`);
			}

			this.canvasBackgroundCtx.beginPath();

			this.canvasBackgroundCtx.clearRect(
				this.chartLeftPadding,
				this.chartTopPadding - 20,
				this.canvasBackgroundCtx.canvas.width - (this.chartLeftPadding + this.chartRightPadding),
				15
			);

			this.canvasBackgroundCtx.font = `10px Arial`;
			this.canvasBackgroundCtx.textAlign = `left`;
			this.canvasBackgroundCtx.textBaseline = `bottom`;

			let fontXCoord = this.chartLeftPadding;

			this.canvasBackgroundCtx.fillStyle = this.fontColor;
			this.canvasBackgroundCtx.fillText(`${countInfo[`total`]} (`, fontXCoord, this.chartTopPadding - 5);
			fontXCoord += this.canvasBackgroundCtx.measureText(countInfo[`total`] + ` (`).width;

			this.canvasBackgroundCtx.fillStyle = this.COLOURS[0];
			this.canvasBackgroundCtx.fillText(countInfo[`0`], fontXCoord, this.chartTopPadding - 5);
			fontXCoord += this.canvasBackgroundCtx.measureText(countInfo[`0`]).width;

			this.canvasBackgroundCtx.fillStyle = this.fontColor;
			this.canvasBackgroundCtx.fillText(`, `, fontXCoord, this.chartTopPadding - 5);
			fontXCoord += this.canvasBackgroundCtx.measureText(`, `).width;

			this.canvasBackgroundCtx.fillStyle = this.COLOURS[1];
			this.canvasBackgroundCtx.fillText(countInfo[`1`], fontXCoord, this.chartTopPadding - 5);
			fontXCoord += this.canvasBackgroundCtx.measureText(countInfo[`1`]).width;

			this.canvasBackgroundCtx.fillStyle = this.fontColor;
			this.canvasBackgroundCtx.fillText(`, `, fontXCoord, this.chartTopPadding - 5);
			fontXCoord += this.canvasBackgroundCtx.measureText(`, `).width;

			this.canvasBackgroundCtx.fillStyle = this.COLOURS[2];
			this.canvasBackgroundCtx.fillText(countInfo[`2`], fontXCoord, this.chartTopPadding - 5);
			fontXCoord += this.canvasBackgroundCtx.measureText(countInfo[`2`]).width;

			this.canvasBackgroundCtx.fillStyle = this.fontColor;
			this.canvasBackgroundCtx.fillText(`)`, fontXCoord, this.chartTopPadding - 5);

			this.canvasBackgroundCtx.closePath();
		},
		initSelectableEvt() {
			$(this.$refs.canvasBody).selectable({
				start: event => {
					this.dragStartMousePosInfo = getCanvasMousePos(this.$refs.canvasBody, event.originalEvent);
				},
				stop: event => {
					this.dragStopMousePosInfo = getCanvasMousePos(this.$refs.canvasBody, event.originalEvent);

					const dragStartX = this.dragStartMousePosInfo.x;
					const dragStartY = this.dragStartMousePosInfo.y;
					const dragStoptX = this.dragStopMousePosInfo.x;
					const dragStopY = this.dragStopMousePosInfo.y;

					let startX = dragStartX < dragStoptX ? dragStartX : dragStoptX;
					let endX = dragStartX < dragStoptX ? dragStoptX : dragStartX;
					let startY = dragStartY < dragStopY ? dragStartY : dragStopY;
					let endY = dragStartY < dragStopY ? dragStopY : dragStartY;

					if (10 > endX - startX || 10 > endY - startY) return;

					this.$startSpinner();

					startX -= 5;
					endX += 5;
					startY -= 5;
					endY += 5;

					const xCoorAlpha =
						(this.xAxisMaxValue - this.xAxisMinValue) / (this.$refs.canvasBody.width - (this.chartLeftPadding + this.chartRightPadding));
					const yCoordAlpha = (this.$refs.canvasBody.height - (this.chartTopPadding + this.chartBottomPadding)) / this.yAxisMaxValue;

					const startFilterTime = (startX - this.chartLeftPadding) * xCoorAlpha + this.xAxisMinValue;
					const endFilterTime = (endX - this.chartLeftPadding) * xCoorAlpha + this.xAxisMinValue;
					const endFilterValue =
						-(startY - this.chartTopPadding - (this.$refs.canvasBody.height - (this.chartTopPadding + this.chartBottomPadding))) /
						yCoordAlpha;
					const startFilterValue =
						-(endY - this.chartTopPadding - (this.$refs.canvasBody.height - (this.chartTopPadding + this.chartBottomPadding))) /
						yCoordAlpha;

					this.dataFilterWorker.postMessage({
						mod: `drag`,
						isSort: true,
						dataArr: window.xViewDataObj[this.options.componentId].saveDataArr,
						startFilterTime: startFilterTime,
						endFilterTime: endFilterTime,
						startFilterValue: startFilterValue,
						endFilterValue: endFilterValue,
						yAxisMaxValue: this.yAxisMaxValue,
						adapterFilterObj: this.adapterFilterObj,
						instanceFilterObj: this.instanceFilterObj,
						transactionFilter: this.transactionFilter.trim()
					});
				}
			});
		},
		openXLogDetailModalGrid(e) {
			this.$stopSpinner();

			if (0 === e.data.dataArr.length) {
				this.$alert({ type: `normal`, message: this.$t(`dashboard.noMatch.criteria`) });
				return;
			}

			this.$openModal({
				title: this.$t(`dashboard.head.detail.xView`),
				fullscreen: true,
				modalHeight: '50%',
				contentList: [
					{
						template: `<TuiGrid ref="tuiGrid" v-bind="options" @click="onClick" />`,
						components: {
							TuiGrid: Grid
						},
						data() {
							return {
								options: {
									pageOptions: {
										useClient: true,
										perPage: 11
									},
									columns: [
										{
											name: `type`,
											header: this.$t(`dashboard.head.status`),
											width: `7%`,
											align: `center`,
											formatter: obj => {
												let status = `<span style="color: #28a745">${this.$t(`dashboard.head.success`)}</span>`;

												if (2 === obj.value) {
													status = `<span style="color: #ed3137">${this.$t(`dashboard.head.error`)}</span>`;
												} else if (1 === obj.value) {
													status = `<span style="color: #ffc107">${this.$t(`dashboard.head.time.out`)}</span>`;
												}

												return status;
											}
										},
										{
											name: `uuid`,
											header: this.$t(`dashboard.head.transactionId`),
											width: `13%`
										},
										{
											name: `transactionId`,
											header: this.$t(`dashboard.head.interface.service.id`),
											width: `10%`
										},
										{
											name: `transactionName`,
											header: this.$t(`dashboard.head.transactionName`),
											width: `15%`
										},
										{
											name: `adapterId`,
											header: this.$t(`igate.adapter.id`),
											width: `20%`
										},
										{
											name: `instanceId`,
											header: this.$t(`igate.instance.id`),
											width: `10%`
										},
										{
											name: `snapshotTimestamp`,
											header: this.$t(`dashboard.head.snapshotTimestamp`),
											width: `10%`,
											align: `center`,
											formatter: obj => moment(new Date(obj.value)).format(`HH:mm:ss`)
										},
										{
											name: `y`,
											header: this.$t(`dashboard.head.responseTime`),
											width: `10%`,
											align: `right`,
											formatter: obj => `${obj.value} ms`
										}
									]
								}
							};
						},
						created() {
							this.options = makeGridOptions(this.options);

							WindowResizeEventBus.$on('MainResize', this.resize);

							SideMenuToggleEventBus.$on('MainResize', this.resize);
						},
						mounted() {
							this.$refs.tuiGrid.invoke(`resetData`, e.data.dataArr);
						},
						methods: {
							onClick(evt) {
								if (`undefined` === typeof evt.rowKey || `rowHeader` === evt.targetType) return;

								openNewTab('103020', () => {
									localStorage.setItem(`searchObj`, JSON.stringify({ transactionId: evt.instance.getRow(evt.rowKey).uuid }));
								});
							},
							resize() {
								setTimeout(() => {
									if (!this.$refs.tuiGrid) return;

									const modalBodyElement = document.body.querySelector(`.modal-body`);
									const modalBodyElementStyle = getComputedStyle(modalBodyElement);

									const bodyPaddingTop = getNumFromStr(modalBodyElementStyle.getPropertyValue('padding-top'));
									const bodyPaddingBottom = getNumFromStr(modalBodyElementStyle.getPropertyValue('padding-bottom'));
									const bodyPaddingLeft = getNumFromStr(modalBodyElementStyle.getPropertyValue('padding-left'));
									const bodyPaddingRight = getNumFromStr(modalBodyElementStyle.getPropertyValue('padding-right'));

									const modalPageHeight = modalBodyElement.querySelector('.tui-grid-pagination').clientHeight;

									this.$refs.tuiGrid.invoke('setWidth', modalBodyElement.clientWidth - bodyPaddingLeft - bodyPaddingRight);
									this.$refs.tuiGrid.invoke(
										'setHeight',
										modalBodyElement.clientHeight - bodyPaddingTop - bodyPaddingBottom - modalPageHeight - 32
									);
								}, 0);
							}
						},
						beforeDestroy() {
							e.data.dataArr = null;
						}
					}
				]
			});
		},
		filterDrawData() {
			window.xViewDataObj[this.options.componentId].drawDataArr = window.xViewDataObj[this.options.componentId].saveDataArr.map(data =>
				cloneObject(data)
			);

			if (
				!(
					Object.keys(this.adapterFilterObj).length > 0 ||
					Object.keys(this.instanceFilterObj).length > 0 ||
					this.transactionFilter.length > 0
				)
			)
				return;

			window.xViewDataObj[this.options.componentId].drawDataArr = window.xViewDataObj[this.options.componentId].drawDataArr.filter(data => {
				const isAdapterFilter = 0 < Object.keys(this.adapterFilterObj).length ? this.adapterFilterObj[data.adapterId] : true;

				const isInstanceFilter = 0 < Object.keys(this.instanceFilterObj).length ? this.instanceFilterObj[data.instanceId] : true;

				const isTransactionFilter = 0 < this.transactionFilter.trim().length ? this.transactionFilter.trim() === data.transactionId : true;

				return isAdapterFilter && isInstanceFilter && isTransactionFilter;
			});
		},
		filterData({ targetId, isFiltered }) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			targetInfo.isFiltered = isFiltered;

			this.initXAxis();

			this.filterDrawData();
		},
		xViewFilter({ instanceList, adapterList }) {
			const _this = this;

			const selectInstanceList = 0 < Object.keys(_this.instanceFilterObj).length ? Object.keys(_this.instanceFilterObj) : [``];
			const selectedAdapterList = 0 < Object.keys(_this.adapterFilterObj).length ? Object.keys(_this.adapterFilterObj) : [``];
			const transactionFilter = _this.transactionFilter ? _this.transactionFilter : ``;

			const modalInstance = this.$openModal({
				title: this.$t(`dashboard.head.filter`),
				modalWidth: `500px`,
				contentList: [
					{
						template: `<div>
								       <div class="form-group">
									       <label class="control-label">{{ $t('igate.instance.id') }}</label>
										   <div class="input-group">
										       <select v-model="selectInstanceList" class="form-control" size="5" multiple style="padding-right: 0px; background: none;">
											   	   <option value="">{{ $t('head.all') }}</option>
											       <option v-for="(instanceId, idx) in instanceList" :value="instanceId">{{ instanceId }}</option>
											   </select>
										   </div>
								       </div>
									   <div class="form-group">
									       <label class="control-label">{{ $t('igate.adapter.id') }}</label>
										   <div class="input-group">
										       <select v-model="selectedAdapterList" class="form-control" size="5" multiple style="padding-right: 0px; background: none;">
											   	   <option value="">{{ $t('head.all') }}</option>
											       <option v-for="(adapterId, idx) in adapterList" :value="adapterId">{{ adapterId }}</option>
											   </select>
										   </div>
									   </div>
									   <div class="form-group">
										   <label class="control-label">{{ $t('dashboard.head.interface.service.id') }}</label>
										   <input type="text" v-model="transactionFilter" class="form-control">
									   </div>						
								   </div>`,
						data() {
							return {
								instanceList,
								adapterList,
								selectInstanceList,
								selectedAdapterList,
								transactionFilter
							};
						}
					}
				],
				buttonList: [
					{
						template: `<button type="button" @click="confirm" class="btn btn-primary">
								       {{ $t('head.ok') }}
								   </button>`,
						methods: {
							confirm() {
								const selectInstanceList = modalInstance.$refs.content[0].selectInstanceList.filter(instanceId => instanceId);
								const selectedAdapterList = modalInstance.$refs.content[0].selectedAdapterList.filter(adapterId => adapterId);

								const instanceFilterObj = {};
								const adapterFilterObj = {};

								if (0 < selectInstanceList.length)
									selectInstanceList.forEach(instanceId => {
										instanceFilterObj[instanceId] = true;
									});

								if (0 < selectedAdapterList.length)
									selectedAdapterList.forEach(adapterId => {
										adapterFilterObj[adapterId] = true;
									});

								_this.instanceFilterObj = instanceFilterObj;
								_this.adapterFilterObj = adapterFilterObj;

								_this.transactionFilter = modalInstance.$refs.content[0].transactionFilter.trim();

								_this.initXAxis();

								_this.filterDrawData();

								modalInstance.hideModal();

								_this.containerUserInfo.containerOptions[`xViewInstanceFilter_${_this.options.componentId}`] = JSON.stringify(
									_this.instanceFilterObj
								);

								_this.containerUserInfo.containerOptions[`xViewadapterFilter_${_this.options.componentId}`] = JSON.stringify(
									_this.adapterFilterObj
								);

								_this.containerUserInfo.containerOptions[`xViewtransactionFilter_${_this.options.componentId}`] =
									_this.transactionFilter;

								const containerInfo = cloneDeep(_this.containerInfo);

								const containerUserInfo = containerInfo.monitorContainerUsers.find(
									monitorContainerUser => monitorContainerUser.pk.containerUserId === _this.userInfo.userId
								);

								containerUserInfo.containerOptions = JSON.stringify(_this.containerUserInfo.containerOptions);

								const httpReq = new HttpReq(`/igate/monitoring/dashboard/container.json`);

								httpReq.create(containerInfo, true);
							}
						}
					}
				]
			});
		},
		exportData() {
			this.$startSpinner();

			this.dataFilterWorker.postMessage({
				mod: `export`,
				isSort: true,
				dataArr: window.xViewDataObj[this.options.componentId].saveDataArr,
				startFilterTime: this.xAxisMinValue,
				endFilterTime: this.xAxisMaxValue,
				startFilterValue: 0,
				endFilterValue: this.yAxisMaxValue,
				yAxisMaxValue: this.yAxisMaxValue,
				csvFileName: `${this.options.componentName}_${Date.now()}`,
				adapterFilterObj: this.adapterFilterObj,
				instanceFilterObj: this.instanceFilterObj,
				transactionFilter: this.transactionFilter.trim()
			});
		},
		exportDataCallBack(e) {
			let csvData = ``;

			let dataArr = e.data.dataArr;

			if (this.dashConstInfo.xViewMaxExportCnt < dataArr.length) {
				csvData += `${this.$t(`dashboard.xview.export.maxCnt`, { 0: this.dashConstInfo.xViewMaxExportCnt })}\r\n`;
				dataArr = dataArr.slice(0, this.dashConstInfo.xViewMaxExportCnt);
			}

			csvData += `${this.$t(`dashboard.head.status`)},`;
			csvData += `${this.$t(`dashboard.head.uuid`)},`;
			csvData += `${this.$t(`dashboard.head.transactionId`)},`;
			csvData += `${this.$t(`dashboard.head.transactionName`)},`;
			csvData += `${this.$t(`igate.adapter.id`)},`;
			csvData += `${this.$t(`igate.instance.id`)},`;
			csvData += `${this.$t(`dashboard.head.snapshotTimestamp`)},`;
			csvData += `${this.$t(`dashboard.head.responseTime`)}`;
			csvData += `\r\n`;

			dataArr.forEach(data => {
				csvData += `${
					2 === data.type
						? this.$t(`dashboard.head.error`)
						: 1 === data.type
						? this.$t(`dashboard.head.time.out`)
						: this.$t(`dashboard.head.success`)
				},`;
				csvData += `${data.uuid},`;
				csvData += `${data.transactionId},`;
				csvData += `${data.transactionName},`;
				csvData += `${data.adapterId},`;
				csvData += `${data.instanceId},`;
				csvData += `${moment(new Date(data.snapshotTimestamp)).format(`HH:mm:ss`)},`;
				csvData += `${data.y}`;
				csvData += `\r\n`;
			});

			downloadCSV(e.data.csvFileName, csvData);

			this.$stopSpinner();
		},
		unload() {
			if (!window.xViewDataObj) return;

			if (window.xViewDataObj[this.options.componentId]) {
				delete window.xViewDataObj[this.options.componentId].saveDataArr;
				delete window.xViewDataObj[this.options.componentId].drawDataArr;

				delete window.xViewDataObj[this.options.componentId];
			}

			if (0 === Object.keys(window.xViewDataObj).length) delete window.xViewDataObj;
		}
	}
};
</script>
<style lang="scss" scoped>
.xViewChartRoot {
	height: calc(100% - 31px);
}
</style>
