<script>
import Detail from '@/components/detail/Detail.vue';
import merge from 'lodash/merge';

export default {
	name: 'Detail',
	extends: Detail,
	methods: {
		loadData(callBack) {
			this.$refs.component.forEach(comp => {
				this.tabAllData = merge(this.tabAllData, comp.getData());
			});

			for (const key in this.tabAllData.calendarHoliday) {
				this.tabAllData.calendarHoliday[key].pk.calendarId = this.tabAllData.calendarId;
			}

			if (callBack) callBack();
		}
	}
};
</script>
