<template>
	<ul class="list-group" style="width: 100%">
		<li class="list-group-item" v-for="(instance, index) in instanceList" :key="index">
			<label class="custom-control custom-checkbox">
				<input
					type="checkbox"
					class="custom-control-input view-disabled"
					v-model="instance.check"
					:disabled="readonly"
					@click="checkInstanceId(index)"
				/>
				<span class="custom-control-label"> {{ instance.instanceId }}</span>
			</label>
		</li>
	</ul>
</template>

<script>
import { HttpReq } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';
import cloneDeep from 'lodash/cloneDeep';

export default {
	name: 'DeploiesInfo',
	extends: DetailProperty,
	props: {
		adapterType: String
	},
	data() {
		return {
			instanceList: [],
			singleCheck: false
		};
	},
	computed: {
		readonly() {
			return 'detail' === this.mode || 'done' === this.mode || !this.adapterType;
		}
	},
	watch: {
		adapterType() {
			if (!this.adapterType) return;

			this.singleCheck = this.adapterType.includes(`File`) || this.adapterType.includes(`DB`);
			this.initData();
		}
	},
	async created() {
		const httpReq = new HttpReq('/igate/instance/list.json');

		const result = await httpReq.read();

		if (`ok` !== result.data.result) return;

		this.instanceList = result.data.object
			.filter(instance => 'T' == instance.instanceType)
			.map(instance => {
				return { instanceId: instance.instanceId };
			});
	},
	methods: {
		getData() {
			const instanceList = this.instanceList.filter(property => property.check);
			const rtnArr = [];

			for (let property of instanceList) {
				rtnArr.push(property.instanceId);
			}

			return {
				instanceDeploies: rtnArr
			};
		},
		initData() {
			this.instanceList.forEach(property => {
				delete property.check;
			});
		},
		checkInstanceId(index) {
			if (!this.singleCheck) return;

			const instanceIdList = cloneDeep(this.instanceList);

			this.instanceList = [];

			instanceIdList.forEach((instance, idx) => {
				this.instanceList.push({ instanceId: instance.instanceId, check: idx === index });
			});
		}
	}
};
</script>
