<template>
	<div style="height: 100%">
		<div class="col-lg-12" style="min-height: 270px; height: 100%">
			<div class="form-group" style="height: 100%">
				<label class="control-label">
					<span>{{ $t('igate.traceLog.message.info') }}</span>
				</label>
				<div class="input-group" style="height: 100%">
					<textarea class="form-control view-disabled" readonly="readonly" style="height: 100%" v-model="messageInfo"></textarea>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import isEmpty from 'lodash/isEmpty';
import { HttpReq } from '@/utils/common';
export default {
	name: 'MessageInfo',
	props: {
		detailData: Object
	},
	data() {
		return {
			dumpHttp: new HttpReq('/igate/traceLog/dump.json'),
			messageInfo: null
		};
	},
	watch: {
		async detailData() {
			if (isEmpty(this.detailData)) return;

			const result = await this.dumpHttp.read(this.detailData);

			this.messageInfo = 'ok' == result.data.result ? result.data.response : result.data.error[0].message;
		}
	}
};
</script>
