<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/mapping/Detail.vue';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'Mapping',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			cudUrl: '/igate/mapping/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'mappingId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'mappingName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'text',
					vModel: 'mappingGroup',
					label: this.$t(`head.group`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'name'
				},
				{
					type: 'dataList',
					vModel: 'privilegeId',
					label: this.$t(`common.privilege`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'searchId',
					optionInfo: {
						url: '/common/privilege/list.json',
						params: {
							privilegeType: `b`
						},
						optionListName: 'privilegeTypeList',
						optionFor: 'option in privilegeTypeList',
						optionValue: 'option.privilegeId',
						optionText: 'option.privilegeId'
					}
				},
				{
					type: 'text',
					vModel: 'mappingDesc',
					label: this.$t(`head.description`),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: '/igate/mapping/search.json',
				totalCntUrl: '/igate/mapping/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'mappingId',
							header: this.$t('head.id'),
							width: '24%'
						},
						{
							name: 'mappingName',
							header: this.$t('head.name'),
							width: '24%'
						},
						{
							header: this.$t('head.group'),
							name: 'mappingGroup',
							width: '12%'
						},
						{
							name: 'privilegeId',
							header: this.$t('common.privilege'),
							width: '10%'
						},
						{
							header: this.$t('head.description'),
							name: 'mappingDesc',
							width: '30%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//detail
			detailPk: ['mappingId'],
			controlUrl: `/igate/mapping/control.json`,
			controlParams: detailData => ({
				mappingId: detailData.mappingId
			}),
			detailButtonList: [{ id: 'dump', isUse: true }],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/mapping/BasicInfo'
				},
				{
					type: 'bundle',
					label: this.$t('head.inoutput.info'),
					url: '/mapping/MappingInfo'
				},
				{
					type: 'basic',
					label: this.$t('head.resource.inuse.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'lockUserId',
									label: this.$t('head.lock.userId'),
									readonly: true,
									val: '0'
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: this.$t('head.updateVersion'),
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			]
		};
	}
};
</script>
