<template>
	<div class="dashboardViewBodyRoot">
		<div class="empty" v-if="isEmpty">
			<p>{{ $t(`dashboard.select.noDashboard`) }}</p>
			<img :src="require('@/assets/img/empty.svg')" class="center-block" />
		</div>
		<component
			ref="dashboardBodyArea"
			:is="dashboardBodyArea"
			@filterTarget="filterTarget"
			:dashConstInfo="dashConstInfo"
			:containerInfo="containerInfo"
			:containerUserInfo="containerUserInfo"
			v-else
		/>
	</div>
</template>

<script>
import { binanrySearch } from '@/utils/dashboard/chart_utils.js';

export default {
	name: `DashboardViewBody`,
	props: {
		containerInfo: Object,
		containerUserInfo: Object,
		perfItemList: Array,
		instanceList: Array,
		adapterList: Array,
		connectorList: Array,
		queueList: Array,
		threadList: Array,
		externalLineList: Array,
		dashConstInfo: Object
	},
	data() {
		return {
			componentDataMapping: {},
			dashboardBodyArea: null,
			isEmpty: true
		};
	},
	watch: {
		containerInfo() {
			this.isEmpty = true;

			if (!this.containerInfo) return;

			this.isEmpty = false;

			this.initDashboardBodyArea();
		}
	},
	methods: {
		addData(dataObj) {
			this.$refs.dashboardBodyArea.addData(dataObj);
		},
		setInterval(intervalObj) {
			this.$refs.dashboardBodyArea.setInterval(intervalObj);
		},
		setRequestAnimationFrame(rafObj) {
			this.$refs.dashboardBodyArea.setRequestAnimationFrame(rafObj);
		},
		unload() {
			this.isEmpty = true;

			if (!this.containerInfo) return;

			this.isEmpty = false;

			this.$refs.dashboardBodyArea.unload();
		},
		filterTarget(param) {
			this.$emit(`filterTarget`, param);
		},
		filterTargetAfter() {
			if (!this.$refs.dashboardBodyArea) return;

			for (const key in this.$refs.dashboardBodyArea.$refs) {
				if (!this.$refs.dashboardBodyArea.$refs[key].filterData) continue;

				this.instanceList.forEach(instnaceInfo => {
					this.$refs.dashboardBodyArea.$refs[key].filterData({
						targetId: instnaceInfo.instanceId,
						isFiltered: instnaceInfo.isFiltered
					});
				});
			}
		},
		appendComponentTag(pComponentObj, parentTag) {
			const appendDiv = document.createElement(`div`);

			appendDiv.style.width = pComponentObj.componentWidth;
			appendDiv.style.height = pComponentObj.componentHeight;

			if (`-1` === pComponentObj.pComponentId) appendDiv.setAttribute(`id`, `root`);

			appendDiv.setAttribute(`component-id`, pComponentObj.componentId);
			this.componentDataMapping[appendDiv.getAttribute(`component-id`)] = pComponentObj;

			if ('h' == pComponentObj.componentTypeDirection) appendDiv.classList.add(`component-horizontal`);
			else if ('v' == pComponentObj.componentTypeDirection) appendDiv.classList.add(`component-vertical`);

			const childComponentList = this.containerInfo.monitorComponents
				.filter(componentInfo => componentInfo.pComponentId === pComponentObj.componentId)
				.sort((a, b) => a.componentOrder - b.componentOrder);

			if (0 < childComponentList.length) {
				childComponentList.forEach(childComponent => {
					if (`G` === childComponent.componentType) {
						this.appendComponentTag(childComponent, appendDiv);
					} else {
						const componentDiv = document.createElement(`div`);

						componentDiv.setAttribute(`component-id`, childComponent.componentId);
						this.componentDataMapping[componentDiv.getAttribute(`component-id`)] = childComponent;

						componentDiv.classList.add(`component`);
						componentDiv.style.width = childComponent.componentWidth;
						componentDiv.style.height = childComponent.componentHeight;

						const card = document.createElement(`section`);
						card.classList.add(`card`);

						const cardHeader = document.createElement(`h2`);
						cardHeader.classList.add(`card-header`);

						const perfItemUl = document.createElement(`ul`);

						const perfItemLi = document.createElement(`li`);
						perfItemLi.classList.add(`nav-link`);

						const perfItemIcon = document.createElement(`i`);
						perfItemIcon.classList.add(this.perfItemList.find(perfItem => perfItem.itemId == childComponent.itemId).itemIcon);

						const perfItemName = document.createElement(`span`);
						perfItemName.innerText = childComponent.componentName;

						perfItemLi.appendChild(perfItemIcon);
						perfItemLi.appendChild(perfItemName);

						perfItemUl.appendChild(perfItemLi);

						const typeList = { targetType: null, inputType: null };

						if (`LINE` === childComponent.chartType || `XVIEW` === childComponent.chartType || `DATATABLE` === childComponent.chartType) {
							const infoList = this.$parent.getTypeList(this.perfItemList.find(perfItem => perfItem.itemId == childComponent.itemId));

							let targetType = infoList.targetTypeList.find(info => info.value == childComponent.targetType).name;

							if (`INSTANCE` !== childComponent.targetType) {
								targetType += '-';

								if (`ADAPTER` === childComponent.targetType) {
									if (0 < childComponent.monitorComponentTargets.length) {
										this.adapterList.forEach(adapterInfo => {
											if (adapterInfo.adapterId === childComponent.monitorComponentTargets[0].pk.componentTargetId)
												targetType += adapterInfo.adapterId;
										});
									}
								} else if (`CONNECTOR` === childComponent.targetType) {
									if (0 < childComponent.monitorComponentTargets.length) {
										this.connectorList.forEach(connectorInfo => {
											if (connectorInfo.connectorId === childComponent.monitorComponentTargets[0].pk.componentTargetId)
												targetType += connectorInfo.connectorId;
										});
									}
								} else if (`QUEUE` === childComponent.targetType) {
									if (0 < childComponent.monitorComponentTargets.length) {
										this.queueList.forEach(queueInfo => {
											if (queueInfo.connectorId === childComponent.monitorComponentTargets[0].pk.componentTargetId)
												targetType += queueInfo.connectorId;
										});
									}
								} else if (`THREAD` === childComponent.targetType) {
									if (0 < childComponent.monitorComponentTargets.length) {
										this.threadList.forEach(threadInfo => {
											if (threadInfo.threadPoolId == childComponent.monitorComponentTargets[0].pk.componentTargetId)
												targetType += threadInfo.threadPoolId;
										});
									}
								}
							}

							typeList.targetType = targetType;
							typeList.inoutType =
								`NONE` !== childComponent.inoutType
									? infoList.inoutTypeList.find(info => info.value == childComponent.inoutType).name
									: ``;

							const perfItemExportLi = document.createElement(`li`);
							perfItemExportLi.classList.add(`nav-link`, `export-nav-link`);

							if (`XVIEW` === childComponent.chartType) {
								const filterSetIcon = document.createElement(`i`);
								filterSetIcon.classList.add(`icon-set`);
								filterSetIcon.setAttribute(`name`, `filterSetIcon`);
								filterSetIcon.setAttribute(`title`, `Filter Set`);
								filterSetIcon.setAttribute(`v-on:click`, `xViewFilter('${childComponent.componentId}')`);

								perfItemExportLi.appendChild(filterSetIcon);
							}

							const exportIcon = document.createElement(`i`);
							exportIcon.classList.add(`icon-export`);
							exportIcon.setAttribute(`name`, `exportIcon`);
							exportIcon.setAttribute(`title`, `Export`);
							exportIcon.setAttribute(`v-on:click`, `exportData('${childComponent.componentId}')`);

							perfItemExportLi.appendChild(exportIcon);

							perfItemUl.appendChild(perfItemExportLi);
						}

						cardHeader.appendChild(perfItemUl);

						const cardBody = document.createElement(`div`);
						cardBody.classList.add(`card-body`);
						cardBody.style.paddingTop = `0px`;

						const component = document.createElement(`component`);

						component.setAttribute(`v-bind:is`, `getComponent('${childComponent.chartType}')`);
						component.setAttribute(`ref`, childComponent.componentId);
						component.setAttribute(`v-on:line-chart-tooltip-sync`, `lineChartTooltipSync`);
						component.setAttribute(`v-on:filter-target`, `filterTarget`);
						component.setAttribute(
							`v-bind:options`,
							JSON.stringify({
								componentId: childComponent.componentId,
								componentName: childComponent.componentName,
								itemId: childComponent.itemId,
								unitType: childComponent.unitType,
								darkmodeYn: this.containerInfo.darkmodeYn,
								remarkYn: this.containerInfo.remarkYn,
								typeList,
								isUseClickEvt: `elapsed` === childComponent.itemId || `deal` === childComponent.itemId ? false : true
							})
						);
						component.setAttribute(`v-bind:dash-const-info`, 'dashConstInfo');
						component.setAttribute(`v-bind:container-info`, 'containerInfo');
						component.setAttribute(`v-bind:container-user-info`, 'containerUserInfo');

						cardBody.appendChild(component);

						card.appendChild(cardHeader);
						card.appendChild(cardBody);

						componentDiv.appendChild(card);

						appendDiv.appendChild(componentDiv);
					}
				});
			}

			if (parentTag) parentTag.appendChild(appendDiv);
			else return appendDiv;
		},
		initDashboardBodyArea() {
			const _this = this;

			const template = this.appendComponentTag(
				this.containerInfo.monitorComponents.find(componentInfo => `-1` === componentInfo.pComponentId)
			).outerHTML;

			this.dashboardBodyArea = {
				template,
				props: {
					dashConstInfo: Object,
					containerInfo: Object,
					containerUserInfo: Object
				},
				mounted() {
					this.initChartTargetInfo();
				},
				methods: {
					getComponent(chartType) {
						let chartName = ``;

						if (`LINE` === chartType) chartName = `DashboardLineChart`;
						else if (`COLUMN` === chartType) chartName = `DashboardColumnChart`;
						else if (`SPEEDBAR` === chartType) chartName = `DashboardSpeedBarChart`;
						else if (`CONNECTOR` === chartType) chartName = `DashboardConnectorChart`;
						else if (`QUEUE` === chartType) chartName = `DashboardQueueChart`;
						else if (`THREAD` === chartType) chartName = `DashboardThreadChart`;
						else if (`INSTANCE` === chartType) chartName = `DashboardInstanceChart`;
						else if (`EXTERNALLINE` === chartType) chartName = `DashboardExternalLineChart`;
						else if (`DATATABLE` === chartType) chartName = `DashboardDataTableChart`;
						else if (`XVIEW` === chartType) chartName = `DashboardXviewChart`;

						return require(`@/views/main/dashboard/${chartName}.vue`).default;
					},
					initChartTargetInfo() {
						for (const key in this.$refs) {
							const chart = this.$refs[key];
							const { itemId, targetType, monitorComponentTargets } = _this.componentDataMapping[key];

							if (`elapsed` === itemId || `deal` === itemId) {
								const seriesArr =
									`elapsed` === itemId
										? [
												{ id: `elapsedMax`, name: this.$t(`dashboard.head.max`), color: this.dashConstInfo.COLORS[0] },
												{ id: `elapsedMin`, name: this.$t(`dashboard.head.min`), color: this.dashConstInfo.COLORS[1] },
												{ id: `elapsedAvg`, name: this.$t(`dashboard.head.avg`), color: this.dashConstInfo.COLORS[2] }
										  ]
										: [
												{ id: `success`, name: this.$t(`dashboard.head.success`), color: `#62d36f` },
												{ id: `timeout`, name: this.$t(`dashboard.head.time.out`), color: `#efc402` },
												{ id: `error`, name: this.$t(`dashboard.head.error`), color: `#ed3137` }
										  ];

								seriesArr.forEach(seriesObj => {
									if (chart.addTarget)
										chart.addTarget({ targetId: seriesObj.id, targetName: seriesObj.name, color: seriesObj.color });
								});
							} else if (`INSTANCE` === targetType) {
								_this.instanceList.forEach(instanceInfo => {
									if (chart.addTarget)
										chart.addTarget({
											targetId: instanceInfo.instanceId,
											targetName: instanceInfo.instanceId,
											color: instanceInfo.color
										});
								});
							} else if (`ADAPTER` === targetType) {
								monitorComponentTargets.forEach((componentInfo, index) => {
									if (chart.addTarget)
										chart.addTarget({
											targetId: componentInfo.pk.componentTargetId,
											targetName: componentInfo.pk.componentTargetId,
											color: this.dashConstInfo.COLORS[index]
										});
								});
							} else if (`CONNECTOR` === targetType) {
								monitorComponentTargets.forEach(componentInfo => {
									const connectorObj = _this.connectorList.find(
										connector => connector.connectorId === componentInfo.pk.componentTargetId
									);

									if (connectorObj) {
										connectorObj.instanceList.forEach(instanceObj => {
											if (chart.addTarget)
												chart.addTarget({
													targetId: instanceObj.pk.instanceId,
													targetName: instanceObj.pk.instanceId
												});
										});
									}
								});
							} else if (`QUEUE` === targetType) {
								monitorComponentTargets.forEach(componentInfo => {
									const queueObj = _this.queueList.find(queue => queue.adapterId === componentInfo.pk.componentTargetId);

									if (queueObj) {
										queueObj.instanceList.forEach(instanceObj => {
											if (chart.addTarget)
												chart.addTarget({
													targetId: instanceObj.pk.instanceId,
													targetName: instanceObj.pk.instanceId
												});
										});
									}
								});
							} else if (`THREAD` === targetType) {
								_this.instanceList
									.filter(instance => `T` === instance.instanceType)
									.forEach(instanceInfo => {
										if (chart.addTarget)
											chart.addTarget({ targetId: instanceInfo.instanceId, targetName: instanceInfo.instanceId });
									});
							} else if (`EXTERNALLINE` === targetType) {
								_this.externalLineList.forEach(externalInfo => {
									if (chart.addTarget)
										chart.addTarget({
											targetId: externalInfo.externalLineId,
											targetName: externalInfo.externalLineName
										});
								});
							}

							if (chart.draw) chart.draw();
						}
					},
					addData(dataObj) {
						for (const key in this.$refs) {
							if (this.$refs[key].addData) this.$refs[key].addData(dataObj[key]);
						}

						dataObj = null;
					},
					setInterval(intervalObj) {
						for (const key in this.$refs) {
							if (!this.$refs[key].setInterval) continue;
							this.$refs[key].setInterval(intervalObj);
						}
					},
					setRequestAnimationFrame(rafObj) {
						for (const key in this.$refs) {
							if (!this.$refs[key].setRequestAnimationFrame) continue;
							this.$refs[key].setRequestAnimationFrame(rafObj);
						}
					},
					unload() {
						for (const key in this.$refs) {
							if (!this.$refs[key].unload) continue;
							this.$refs[key].unload();
						}
					},
					lineChartTooltipSync(paramObj) {
						_this.containerInfo.monitorComponents.forEach(monitorComponent => {
							const { componentId, chartType } = monitorComponent;

							if (`LINE` !== chartType) return;

							let findDataSetIdx = null;
							let findDataObjIdx = null;

							const chartObj = this.$refs[componentId].chartObj;

							if (chartObj.canvas == paramObj.chartObj.canvas) {
								findDataSetIdx = paramObj.datasetIndex;
								findDataObjIdx = paramObj.dataIndex;
							} else {
								for (let j = 0; j < chartObj.data.datasets.length; j++) {
									const dataset = chartObj.data.datasets[j];

									const findDataObj = binanrySearch(dataset.data, paramObj.standardTimeMillis);

									if (!findDataObj) continue;

									findDataSetIdx = j;
									findDataObjIdx = findDataObj.idx;

									break;
								}
							}

							if (null === findDataSetIdx || null === findDataObjIdx) return;

							const tmpDataset = chartObj.data.datasets[findDataSetIdx];

							const _view = tmpDataset._meta[Object.keys(tmpDataset._meta)[0]].data[findDataObjIdx]._view;

							if (!_view.x || !_view.y) return;

							const rect = chartObj.canvas.getBoundingClientRect();

							chartObj.canvas.dispatchEvent(new MouseEvent('mousemove', { clientX: rect.left, clientY: rect.top }));
							chartObj.canvas.dispatchEvent(new MouseEvent('mousemove', { clientX: rect.left + _view.x, clientY: rect.top + _view.y }));
						});
					},
					filterTarget(param) {
						this.$emit(`filterTarget`, param);
					},
					xViewFilter(componentId) {
						this.$refs[componentId].xViewFilter({
							instanceList: _this.instanceList.filter(instance => `T` === instance.instanceType).map(instance => instance.instanceId),
							adapterList: _this.adapterList.map(adapter => adapter.adapterId)
						});
					},
					exportData(componentId) {
						if (this.$refs[componentId].exportData) this.$refs[componentId].exportData();
					}
				}
			};
		}
	}
};
</script>

<style lang="scss" scoped>
.dashboardViewBodyRoot {
	width: 100%;
}
</style>
