import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

import App from '@/App';
import Login from '@/views/Login';
import Layout from '@/views/Layout';
import LayoutExternal from '@/views/LayoutExternal';

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{ path: '/app/login.html', name: 'login', component: Login },
		{ path: '/app/layout.html', name: 'layout', component: Layout },
		{ path: '/app/layoutExternal.html', name: 'layoutExternal', component: LayoutExternal },
		{ path: '/', name: 'app', component: App }
	]
});

export default router;

router.beforeEach((to, from, next) => {
	if (`c` === to.query._client_mode) {
		sessionStorage.setItem(`externalMenuUrl`, to.path);

		const param = 0 !== Object.keys(to.query).filter(key => key !== `_client_mode`).length ? to.query : null;

		if (null !== param) {
			delete param['_client_mode'];

			sessionStorage.setItem(`externalMenuParam`, JSON.stringify(param));
			localStorage.setItem(`searchObj`, JSON.stringify(param));
		}
	}

	next();
});
