<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/connector/Detail.vue';
import { getPrivilege, HttpReq, escapeHtml } from '@/utils/common.js';

export default {
	name: 'Connector',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	async mounted() {
		const result = await new HttpReq(`/common/property/properties.json`).read({ propertyId: 'List.Connector.Type', orderByKey: true });
		if (`ok` === result.data.result) this.connectorTypeList = result.data.object;

		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => this.search(this.$refs.search.getSearchObj()));
		}
	},
	data() {
		return {
			connectorTypeList: [],

			cudUrl: '/igate/connector/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'connectorId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'connectorName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'modal',
					vModel: 'adapterId',
					label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.adapter'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'adapterId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'adapterName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'text',
									vModel: 'adapterDesc',
									label: this.$t('head.description'),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									placeholder: this.$t('head.searchCoditionList'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/adapter/searchPopup.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('head.id'),
										name: 'adapterId'
									},
									{
										header: this.$t('head.name'),
										name: 'adapterName'
									},
									{
										header: this.$t('head.description'),
										name: 'adapterDesc'
									},
									{
										header: this.$t('igate.adapter.structure.request'),
										name: 'requestStructure'
									},
									{
										header: this.$t('igate.adapter.structure.response'),
										name: 'responseStructure'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => rowInfo.adapterId
					}
				},
				{
					type: 'text',
					vModel: 'socketAddress',
					label: `Socket Address`,
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'socketPort',
					label: `Socket Port`,
					placeholder: this.$t('head.searchData'),
					regExpType: 'num'
				},
				{
					type: 'select',
					vModel: 'connectorType',
					label: this.$t('common.type'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Connector.Type',
							orderByKey: true
						},
						optionListName: 'connectorTypeList',
						optionFor: 'option in connectorTypeList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'text',
					vModel: 'connectorDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'add', isUse: true }
			],

			// Grid
			gridInfo: {
				url: '/igate/connector/search.json',
				totalCntUrl: '/igate/connector/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'connectorId',
							header: this.$t('head.id'),
							width: '30%'
						},
						{
							name: 'connectorName',
							header: this.$t('head.name'),
							width: '40%'
						},
						{
							name: 'adapterId',
							header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
							width: '30%'
						},
						{
							name: 'connectorType',
							header: this.$t('common.type'),
							width: '30%',
							align: `center`,
							formatter: ({ row }) => {
								const findConnectorType = this.connectorTypeList.find(info => info.pk.propertyKey === row.connectorType);
								return findConnectorType ? escapeHtml(findConnectorType.propertyValue) : '';
							}
						},

						{
							name: 'connectorDesc',
							header: this.$t('head.description'),
							width: '40%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['connectorId'],
			controlUrl: '/igate/connector/control.json',
			controlParams: detailData => {
				return {
					connectorId: detailData.connectorId,
					instance: null
				};
			},
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'guide', isUse: true, url: '/manual/Connector Guide.htm' },
				{ id: 'dump', isUse: true },
				{ id: 'start', isUse: true },
				{ id: 'stop', isUse: true },
				{ id: 'stopForce', isUse: true },
				{ id: 'interrupt', isUse: true },
				{ id: 'block', isUse: ({ requestDirection }) => `O` === requestDirection },
				{ id: 'unblock', isUse: ({ requestDirection }) => `O` === requestDirection }
			],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/connector/BasicInfo'
				},
				{
					type: 'bundle',
					label: `${this.$t('igate.connector')} ${this.$t('head.property')}`,
					url: '/connector/PropertyInfo'
				},
				{
					type: 'property',
					label: this.$t('igate.adapter'),
					dataKey: 'connectorAdapters',
					addRowInfo: {
						type: 'modal',
						modalInfo: {
							title: this.$t('igate.adapter'),
							search: {
								list: [
									{
										type: 'text',
										vModel: 'adapterId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'text',
										vModel: 'adapterName',
										label: this.$t('head.name'),
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'text',
										vModel: 'adapterDesc',
										label: this.$t('head.description'),
										placeholder: this.$t('head.searchComment'),
										regExpType: 'desc'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										placeholder: this.$t('head.searchCoditionList'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								]
							},
							button: {
								list: [{ id: 'initialize', isUse: true }]
							},
							grid: {
								url: '/igate/adapter/searchPopup.json',
								totalCntUrl: '/igate/adapter/rowCount.json',
								paging: {
									isUse: true,
									side: 'server'
								},
								options: {
									columns: [
										{
											header: this.$t('head.id'),
											name: 'adapterId',
											width: '20%'
										},
										{
											header: this.$t('head.name'),
											name: 'adapterName',
											width: '20%'
										},
										{
											header: this.$t('head.description'),
											name: 'adapterDesc',
											width: '20%'
										},
										{
											header: this.$t('igate.adapter.structure.request'),
											name: 'requestStructure',
											width: '20%'
										},
										{
											header: this.$t('igate.adapter.structure.response'),
											name: 'responseStructure',
											width: '20%'
										}
									]
								}
							},
							rowClickedCallback: function (rowInfo, propertyList) {
								return {
									duplicate:
										0 === propertyList.length
											? false
											: propertyList.some(function (property) {
													return property[`pk.adapterId`] === rowInfo.adapterId;
											  }),
									propertyInfo: { 'pk.adapterId': rowInfo.adapterId }
								};
							}
						}
					},
					content: [
						{
							type: 'text',
							vModel: 'pk.adapterId',
							label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
							readonly: true
						}
					]
				},
				{
					type: 'bundle',
					label: `${this.$t('igate.instance')} ${this.$t('head.id')}`,
					url: '/connector/DeploiesInfo'
				}
			]
		};
	}
};
</script>
