<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('head.insert')">
					<i class="icon-plus-circle"></i>
				</button>
				<label class="col">{{ $t('common.property.key') }}</label>
				<label class="col">{{ $t('common.property.value') }}</label>
				<label class="col">{{ $t('head.description') }}</label>
			</div>
			<div class="form-table-wrap">
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('head.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div style="width: 100%">
							<dataList
								:ref="`component${bodyIndex}`"
								vModel="pk.propertyKey"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['pk.propertyKey']"
								:readonly="readonly"
								:optionInfo="keyIdOptionInfo"
							/>
						</div>
					</div>

					<div class="col">
						<div style="width: 100%">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="propertyValue"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['propertyValue']"
								:readonly="readonly"
							/>
						</div>
					</div>

					<div class="col">
						<div style="width: 100%">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="propertyDesc"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['propertyDesc']"
								:readonly="readonly"
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import isEmpty from 'lodash/isEmpty';
import { getUUID, HttpReq } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';

export default {
	name: 'RecordExtendInfo',
	extends: DetailProperty,
	data() {
		return {
			recordPropertyList: [],
			keyIdOptionInfo: {
				url: '/common/property/properties.json',
				params: {
					propertyId: 'Property.Record',
					orderByKey: true
				},
				optionListName: 'recordPropertyList',
				optionFor: 'option in recordPropertyList',
				optionValue: 'option.pk.propertyKey',
				optionText: 'option.pk.propertyKey'
			}
		};
	},
	async created() {
		const httpReq = new HttpReq('/common/property/properties.json');

		const result = await httpReq.read({ propertyId: 'Property.Record', orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.recordPropertyList = result.data.object;
	},
	methods: {
		async setPropertyList() {
			this.propertyList = [];

			if (isEmpty(this.detailData)) return;

			for (const key in this.detailData) {
				if (!key.startsWith(`options.`)) continue;

				this.propertyList.push({
					idx: getUUID(),
					'pk.propertyKey': key.replace(`options.`, ``),
					propertyValue: this.detailData[key],
					propertyDesc: null
				});

				const property = this.recordPropertyList.find(property => key.replace(`options.`, ``) === property.pk.propertyKey);

				if (property) {
					this.propertyList[this.propertyList.length - 1].propertyDesc = property.propertyDesc;
				}
			}
		}
	}
};
</script>
