<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/service/Detail.vue';
import { getPrivilege, HttpReq, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'Service',
	extends: Basic,
	components: {
		Detail
	},
	async created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;

		const result = await new HttpReq(`/common/property/properties.json`).read({ propertyId: 'List.Service.ServiceType', orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.serviceTypeList = result.data.object;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}

		if (localStorage.getItem('selectedServiceInfo')) {
			const selectedServiceInfo = JSON.parse(localStorage.getItem('selectedServiceInfo'));
			localStorage.removeItem('selectedServiceInfo');

			this.initDetail('detail', {
				id: selectedServiceInfo.serviceId,
				isNewTab: true
			});
		}
	},
	data() {
		return {
			serviceTypeList: [],

			cudUrl: '/igate/service/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'serviceId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'serviceName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'serviceType',
					label: this.$t('common.type'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Service.ServiceType',
							orderByKey: true
						},
						optionListName: 'serviceTypeList',
						optionFor: 'option in serviceTypeList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'modal',
					vModel: 'adapterId',
					label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.adapter'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'adapterId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'adapterName',
									label: this.$t('common.type'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'text',
									vModel: 'adapterDesc',
									label: this.$t('head.description'),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									placeholder: this.$t('head.searchCoditionList'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/adapter/searchPopup.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('head.id'),
										name: 'adapterId'
									},
									{
										header: this.$t('head.name'),
										name: 'adapterName'
									},
									{
										header: this.$t('head.description'),
										name: 'adapterDesc'
									},
									{
										header: this.$t('igate.adapter.structure.request'),
										name: 'requestStructure'
									},
									{
										header: this.$t('igate.adapter.structure.response'),
										name: 'responseStructure'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => rowInfo.adapterId
					}
				},
				{
					type: 'text',
					vModel: 'serviceGroup',
					label: this.$t('head.group'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'dataList',
					vModel: 'privilegeId',
					label: this.$t(`common.privilege`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'searchId',
					optionInfo: {
						url: '/common/privilege/list.json',
						params: {
							privilegeType: `b`
						},
						optionListName: 'privilegeTypeList',
						optionFor: 'option in privilegeTypeList',
						optionValue: 'option.privilegeId',
						optionText: 'option.privilegeId'
					}
				},
				{
					type: 'text',
					vModel: 'serviceDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'excelExport', isUse: true, exportUrl: '/igate/service/exportExcel.json', fileName: `Service ExcelExport` }
			],

			// Grid
			gridInfo: {
				url: '/igate/service/search.json',
				totalCntUrl: '/igate/service/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'serviceId',
							header: this.$t('head.id'),
							width: '15%'
						},
						{
							name: 'serviceName',
							header: this.$t('head.name'),
							width: '15%'
						},
						{
							name: 'serviceType',
							header: this.$t('common.type'),
							align: `center`,
							width: '10%',
							formatter: ({ value }) => this.serviceTypeList.find(serviceType => serviceType.pk.propertyKey === value).propertyValue
						},
						{
							name: 'adapterId',
							header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
							width: '15%'
						},
						{
							name: 'serviceGroup',
							header: this.$t('head.group'),
							width: '15%'
						},
						{
							name: 'privilegeId',
							header: this.$t('common.privilege'),
							width: '10%'
						},
						{
							header: this.$t('head.description'),
							name: 'serviceDesc',
							width: '20%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['serviceId'],
			controlUrl: `/igate/service/control.json`,
			controlParams: detailData => ({
				serviceId: detailData.serviceId
			}),
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [{ id: 'dump', isUse: true }],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/service/BasicInfo'
				},
				{
					type: 'bundle',
					url: '/service/ExtendInfo',
					label: this.$t('head.extension.info')
				},
				{
					type: 'basic',
					label: this.$t('head.resource.inuse.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'lockUserId',
									label: this.$t('head.lock.userId'),
									readonly: true,
									val: '0'
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: this.$t('head.updateVersion'),
									readonly: true,
									val: '0'
								}
							],
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			]
		};
	}
};
</script>
