<template>
	<div class="connectorChartRoot">
		<div class="row gate-row-wrp flex-1" :style="style">
			<div class="col-6" v-for="n in 2" :key="n">
				<component
					v-for="(targetInfo, idx) in targetInfoList ? targetInfoList.filter((targetInfo, idx) => n - 1 === idx % 2) : []"
					:is="targetInfo.component"
					:data="targetInfo.data"
					@filterTarget="filterTarget"
					:key="`${idx}_${targetInfo.targetId}`"
				/>
			</div>
		</div>
		<div class="d-flex-space" v-if="isShowLegend">
			<div class="legend legend-square">
				<span class="status"><i class="dot bg-cht-11"></i>{{ $t(`dashboard.legend.square.activeSession`) }}</span>
				<span class="status"><i class="dot bg-cht-11-l"></i>{{ $t(`dashboard.legend.square.idleSession`) }}</span>
				<span class="status"><i class="dot bg-cht-2"></i>{{ $t(`dashboard.legend.square.activeThread`) }} </span>
				<span class="status"><i class="dot bg-cht-2-l"></i> {{ $t(`dashboard.legend.square.idleThread`) }}</span>
			</div>
			<div class="legend">
				<span class="status"><i class="dot bg-normal"></i>Normal</span>
				<span class="status"><i class="dot bg-warn"></i>Warn</span>
				<span class="status"><i class="dot bg-error"></i>Error</span>
				<span class="status"><i class="dot bg-fail"></i>Fail</span>
				<span class="status"><i class="dot bg-cht-3"></i>Starting</span>
				<span class="status"><i class="dot bg-cht-2"></i>Stoping</span>
				<span class="status"><i class="dot bg-cht-4"></i>Blocking</span>
				<span class="status"><i class="dot bg-down"></i>Down</span>
			</div>
		</div>
	</div>
</template>

<script>
import { escapeHtml } from '@/utils/common.js';

export default {
	name: `DashboardConnectorChart`,
	props: {
		options: Object
	},
	data() {
		return {
			targetInfoList: [],
			isShowLegend: false,
			style: {
				height: null
			}
		};
	},
	mounted() {
		this.initConnectorChart();
	},
	methods: {
		initConnectorChart() {
			this.isShowLegend = `Y` === this.options.remarkYn;

			this.$nextTick(() => {
				this.style.height = this.isShowLegend ? `calc(100% - ${this.$el.querySelector(`.d-flex-space`).offsetHeight}px)` : `100%`;
			});
		},
		addTarget(targetObj) {
			targetObj.data = {
				targetId: targetObj.targetId,
				status: `Normal`,
				isDown: false,
				isFiltered: false,
				sessionInuse: 0,
				sessionWait: 0,
				threadInuse: 0,
				threadWait: 0
			};

			targetObj.component = {
				template: `
					<div class="gate-row" :style="rootElementStyle">
						<div class="gate">
							<i class="iconb-warn" title="Down" v-if="isDown"></i>
							<i :class="[statusDefaultClass, statusDataClass]"></i>
							<a href="javascript:void(0)" class="t-xs" data-toggle="tooltip" :title="targetId" @click="filterTarget">{{ targetId }}</a>
						</div>
						<div class="gate-graph">
							<div class="progress up">
								<div class="progress-bar bg-cht-11" role="progressbar" :style="sessionInuseGraphStyle" data-toggle="tooltip" data-placement="top">
									<span class="t-xs">{{ sessionInuse }}</span>
								</div>
								<div class="progress-bar bg-cht-11-l" role="progressbar" :style="sessionWaitGraphStyle" data-toggle="tooltip" data-placement="top">
									<span class="t-xs">{{ sessionWait }}</span>
								</div>
							</div>
							<div class="progress">
								<div class="progress-bar bg-cht-2" role="progressbar" :style="threadInuseGraphStyle" data-toggle="tooltip" data-placement="bottom">
									<span class="t-xs">{{ threadInuse }}</span>
								</div>
								<div class="progress-bar bg-cht-2-l" role="progressbar" :style="threadWaitGraphStyle" data-toggle="tooltip" data-placement="bottom">
									<span class="t-xs">{{ threadWait }}</span>
								</div>
							</div>
						</div>
						<div class="status-msg">
							<i class="icon-using" data-toggle="tooltip" data-placement="top" title="${this.$t(`dashboard.wait.session`)}" style="display: none;"></i> 
							<i class="icon-thread" data-toggle="tooltip" data-placement="top" title="${this.$t(`dashboard.create.session`)}" style="display: none;"></i>
							<i class="icon-screated" data-toggle="tooltip" data-placement="top" title="${this.$t(`dashboard.using.session`)}" style="display: none;"></i> 
						</div>
					</div>				
				`,
				props: {
					data: {
						type: Object,
						default: () => ({
							targetId: null,
							status: `Normal`,
							isDown: false,
							isFiltered: false,
							sessionInuse: 0,
							sessionWait: 0,
							threadInuse: 0,
							threadWait: 0
						})
					}
				},
				computed: {
					targetId() {
						return escapeHtml(this.data.targetId);
					},
					statusDataClass() {
						const status = this.data.status;

						if (`Normal` === status) return `bg-normal`;
						else if (`Warn` === status) return `bg-warn`;
						else if (`Error` === status) return `bg-error`;
						else if (`Fail` === status) return `bg-fail`;
						else if (`Starting` === status) return `bg-cht-3`;
						else if (`Stoping` === status) return `bg-cht-2`;
						else if (`Blocking` === status) return `bg-cht-4`;
						else if (`Down` === status) return `bg-down`;
						else return `bg-danger`;
					},
					isDown() {
						return this.data.isDown;
					},
					rootElementStyle() {
						return {
							opacity: this.data.isFiltered ? 0.2 : 1.0
						};
					},
					sessionInuse() {
						return this.data.sessionInuse;
					},
					sessionWait() {
						return this.data.sessionWait;
					},
					threadInuse() {
						return this.data.threadInuse;
					},
					threadWait() {
						return this.data.threadWait;
					},
					sessionInuseGraphStyle() {
						return {
							width: `${((this.sessionInuse / (this.sessionInuse + this.sessionWait)) * 100 || 0).toFixed(2)}%`
						};
					},
					sessionWaitGraphStyle() {
						return {
							width: `${((this.sessionWait / (this.sessionInuse + this.sessionWait)) * 100 || 0).toFixed(2)}%`
						};
					},
					threadInuseGraphStyle() {
						return {
							width: `${((this.threadInuse / (this.threadInuse + this.threadWait)) * 100 || 0).toFixed(2)}%`
						};
					},
					threadWaitGraphStyle() {
						return {
							width: `${((this.threadWait / (this.threadInuse + this.threadWait)) * 100 || 0).toFixed(2)}%`
						};
					}
				},
				data() {
					return {
						statusDefaultClass: `status`
					};
				},
				methods: {
					filterTarget() {
						this.$emit(`filterTarget`, {
							targetId: this.targetId,
							isFiltered: this.data.isFiltered
						});
					}
				}
			};

			this.targetInfoList.push(targetObj);
		},
		deleteTarget(targetId) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			this.targetInfoList = this.targetInfoList.filter(targetInfo => targetInfo.targetId !== targetId);
		},
		deleteTargetAll() {
			this.targetInfoList = [];
		},
		updateTarget(targetId, updateOption) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			if (updateOption.isDataEmpty) {
				targetInfo.data.status = `Down`;
				targetInfo.data.isDown = `Y` !== updateOption.downStatus;
				targetInfo.data.sessionInuse = 0;
				targetInfo.data.sessionWait = 0;
				targetInfo.data.threadInuse = 0;
				targetInfo.data.threadWait = 0;
			} else {
				targetInfo.data.isDown = false;
			}
		},
		addData(dataArr) {
			dataArr.forEach(dataInfo => {
				const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === dataInfo.instanceId);

				if (!targetInfo) return;

				targetInfo.data.status = dataInfo.status;
				targetInfo.data.sessionInuse = dataInfo.sessionInuse;
				targetInfo.data.sessionWait = dataInfo.sessionWait;
				targetInfo.data.threadInuse = dataInfo.threadInuse;
				targetInfo.data.threadWait = dataInfo.threadWait;
			});
		},
		filterTarget(param) {
			this.$emit(`filter-target`, param);
		},
		filterData({ targetId, isFiltered }) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			targetInfo.data.isFiltered = isFiltered;
		},
		unload() {
			this.targetInfoList = null;
		}
	}
};
</script>
<style lang="scss" scoped>
.connectorChartRoot {
	height: 100%;
}
</style>
