<template>
	<div class="sub-bar">
		<span v-if="null !== totalCount && viewer">{{ $t('head.totalCount', { 0: totalCount }) }}</span>
		<div class="ml-auto form-inline m-full">
			<a href="javascript:void(0);" class="btn btn-m" @click="newTab" v-if="newTabBtn && viewer && !isTools" :title="$t('common.open.new.tab')">
				<i class="icon-info"></i><span class="hide">{{ $t('common.open.new.tab') }}</span>
			</a>
			<span v-if="refreshBtn && viewer">
				<span v-if="!isStartRefresh">
					<select v-model="timerSeconds" class="form-control" style="min-width: 0px; width: 80px">
						<option v-for="seconds in timerSecondsList" :value="seconds" :key="seconds">{{ seconds }}</option>
					</select>
					{{ $t('igate.seconds') }}
				</span>
				<span v-else>
					<input type="text" v-model="displayTimerSeconds" class="form-control" style="min-width: 0px; width: 80px" readonly="readonly" />
					{{ $t('igate.seconds') }}
				</span>
				<a href="javascript:void(0);" class="btn btn-m" @click="refresh" :title="$t('igate.refresh')">
					<i :class="{ 'icon-play': !isStartRefresh, 'icon-pause': isStartRefresh }"></i>
					<span class="hide">{{ $t('igate.refresh') }}</span>
				</a>
			</span>
			<a href="javascript:void(0);" class="btn" @click="updateStatus(`ready`)" v-if="updateReadyBtn && viewer" :title="$t('head.retry')">
				<span class="hide">{{ $t('head.retry') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn" @click="updateStatus(`cancel`)" v-if="updateCancelBtn && viewer" :title="$t('head.cancel')">
				<span class="hide">{{ $t('head.cancel') }}</span>
			</a>

			<a
				href="javascript:void(0);"
				class="viewGroup btn btn-m btn-primary"
				@click="fileMake"
				v-if="fileMakeBtn && editor"
				:title="$t('head.make')"
			>
				{{ $t(`head.make`) }}
			</a>
			<a href="javascript:void(0);" class="btn btn-m btn-primary" @click="fileImport" v-if="fileImportBtn && editor" :title="$t('head.import')">
				{{ $t(`head.import`) }}
			</a>
			<a href="javascript:void(0);" class="btn btn-m" @click="gridControl(`start`)" v-if="startBtn && editor" :title="$t('head.execute')">
				<i class="icon-play"></i><span class="hide">{{ $t('head.execute') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn btn-m" @click="gridControl(`stop`)" v-if="stopBtn && editor" :title="$t('head.stop')">
				<i class="icon-pause"></i><span class="hide">{{ $t('head.stop') }}</span>
			</a>
			<a
				href="javascript:void(0);"
				class="btn btn-m"
				@click="gridControl(`stopForce`)"
				v-if="stopForceBtn && editor"
				:title="$t('head.stop.force')"
			>
				<i class="icon-x"></i><span class="hide">{{ $t('head.stop.force') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn" @click="gridControl(`interrupt`)" v-if="interruptBtn && editor" :title="$t('head.interrupt')">
				<span class="hide">{{ $t('head.interrupt') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn" @click="gridControl(`block`)" v-if="blockBtn && editor" :title="$t('head.block')">
				<span class="hide">{{ $t('head.block') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn" @click="gridControl(`unblock`)" v-if="unblockBtn && editor" :title="$t('head.unblock')">
				<span class="hide">{{ $t('head.unblock') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn btn-m" @click="initialize" v-if="initializeBtn && viewer" :title="$t('head.initialize')">
				<i class="icon-reset"></i><span class="hide">{{ $t('head.initialize') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn btn-m" @click="excelExport" v-if="excelExportBtn && viewer">
				<i class="icon-result"></i><span class="hide">{{ $t('head.excel.output') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn btn-m" @click="excelImport" v-if="excelImportBtn && viewer" :title="$t('head.excel.upload')">
				<i class="icon-result"></i><span class="hide">{{ $t('head.excel.upload') }}</span>
			</a>
			<a href="javascript:void(0);" class="btn btn-m btn-primary" @click="add" v-if="addBtn && editor" :title="$t('head.insert')">
				<i class="icon-plus"></i><span class="hide">{{ $t('head.insert') }}</span>
			</a>
			<a
				href="javascript:void(0);"
				class="btn"
				@click="reorder"
				v-if="reorderBtn && editor"
				:title="$t('igate.transactionRestriction.reOrder')"
			>
				<span class="hide">{{ $t('igate.transactionRestriction.reOrder') }}</span>
			</a>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';

export default {
	name: 'btn',
	props: {
		list: Array,
		viewer: Boolean,
		editor: Boolean,
		totalCount: String,
		gridInstanceInfo: Object
	},
	data() {
		return {
			addBtn: false,
			initializeBtn: false,
			newTabBtn: false,
			excelImportBtn: false,
			excelExportBtn: false,
			refreshBtn: false,
			timerSeconds: 60,
			displayTimerSeconds: 60,
			timerSecondsList: [10, 20, 30, 40, 50, 60],
			isStartRefresh: false,
			refreshIntervalId: null,
			updateReadyBtn: false,
			updateCancelBtn: false,
			startBtn: false,
			stopBtn: false,
			stopForceBtn: false,
			interruptBtn: false,
			blockBtn: false,
			unblockBtn: false,
			reorderBtn: false,
			fileImportBtn: false,
			fileMakeBtn: false
		};
	},
	computed: {
		...mapState(['isTools'])
	},
	created() {
		this.useBtn();
	},
	beforeDestroy() {
		clearInterval(this.refreshIntervalId);
	},
	watch: {
		list: {
			deep: true,
			handler() {
				this.useBtn();
			}
		},
		gridInstanceInfo() {
			this.useBtn();
		}
	},
	methods: {
		initialize() {
			this.$emit('initialize');
		},
		add() {
			this.$emit('add');
		},
		newTab() {
			this.$emit('newTab');
		},
		excelExport() {
			this.$emit('excelExport');
		},
		excelImport() {
			this.$emit('excelImport');
		},
		useBtn() {
			this.list.forEach(info => {
				this[`${info.id}Btn`] = `function` === typeof info.isUse ? info.isUse(this.gridInstanceInfo) : info.isUse;
			});
		},
		refresh() {
			this.isStartRefresh = !this.isStartRefresh;

			if (this.isStartRefresh) {
				this.displayTimerSeconds = this.timerSeconds;

				this.refreshIntervalId = setInterval(() => {
					if (0 >= this.displayTimerSeconds) {
						this.displayTimerSeconds = this.timerSeconds;
						this.$emit(`refresh`);
					} else {
						--this.displayTimerSeconds;
					}
				}, 1000);
			} else {
				clearInterval(this.refreshIntervalId);
			}
		},
		updateStatus(mod) {
			this.$emit('updateStatus', mod);
		},
		gridControl(mod) {
			if (`stopForce` === mod) {
				this.$confirm({
					type: `warn`,
					message: this.$t('igate.control.stopForce.alert'),
					callback: () => {
						this.$emit('gridControl', mod);
					}
				});
			} else {
				this.$emit('gridControl', mod);
			}
		},
		reorder() {
			this.$emit('reorder');
		},
		fileMake() {
			this.$emit('fileMake');
		},
		fileImport() {
			this.$emit('fileImport');
		}
	}
};
</script>
