<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/user/Detail.vue';
import moment from 'moment';
import { HttpReq, getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'User',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	data() {
		return {
			cudUrl: '/common/user/edit.json',

			clearHttpReq: new HttpReq(`/common/user/clear.json`),

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'userId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'userName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName')
				},
				{
					type: 'text',
					vModel: 'department',
					label: this.$t('common.user.department'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'telephone',
					label: this.$t('common.user.telephone'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'mobilephone',
					label: this.$t('common.user.mobilephone'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'email',
					label: this.$t('common.user.email'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'add', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: '/common/user/search.json',
				totalCntUrl: '/common/user/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('head.id'),
							name: 'userId',
							width: '40%'
						},
						{
							header: this.$t('head.name'),
							name: 'userName',
							width: '40%'
						},
						{
							header: this.$t('common.user.department'),
							name: 'department',
							width: '30%'
						},
						{
							header: this.$t('common.user.telephone'),
							name: 'telephone',
							width: '30%'
						},
						{
							header: this.$t('common.user.mobilephone'),
							name: 'mobilephone',
							width: '30%'
						},
						{
							header: this.$t('common.user.email'),
							name: 'email',
							width: '30%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['userId'],
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true }
			],
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('head.basic.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'userId',
									label: this.$t('head.id'),
									isPK: true,
									regExpType: 'id'
								},
								{
									type: 'text',
									vModel: 'userName',
									label: this.$t('head.name'),
									isRequired: true
								},
								{
									type: 'text',
									vModel: 'department',
									label: this.$t('common.user.department')
								},
								{
									type: 'text',
									vModel: 'telephone',
									label: this.$t('common.user.telephone')
								}
							],
							[
								{
									type: 'text',
									vModel: 'mobilephone',
									label: this.$t('common.user.mobilephone')
								},
								{
									type: 'text',
									vModel: 'email',
									label: this.$t('common.user.email')
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true
								}
							]
						]
					]
				},
				{
					type: 'basic',
					label: this.$t('common.user.authentication.info'),
					content: [
						[
							[
								{
									type: 'select',
									vModel: 'userDisableYn',
									label: this.$t('common.user.disable'),
									val: 'N',
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Yn',
											orderByKey: true
										},
										optionListName: 'openWindowList',
										optionFor: 'option in openWindowList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'text',
									vModel: 'loginIp',
									label: this.$t('common.user.loginIp'),
									readonly: true
								},
								{
									type: 'password',
									vModel: 'passwordNew',
									label: this.$t('common.user.newPassword'),
									isRequired: true,
									warning: true,
									warningLabel: this.$t('common.user.validationPassCheck'),
									regExpType: 'password'
								}
							],
							[
								{
									type: 'singleDaterange',
									vModel: 'passwordExpiration',
									label: this.$t('common.user.passwordExpiration'),
									val: moment(new Date().setMonth(new Date().getMonth() + 3)).format('YYYY-MM-DD HH:mm:ss')
								},
								{
									type: 'singleDaterange',
									vModel: 'userExpiration',
									label: this.$t('common.user.userExpiration'),
									val: moment(new Date().setMonth(new Date().getMonth() + 3)).format('YYYY-MM-DD HH:mm:ss')
								},
								{
									type: 'text',
									vModel: 'loginAttempts',
									val: '0',
									label: this.$t('common.user.loginAttempts'),
									regExpType: 'num'
								},
								{
									type: 'text',
									vModel: 'loginTimestamp',
									label: this.$t('common.user.loginTimestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				},
				{
					type: 'bundle',
					url: '/user/UserDetailPrivileges',
					label: `${this.$t(`common.user`)} ${this.$t(`common.privilege`)} ${this.$t(`head.info`)}`
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	},
	async mounted() {
		await this.clearHttpReq.read();

		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	methods: {
		async search(searchObj) {
			await this.clearHttpReq.read();
			this.gridSearchObj = searchObj;
		}
	}
};
</script>
