<template>
	<div class="propertyTab">
		<div class="viewGroup row" style="width: 100%; margin: 0px 0px 1.25rem" v-if="readonly">
			<div class="col-lg-2 searchArea-col">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.calendar.date') }}</span>
					</label>
					<select class="form-control" v-model="holidayYear" @change="holidayEqual">
						<option v-for="option in holidayYearList" :key="option" :value="option" v-text="option"></option>
					</select>
				</div>
			</div>
		</div>

		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('head.insert')">
					<i class="icon-plus-circle"></i>
				</button>
				<label class="col">{{ $t('igate.calendar.date') }}</label>
				<label class="col">{{ $t('common.desc') }}</label>
			</div>
			<div class="form-table-wrap">
				<div v-if="!readonly">
					<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="propertyObj.idx">
						<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" :title="$t('head.delete')">
							<i class="icon-minus-circle"></i>
						</button>
						<div class="col">
							<div class="detail-content-common">
								<datePicker
									:ref="`component${bodyIndex}`"
									vModel="pk.holidayDate"
									:val="propertyObj['pk.holidayDate']"
									:readonly="readonly"
									:isEmptyPlaceholder="true"
									style="margin-top: 7px"
								/>
							</div>
						</div>

						<div class="col">
							<div class="detail-content-regExp">
								<inputText
									:ref="`component${bodyIndex}`"
									vModel="holidayDesc"
									:classObject="{
										'form-control': false,
										'regExp-text': true,
										'view-disabled': true
									}"
									:val="propertyObj['holidayDesc']"
									:readonly="readonly"
									regExpType="desc"
									@setLengthCnt="info => setLengthCnt({ [`holidayDesc_${bodyIndex}`]: info['holidayDesc'] })"
								/>
								<span class="letterLength">
									{{ `(${curLengthCnt[`holidayDesc_${bodyIndex}`]}/${maxLengthCnt[`holidayDesc_${bodyIndex}`]})` }}
								</span>
							</div>
						</div>
					</div>
				</div>
				<div v-else>
					<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyListShow" :key="bodyIndex">
						<div class="col">
							<div class="detail-content-common">
								<datePicker
									:ref="`component${bodyIndex}`"
									vModel="pk.holidayDate"
									:val="propertyObj['pk.holidayDate']"
									:readonly="readonly"
									:isEmptyPlaceholder="true"
									style="margin-top: 7px"
								/>
							</div>
						</div>

						<div class="col">
							<div class="detail-content-common">
								<inputText
									:ref="`component${bodyIndex}`"
									vModel="holidayDesc"
									:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
									:val="propertyObj['holidayDesc']"
									:readonly="readonly"
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import isNil from 'lodash/isNil';
import { parseHierarchyObj, parseFlattenObj, getUUID, getRegExpInfo } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';
import datePicker from '@/components/element/datePicker';
import moment from 'moment';

export default {
	name: 'CalendarHoliday',
	extends: DetailProperty,
	components: {
		datePicker
	},
	data() {
		return {
			holidayYearList: [`${this.$t('head.all')}`],
			holidayYear: null,
			propertyListShow: [],

			regArr: [
				{
					key: 'holidayDesc',
					regExpType: 'desc'
				}
			],

			holidayDescContent: {
				readonly: false,
				regExpType: 'desc'
			}
		};
	},
	mounted() {
		this.holidayYear = new Date().getFullYear().toString();
	},
	created() {
		for (let year = new Date().getFullYear() + 10; year >= new Date().getFullYear() - 10; year--) {
			this.holidayYearList.push(year);
		}
	},
	methods: {
		filterPropertyList() {
			this.propertyListShow = this.propertyList.filter(ele => {
				if (this.holidayYear == ele['pk.holidayDate'].substring(0, 4)) return ele;
				else if (this.holidayYear === this.$t('head.all')) return ele;
			});
		},
		holidayEqual() {
			this.filterPropertyList();
		},
		getData() {
			const rtnArr = [];

			for (let i = 0; i < this.propertyList.length; i++) {
				let rtnObj = {};

				this.$refs[`component${i}`].forEach(comp => {
					rtnObj = merge(rtnObj, comp.getData());
				});

				rtnObj['pk.holidayDate'] = moment(rtnObj['pk.holidayDate']).format('YYYYMMDD');

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				calendarHoliday: rtnArr
			};
		},
		addProperty() {
			const rowInfo = {
				idx: getUUID(),
				'pk.calendarId': null,
				'pk.holidayDate': null,
				holidayDesc: null
			};

			this.regArr.forEach(element => {
				if (element.regExpType) {
					const key = `${element.key}_${this.propertyList.length}`;
					this.setLengthCnt({ [key]: 0 });
					this.maxLengthCnt[key] = getRegExpInfo(element.regExpType).maxLength;
				}

				rowInfo[element.key] = element.val ? element.val : null;
			});

			this.propertyList.push(rowInfo);
		},
		removeProperty(index) {
			this.propertyList.splice(index, 1);

			this.$nextTick(() => {
				this.curLengthCnt = {};
				this.maxLengthCnt = {};

				this.propertyList.forEach((property, bodyIndex) => {
					let rtnObj = {};

					this.$refs[`component${bodyIndex}`].forEach(comp => {
						rtnObj = merge(rtnObj, comp.getData());
					});

					for (const key in rtnObj) {
						if (`pk.holidayDate` === key) continue;

						this.setLengthCnt({ [`${key}_${bodyIndex}`]: isNil(rtnObj[key]) ? 0 : String(rtnObj[key]).length });
						this.maxLengthCnt[`${key}_${bodyIndex}`] = getRegExpInfo(
							this.regArr.find(element => element.key === key).regExpType
						).maxLength;
					}
				});
			});
		},
		async setPropertyList() {
			if ('undefined' !== typeof this.detailData.calendarHoliday) {
				this.propertyList = [...this.detailData.calendarHoliday];

				this.propertyList.forEach((propertyInfo, index) => {
					propertyInfo.pk[`holidayDate`] = moment(propertyInfo.pk[`holidayDate`]).format('YYYY-MM-DD');
					propertyInfo.idx = getUUID();

					this.regArr.forEach(element => {
						const key = `${element.key}_${index}`;

						this.setLengthCnt({ [key]: propertyInfo[element.key] ? propertyInfo[element.key].length : 0 });
						this.maxLengthCnt[key] = getRegExpInfo(element.regExpType).maxLength;
					});
				});
			} else {
				this.propertyList = [];
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));

			this.filterPropertyList();
		}
	}
};
</script>
