<template>
	<div class="propertyTab">
		<div class="form-table form-table-responsive">
			<div class="form-table-head">
				<button type="button" class="btn-icon" @click="addProperty" v-if="!readonly" :title="$t('head.insert')">
					<i class="icon-plus-circle"></i>
				</button>
				<label class="col">{{ $t('igate.mappingDictionary.targetFieldPath') }}</label>
				<label class="col">{{ $t('igate.mappingDictionary.mappingExpression') }}</label>
			</div>
			<div class="form-table-wrap">
				<div class="form-table-body" v-for="(propertyObj, bodyIndex) in propertyList" :key="`${propertyObj.idx}`">
					<button type="button" class="btn-icon" @click="removeProperty(bodyIndex)" v-if="!readonly" :title="$t('head.delete')">
						<i class="icon-minus-circle"></i>
					</button>
					<div class="col">
						<div style="width: 100%">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="pk.targetFieldPath"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['pk.targetFieldPath']"
								:readonly="readonly"
							/>
						</div>
					</div>

					<div class="col">
						<div style="width: 100%">
							<inputText
								:ref="`component${bodyIndex}`"
								vModel="mappingExpression"
								:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
								:val="propertyObj['mappingExpression']"
								:readonly="readonly"
							/>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { HttpReq, getUUID, parseFlattenObj, parseHierarchyObj } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';
import merge from 'lodash/merge';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'RecordExtendInfo',
	extends: DetailProperty,
	data() {
		return {
			modalInfo: {
				title: this.$t('igate.mapping'),
				addMultiRow: true,
				search: {
					list: [
						{
							type: 'text',
							vModel: 'mappingId',
							label: this.$t('head.id'),
							placeholder: this.$t('head.searchId'),
							regExpType: 'id'
						},
						{
							type: 'dataList',
							vModel: 'pageSize',
							label: this.$t('head.listCount'),
							val: '10',
							optionInfo: {
								optionFor: 'option in [10, 100, 1000]',
								optionValue: 'option',
								optionText: 'option'
							}
						}
					]
				},
				button: {
					list: [{ id: 'initialize', isUse: true }]
				},
				grid: {
					url: '/igate/mapping/searchPopup.json',
					totalCntUrl: '/igate/mapping/rowCount.json',
					paging: {
						isUse: true,
						side: 'server'
					},
					options: {
						columns: [
							{
								header: this.$t('head.id'),
								name: 'mappingId'
							},
							{
								header: this.$t('head.name'),
								name: 'mappingName'
							},
							{
								header: this.$t('head.description'),
								name: 'mappingDesc'
							},
							{
								header: this.$t('common.type'),
								name: 'mappingType',
								align: 'center',
								formatter: ({ row }) => {
									const mappingTypeTranslateObj = {
										H: 'Header',
										I: 'Individual'
									};

									return mappingTypeTranslateObj[row.mappingType];
								}
							}
						]
					}
				}
			}
		};
	},
	methods: {
		getData() {
			const rtnArr = [];

			for (let i = 0; i < this.propertyList.length; i++) {
				let rtnObj = {};

				this.$refs[`component${i}`].forEach(comp => {
					rtnObj = merge(rtnObj, comp.getData());
				});

				rtnArr.push(parseHierarchyObj(rtnObj));
			}

			return {
				mappingDictionaryDetails: rtnArr
			};
		},
		async setPropertyList() {
			if (isEmpty(this.detailData)) {
				this.propertyList = [];
				return;
			}

			if ('undefined' !== typeof this.detailData.mappingDictionaryDetails) {
				this.propertyList = [...this.detailData.mappingDictionaryDetails];

				let result = null;

				if (this.httpReq) {
					result = await this.httpReq.read(merge(this.detailInfo.params, this.detailData));

					if ('ok' !== result.data.result) {
						this.$alert({ type: 'warn', message: `${result.data.error[0].message}` });
						return;
					}
				}

				if (this.detailInfo.setPropertyCallBack) {
					this.propertyList = this.detailInfo.setPropertyCallBack(this.propertyList, result ? result.data.object : null);
				}

				this.propertyList.forEach(propertyInfo => {
					propertyInfo.idx = getUUID();
				});
			} else {
				this.propertyList = [];
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		},
		async addProperty() {
			this.openModal(
				merge(this.modalInfo, {
					rowClickedCallback: async (rowInfo, propertyList) => {
						const result = await new HttpReq('/igate/mapping/object.json').read({ mappingId: rowInfo.mappingId });

						if ('ok' !== result.data.result || isEmpty(result.data.object.mappingDetails)) return;

						const mappingRuleDetails = result.data.object.mappingDetails[0].mappingRuleObject.mappingRuleDetails;

						const propertyInfo = [];

						for (const ruleDetailInfo of mappingRuleDetails) {
							const duplicate =
								0 === propertyList.length
									? false
									: propertyList.some(property => property['pk.targetFieldPath'] === ruleDetailInfo.targetFieldPath);

							if (duplicate) continue;

							propertyInfo.push({
								'pk.targetFieldPath': ruleDetailInfo.targetFieldPath,
								mappingExpression: ruleDetailInfo.mappingExpression
							});
						}

						return {
							propertyInfo: propertyInfo
						};
					}
				})
			);
		}
	}
};
</script>
