<script>
import Basic from '@/components/main/Basic.vue';
import { getPrivilege, HttpReq } from '@/utils/common.js';
import moment from 'moment';

export default {
	name: 'ServiceRestriction',
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	async mounted() {
		const result = await new HttpReq('/igate/serviceRestriction/dateFormat.json').read();

		if ('error' === result.data.result) {
			this.$alert({ type: 'warn', message: this.$t('head.fail') });
			return;
		}

		this.dateFormat = result.data.object;

		const dateFormatType = 'HHmmss' === this.dateFormat ? 'time' : 'singleDaterange';

		this.searchList = [
			{ type: dateFormatType, vModel: 'startTime', label: this.$t('head.from') },
			{
				type: 'select',
				vModel: 'enableYn',
				label: this.$t('igate.transactionRestriction.enableYn'),
				optionInfo: {
					url: '/common/property/properties.json',
					params: {
						propertyId: 'List.Yn',
						orderByKey: true
					},
					optionListName: 'enableList',
					optionFor: 'option in enableList',
					optionValue: 'option.pk.propertyKey',
					optionText: 'option.propertyValue'
				}
			},
			{
				type: 'select',
				vModel: 'whitelistYn',
				label: this.$t('igate.transactionRestriction.whiteListYn'),
				optionInfo: {
					url: '/common/property/properties.json',
					params: {
						propertyId: 'List.Yn',
						orderByKey: true
					},
					optionListName: 'whitelistList',
					optionFor: 'option in whitelistList',
					optionValue: 'option.pk.propertyKey',
					optionText: 'option.propertyValue'
				}
			},
			{
				type: 'text',
				vModel: 'ruleId',
				label: `${this.$t('igate.transactionRestriction')} ${this.$t('head.id')}`,
				placeholder: this.$t('head.searchId'),
				regExpType: 'searchId'
			},
			{
				type: 'dataList',
				vModel: 'pageSize',
				label: this.$t('head.listCount'),
				val: '10',
				optionInfo: {
					optionFor: 'option in [10, 100, 1000]',
					optionValue: 'option',
					optionText: 'option'
				}
			}
		];

		this.detailTabList = [
			{
				type: 'basic',
				label: this.$t('head.basic.info'),
				content: [
					[
						[
							{
								type: 'text',
								vModel: 'ruleId',
								label: `${this.$t('igate.transactionRestriction')} ${this.$t('head.id')}`,
								isPK: true,
								regExpType: 'id'
							},
							{ type: 'text', vModel: 'rulePriority', label: this.$t('igate.transactionRestriction.rulePriority'), regExpType: 'num' },
							{
								type: 'select',
								vModel: 'whitelistYn',
								label: this.$t('igate.transactionRestriction.whiteListYn'),
								optionInfo: {
									url: '/common/property/properties.json',
									params: {
										propertyId: 'List.Yn',
										orderByKey: true
									},
									optionListName: 'whitelistList',
									optionFor: 'option in whitelistList',
									optionValue: 'option.pk.propertyKey',
									optionText: 'option.propertyValue'
								}
							}
						],
						[
							{ type: dateFormatType, vModel: 'startTime', label: this.$t('head.from') },
							{ type: dateFormatType, vModel: 'endTime', label: this.$t('head.to') },
							{ type: 'text', vModel: 'restrictionMessage', label: this.$t('igate.transactionRestriction.message') },
							{
								type: 'select',
								vModel: 'enableYn',
								label: this.$t('igate.transactionRestriction.enableYn'),
								optionInfo: {
									url: '/common/property/properties.json',
									params: {
										propertyId: 'List.Yn',
										orderByKey: true
									},
									optionListName: 'enableList',
									optionFor: 'option in enableList',
									optionValue: 'option.pk.propertyKey',
									optionText: 'option.propertyValue'
								}
							}
						]
					]
				]
			},
			{
				type: 'property',
				dataKey: 'serviceRestrictionConds',
				label: `${this.$t('igate.transactionRestriction')} ${this.$t('igate.transactionRestriction.value')}`,
				addRowInfo: {
					type: 'common'
				},
				content: [
					{
						type: 'select',
						vModel: 'pk.restrictionType',
						label: this.$t('common.type'),
						optionInfo: {
							url: '/common/property/properties.json',
							params: {
								propertyId: 'List.ServiceRestriction.Type',
								orderByKey: true
							},
							optionListName: 'restrictionTypeList',
							optionFor: 'option in restrictionTypeList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}
					},
					{
						type: 'select',
						vModel: 'restrictionOperator',
						label: this.$t('igate.transactionRestriction.operator'),
						optionInfo: {
							url: '/common/property/properties.json',
							params: {
								propertyId: 'List.TransactionRestriction.Operator',
								orderByKey: true
							},
							optionListName: 'restrictionOperatorList',
							optionFor: 'option in restrictionOperatorList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}
					},
					{
						type: 'text',
						vModel: 'restrictionValue',
						label: `${this.$t('igate.transactionRestriction.value')}`
					}
				]
			}
		];

		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	methods: {
		changeTime(pTime, isDisplay) {
			if (!pTime) return;

			if (!isDisplay) {
				let convertTime = pTime.replace(/:/gi, '');
				convertTime = convertTime.replace(/-/gi, '');
				convertTime = convertTime.replace(/(\s*)/g, '');

				return convertTime;
			} else {
				pTime = pTime.replace(/[^.0-9]/g, '');

				if (14 === this.dateFormat.length && 6 === pTime.length) {
					pTime = moment().format('YYYYMMDD') + pTime;
				} else if (6 === this.dateFormat.length && 14 === pTime.length) {
					pTime = pTime.substring(8);
				}

				if (14 == pTime.length) {
					let date = pTime.substring(0, 8);
					let time = pTime.substring(8);

					date = date.replace(/(.{4})/g, '$1-');
					date = date.replace(/(.{7})/g, '$1-');
					date = date.slice(0, -1);

					time = time.replace(/(.{2})/g, '$1:');
					time = time.slice(0, -1);

					return date + ' ' + time;
				} else if (6 == pTime.length) {
					let time = pTime.replace(/(.{2})/g, '$1:');

					time = time.slice(0, -1);

					return time;
				}
			}
		}
	},
	data() {
		return {
			dateFormat: null,
			cudUrl: '/igate/serviceRestriction/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'add', isUse: true },
				{ id: 'reorder', reorderUrl: '/igate/serviceRestriction/reorder.json', isUse: true }
			],

			// Grid
			gridInfo: {
				url: '/igate/serviceRestriction/search.json',
				totalCntUrl: '/igate/serviceRestriction/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'rulePriority',
							header: this.$t('igate.transactionRestriction.rulePriority'),
							align: 'center',
							width: '10%'
						},
						{
							name: 'startTime',
							header: this.$t('head.from'),
							align: 'center',
							width: '20%',
							formatter: value => this.changeTime(value.row.startTime, true)
						},
						{
							name: 'endTime',
							header: this.$t('head.to'),
							align: 'center',
							width: '20%',
							formatter: value => this.changeTime(value.row.endTime, true)
						},
						{
							name: 'enableYn',
							header: this.$t('igate.transactionRestriction.enableYn'),
							align: 'center',
							width: '10%',
							formatter: value => ('Y' == value.row.enableYn ? 'Yes' : 'No')
						},
						{
							name: 'whitelistYn',
							header: this.$t('igate.transactionRestriction.whiteListYn'),
							align: 'center',
							width: '10%',
							formatter: value => ('Y' == value.row.whitelistYn ? 'Yes' : 'No')
						},
						{
							name: 'ruleId',
							header: `${this.$t('igate.transactionRestriction')} ${this.$t('head.id')}`,
							align: 'left',
							width: '30%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['ruleId'],
			controlUrl: '/igate/serviceRestriction/control.json',
			controlParams: detailData => ({
				ruleId: detailData.ruleId
			}),
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [
				{ id: 'insert', isUse: true },
				{ id: 'update', isUse: true },
				{ id: 'delete', isUse: true },
				{ id: 'dump', isUse: true }
			],
			detailTabList: []
		};
	}
};
</script>
