<template>
	<div>
		<DashboardConfigStructure
			ref="dashboardConfigStructure"
			mod="add"
			:containerInfoOrg="containerInfo"
			:dashConstInfo="dashConstInfo"
			@changeContainer="changeContainer"
		>
			<template slot="dashboardRbtnConfigArea">
				<a href="javascript:void(0);" class="btn btn-primary" @click="saveContainer"> {{ $t('head.save') }} </a>
			</template>
		</DashboardConfigStructure>
	</div>
</template>

<script>
import { mapState } from 'vuex';
import isNumber from 'lodash/isNumber';
import merge from 'lodash/merge';
import cloneDeep from 'lodash/cloneDeep';
import DashboardConfigStructure from '@/views/main/dashboard/DashboardConfigStructure.vue';
import { HttpReq } from '@/utils/common.js';

export default {
	name: `DashboardAdd`,
	props: {
		containerInfo: Object,
		dashConstInfo: Object
	},
	components: {
		DashboardConfigStructure
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		async saveContainer() {
			if (0 === this.$refs.dashboardConfigStructure.componentList.length) {
				this.$alert({
					type: `alert`,
					message: this.$t(`dashboard.deploy.noComponent`)
				});

				return;
			}

			let checkNormal = true;
			let warnAlertMsg = null;

			const containerOptions = this.$refs.dashboardConfigStructure.containerOptions;

			for (const key in containerOptions) {
				if (!key.startsWith(`xView`) && !key.startsWith(`datatable`) && !key.startsWith(`instanceSummary`)) continue;

				if (key.startsWith(`xView`)) {
					if (!isNumber(containerOptions[key])) {
						warnAlertMsg = this.$t(`dashboard.enter.xview.numericalCheck`);
						checkNormal = false;
						break;
					}
				}

				if (key.startsWith(`xViewTrans`)) {
					if (containerOptions[key] < 0.1 || containerOptions[key] > 1.0) {
						warnAlertMsg = this.$t(`dashboard.enter.xview.rangeCheck`);
						checkNormal = false;
						break;
					}
				}

				if (key.startsWith(`datatable`) || key.startsWith(`instanceSummary`)) {
					if (!isNumber(containerOptions[key])) {
						warnAlertMsg = this.$t(`dashboard.enter.number`);
						checkNormal = false;
						break;
					}
				}
			}

			if (!checkNormal) {
				this.$alert({ type: `warn`, message: warnAlertMsg });
				return false;
			}

			const httpReq = new HttpReq(`/igate/monitoring/dashboard/container.json`);

			const result = await httpReq.update(
				merge(
					{
						...cloneDeep(this.containerInfo),
						monitorComponents: cloneDeep(this.$refs.dashboardConfigStructure.componentList).map(element => {
							element.containerId = this.containerInfo.containerId;
							return element;
						})
					},
					{
						...cloneDeep(this.$refs.dashboardConfigStructure.containerInfo),
						monitorContainerUsers: [
							{
								pk: {
									containerId: this.containerInfo.containerId,
									containerUserId: this.userInfo.userId
								},
								selectYn: `Y`,
								containerOptions: JSON.stringify(containerOptions)
							}
						]
					}
				),
				true
			);

			if (`ok` !== result.data.result) return;

			this.$alert({
				type: `normal`,
				message: this.$t(`dashboard.add.success`)
			});

			this.changeContainer();
		},
		changeContainer() {
			this.unloadContainer();

			this.$emit(`changeContainer`, `view`);
		},
		unloadContainer() {
			this.$refs.dashboardConfigStructure.unloadContainer();
		}
	}
};
</script>
