<template>
	<ul class="list-group" style="width: 100%">
		<li class="list-group-item" v-for="(property, index) in propertyList" :key="index">
			<label class="custom-control custom-checkbox">
				<input type="checkbox" class="custom-control-input view-disabled" v-model="property.check" :disabled="readonly" />
				<span class="custom-control-label">{{ property.instanceId }}</span>
			</label>
		</li>
	</ul>
</template>

<script>
import { parseFlattenObj, HttpReq } from '@/utils/common.js';
import DetailProperty from '@/components/detail/DetailProperty.vue';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'DeploiesInfo',
	extends: DetailProperty,
	data() {
		return {
			instanceList: [],
			propertyList: []
		};
	},
	async created() {
		const httpReq = new HttpReq('/igate/instance/list.json');

		const result = await httpReq.read();

		if (`ok` !== result.data.result) return;

		this.instanceList = result.data.object.filter(instance => 'T' == instance.instanceType);
	},
	methods: {
		getData() {
			const propertyList = this.propertyList.filter(property => property.check);
			const rtnArr = [];

			for (let property of propertyList) {
				rtnArr.push({ 'pk.instanceId': property.instanceId });
			}

			return {
				connectorDeploies: rtnArr
			};
		},
		async setPropertyList() {
			const instanceList = this.instanceList.map(deploies => deploies.instanceId);
			const connectorDeploies = isEmpty(this.detailData.connectorDeploies)
				? []
				: this.detailData.connectorDeploies.map(deploies => deploies.pk.instanceId);

			this.propertyList = [];

			for (const instanceId of instanceList) {
				this.propertyList.push({ instanceId: instanceId, check: connectorDeploies.includes(instanceId) });
			}

			this.propertyList = this.propertyList.map(property => parseFlattenObj(property));
		}
	}
};
</script>
