<template>
	<div class="threadChartRoot">
		<div class="row gate-row-wrp flex-1" :style="style">
			<div class="col-6" v-for="n in 2" :key="n">
				<component
					v-for="(targetInfo, idx) in targetInfoList ? targetInfoList.filter((targetInfo, idx) => n - 1 === idx % 2) : []"
					:is="targetInfo.component"
					:data="targetInfo.data"
					@filterTarget="filterTarget"
					:key="`${idx}_${targetInfo.targetId}`"
				/>
			</div>
		</div>
		<div class="d-flex-space" v-if="isShowLegend">
			<div class="legend legend-square">
				<span class="status"><i class="dot bg-cht-10"></i>{{ $t(`dashboard.legend.square.activeThread`) }}</span>
				<span class="status"><i class="dot bg-cht-10-l"></i>{{ $t(`dashboard.legend.square.idleThread`) }}</span>
			</div>
			<div class="legend">
				<span class="status"><i class="dot bg-normal"></i>{{ $t(`dashboard.legend.status.normal`) }}</span>
				<span class="status"><i class="dot bg-warn"></i>{{ $t(`dashboard.legend.status.warn`) }}</span>
				<span class="status"><i class="dot bg-cht-1"></i>{{ $t(`dashboard.legend.status.shutdown`) }}</span>
				<span class="status"><i class="dot bg-down"></i>{{ $t(`dashboard.legend.status.terminating`) }}</span>
				<span class="status"><i class="dot bg-alarm"></i>{{ $t(`dashboard.legend.status.terminated`) }}</span>
				<span class="status"><i class="dot bg-danger"></i>{{ $t(`dashboard.legend.status.fail`) }}</span>
			</div>
		</div>
	</div>
</template>

<script>
import { escapeHtml } from '@/utils/common.js';

export default {
	name: `DashboardThreadChart`,
	props: {
		options: Object
	},
	data() {
		return {
			targetInfoList: [],
			isShowLegend: false,
			style: {
				height: null
			}
		};
	},
	mounted() {
		this.initThreadChart();
	},
	methods: {
		initThreadChart() {
			this.isShowLegend = `Y` === this.options.remarkYn;

			this.$nextTick(() => {
				this.style.height = this.isShowLegend ? `calc(100% - ${this.$el.querySelector(`.d-flex-space`).offsetHeight}px)` : `100%`;
			});
		},
		addTarget(targetObj) {
			targetObj.data = {
				targetId: targetObj.targetId,
				status: `Normal`,
				isDown: false,
				isFiltered: false,
				threadActive: 0,
				threadIdle: 0
			};

			targetObj.component = {
				template: `
					<div class="gate-row" :style="rootElementStyle">
						<div class="gate">
							<i class="iconb-warn" title="Down" v-if="isDown"></i>
							<i :class="[statusDefaultClass, statusDataClass]"></i>
							<a href="javascript:void(0)" class="t-xs" data-toggle="tooltip" :title="targetId" @click="filterTarget">{{ targetId }}</a>
						</div>
						<div class="gate-graph">
							<div class="progress up">
								<div class="progress-bar bg-cht-9" role="progressbar" :style="activeGraphStyle">
									<span class="t-xs">{{ threadActive }}</span>
								</div>
							</div>
							<div class="progress">
								<div class="progress-bar bg-cht-1" role="progressbar" :style="idleGraphStyle">
									<span class="t-xs">{{ threadIdle }}</span>
								</div>
							</div>
						</div>
						<div class="status-msg"></div>
					</div>				
				`,
				props: {
					data: {
						type: Object,
						default: () => ({
							targetId: null,
							status: `Normal`,
							isDown: false,
							isFiltered: false,
							threadActive: 0,
							threadIdle: 0
						})
					}
				},
				computed: {
					targetId() {
						return escapeHtml(this.data.targetId);
					},
					statusDataClass() {
						const status = this.data.status;

						if (`Normal` === status) return `bg-normal`;
						else if (`Warn` === status) return `bg-warn`;
						else return `bg-normal`;
					},
					isDown() {
						return this.data.isDown;
					},
					rootElementStyle() {
						return {
							opacity: this.data.isFiltered ? 0.2 : 1.0
						};
					},
					threadActive() {
						return this.data.threadActive;
					},
					threadIdle() {
						return this.data.threadIdle;
					},
					activeGraphStyle() {
						return {
							width: `${((this.threadActive / (this.threadActive + this.threadIdle)) * 100 || 0).toFixed(2)}%`
						};
					},
					idleGraphStyle() {
						return {
							width: `${((this.threadIdle / (this.threadActive + this.threadIdle)) * 100 || 0).toFixed(2)}%`
						};
					}
				},
				data() {
					return {
						statusDefaultClass: `status`
					};
				},
				methods: {
					filterTarget() {
						this.$emit(`filterTarget`, {
							targetId: this.targetId,
							isFiltered: this.data.isFiltered
						});
					}
				}
			};

			this.targetInfoList.push(targetObj);
		},
		deleteTarget(targetId) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			this.targetInfoList = this.targetInfoList.filter(targetInfo => targetInfo.targetId !== targetId);
		},
		updateTarget(targetId, updateOption) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			if (updateOption.isDataEmpty) {
				targetInfo.data.status = `Down`;
				targetInfo.data.isDown = `Y` !== updateOption.downStatus;
				targetInfo.data.threadActive = 0;
				targetInfo.data.threadIdle = 0;
			} else {
				targetInfo.data.isDown = false;
			}
		},
		addData(dataArr) {
			dataArr.forEach(dataInfo => {
				const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === dataInfo.instanceId);

				if (!targetInfo) return;

				targetInfo.data.status = dataInfo.status;
				targetInfo.data.threadActive = dataInfo.threadActive;
				targetInfo.data.threadIdle = dataInfo.threadIdle;
			});
		},
		filterTarget(param) {
			this.$emit(`filter-target`, param);
		},
		filterData({ targetId, isFiltered }) {
			const targetInfo = this.targetInfoList.find(targetInfo => targetInfo.targetId === targetId);

			if (!targetInfo) return;

			targetInfo.data.isFiltered = isFiltered;
		},
		unload() {
			this.targetInfoList = null;
		}
	}
};
</script>
<style lang="scss" scoped>
.threadChartRoot {
	height: 100%;

	.gate-row-wrp {
		height: calc(100% - 31px);
	}
}
</style>
