<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.id') }}</span>
						<b class="icon-star"></b>
					</label>
					<inputText
						ref="recordId"
						vModel="recordId"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
						:readonly="readonly || getReadonly({ isPK: true, readonly: false })"
					/>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.name') }}</span>
					</label>
					<inputText
						ref="recordName"
						vModel="recordName"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
					/>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>Private</span>
						<b class="icon-star"></b>
					</label>
					<selectBox
						ref="privateYn"
						vModel="privateYn"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: '/common/property/properties.json',
							params: {
								propertyId: 'List.Yn',
								orderByKey: true
							},
							optionListName: 'privateYnList',
							optionFor: 'option in privateYnList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}"
					/>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.privilege') }}</span>
						<b class="icon-star"></b>
					</label>
					<select
						ref="privilegeId"
						v-model="privilegeId"
						class="form-control view-disabled"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
					>
						<option v-for="option in privilegeTypeList" :key="option.privilegeId" :value="option.privilegeId" :disabled="option.disabled">
							{{ option.privilegeId }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.record.metaDomain') }}</span>
					</label>
					<inputText
						ref="metaDomain"
						vModel="metaDomain"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: false }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
					/>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('common.type') }}</span>
						<b class="icon-star"></b>
					</label>
					<select
						ref="recordType"
						v-model="recordType"
						class="form-control view-disabled"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
					>
						<option
							v-for="(recordTypeObj, index) in recordTypeArr"
							:key="index"
							:value="recordTypeObj.pk.propertyKey"
							:disabled="recordTypeObj.disabled"
						>
							{{ recordTypeFormatter(recordTypeObj.pk.propertyKey) }}
						</option>
					</select>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.group') }}</span>
					</label>
					<inputText
						ref="recordGroup"
						vModel="recordGroup"
						:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
						:readonly="readonly || getReadonly({ isPK: true, readonly: false })"
					/>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('head.description') }}</span>
					</label>
					<textArea
						ref="recordDesc"
						vModel="recordDesc"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:height="'130px'"
					/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import BasicInfo from '@/views/detail/modelRecord/BasicInfo.vue';
import { HttpReq } from '@/utils/common.js';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'BasicInfo',
	extends: BasicInfo,
	data() {
		return {
			namingRuleHttp: new HttpReq('/common/property/properties.json'),
			namingRules: []
		};
	},
	props: {
		modalType: String
	},
	watch: {
		namingKeys() {
			let keys;

			switch (this.modalType) {
				case 'SI':
					keys = { id: 'ServiceRequestRecordID', name: 'ServiceRequestRecordName' };
					break;
				case 'SO':
					keys = { id: 'ServiceResponseRecordID', name: 'ServiceResponseRecordName' };
					break;
				case 'II':
					keys = { id: 'InterfaceRequestRecordID', name: 'InterfaceRequestRecordName' };
					break;
				case 'IO':
					keys = { id: 'InterfaceResponseRecordID', name: 'InterfaceResponseRecordName' };
					break;
			}

			return {
				id: this.namingFormatter(keys.id),
				name: this.namingFormatter(keys.name)
			};
		}
	},
	async created() {
		const result = await this.namingRuleHttp.read({ propertyId: 'ImgOption.NamingRules', orderByKey: true });

		if (`ok` !== result.data.result) return;

		this.namingRules = result.data.object;
	},
	methods: {
		namingFormatter(key) {
			return this.namingRules.filter(property => key === property.pk.propertyKey).map(property => property.propertyValue)[0];
		},
		recordNameFormatter() {
			if (!isEmpty(this.detailData) && (this.detailData.recordName || (this.detailData.recordType && 'I' !== this.detailData.recordType))) {
				return this.detailData.recordName;
			}

			return this.namingKeys.name.replace('{0}', this.modalData.recordId);
		}
	}
};
</script>
