<script>
import Basic from '@/components/main/Basic.vue';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'Operation',
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		} else {
			this.$nextTick(() => {
				this.search(this.$refs.search.getSearchObj());
			});
		}
	},
	data() {
		return {
			cudUrl: '/igate/operation/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'text',
					vModel: 'operationId',
					label: this.$t('head.id'),
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'operationName',
					label: this.$t('head.name'),
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'select',
					vModel: 'operationType',
					label: this.$t('common.type'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Operation.OperationType',
							orderByKey: true
						},
						optionListName: 'operationTypeList',
						optionFor: 'option in operationTypeList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue',
						optionIf: `option.pk.propertyKey !== 'S'`
					}
				},
				{
					type: 'text',
					vModel: 'operationGroup',
					label: this.$t('head.group'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'dataList',
					vModel: 'privilegeId',
					label: this.$t(`common.privilege`),
					placeholder: this.$t('head.searchData'),
					regExpType: 'searchId',
					optionInfo: {
						url: '/common/privilege/list.json',
						params: {
							privilegeType: `b`
						},
						optionListName: 'privilegeTypeList',
						optionFor: 'option in privilegeTypeList',
						optionValue: 'option.privilegeId',
						optionText: 'option.privilegeId'
					}
				},
				{
					type: 'text',
					vModel: 'operationDesc',
					label: this.$t('head.description'),
					placeholder: this.$t('head.searchComment'),
					regExpType: 'desc'
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: '/igate/operation/search.json',
				totalCntUrl: '/igate/operation/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							header: this.$t('head.id'),
							name: 'operationId',
							width: '20%'
						},
						{
							header: this.$t('head.name'),
							name: 'operationName',
							width: '20%'
						},
						{
							header: this.$t('common.type'),
							name: 'operationType',
							width: '10%',
							align: `center`,
							formatter: ({ value }) => (`A` === value ? `Adapter` : `I` === value ? `Interface` : `J` === value ? `Job` : `User`)
						},
						{
							header: this.$t('head.group'),
							name: 'operationGroup',
							width: '20%'
						},
						{
							name: 'privilegeId',
							header: this.$t('common.privilege'),
							width: '10%'
						},
						{
							header: this.$t('head.description'),
							name: 'operationDesc',
							width: '20%'
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['operationId'],
			detailButtonList: [],
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('head.basic.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'operationId',
									label: this.$t('head.id'),
									isPK: true
								},
								{
									type: 'text',
									vModel: 'operationName',
									label: this.$t('head.name')
								},
								{
									type: 'select',
									vModel: 'privateYn',
									label: `Private`,
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Yn',
											orderByKey: true
										},
										optionListName: 'privateYnList',
										optionFor: 'option in privateYnList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'select',
									vModel: 'operationType',
									label: this.$t(`common.type`),
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Operation.OperationType',
											orderByKey: true
										},
										optionListName: 'operationTypeList',
										optionFor: 'option in operationTypeList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue',
										optionIf: `option.pk.propertyKey !== 'S'`
									}
								}
							],
							[
								{
									type: 'select',
									vModel: 'operationLogLevel',
									label: `Log Level`,
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.LogLevel',
											orderByKey: false
										},
										optionListName: 'logLevelList',
										optionFor: 'option in logLevelList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'select',
									vModel: 'xaTransactionAttribute',
									label: `XA-Transaction`,
									optionInfo: {
										optionFor: `option in [{value: 'S', name: 'Supports'}, {value: 'M', name: 'Mandatory'}, {value: 'N', name: 'Requires New'}, {value: 'X', name: 'Not Supported'}]`,
										optionValue: 'option.value',
										optionText: 'option.name'
									}
								},
								{
									type: 'text',
									vModel: 'operationGroup',
									label: this.$t('head.group')
								},
								{
									type: 'text',
									vModel: 'privilegeId',
									label: this.$t('common.privilege')
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'operationDesc',
									label: this.$t('head.description'),
									height: '200px'
								}
							]
						]
					]
				},
				{
					type: `bundle`,
					label: this.$t(`igate.operation.workflow`),
					url: `/operation/DetailWorkFlow`
				},
				{
					type: `bundle`,
					label: `XML`,
					url: `/operation/DetailXML`
				},
				{
					type: 'basic',
					label: this.$t('head.resource.inuse.info'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'lockUserId',
									label: this.$t('head.lock.userId'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateVersion',
									label: this.$t('head.updateVersion'),
									readonly: true
								}
							],
							[
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('head.update.userId'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('head.update.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				}
			],
			detailInfo: {},
			isDetailView: false,
			detailMode: null
		};
	}
};
</script>
