<template>
	<div>
		<div class="row frm-row">
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.id') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`adapterId`])">
								{{ `(${curLengthCnt[`adapterId`]}/${maxLengthCnt[`adapterId`]})` }}
							</span>
						</span>
						<b class="icon-star"></b>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`adapterId`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`adapterId`]) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="adapterId"
							vModel="adapterId"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`adapterId`]),
								'regExp-text': !getReadonly(regExpInfo[`adapterId`]),
								'view-disabled': true,
								dataKey: true
							}"
							:readonly="readonly || getReadonly(regExpInfo[`adapterId`])"
							regExpType="id"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('head.name') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`adapterName`])">
								{{ `(${curLengthCnt[`adapterName`]}/${maxLengthCnt[`adapterName`]})` }}
							</span>
						</span>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`adapterName`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`adapterName`]) ? '8px' : '0px'}` }"
					>
						<inputText
							ref="adapterName"
							vModel="adapterName"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`adapterName`]),
								'regExp-text': !getReadonly(regExpInfo[`adapterName`]),
								'view-disabled': true
							}"
							:readonly="readonly"
							regExpType="name"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ this.$t('common.type') }}</span>
					</label>
					<label class="control-label warningLabel" v-if="!readonly">
						<span>{{ $t('igate.adapter.alert.type.empty') }}</span>
					</label>
					<selectBox
						ref="adapterType"
						vModel="adapterType"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: '/common/property/properties.json',
							params: {
								propertyId: 'List.Adapter.Type',
								orderByKey: true
							},
							optionListName: 'adapterTypeList',
							optionFor: 'option in adapterTypeList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}"
						@changeEvt="changeEvt"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('igate.adapter.charset') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`charset`])">
								{{ `(${curLengthCnt[`charset`]}/${maxLengthCnt[`charset`]})` }}
							</span>
						</span>
						<b class="icon-star"></b>
					</label>
					<div
						:class="`detail-content-${!getReadonly(regExpInfo[`charset`]) ? 'regExp' : 'common'}`"
						:style="{ 'padding-left': `${!getReadonly(regExpInfo[`charset`]) ? '8px' : '0px'}` }"
					>
						<dataList
							ref="charset"
							vModel="charset"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`charset`]),
								'regExp-text': !getReadonly(regExpInfo[`charset`]),
								'view-disabled': true
							}"
							:readonly="readonly"
							:optionInfo="{
								url: '/common/property/properties.json',
								params: {
									propertyId: 'Property.Adapter.Chartset',
									orderByKey: true
								},
								optionListName: 'adapterChartsetList',
								optionFor: 'option in adapterChartsetList',
								optionValue: 'option.pk.propertyKey',
								optionText: 'option.propertyValue'
							}"
							@setLengthCnt="setLengthCnt"
						/>
					</div>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter.endian') }}</span>
						<b class="icon-star"></b>
					</label>
					<selectBox
						ref="endian"
						vModel="endian"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: '/common/property/properties.json',
							params: {
								propertyId: 'List.Adapter.Endian',
								orderByKey: true
							},
							optionListName: 'adapterEndianList',
							optionFor: 'option in adapterEndianList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter.queue.mode') }}</span>
						<b class="icon-star"></b>
					</label>
					<selectBox
						ref="queueMode"
						vModel="queueMode"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: '/common/property/properties.json',
							params: {
								propertyId: 'List.Adapter.Queuemode',
								orderByKey: true
							},
							optionListName: 'adapterQueueModeList',
							optionFor: 'option in adapterQueueModeList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}"
					/>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ `${$t('head.queue')} ${$t('head.log.level')}` }}</span>
						<b class="icon-star"></b>
					</label>
					<selectBox
						ref="queueLogLevel"
						vModel="queueLogLevel"
						:classObject="{ 'form-control': true, 'view-disabled': true }"
						:readonly="readonly || getReadonly({ isPK: false, readonly: false })"
						:optionInfo="{
							url: '/common/property/properties.json',
							params: {
								propertyId: 'List.LogLevel',
								orderByKey: false
							},
							optionListName: 'queueLogLevelList',
							optionFor: 'option in queueLogLevelList',
							optionValue: 'option.pk.propertyKey',
							optionText: 'option.propertyValue'
						}"
					/>
				</div>
			</div>
			<div class="col-lg-4">
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter.telegramHandler') }}</span>
					</label>
					<div class="input-group">
						<inputText
							ref="telegramHandler"
							vModel="telegramHandler"
							:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
							:readonly="true"
						/>
						<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openActivityModal" v-if="!readonly">
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="$refs[`telegramHandler`].setData(null)"
							v-if="!readonly"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter.structure.request') }}</span>
					</label>
					<div class="input-group">
						<inputText
							ref="requestStructure"
							vModel="requestStructure"
							:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
							:readonly="true"
						/>
						<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openRecordModal('I')" v-if="!readonly">
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="$refs[`requestStructure`].setData(null)"
							v-if="!readonly"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
				<div class="form-group">
					<label class="control-label">
						<span>{{ $t('igate.adapter.structure.response') }}</span>
					</label>
					<div class="input-group">
						<inputText
							ref="responseStructure"
							vModel="responseStructure"
							:classObject="{ 'form-control': true, 'view-disabled': true, dataKey: true }"
							:readonly="true"
						/>
						<button type="button" class="btn" style="margin-left: 5px; min-width: 0px" @click="openRecordModal('O')" v-if="!readonly">
							<i class="icon-srch"></i>{{ $t('head.search') }}
						</button>
						<button
							type="button"
							class="btn"
							style="margin-left: 5px; min-width: 0px"
							@click="$refs[`responseStructure`].setData(null)"
							v-if="!readonly"
							:title="$t('head.initialize')"
						>
							<i class="icon-reset" style="margin-right: 0px"></i>
						</button>
					</div>
				</div>
			</div>
		</div>
		<div class="row frm-row">
			<div class="col-lg-12">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ $t('head.description') }}

							<span class="letterLength" v-if="!getReadonly(regExpInfo[`adapterDesc`])">
								{{ `(${curLengthCnt[`adapterDesc`]}/${maxLengthCnt[`adapterDesc`]})` }}
							</span>
						</span>
					</label>
					<div :class="`detail-content-${!getReadonly(regExpInfo[`adapterDesc`]) ? 'regExp' : 'common'}`">
						<textArea
							ref="adapterDesc"
							vModel="adapterDesc"
							:classObject="{
								'form-control': getReadonly(regExpInfo[`adapterDesc`]),
								'regExp-textarea': !getReadonly(regExpInfo[`adapterDesc`]),
								'view-disabled': true
							}"
							:readonly="readonly"
							regExpType="desc"
							@setLengthCnt="setLengthCnt"
							:height="'200px'"
						/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { getRegExpInfo, HttpReq } from '@/utils/common.js';
import merge from 'lodash/merge';

export default {
	name: 'BasicInfo',
	props: {
		detailData: Object,
		mode: String
	},
	extends: DetailBasic,
	data() {
		return {
			keyArr: [
				'adapterId',
				'adapterName',
				'adapterType',
				'charset',
				'endian',
				'queueMode',
				'queueLogLevel',
				'requestStructure',
				'responseStructure',
				'telegramHandler',
				'adapterId',
				'adapterDesc'
			],

			regExpInfo: {
				adapterId: {
					regExpType: 'id',
					isPK: true,
					readonly: false
				},
				adapterName: {
					regExpType: 'name',
					isPK: false,
					readonly: false
				},
				adapterDesc: {
					regExpType: 'desc',
					isPK: false,
					readonly: false
				},
				charset: {
					regExpType: 'default',
					isPK: false,
					readonly: false
				}
			}
		};
	},
	methods: {
		getReadonly(colInfo) {
			return this.readonly || (colInfo.isPK && 'modify' === this.mode);
		},
		getData() {
			let rtnObj = {};

			for (let key in this.$refs) {
				if (0 === this.$refs[key].length) continue;

				rtnObj = merge(rtnObj, this.$refs[key].getData());
			}

			return rtnObj;
		},
		initData() {
			const detailDataFormalize = {};

			this.keyArr.forEach(key => {
				detailDataFormalize[key] =
					'undefined' !== typeof this.detailData[key] && null !== this.detailData[key] ? String(this.detailData[key]) : null;

				const regExpType = !Object.keys(this.regExpInfo).includes(key) ? 'default' : this.regExpInfo[key].regExpType;

				this.setLengthCnt({ [key]: 0 });
				this.maxLengthCnt[key] = getRegExpInfo(regExpType).maxLength;
			});

			for (const key in this.$refs) {
				if (!this.$refs[key].setData) continue;

				if (`queueMode` === key)
					this.$refs[key].setData(
						!detailDataFormalize[this.$refs[key]._props.vModel] ? `X` : detailDataFormalize[this.$refs[key]._props.vModel]
					);
				else if (`queueLogLevel` === key)
					this.$refs[key].setData(
						!detailDataFormalize[this.$refs[key]._props.vModel] ? `N/A` : detailDataFormalize[this.$refs[key]._props.vModel]
					);
				else this.$refs[key].setData(detailDataFormalize[this.$refs[key]._props.vModel]);
			}
		},
		async changeEvt(info) {
			// telegram handler
			let result = await new HttpReq('/common/property/properties.json').read({
				propertyId: `Telegram.Adapter.${info.value}`,
				orderByKey: true
			});

			if ('ok' !== result.data.result) throw result.data;

			this.$refs.telegramHandler.setData(0 < result.data.object.length ? result.data.object[0].propertyValue : ``);

			// charset
			result = await new HttpReq('/common/property/properties.json').read({
				propertyId: `Charset.Adapter.${info.value}`,
				orderByKey: true
			});

			if ('ok' !== result.data.result) throw result.data;

			this.$refs.charset.setData(0 < result.data.object.length ? result.data.object[0].propertyValue : ``);

			this.$emit('changeProperties', info.value);
		},
		openRecordModal(type) {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.record'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'recordId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'text',
										vModel: 'recordName',
										label: this.$t('head.name'),
										placeholder: this.$t('head.searchName'),
										regExpType: 'name'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/record/searchPopup.json',
									totalCntUrl: '/igate/record/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												header: this.$t('head.id'),
												name: 'recordId',
												width: '40%'
											},
											{
												header: this.$t('head.name'),
												name: 'recordName',
												width: '40%'
											},
											{
												header: `${this.$t('common.privilege')}`,
												name: 'privilegeId',
												width: '20%'
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs['I' === type ? 'requestStructure' : 'responseStructure'].setData(info.recordId);
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		},
		openActivityModal() {
			const _this = this;
			const modalInstane = this.$openModal({
				title: this.$t('igate.activity'),
				modalWidth: '1000px',
				contentList: [
					{
						template: `<div>
                                <Search :list="searchList" @search="search" ref="search"/>
                                    <Main
                                    :buttonList="buttonList"
                                    :gridInfo="gridInfo"
                                    :gridSearchObj="gridSearchObj"
                                    :gridType="'modalBasic'"
                                    :viewer="true"
                                    @rowClicked="rowClicked"
                                    @initialize="initialize"
                                />
                            </div>`,
						components: {
							Search: require('@/components/Search.vue').default,
							Main: require('@/components/Main.vue').default
						},
						data() {
							return {
								//Search
								searchList: [
									{
										type: 'text',
										vModel: 'activityId',
										label: this.$t('head.id'),
										placeholder: this.$t('head.searchId'),
										regExpType: 'id'
									},
									{
										type: 'select',
										vModel: 'activityType',
										label: this.$t('common.type'),
										val: 'H',
										readonly: true,
										optionInfo: {
											url: '/common/property/properties.json',
											params: {
												propertyId: 'List.Activity.ActivityType',
												orderByKey: true
											},
											optionListName: 'activityTypes',
											optionFor: 'option in activityTypes',
											optionValue: 'option.pk.propertyKey',
											optionText: 'option.propertyValue'
										}
									},
									{
										type: 'text',
										vModel: 'activityDesc',
										label: this.$t('head.description'),
										placeholder: this.$t('head.searchComment'),
										regExpType: 'desc'
									},
									{
										type: 'dataList',
										vModel: 'pageSize',
										label: this.$t('head.listCount'),
										val: '10',
										optionInfo: {
											optionFor: 'option in [10, 100, 1000]',
											optionValue: 'option',
											optionText: 'option'
										}
									}
								],

								//Button
								buttonList: [{ id: 'initialize', isUse: true }],

								// Grid
								gridInfo: {
									url: '/igate/activity/searchPopup.json',
									totalCntUrl: '/igate/activity/rowCount.json',
									paging: {
										isUse: true,
										side: 'server'
									},
									options: {
										columns: [
											{
												name: 'activityId',
												header: this.$t('head.id')
											},
											{
												name: 'activityName',
												header: this.$t('head.name')
											},
											{
												name: 'activityType',
												header: this.$t('common.type'),
												val: 'H',
												formatter: ({ row }) => {
													const activityTypeObj = {
														F: 'igate.activity.type.control',
														A: 'igate.activity.type.activity',
														S: 'igate.activity.type.service',
														C: 'igate.activity.type.codec',
														T: 'igate.activity.type.transform',
														I: 'igate.activity.type.internal',
														H: 'igate.activity.type.telegram.handler'
													};

													return this.$t(activityTypeObj[row.activityType]);
												}
											},
											{
												name: 'activityDesc',
												header: this.$t('head.description')
											}
										]
									}
								},
								gridSearchObj: {}
							};
						},
						methods: {
							search(searchObj) {
								this.gridSearchObj = searchObj;
							},
							rowClicked(info) {
								_this.$refs.telegramHandler.setData(info.activityId);
								modalInstane.hideModal();
							},
							initialize() {
								this.$refs.search.initialize();
							}
						}
					}
				]
			});
		}
	}
};
</script>
