<script>
import Basic from '@/components/main/Basic.vue';
import Main from '@/views/main/batchLog/Main.vue';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';
import { mapState } from 'vuex';

export default {
	name: 'BatchLog',
	extends: Basic,
	components: {
		Main
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		this.initNewTabSearchObj();
	},
	computed: {
		...mapState(['userInfo', 'isTools'])
	},
	methods: {
		hasNewTabPrivilege(privilegeId) {
			return this.userInfo.totalUserPrivileges.find(userPrivilegeInfo => privilegeId === userPrivilegeInfo.privilegeId);
		}
	},
	data() {
		return {
			cudUrl: '/igate/batchLog/object.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'singleDaterange', vModel: 'fromLogDateTime', label: this.$t('head.from'), dateRangeType: 'from' },
				{ type: 'singleDaterange', vModel: 'toLogDateTime', label: this.$t('head.to'), dateRangeType: 'to' },
				{
					type: 'text',
					vModel: 'transactionId',
					label: `${this.$t('head.transaction')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'logCode',
					label: `${this.$t('head.log')} ${this.$t('head.classification')}`,
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'modal',
					modalInfo: {
						title: this.$t('igate.instance'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'instanceId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'instanceNode',
									label: this.$t('igate.instance.node'),
									placeholder: this.$t('head.searchData')
								},
								{
									type: 'select',
									vModel: 'instanceType',
									label: this.$t('common.type'),
									val: `T`,
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Instance.InstanceType',
											orderByKey: true
										},
										optionListName: 'instanceTypes',
										optionFor: 'option in instanceTypes',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue',
										optionIf: `'T' === option.pk.propertyKey`,
										isViewAll: false
									}
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/instance/searchPopup.json',
							totalCntUrl: '/igate/instance/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'instanceId',
										header: this.$t('head.id'),
										width: '25%'
									},
									{
										name: 'instanceType',
										header: this.$t('common.type'),
										width: '25%',
										formatter: ({ row }) => {
											switch (row.instanceType) {
												case 'T': {
													return this.$t('igate.instance.type.trx');
												}
												case 'A': {
													return this.$t('igate.instance.type.adm');
												}
												case 'L': {
													return this.$t('igate.instance.type.log');
												}
												case 'M': {
													return this.$t('igate.instance.type.mnt');
												}
											}
										}
									},
									{
										name: 'instanceAddress',
										header: this.$t('igate.instance.address'),
										width: '25%'
									},
									{
										name: 'instanceNode',
										header: this.$t('igate.instance.node'),
										width: '25%'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => rowInfo.instanceId
					},
					vModel: 'instanceId',
					label: `${this.$t('igate.instance')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'modal',
					vModel: 'adapterId',
					label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.adapter'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'adapterId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'adapterName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'text',
									vModel: 'adapterDesc',
									label: this.$t('head.description'),
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/adapter/searchPopup.json',
							totalCntUrl: '/igate/adapter/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'adapterId',
										header: this.$t('head.id'),
										width: '20%'
									},
									{
										name: 'adapterName',
										header: this.$t('head.name'),
										width: '20%'
									},
									{
										name: 'adapterDesc',
										header: this.$t('head.description'),
										width: '20%'
									},
									{
										name: 'requestStructure',
										header: this.$t('igate.adapter.structure.request'),
										width: '20%'
									},
									{
										name: 'responseStructure',
										header: this.$t('igate.adapter.structure.response'),
										width: '20%'
									}
								]
							}
						},
						rowClickedCallback: function (rowInfo) {
							return rowInfo.adapterId;
						}
					}
				},
				{
					type: 'modal',
					vModel: 'connectorId',
					label: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.connector'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'connectorId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'connectorName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/connector/searchPopup.json',
							totalCntUrl: '/igate/connector/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'connectorId',
										header: this.$t('head.id'),
										width: '25%'
									},
									{
										name: 'connectorName',
										header: this.$t('head.name'),
										width: '25%'
									},
									{
										name: 'connectorDesc',
										header: this.$t('head.description'),
										width: '25%'
									},
									{
										name: 'connectorType',
										header: this.$t('common.type'),
										width: '25%'
									}
								]
							}
						},
						rowClickedCallback: function (rowInfo) {
							return rowInfo.connectorId;
						}
					}
				},
				{
					type: 'text',
					vModel: 'sessionId',
					label: `${this.$t('igate.connectorControl.session')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'remoteAddr',
					label: this.$t(`head.ip`),
					placeholder: this.$t('head.searchId'),
					regExpType: 'ip'
				},
				{
					type: 'modal',
					vModel: 'interfaceServiceId',
					label: `${this.$t('igate.service')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.service'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'serviceId',
									label: this.$t('head.id'),
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'serviceName',
									label: this.$t('head.name'),
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/service/searchPopup.json',
							totalCntUrl: '/igate/service/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										name: 'serviceId',
										header: this.$t('head.id'),
										width: '25%'
									},
									{
										name: 'serviceName',
										header: this.$t('head.name'),
										width: '25%'
									},
									{
										name: 'serviceDesc',
										header: this.$t('head.description'),
										width: '25%'
									},
									{
										name: 'serviceType',
										header: this.$t('common.type'),
										width: '25%'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => rowInfo.serviceId
					}
				},
				{
					type: 'singleDate',
					vModel: 'fileDate',
					label: `${this.$t('head.file')} ${this.$t('head.date')}`,
					placeholder: this.$t('head.searchFileStamp')
				},
				{
					type: 'text',
					vModel: 'fileName',
					label: `${this.$t('head.file')} ${this.$t('head.name')}`,
					placeholder: this.$t('head.searchName'),
					regExpType: 'name'
				},
				{
					type: 'text',
					vModel: 'tableLastKey',
					label: this.$t('igate.batchLog.tableLastKey'),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					placeholder: this.$t('head.searchCoditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'refresh', isUse: true },
				{ id: 'newTab', isUse: true }
			],

			// Grid
			gridInfo: {
				url: '/igate/batchLog/search.json',
				totalCntUrl: '/igate/batchLog/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'transactionId',
							header: `${this.$t('head.transaction')} ${this.$t('head.id')}`,
							width: '12%'
						},
						{
							name: 'logCode',
							header: `${this.$t('head.log')} ${this.$t('head.classification')}`,
							align: 'center',
							width: '6%'
						},
						{
							name: 'adapterId',
							header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
							width: '9%'
						},
						{
							name: 'interfaceServiceId',
							header: `${this.$t('igate.interface')} ${this.$t('igate.service')} ${this.$t('head.id')}`,
							width: '10%'
						},
						{
							name: 'exceptionCode',
							header: this.$t('igate.exceptionCode'),
							align: 'center',
							width: '8%'
						},
						{
							name: 'startTimestamp',
							header: this.$t('igate.traceLog.requestTimestamp'),
							align: 'center',
							width: '12.5%',
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							name: 'endTimestamp',
							header: this.$t('igate.traceLog.responseTimestamp'),
							align: 'center',
							width: '12.5%',
							formatter: obj => changeDateFormat(obj.value)
						},
						{
							name: 'instanceId',
							header: `${this.$t('igate.instance')} ${this.$t('head.id')}`,
							width: '8%'
						},
						{
							name: 'connectorId',
							header: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
							width: '10%'
						},
						{
							name: 'safMessage',
							header: this.$t('igate.batchLog.retryInfo'),
							align: 'center',
							width: '8%',
							formatter: ({ row }) => {
								if (row.exceptionCode && this.hasNewTabPrivilege(`SafMessageViewer`) && !this.isTools) {
									return `<div><a type="button" class="btn-m icon-srch"></a></div>`;
								} else {
									return `<div><a>-</a></div>`;
								}
							}
						},
						{
							name: 'traceLog',
							header: this.$t('igate.batchLog.logInfo'),
							align: 'center',
							width: '8%',
							formatter: () => {
								if (this.hasNewTabPrivilege(`TraceLogViewer`) && !this.isTools) {
									return `<div><a type="button" class="btn-m icon-srch"></a></div>`;
								} else {
									return `<div><a>-</a></div>`;
								}
							}
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['pk.logId', 'pk.logDateTime'],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/batchLog/BasicInfo'
				}
			]
		};
	}
};
</script>
