<template>
	<div class="col-lg-6">
		<div class="form-group">
			<label class="control-label">
				<span>{{ `${this.$t('igate.connector')} ID` }} </span>
			</label>
		</div>
		<div class="form-group" v-for="(connector, index) in connectorList" :key="index">
			<div class="input-group">
				<input type="text" class="form-control view-disabled readonly disabled" :value="connector" disabled readonly />
			</div>
		</div>
	</div>
</template>

<script>
import DetailProperty from '@/components/detail/DetailProperty.vue';
import isEmpty from 'lodash/isEmpty';

export default {
	name: 'ConnectorInfo',
	extends: DetailProperty,
	data() {
		return {
			connectorList: []
		};
	},
	methods: {
		setPropertyList() {
			this.connectorList = isEmpty(this.detailData) || isEmpty(this.detailData.connectors) ? [] : this.detailData.connectors;
		}
	}
};
</script>
