<template>
	<div class="table-responsive">
		<TuiGrid ref="tuiGrid" v-bind="gridOptions" @click="onClick" />
	</div>
</template>

<script>
import moment from 'moment';
import { Grid } from '@toast-ui/vue-grid';
import { makeGridOptions, openNewTab, downloadCSV } from '@/utils/common.js';

export default {
	name: `DashboardDataTableChart`,
	components: {
		TuiGrid: Grid
	},
	props: {
		options: Object
	},
	data() {
		return {
			originWidth: null,
			gridOptions: {
				columns: [
					{
						name: `status`,
						header: this.$t(`dashboard.head.status`),
						width: `5%`,
						align: `center`,
						formatter: obj =>
							`M` === obj.value
								? `<span style="color: #ffc107">${this.$t(`head.warn`)}</span>`
								: `L` === obj.value
								? `<span style="color: #ed3137">${this.$t(`head.serious`)}</span>`
								: `<span style="color: #28a745">${this.$t(`dashboard.head.success`)}</span>`
					},
					{
						name: `uuid`,
						header: this.$t(`dashboard.head.transactionId`),
						width: `13%`
					},
					{
						name: `transactionId`,
						header: this.$t(`dashboard.head.interface.service.id`),
						width: `15%`
					},
					{
						name: `transactionName`,
						header: this.$t(`dashboard.head.transactionName`),
						width: `15%`
					},
					{
						name: `adapterId`,
						header: this.$t(`igate.adapter.id`),
						width: `15%`
					},
					{
						name: `instanceId`,
						header: this.$t(`igate.instance.id`),
						width: `10%`
					},
					{
						name: `snapshotTimestamp`,
						header: this.$t(`dashboard.head.snapshotTimestamp`),
						width: `15%`,
						align: `center`,
						formatter: obj => moment(new Date(obj.value)).format(`HH:mm:ss`)
					}
				],
				scrollY: false
			}
		};
	},
	created() {
		this.gridOptions = makeGridOptions(this.gridOptions);
	},
	mounted() {
		this.originWidth = this.$el.clientWidth;
		this.$refs.tuiGrid.invoke(`resetData`, []);
	},
	methods: {
		addData(dataArr) {
			this.$refs.tuiGrid.invoke(`resetData`, dataArr);
		},
		onClick(evt) {
			if (`undefined` === typeof evt.rowKey || `rowHeader` === evt.targetType) return;

			openNewTab(`103020`, () => {
				localStorage.setItem(`searchObj`, JSON.stringify({ transactionId: evt.instance.getRow(evt.rowKey).uuid }));
			});
		},
		setInterval() {
			if (this.originWidth === this.$el.clientWidth) return;

			this.originWidth = this.$el.clientWidth;

			this.$refs.tuiGrid.invoke(`setWidth`, this.$el.clientWidth);
		},
		exportData() {
			this.$startSpinner();

			let csvData = ``;

			csvData += this.$refs.tuiGrid
				.invoke(`getColumns`)
				.map(column => column.header)
				.join(`,`);
			csvData += `\r\n`;

			this.$refs.tuiGrid.invoke(`getData`).forEach(data => {
				csvData += `${
					`M` === data.status ? this.$t(`head.warn`) : `L` === data.status ? this.$t(`head.serious`) : this.$t(`dashboard.head.success`)
				},`;
				csvData += `${data.uuid},`;
				csvData += `${data.transactionId},`;
				csvData += `${data.transactionName},`;
				csvData += `${data.adapterId},`;
				csvData += `${data.instanceId},`;
				csvData += `${moment(new Date(data.snapshotTimestamp)).format(`HH:mm:ss`)}`;
				csvData += `\r\n`;
			});

			downloadCSV(`${this.options.componentName}_${Date.now()}`, csvData);

			this.$stopSpinner();
		}
	}
};
</script>
