<script>
import Basic from '@/components/main/Basic.vue';
import Detail from '@/views/detail/traceLog/Detail.vue';
import { getPrivilege } from '@/utils/common.js';

export default {
	name: 'TraceLog',
	extends: Basic,
	components: {
		Detail
	},
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		this.initNewTabSearchObj();

		if (localStorage.getItem('selectedTraceLogInfo')) {
			const selectedTraceLogInfo = JSON.parse(localStorage.getItem('selectedTraceLogInfo'));
			localStorage.removeItem('selectedTraceLogInfo');

			this.initDetail('detail', {
				...selectedTraceLogInfo,
				isNewTab: true
			});
		}
	},
	data() {
		return {
			cudUrl: '/igate/traceLog/object.json',
			controlUrl: '/igate/record/control.json',

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{ type: 'singleDaterange', vModel: 'fromLogDateTime', label: this.$t('head.from'), dateRangeType: 'from' },
				{ type: 'singleDaterange', vModel: 'toLogDateTime', label: this.$t('head.to'), dateRangeType: 'to' },
				{
					type: 'dateCalc',
					vModel: 'rangeTime',
					label: this.$t('head.from.time'),
					unit: 'm',
					val: 10,
					optionInfo: {
						optionFor: `option in [1, 3, 5, 10]`,
						optionValue: 'option',
						optionText: 'option'
					},
					dateVModel: [
						{ dateRangeType: 'from', vModel: 'fromLogDateTime' },
						{ dateRangeType: 'to', vModel: 'toLogDateTime' }
					]
				},
				{
					type: 'text',
					vModel: 'transactionId',
					label: `${this.$t('head.transaction')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'logCode',
					label: `${this.$t('head.log')} ${this.$t('head.classification')}`,
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'select',
					vModel: 'instanceId',
					label: `${this.$t('igate.instance')} ${this.$t('head.id')}`,
					optionInfo: {
						url: '/igate/instance/list.json',
						optionListName: 'traceLogInstances',
						optionFor: 'option in traceLogInstances',
						optionValue: 'option.instanceId',
						optionText: 'option.instanceId',
						optionIf: `option.instanceType == 'T'`
					}
				},
				{
					type: 'modal',
					vModel: 'adapterId',
					label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.adapter'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'adapterId',
									label: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'adapterName',
									label: `${this.$t('igate.adapter')} ${this.$t('head.name')}`,
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'text',
									vModel: 'adapterDesc',
									label: `${this.$t('igate.adapter')} ${this.$t('head.description')}`,
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/adapter/searchPopup.json',
							totalCntUrl: '/igate/adapter/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: `${this.$t('igate.adapter')} ${this.$t('head.id')}`,
										name: 'adapterId'
									},
									{
										header: `${this.$t('igate.adapter')} ${this.$t('head.name')}`,
										name: 'adapterName'
									},
									{
										header: `${this.$t('igate.adapter')} ${this.$t('head.description')}`,
										name: 'adapterDesc'
									},
									{
										header: this.$t('igate.adapter.structure.request'),
										name: 'requestStructure'
									},
									{
										header: this.$t('igate.adapter.structure.response'),
										name: 'responseStructure'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => {
							return rowInfo.adapterId;
						}
					}
				},
				{
					type: 'modal',
					vModel: 'connectorId',
					label: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.connector'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'connectorId',
									label: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'connectorName',
									label: `${this.$t('igate.connector')} ${this.$t('head.name')}`,
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'select',
									vModel: 'connectorType',
									label: `${this.$t('igate.connector')} ${this.$t('common.type')}`,
									optionInfo: {
										url: '/common/property/properties.json',
										params: {
											propertyId: 'List.Connector.Type',
											orderByKey: true
										},
										optionListName: 'connectorTypes',
										optionFor: 'option in connectorTypes',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									}
								},
								{
									type: 'text',
									vModel: 'connectorDesc',
									label: `${this.$t('igate.connector')} ${this.$t('head.description')}`,
									placeholder: this.$t('head.searchComment'),
									regExpType: 'desc'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/connector/searchPopup.json',
							totalCntUrl: '/igate/connector/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: `${this.$t('igate.connector')} ${this.$t('head.id')}`,
										name: 'connectorId'
									},
									{
										header: `${this.$t('igate.connector')} ${this.$t('head.name')}`,
										name: 'connectorName'
									},
									{
										header: `${this.$t('igate.connector')} ${this.$t('head.description')}`,
										name: 'connectorDesc'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => {
							return rowInfo.connectorId;
						}
					}
				},
				{
					type: 'text',
					vModel: 'sessionId',
					label: `${this.$t('igate.connectorControl.session')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId'
				},
				{
					type: 'text',
					vModel: 'remoteAddr',
					label: this.$t(`head.ip`),
					placeholder: this.$t('head.searchId'),
					regExpType: 'ip'
				},
				{
					type: 'modal',
					vModel: 'interfaceId',
					label: `${this.$t('igate.interface')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.interface'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'interfaceId',
									label: `${this.$t('igate.interface')} ${this.$t('head.id')}`,
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'interfaceName',
									label: `${this.$t('igate.interface')} ${this.$t('head.name')}`,
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/interface/searchPopup.json',
							totalCntUrl: '/igate/interface/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: `${this.$t('igate.interface')} ${this.$t('head.id')}`,
										name: 'interfaceId'
									},
									{
										header: `${this.$t('igate.interface')} ${this.$t('head.name')}`,
										name: 'interfaceName'
									},
									{
										header: `${this.$t('igate.interface')} ${this.$t('head.description')}`,
										name: 'interfaceDesc'
									},
									{
										header: `${this.$t('igate.interface')} ${this.$t('common.type')}`,
										name: 'interfaceType'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => {
							return rowInfo.interfaceId;
						}
					}
				},
				{
					type: 'modal',
					vModel: 'serviceId',
					label: `${this.$t('igate.service')} ${this.$t('head.id')}`,
					placeholder: this.$t('head.searchId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('igate.service'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'serviceId',
									label: `${this.$t('igate.service')} ${this.$t('head.id')}`,
									placeholder: this.$t('head.searchId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'serviceName',
									label: `${this.$t('igate.service')} ${this.$t('head.name')}`,
									placeholder: this.$t('head.searchName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('head.listCount'),
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: '/igate/service/searchPopup.json',
							totalCntUrl: '/igate/service/rowCount.json',
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: `${this.$t('igate.service')} ${this.$t('head.id')}`,
										name: 'serviceId'
									},
									{
										header: `${this.$t('igate.service')} ${this.$t('head.name')}`,
										name: 'serviceName'
									},
									{
										header: `${this.$t('igate.service')} ${this.$t('head.description')}`,
										name: 'serviceDesc'
									},
									{
										header: `${this.$t('igate.service')} ${this.$t('common.type')}`,
										name: 'serviceType'
									}
								]
							}
						},
						rowClickedCallback: rowInfo => {
							return rowInfo.serviceId;
						}
					}
				},
				{
					type: 'text',
					vModel: 'externalTransaction',
					label: this.$t(`igate.externalTransaction`),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'externalMessage',
					label: this.$t(`igate.externalMessage`),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'text',
					vModel: 'responseCode',
					label: this.$t(`igate.traceLog.responseCode`),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'select',
					vModel: 'timeoutYn',
					label: this.$t('head.timeoutYn'),
					optionInfo: {
						url: '/common/property/properties.json',
						params: {
							propertyId: 'List.Yn',
							orderByKey: true
						},
						optionListName: 'timeoutYnList',
						optionFor: 'option in timeoutYnList',
						optionValue: 'option.pk.propertyKey',
						optionText: 'option.propertyValue'
					}
				},
				{
					type: 'text',
					vModel: 'exceptionCode',
					label: this.$t(`head.exception.code`),
					placeholder: this.$t('head.searchData')
				},
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('head.listCount'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'initialize', isUse: true },
				{ id: 'newTab', isUse: true },
				{
					id: 'excelExport',
					isUse: true,
					exportUrl: '/igate/traceLog/exportExcel.json',
					fileName: `TraceLog ExcelExport`
				}
			],

			// Grid
			gridInfo: {
				url: '/igate/traceLog/search.json',
				totalCntUrl: '/igate/traceLog/rowCount.json',
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					columns: [
						{
							name: 'transactionId',
							header: `${this.$t('head.transaction')} ${this.$t('head.id')}`,
							width: '9%'
						},
						{
							name: 'messageId',
							header: `${this.$t('igate.message')} ${this.$t('head.id')}`,
							width: '6%'
						},
						{
							name: 'logCode',
							header: `${this.$t('head.log')} ${this.$t('head.classification')}`,
							align: 'center',
							width: '6%'
						},
						{
							name: 'adapterId',
							header: `${this.$t('igate.adapter')}`,
							width: '8%'
						},
						{
							name: 'interfaceId',
							header: `${this.$t('igate.interface')}`,
							width: '9%'
						},
						{
							name: 'serviceId',
							header: `${this.$t('igate.service')}`,
							width: '9%'
						},
						{
							name: 'externalTransaction',
							header: `${this.$t('igate.externalTransaction')}`,
							width: '9%'
						},
						{
							name: 'externalMessage',
							header: `${this.$t('igate.externalMessage')}`,
							width: '9%'
						},
						{
							name: 'requestTimestamp',
							header: `${this.$t('igate.traceLog.requestTimestamp')}`,
							align: 'center',
							width: '11%'
						},
						{
							name: 'responseTimestamp',
							header: `${this.$t('igate.traceLog.responseTimestamp')}`,
							align: 'center',
							width: '11%'
						},
						{
							name: 'instanceId',
							header: `${this.$t('igate.instance')}`,
							width: '6%'
						},
						{
							name: 'connectorId',
							header: `${this.$t('igate.connector')}`,
							width: '9%'
						},
						{
							name: 'partitionId',
							hidden: true
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['pk.logId', 'pk.logDateTime'],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailButtonList: [],
			detailTabList: [
				{
					type: 'bundle',
					label: this.$t('head.basic.info'),
					url: '/traceLog/BasicInfo'
				},
				{
					type: `bundle`,
					label: this.$t('igate.traceLog.message.info'),
					url: `/traceLog/MessageInfo`
				},
				{
					type: 'grid',
					label: this.$t('head.model.info'),
					url: '/igate/traceLog/record.json',
					options: {
						columnOptions: {
							resizable: true
						},
						treeColumnOptions: {
							name: 'fieldId'
						},
						columns: [
							{
								name: 'fieldId',
								header: `${this.$t('head.field')} ${this.$t('head.id')}`
							},
							{
								name: 'fieldName',
								header: `${this.$t('head.field')} ${this.$t('head.name')}`
							},
							{
								name: 'fieldType',
								header: `${this.$t('head.field')} ${this.$t('common.type')}`,
								formatter: ({ row }) => {
									if (!row.fieldType || !row.fieldLength) return '';

									const fieldTypeTranslateObj = {
										B: 'Byte',
										S: 'Short',
										I: 'Int',
										L: 'Long',
										F: 'Float',
										D: 'Double',
										T: 'String',
										N: 'Numeric',
										P: 'Decimal',
										A: 'Raw',
										p: 'Timestamp',
										b: 'Boolean',
										R: 'Record',
										v: 'Individual'
									};

									return `${fieldTypeTranslateObj[row.fieldType]}(${row.fieldLength})`;
								}
							},
							{
								header: this.$t('common.property.value'),
								name: 'value'
							},
							{
								header: 'Hex',
								name: 'rawValue'
							}
						]
					}
				}
			]
		};
	}
};
</script>
