<template>
	<div>
		<div ref="topology" :style="topologyStyle"></div>
	</div>
</template>

<script>
import cytoscape from 'cytoscape';
import cloneDeep from 'lodash/cloneDeep';
import { Grid } from '@toast-ui/vue-grid';
import { getHttpUrl, HttpReq, getUUID, escapeHtml, makeGridOptions, getNumFromStr, DetailToggleEventBus, openNewTab } from '@/utils/common.js';

export default {
	name: `DetailWorkFlow`,
	props: {
		detailData: Object
	},
	data() {
		return {
			scriptUrlList: [`/custom/js/xml2json.js`],
			isScriptLoad: false,
			jsonObj: null,
			imgObj: {
				Break: { imgName: 'break', img: null },
				Case: { imgName: 'case', img: null },
				Condition: { imgName: 'condition', img: null },
				Continue: { imgName: 'continue', img: null },
				Default: { imgName: 'default', img: null },
				End: { imgName: 'end', img: null },
				FaultHandlers: { imgName: 'faultHandler', img: null },
				ForEach: { imgName: 'for', img: null },
				Fork: { imgName: 'fork', img: null },
				Activity: { imgName: 'activity', img: null, imgCustom: null },
				Operation: { imgName: 'operation', img: null },
				Query: { imgName: 'query', img: null },
				InvokeActivity: { imgName: 'activity', img: null },
				InvokeOperation: { imgName: 'operation', img: null },
				ReThrow: { imgName: 'rethrow', img: null },
				Start: { imgName: 'start', img: null },
				Scope: { imgName: 'scope', img: null },
				Script: { imgName: 'script', img: null },
				Switch: { imgName: 'switch', img: null },
				Throw: { imgName: 'throw', img: null },
				While: { imgName: 'while', img: null },
				Service: { imgName: 'service', img: null },
				Response: { imgName: 'activity', img: null }
			},
			isDetailExist: {
				Script: true,
				ForEach: true,
				Operation: true,
				InvokeOperation: true,
				Activity: true,
				InvokeActivity: true,
				Fork: true,
				Condition: true,
				FaultHandlers: true,
				Throw: true,
				While: true,
				Case: true,
				Query: true,
				Service: true,
				Response: true
			},
			topologyStyle: {
				width: null,
				height: null
			}
		};
	},
	async created() {
		const promiseList = [];

		this.scriptUrlList.forEach(scriptUrl => {
			const script = document.createElement('script');
			script.type = 'text/javascript';
			script.src = getHttpUrl(scriptUrl);

			document.querySelector('#ct').appendChild(script);

			promiseList.push(
				new Promise(resolve => {
					script.addEventListener('load', () => {
						resolve();
					});
				})
			);
		});

		await Promise.all(promiseList);

		for (const key in this.imgObj) {
			this.imgObj[key].img = new Image();
			this.imgObj[key].img.src = require(`@/assets/img/operation/${this.imgObj[key].imgName}.png`);

			if ('Activity' === key) {
				this.imgObj[key].imgCustom = new Image();
				this.imgObj[key].imgCustom.src = require(`@/assets/img/operation/activityCustom.png`);
			}
		}

		this.isScriptLoad = true;

		this.initData();
	},
	watch: {
		detailData: {
			deep: true,
			handler() {
				this.initData();
			}
		}
	},
	methods: {
		initData() {
			if (!this.detailData.operationRuleStr) return;

			if (!this.isScriptLoad) return;

			this.jsonObj = JSON.parse(xml2json($.parseXML(this.detailData.operationRuleStr), ' '));

			this.refreshWorkflow();
		},
		async refreshWorkflow() {
			const cy = cytoscape({
				container: this.$refs.topology
			});

			const canvas = cy.container().querySelectorAll(`canvas`)[2];
			const ctx = canvas.getContext('2d');

			const nodes = [];
			const edges = [];

			this.createTopology(ctx, this.jsonObj.Operation, nodes, edges);

			edges.forEach(edgeInfo => {
				const info = nodes.find(node => node.data.id === edgeInfo.data.source);
				const nodeType = info.data.nodeType;

				if ('Start' === nodeType && edges.find(tmpEdgeInfo => edgeInfo.data.source === tmpEdgeInfo.data.source)) {
					edgeInfo.data.value = edgeInfo.data.value ? edgeInfo.data.value : `#${edgeInfo.data.orgData['@order']}`;
				} else if ('Script' === nodeType && edges.find(tmpEdgeInfo => edgeInfo.data.source === tmpEdgeInfo.data.source)) {
					edgeInfo.data.value = edgeInfo.data.value ? edgeInfo.data.value : `#${edgeInfo.data.orgData['@order']}`;
				} else if ('FaultHandlers' === nodeType) {
					edgeInfo.data.value = edgeInfo.data.value ? edgeInfo.data.value : `#${edgeInfo.data.orgData['@order']}`;
				} else if ('Activity' === nodeType && edges.find(tmpEdgeInfo => edgeInfo.data.source === tmpEdgeInfo.data.source)) {
					edgeInfo.data.value = `#${edgeInfo.data.orgData['@order']}` + edgeInfo.data.value ? ` ${edgeInfo.data.value}` : ``;
				}
			});

			cy.add(nodes);
			cy.add(edges);

			// style
			const isMoon = localStorage.getItem(`theme`) && `moon` === localStorage.getItem(`theme`);

			cy.style()
				.selector('node')
				.style({
					width: e => ctx.measureText(e._private.data.name).width + 60,
					height: '20',
					label: 'data(name)',
					'font-size': '12px',
					'text-valign': 'center',
					'text-halign': 'center',
					'background-color': isMoon ? `#1f2337` : `#fff`,
					color: isMoon ? `#fff` : `#151826`,
					'border-width': '1',
					'border-color': isMoon ? `#8a8b92` : `#808080`,
					'text-margin-x': '7px',
					'overlay-color': isMoon ? `#fff` : `#000`,
					shape: () => 'round-rectangle'
				})
				.selector('edge')
				.style({
					width: '1',
					'line-color': isMoon ? `#8a8b92` : `#808080`,
					'target-arrow-color': isMoon ? `#8a8b92` : `#808080`,
					'target-arrow-shape': 'triangle',
					'curve-style': 'bezier',
					label: 'data(value)',
					'font-size': '11px',
					color: isMoon ? `#fff` : `#151826`
				})
				.selector('node[isGroup="true"]')
				.style({
					'text-valign': 'top',
					'text-halign': 'center',
					'min-height': '150',
					padding: '5'
				})
				.update();
			// style

			let activityInfoList = [];

			const activityIdList = nodes.filter(node => 'Activity' === node.data.nodeType).map(node => node.data.orgData['@id']);

			if (0 < activityIdList.length) {
				const activityHttpReq = new HttpReq(`/igate/activity/list.json`);

				const rtnParam = {};

				activityIdList.forEach((activityId, idx) => {
					rtnParam[`activityIdList[${idx}]`] = activityId;
				});

				const activityHttpReqResult = await activityHttpReq.read(rtnParam);

				if (`ok` === activityHttpReqResult.data.result) {
					activityInfoList = activityHttpReqResult.data.object;
				}
			}

			cy.onRender(() => {
				let width = 0,
					height = 0;

				cy.nodes().forEach(info => {
					width = Math.max(width, info._private.bodyBounds.x2 + info._private.bodyBounds.w);
					height = Math.max(height, info._private.bodyBounds.y2 + info._private.bodyBounds.h);
				});

				const panel = document.getElementById('panel');

				const panelBody = panel.getElementsByClassName('panel-body')[0];
				const panelBodyComputedStyle = getComputedStyle(panelBody);

				const panelBodyPaddingLeft = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-left'));
				const panelBodyPaddingRight = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-right'));
				const panelBodyPaddingTop = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-top'));
				const panelBodyPaddingBottom = getNumFromStr(panelBodyComputedStyle.getPropertyValue('padding-bottom'));

				width = Math.max(width, panelBody.clientWidth - (panelBodyPaddingLeft + panelBodyPaddingRight));
				height = Math.max(height, panelBody.clientHeight - (panelBodyPaddingTop + panelBodyPaddingBottom));

				this.topologyStyle.width = `${width}px`;
				this.topologyStyle.height = `${height}px`;

				ctx.beginPath();

				cy.nodes().each(e => {
					if (e._private.data.nodeType) {
						let img = null;

						if (`Activity` === e._private.data.nodeType) {
							const tmpActivityInfoList = activityInfoList.filter(
								activityInfo => activityInfo.activityId === e._private.data.orgData['@id']
							);

							img =
								0 === tmpActivityInfoList.length || 'undefined' === typeof tmpActivityInfoList[0].activityProject
									? this.imgObj[e._private.data.nodeType].img
									: this.imgObj[e._private.data.nodeType].imgCustom;
						} else {
							img = this.imgObj[e._private.data.nodeType] ? this.imgObj[e._private.data.nodeType].img : this.imgObj['Default'].img;
						}

						ctx.drawImage(
							img,
							e._private.rscratch.labelX - e._private.rscratch.labelWidth / 2 - img.width + 5,
							e._private.rscratch.labelY - (0 === e._private.children.length ? img.height / 2 : img.height)
						);
					}
				});

				ctx.closePath();
			});

			cy.on('tap', 'node', async evt => {
				const nodeInfo = evt.target;

				const nodeType = nodeInfo._private.data.nodeType;

				if (!this.isDetailExist[nodeType]) return;

				if (nodeInfo.selected()) {
					if ([`Query`, `Service`, `Response`].find(pNodeType => pNodeType === nodeType)) {
						const data = nodeInfo._private.data;

						let mappingId = null;

						for (let i = 0; i < data.orgData.Parameter.length; i++) {
							let desc = data.orgData.Parameter[i]['@desc'];

							if (-1 < desc.toLowerCase().indexOf('mapping')) {
								mappingId = data.orgData.Parameter[i].Value['#cdata'];
								break;
							}
						}

						if (mappingId) {
							const mappingHttpReq = new HttpReq('/igate/mapping/object.json');

							const mappingHttpReqResult = await mappingHttpReq.read({
								mappingId: mappingId.substring(1, mappingId.length - 1)
							});

							if (`ok` === mappingHttpReqResult.data.result) {
								localStorage.removeItem('selectedRowMapping');
								localStorage.setItem(
									'selectedRowMapping',
									JSON.stringify({
										mappingId: mappingHttpReqResult.data.object.mappingId,
										mappingType: mappingHttpReqResult.data.object.mappingType
									})
								);

								//맵핑
								openNewTab(101020);
							} else {
								this.$alert({
									type: 'warn',
									message:
										mappingHttpReqResult.data.error[0].className +
										(mappingHttpReqResult.data.error[0].message ? '<hr/>' + mappingHttpReqResult.data.error[0].message : ''),
									isXSSMode: false
								});
							}
						}
					} else {
						const detailInfo = this.getDetailInfo(nodeInfo, cy);

						this.$openModal({
							title: detailInfo.title,
							titleClickEvt: detailInfo.titleClickEvt,
							modalWidth: detailInfo.size,
							contentList: [
								{
									template: detailInfo.bodyHtml,
									components: {
										TuiGrid: Grid
									},
									data() {
										return {
											gridOptions: detailInfo.gridOptions
										};
									}
								}
							]
						});
					}
				}
			});

			window.workflowResize = () => {
				cy.resize();

				const isMoon = localStorage.getItem(`theme`) && `moon` === localStorage.getItem(`theme`);

				cy.style()
					.selector('node')
					.style({
						'background-color': isMoon ? `#1f2337` : `#fff`,
						color: isMoon ? `#fff` : `#151826`,
						'border-color': isMoon ? `#8a8b92` : `#808080`,
						'overlay-color': isMoon ? `#fff` : `#000`
					})
					.selector('edge')
					.style({
						'line-color': isMoon ? `#8a8b92` : `#808080`,
						'target-arrow-color': isMoon ? `#8a8b92` : `#808080`,
						color: isMoon ? `#fff` : `#151826`
					})
					.update();
			};

			DetailToggleEventBus.$off(`MainResize`, window.workflowResize);
			DetailToggleEventBus.$on(`MainResize`, window.workflowResize);

			cy.userZoomingEnabled(false);
			cy.userPanningEnabled(false);
		},
		createTopology(ctx, info, nodes, edges, parentNodeInfo) {
			const uuid = getUUID();

			const childNodeSizeInfo = {
				minX: Number.MAX_SAFE_INTEGER,
				maxX: 0,
				minY: Number.MAX_SAFE_INTEGER,
				maxY: 0
			};

			for (const key in info.Activities) {
				const subInfo = info.Activities[key];

				if (!subInfo) return;

				const nodeInfoArr = Array.isArray(subInfo) ? subInfo : [subInfo];

				nodeInfoArr.forEach(nodeInfo => {
					nodes.push({
						group: 'nodes',
						data: {
							id: `${nodeInfo['@index']}_${uuid}`,
							name: nodeInfo['@desc'],
							nodeType: key,
							orgData: cloneDeep(nodeInfo)
						},
						position: {
							x: Number(nodeInfo['@xPos']),
							y: Number(nodeInfo['@yPos'])
						}
					});

					if (!isNaN(nodeInfo['@index'])) nodes[nodes.length - 1].data.name += ` #${nodeInfo['@index']}`;

					nodes[nodes.length - 1].position.x += (ctx.measureText(nodes[nodes.length - 1].data.name).width + 60) / 2;
					nodes[nodes.length - 1].position.y += 10;

					if (nodeInfo.Activities) nodes[nodes.length - 1].position.y += 15;

					if (parentNodeInfo) {
						nodes[nodes.length - 1].data.parent = parentNodeInfo.data.id;
						nodes[nodes.length - 1].position.x += parentNodeInfo.position.x;
						nodes[nodes.length - 1].position.y += parentNodeInfo.position.y;

						childNodeSizeInfo.minX = Math.min(childNodeSizeInfo.minX, nodes[nodes.length - 1].position.x);
						childNodeSizeInfo.minY = Math.min(childNodeSizeInfo.minY, nodes[nodes.length - 1].position.y);
						childNodeSizeInfo.maxX = Math.max(childNodeSizeInfo.maxX, nodes[nodes.length - 1].position.x);
						childNodeSizeInfo.maxY = Math.max(childNodeSizeInfo.maxY, nodes[nodes.length - 1].position.y);
					}

					if (nodeInfo.Activities) {
						nodes[nodes.length - 1].data.isGroup = 'true';

						const tmpChildNodeSizeInfo = this.createTopology(ctx, nodeInfo, nodes, edges, cloneDeep(nodes[nodes.length - 1]));

						if (parentNodeInfo) {
							childNodeSizeInfo.minX = Math.min(childNodeSizeInfo.minX, tmpChildNodeSizeInfo.minX);
							childNodeSizeInfo.minY = Math.min(childNodeSizeInfo.minY, tmpChildNodeSizeInfo.minY);
							childNodeSizeInfo.maxX = Math.max(childNodeSizeInfo.maxX, tmpChildNodeSizeInfo.maxX);
							childNodeSizeInfo.maxY = Math.max(childNodeSizeInfo.maxY, tmpChildNodeSizeInfo.maxY);
						}
					}
				});
			}

			if (parentNodeInfo) {
				nodes.push({
					group: 'nodes',
					data: {
						id: `start_${uuid}`,
						name: 'Start',
						nodeType: 'Start',
						parent: parentNodeInfo.data.id
					},
					position: {
						x: (childNodeSizeInfo.maxX + childNodeSizeInfo.minX) / 2,
						y: parentNodeInfo.position.y
					}
				});

				nodes.push({
					group: 'nodes',
					data: {
						id: `end_${uuid}`,
						name: 'End',
						nodeType: 'End',
						parent: parentNodeInfo.data.id
					},
					position: {
						x: (childNodeSizeInfo.maxX + childNodeSizeInfo.minX) / 2,
						y: childNodeSizeInfo.maxY + 40
					}
				});

				childNodeSizeInfo.maxY += 40;
			}

			if (info.Transitions && info.Transitions.Transition) {
				const transition = info.Transitions.Transition;

				transition.forEach((info, idx) => {
					edges.push({
						group: `edges`,
						data: {
							id: `edge${idx + 1}_${uuid}`,
							source: `${info['@from']}_${uuid}`,
							target: `${info['@to']}_${uuid}`,
							value: info['@desc'] ? info['@desc'] : '',
							orgData: cloneDeep(info)
						}
					});
				});
			}

			if (parentNodeInfo) return childNodeSizeInfo;
		},
		getDetailInfo(nodeInfo, cy) {
			const rtnObj = {
				title: '',
				size: '',
				bodyHtml: '',
				gridOptions: null
			};

			const data = nodeInfo._private.data;
			const nodeType = data.nodeType;

			if ([`Script`].find(pNodeType => pNodeType === nodeType)) {
				rtnObj.title = nodeType;
				rtnObj.size = '550px';
				rtnObj.bodyHtml += `
                    <div class="form-group">
                        <textarea class="form-control" style="width: 500px; height: 300px;" disabled="disabled">
                            ${escapeHtml(data.orgData.Value['#cdata'])}
                        </textarea>
                    </div>                                        
                `;
			} else if ([`While`, `Case`].find(pNodeType => pNodeType === nodeType)) {
				rtnObj.title = nodeType;
				rtnObj.size = '550px';
				rtnObj.bodyHtml += `
                    <div class="form-group">
                        <textarea class="form-control" style="width: 500px; height: 300px;" disabled="disabled">
                            ${escapeHtml(data.orgData.Condition.Value['#cdata'])}
                        </textarea>         
                    </div>                                           
                `;
			} else {
				rtnObj.title = nodeType;
				rtnObj.size = '550px';
				rtnObj.bodyHtml += `
                    <div class="form-group"style="width: 500px;">
                        <TuiGrid ref="tuiGrid" v-bind="gridOptions" />
                    </div>
                `;
				rtnObj.gridOptions = {
					columns: [],
					data: []
				};

				if ([`ForEach`, `Operation`, `InvokeOperation`, `Fork`, `Activity`, `InvokeActivity`].find(pNodeType => pNodeType === nodeType)) {
					rtnObj.gridOptions.columns = [
						{ name: '@type', header: this.$t(`head.type`), width: '20%' },
						{ name: '@from', header: this.$t(`common.type`), width: '15%', align: 'center' },
						{ name: '@cdata', header: this.$t(`common.property.value`), width: '35%' },
						{ name: '@desc', header: this.$t(`common.desc`), width: '30%' }
					];

					if ('Activity' === nodeType && data.orgData['@id']) {
						rtnObj.title += `(id: ${escapeHtml(data.orgData['@id'])})`;
						rtnObj.titleClickEvt = this.openActivityNewTab.bind(this, escapeHtml(data.orgData['@id']));
					}

					const parameterArr = Array.isArray(data.orgData.Parameter) ? data.orgData.Parameter : [data.orgData.Parameter];

					rtnObj.gridOptions.data = parameterArr.map(info => {
						if (info) {
							info['@cdata'] = info.Value['#cdata'];
							return info;
						} else {
							return { '@type': null, '@from': null, '@cdata': null, '@desc': null };
						}
					});
				} else if ([`FaultHandlers`, `Condition`].find(pNodeType => pNodeType === nodeType)) {
					let typeArr = null;

					if ('FaultHandlers' === nodeType) {
						rtnObj.gridOptions.columns = [
							{ name: '@type', header: this.$t(`common.type`), width: '30%' },
							{ name: '@target', header: this.$t(`dashboard.head.target`), width: '30%' },
							{ name: '@desc', header: this.$t(`common.desc`), width: '40%' }
						];

						typeArr = ['Catch', 'CatchAll'];
					} else if ('Condition' === nodeType) {
						rtnObj.gridOptions.columns = [
							{ name: '@type', header: this.$t(`common.condition`), width: '30%' },
							{ name: '@target', header: this.$t(`dashboard.head.target`), width: '30%' },
							{ name: '@desc', header: this.$t(`common.desc`), width: '40%' }
						];

						typeArr = ['When', 'Others'];
					}

					typeArr.forEach(type => {
						if (!data.orgData[type]) return;

						const orgDataArr = Array.isArray(data.orgData[type]) ? data.orgData[type] : [data.orgData[type]];

						orgDataArr.forEach(tmpData => {
							let target = '';

							cy.edges(`[source = "${data.id}"]`).forEach(info => {
								if (tmpData['@target'] !== info._private.data.target.split('_')[0]) return;

								const nodeInfo = cy.nodes(`[id = "${info._private.data.target}"]`);

								target = `[${nodeInfo[0]._private.data.name}] [${nodeInfo[0]._private.data.orgData['@index']}]`;
							});

							rtnObj.gridOptions.data.push({ '@type': type, '@target': target, '@desc': tmpData['@desc'] });
						});
					});
				} else if ([`Throw`].find(pNodeType => pNodeType === nodeType)) {
					rtnObj.gridOptions.columns = [
						{ name: '@name', header: this.$t(`head.name`), width: '50%' },
						{ name: '@type', header: this.$t(`head.type`), width: '15%' },
						{ name: '@from', header: this.$t(`common.type`), width: '20%' },
						{ name: '@cdata', header: this.$t(`common.property.value`), width: '15%' }
					];

					if (Array.isArray(data.orgData.Parameter)) {
						rtnObj.title += ` ( ${this.$t('common.error.name')} : ${data.orgData.Parameter[0].Value['#cdata']})`;

						rtnObj.gridOptions.data = data.orgData.Parameter.slice(1).map((info, idx) => {
							info['@cdata'] = info.Value['#cdata'];
							info['@name'] = `arg # ${idx + 1} [ ${info['@type']} (${info['@desc']}) ] : (${info['@from']}) ${info.Value['#cdata']}`;

							return info;
						});
					}
				}

				rtnObj.gridOptions.onGridMounted = evt => {
					rtnObj.gridOptions.onGridUpdated(evt);
				};

				rtnObj.gridOptions = makeGridOptions(rtnObj.gridOptions);
			}

			return rtnObj;
		},
		openActivityNewTab(activityId) {
			localStorage.removeItem('selectedMenuPathIdListNewTab');
			localStorage.removeItem('searchObj');

			localStorage.setItem('selectedMenuPathIdListNewTab', JSON.stringify(['100000', '102000', '102060']));
			localStorage.setItem('searchObj', JSON.stringify({ activityId: activityId }));

			window.open(location.href);
		}
	},
	beforeDestroy() {
		delete window.workflowResize;

		document
			.querySelector('#ct')
			.querySelectorAll('script')
			.forEach(info => {
				document.querySelector('#ct').removeChild(info);
			});
	}
};
</script>
